/** @jsxImportSource @emotion/react */
import React, { useMemo, useState, useEffect } from 'react'
import { AgGridReact, AgGridReactProps } from 'ag-grid-react'
import { ColDef, ColGroupDef, SelectionChangedEvent, GridApi, ColumnApi, GridReadyEvent } from 'ag-grid-community'

import 'ag-grid-community/dist/styles/ag-grid.css'
// import 'ag-grid-community/dist/styles/ag-theme-balham.css'
// import 'ag-grid-community/dist/styles/ag-theme-balham-dark.css'
import 'ag-grid-community/dist/styles/ag-theme-material.css'
import './DataGrid.css'
import { merge } from 'lodash'

export interface DataGridApis {
  api: GridApi
  columnApi: ColumnApi
}

export interface DataGridProps<T> extends AgGridReactProps {
  rows: T[]
  columnDefs: (ColDef | ColGroupDef)[]
  onSelectedRowsChanged?: (rows: T[]) => void
  onGridApisReady?: (params: DataGridApis) => void
  rowIdProperty?: keyof T | 'id'
}

export const DataGrid = <T,>({
  rows,
  columnDefs,
  onSelectedRowsChanged,
  onGridApisReady,
  context,
  gridOptions,
  rowIdProperty = 'id',
  ...restProps
}: DataGridProps<T>): JSX.Element => {
  // GRID APIS
  const [grid, setGrid] = useState<DataGridApis>()

  // EVENT HANDLERS

  const handleOnSelectionChanged = ({ api }: SelectionChangedEvent): void => {
    if (!onSelectedRowsChanged) return
    onSelectedRowsChanged(api.getSelectedRows())
  }

  const handleGridReady = ({ api, columnApi }: GridReadyEvent) => {
    setGrid({ api, columnApi })
    if (onGridApisReady) onGridApisReady({ api, columnApi })
  }

  // CONTEXT AND REFRESH

  useEffect(() => {
    if (!grid?.api) return

    // must force a refresh when context changes
    grid.api.refreshCells()
    grid.api.refreshHeader()
  }, [context, grid])

  // OTHER OPTIONS

  const memoizedGridOptions = useMemo(
    // need stable, per instance grid options
    () =>
      merge(
        {
          suppressCellSelection: true,
          defaultColDef: {
            sortable: true,
            resizable: true,
            suppressMovable: true,
          },
        },
        gridOptions
      ),
    [gridOptions]
  )

  // RENDER

  return (
    <div className="ag-theme-material" css={{ width: '100%', height: '100%' }}>
      <AgGridReact
        rowData={rows}
        immutableData
        gridOptions={memoizedGridOptions}
        columnDefs={columnDefs}
        onSelectionChanged={handleOnSelectionChanged}
        animateRows
        containerStyle={{ width: '100%', height: '100%' }}
        getRowNodeId={(data) => data[rowIdProperty]}
        context={context}
        // frameworkComponents={frameworkComponents}
        // isRowSelectable={isRowSelectable}
        onGridReady={handleGridReady}
        {...restProps}
      />
    </div>
  )
}
