import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

import { TranslationGroupInput } from './createTranslationGroup'

interface ExistingTranslationGroupInput extends Omit<TranslationGroupInput, 'translationId'> {
  id: number
}

export const updateTranslationGroupMutation = gql`
  mutation ($id: Int!, $translationGroup: TranslationGroupInput!) {
    updateTranslationGroup(id: $id, translationGroup: $translationGroup)
  }
`

export const updateTranslationGroup =
  (api: Api, invalidate: InvalidateFn) =>
  async (translationGroup: ExistingTranslationGroupInput): Promise<true> => {
    const { id, ...restGroup } = translationGroup
    const data = await api.mutation<{
      updateTranslationGroup: number // NOTE: number of records changed
    }>(updateTranslationGroupMutation, { id, translationGroup: restGroup })

    if (!data.updateTranslationGroup) throw new ApiError('translation group did not update')

    invalidate('translations')
    return true
  }
