import gql from 'graphql-tag'
import log from 'loglevel'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

export const finalizePendingBoxMutation = gql`
  mutation ($boxId: Int!) {
    finalizePendingBox(boxId: $boxId) {
      success
      stats
      errors
      warnings
    }
  }
`

export const finalizePendingBox =
  (api: Api, invalidate: InvalidateFn) =>
  async ({ boxId }: { boxId: number }): Promise<true> => {
    const data = await api.mutation<{
      finalizePendingBox: { success: boolean; errors: string[] }
    }>(finalizePendingBoxMutation, { boxId })

    const { success, errors } = data.finalizePendingBox

    if (!success) {
      if (!errors || errors.length === 0) throw new ApiError('unknown error')
      log.warn(data.finalizePendingBox)
      throw new ApiError('submission data contained errors')
    }

    invalidate('boxes')
    invalidate('boxSamples')
    return true
  }
