import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

interface BoxUpdate {
  boxId: number
  boxStateId: number
  measurements: boolean
  midges: boolean
  description: string
  modelIds: number[]
  receivedDate: string | null
}

export const updateBoxMutation = gql`
  mutation (
    $boxId: Int!
    $boxStateId: Int!
    $customerId: Int!
    $alias: String
    $description: String
    $measurements: Boolean!
    $midges: Boolean!
    $modelIds: [Int]
    $receivedDate: String
  ) {
    setBoxInfo(
      boxId: $boxId
      boxStateId: $boxStateId
      customerId: $customerId
      alias: $alias
      description: $description
      measurements: $measurements
      midges: $midges
      models: $modelIds
      boxReceivedDate: $receivedDate
    )
  }
`

export const updateBox =
  (api: Api, invalidate: InvalidateFn) =>
  async (boxUpdate: BoxUpdate): Promise<true> => {
    const data = await api.mutation<{
      setBoxInfo: number // NOTE: count of records changed
    }>(updateBoxMutation, { ...boxUpdate })

    if (!data.setBoxInfo) throw new ApiError('box did not update')

    invalidate('boxes')
    invalidate('samples')
    return true
  }
