/** @jsxImportSource @emotion/react */
import React, { useEffect, useState } from 'react'
import { atom, selector, useRecoilState, useRecoilValue } from 'recoil'
import { countBy } from 'lodash'
import MuiButton from '@mui/material/Button'

import {
  ConfirmationDialog,
  MyUser,
  Project,
  Title,
  ReadOnlyField,
  useApi,
  useNotifications,
  useUpdatableState,
  formatUtcDateTimeToAppDate,
  TextField,
  AutocompleteField,
  CheckboxField,
  Menu,
  Divider,
} from '@namcbugdb/react-common'
import * as calls from '../../data/calls'
import * as selectors from '../../data/selectors'

import { AccordionPanel } from '@namcbugdb/react-common'
import { ProjectSamples } from './ProjectSamples'
import { ProjectSitesMapPreview } from './ProjectSitesMapPreview'

import { ConfirmNavigateAway, useNavigate } from '../AppRouter'
import { ModelsField } from '../ModelsField'

export interface ProjectDetailProps {
  project: Project | null
}

interface Fields {
  name: string
  contactId: number
  isPrivate: boolean
  description: string
  modelIds: number[]
}

type FieldErrors = Partial<Record<keyof Fields, boolean>> | undefined

interface State {
  editMode: 'stopped' | 'started' | 'stopping' | 'deleting'
  isDirty: boolean
  values: Fields
  lastValues?: Fields
}

const isExpandedState = atom<boolean>({
  key: 'ProjectDetail/isExpandedState',
  default: true,
})

const selectUserOptions = selector({
  key: 'ProjectDetail/selectUserOptions',
  get: async ({ get }) => {
    const users = get(selectors.selectUsers)

    const userOptions = users.map(({ id: value, fullName, organizationName }) => ({
      value,
      label: `${fullName} (${organizationName})`,
    }))

    const labelCount = countBy(userOptions, 'label')

    return userOptions.map(({ value, label }) => ({
      value,
      label: labelCount[label] > 1 ? `${label} [${value}]` : label,
    }))
  },
})

const getInitialState = ({ project, user }: Pick<ProjectDetailProps, 'project'> & { user: MyUser }): State => ({
  editMode: project ? 'stopped' : 'started',
  isDirty: false,
  values: {
    name: project?.name ?? '',
    contactId: project?.contactId ?? user.id,
    isPrivate: project?.isPrivate ?? true,
    description: project?.description ?? '',
    modelIds: project?.modelIds ?? [],
  },
})

export const ProjectDetail = ({ project }: ProjectDetailProps): JSX.Element => {
  const api = useApi()
  const invalidate = selectors.useInvalidate()
  const notifications = useNotifications()
  const navigate = useNavigate()
  const user = useRecoilValue(selectors.selectMyUser)

  const isNewProject = !project

  // STATE

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [state, setState, updateState] = useUpdatableState<State>(getInitialState({ project, user }), [project])
  const [isPanelExpanded, setIsPanelExpanded] = useRecoilState(isExpandedState)
  const [isConfirmDeleteOpen, setIsConfirmDeleteOpen] = useState(false)

  const { values, editMode, isDirty } = state

  const errors: FieldErrors = (() => {
    const result: FieldErrors = {
      name: !values.name.trim(),
    }
    if (!Object.values(result).some((value) => value)) return undefined
    return result
  })()

  const isStopped = editMode === 'stopped'
  const isEditing = editMode === 'started'
  const isSaving = editMode === 'stopping'
  const isDeleting = editMode === 'deleting'

  // EFFECTS

  useEffect(() => {
    if (isEditing) setIsPanelExpanded(true)
  }, [isEditing])

  // HANDLERS

  const setFieldValue = (fieldName: keyof Fields, value: unknown) => {
    updateState((draft) => {
      Object.assign(draft.values, { [fieldName]: value })
      draft.isDirty = true
    })
  }

  const handleEdit = () => {
    updateState((draft) => {
      draft.editMode = 'started'
      draft.lastValues = draft.values
    })
  }

  const handleCancel = () => {
    updateState((draft) => {
      if (!draft.lastValues) throw new Error('no values to revert to')
      draft.editMode = 'stopped'
      draft.values = draft.lastValues
      draft.isDirty = false
    })
  }

  const handleSave = async () => {
    try {
      updateState((draft) => {
        draft.editMode = 'stopping'
      })
      const projectInput = {
        name: values.name.trim(),
        isPrivate: values.isPrivate,
        description: values.description.trim(),
        contactId: values.contactId,
        modelIds: values.modelIds,
      }
      if (isNewProject) {
        // new project
        const projectId = await calls.createProject(api, invalidate)(projectInput)
        updateState((draft) => {
          draft.editMode = 'stopped'
          draft.isDirty = false
        })
        window.setTimeout(() => {
          navigate.projectDetail({ projectId }, true) // let state update
        })
      } else {
        // existing project
        await calls.updateProject(
          api,
          invalidate
        )({
          ...projectInput,
          id: project.id,
        })
        updateState((draft) => {
          draft.editMode = 'stopped'
          draft.isDirty = false
        })
      }
    } catch (err) {
      if ((err as Error).message === 'duplicate key value violates unique constraint "projects_project_name_key"') {
        notifications.error(`Failed to save: name "${values.name}" already in use`)
      } else {
        notifications.error(`Failed to save: ${err}`)
      }
      updateState((draft) => {
        draft.editMode = 'started'
      })
    }
  }

  const handleDelete = async () => {
    try {
      if (!project) throw new Error('No project.')
      updateState((draft) => {
        draft.editMode = 'deleting'
      })
      await calls.deleteProject(api, invalidate)(project)
      notifications.info(`Deleted project ${project.name}`)
      navigate.projects()
    } catch (err) {
      notifications.error(`Failed to delete: ${err}`)
      updateState((draft) => {
        draft.editMode = 'stopped'
      })
    }
  }

  // RENDER

  return (
    <>
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        <AccordionPanel
          value={isPanelExpanded}
          onChange={setIsPanelExpanded}
          locked={isEditing}
          summary={
            !isEditing ? (
              <Title>
                Project {values.name} <Title.Subtitle>({project?.sampleCount ?? 0} samples)</Title.Subtitle>
              </Title>
            ) : (
              <div css={{ display: 'flex', alignItems: 'baseline' }}>
                <Title>Project&nbsp;</Title>
                <TextField
                  value={values.name}
                  onChange={(value) => setFieldValue('name', value)}
                  error={errors?.name}
                  css={{ width: '30em' }}
                  InputProps={{ sx: { fontWeight: '700' } }} // roughly match <Title>
                />
              </div>
            )
          }
          actions={
            <div css={{ display: 'flex', gap: '0.5em' }}>
              {(isStopped || isDeleting) && (
                <Menu
                  label="Actions"
                  disabled={isDeleting}
                  items={[
                    {
                      key: 'edit',
                      label: 'Edit Project',
                      onClick: handleEdit,
                    },
                    Divider,
                    {
                      key: 'delete',
                      label: 'Delete Project',
                      onClick: () => {
                        setIsConfirmDeleteOpen(true)
                      },
                    },
                  ]}
                ></Menu>
              )}
              {(isEditing || isSaving) && (
                <>
                  {!isNewProject && (
                    <MuiButton onClick={handleCancel} variant="outlined" disabled={isSaving}>
                      Cancel
                    </MuiButton>
                  )}
                  <MuiButton onClick={handleSave} variant="contained" disabled={!isDirty || isSaving || !!errors}>
                    Save
                  </MuiButton>
                </>
              )}
            </div>
          }
        >
          <div
            css={{
              flex: 1,
              display: 'grid',
              gap: '0.75em',
              gridTemplateColumns: 'auto 1fr auto',
              gridTemplateAreas: `
              "id              contactId          map"
              "createdDate     isPrivate          map"
              ".               modelIds           map"
              ".               description        map"
              ".               .                  map"
            `,
            }}
          >
            <ReadOnlyField gridArea="id" value={project?.id ?? ''} label="Project ID" />
            <ReadOnlyField
              gridArea="createdDate"
              value={formatUtcDateTimeToAppDate(project?.createdDate ?? null)}
              label="Created On"
            />
            <AutocompleteField
              gridArea="contactId"
              value={values.contactId}
              onChange={(value) => setFieldValue('contactId', value)}
              valueLabel={project?.contactName ?? ''}
              optionsSelector={selectUserOptions}
              label="Contact Name"
              readOnly={!isEditing}
            />
            <CheckboxField
              gridArea="isPrivate"
              value={!values.isPrivate}
              onChange={(value) => setFieldValue('isPrivate', !value)}
              label="Project samples appear in the public web map"
              readOnly={!isEditing}
            />
            <ModelsField
              gridArea="modelIds"
              value={values.modelIds}
              onChange={(value) => setFieldValue('modelIds', value)}
              label="Models"
              readOnly={!isEditing}
            />
            <TextField
              gridArea="description"
              value={values.description}
              onChange={(value) => setFieldValue('description', value)}
              label="Description"
              readOnly={!isEditing}
              multiline
              minRows={3}
            />
            <div css={{ gridArea: 'map', minHeight: '300px' }}>
              {!isEditing && project && <ProjectSitesMapPreview projectId={project.id} projectName={values.name} />}
            </div>
          </div>
        </AccordionPanel>
        {project && <ProjectSamples projectId={project.id} projectName={values.name} canModify={isStopped} />}
      </div>
      <ConfirmationDialog
        title="Confirm Delete Project"
        isOpen={isConfirmDeleteOpen}
        onClose={() => {
          setIsConfirmDeleteOpen(false)
        }}
        onConfirm={handleDelete}
      >
        Are you sure you want to delete this project?
      </ConfirmationDialog>
      {isDirty && <ConfirmNavigateAway message="Some changes are not saved. Are you sure you want to leave?" />}
    </>
  )
}
