import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { Organism } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

export const deleteOrganismMutation = gql`
  mutation ($organismId: Int!) {
    deleteOrganism(organismId: $organismId)
  }
`

export const deleteOrganism =
  (api: Api, invalidate: InvalidateFn) =>
  async (organism: Organism): Promise<true> => {
    const data = await api.mutation<{ deleteOrganism: number }>(deleteOrganismMutation, {
      organismId: organism.id,
    })

    if (!data.deleteOrganism) throw new ApiError('organism record did not delete')

    invalidate('samples')
    return true
  }
