import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

import { OrganizationInput } from './createOrganization'

interface ExistingOrganizationInput extends OrganizationInput {
  id: number
}

export const updateOrganizationMutation = gql`
  mutation ($id: Int!, $organization: OrganizationInput!) {
    updateOrganization(id: $id, organization: $organization)
  }
`

export const updateOrganization =
  (api: Api, invalidate: InvalidateFn) =>
  async (organization: ExistingOrganizationInput): Promise<true> => {
    const { id, name: organizationName, typeId: organizationTypeId, ...restOrganization } = organization
    const data = await api.mutation<{
      updateOrganization: number // NOTE: id of organization
    }>(updateOrganizationMutation, {
      id,
      organization: {
        organizationName,
        organizationTypeId,
        ...restOrganization,
      },
    })

    if (!data.updateOrganization) throw new ApiError('organization did not update')

    invalidate('organizations')
    return true
  }
