/** @jsxImportSource @emotion/react */
import React, { useMemo } from 'react'
import { useRecoilState, atom, useRecoilValue } from 'recoil'
import MuiButton from '@mui/material/Button'
import { BoxStates, GrantAction, NAMCResource } from '@namcbugdb/common'
// import log from 'loglevel'

import { DataGrid, renderers, DataGridRecordCount } from '../DataGrid'
import * as selectors from '../../data/selectors'
import {
  Box,
  SelectField,
  FilterChips,
  Filter,
  withSuspense,
  TextFilterField,
  useTextFilterField,
} from '@namcbugdb/react-common'

import { privilegedColumnDefs } from './columnDefs'
import { useNavigate } from '../AppRouter'
import { Fallback } from './Fallback'

export interface PrivilegedBoxesListingProps {
  boxes: Box[]
  boxStates: BoxStates[]
  isLoading: boolean
}

export interface BoxRow extends Box {
  boxId: {
    label: number
    onClick: () => void
  }
  customer: {
    label: string
    onFilter: () => void
    disabled: boolean
  }
}

const ANY_BOX_STATE = 'ANY'
const ACTIVE_BOX_STATE = 'Active'

const boxStateNameFilterState = atom({
  key: 'PrivilegedBoxesListing/boxStateNameFilter',
  default: ACTIVE_BOX_STATE,
})

const textFilterState = atom({
  key: 'PrivilegedBoxesListing/textFilter',
  default: '',
})

const addedFiltersState = atom<Filter<Box>[]>({
  key: 'PrivilegedBoxesListing/addedFilters',
  default: [],
})

export const PrivilegedBoxesListing = withSuspense(
  ({ boxes, boxStates, isLoading }: PrivilegedBoxesListingProps): JSX.Element => {
    const navigate = useNavigate()
    const user = useRecoilValue(selectors.selectMyUser)

    // STATE AND REFS

    const [boxStateNameFilter, setBoxStateNameFilter] = useRecoilState(boxStateNameFilterState)
    const [textFilter, setTextFilter, textFilterDebounced, clearTextFilter] = useTextFilterField(
      useRecoilState(textFilterState)
    )
    const [addedFilters, setAddedFilters] = useRecoilState(addedFiltersState)

    // FILTERS

    const filters = useMemo(() => {
      const result: Filter<Box>[] = []

      if (boxStateNameFilter !== ANY_BOX_STATE) {
        result.push({
          id: 'state',
          label: boxStateNameFilter,
          filterFn: (box) => box.boxState === boxStateNameFilter,
          onDelete: () => {
            setBoxStateNameFilter(ANY_BOX_STATE)
          },
        })
      }

      if (textFilterDebounced) {
        if (textFilterDebounced.match(/^\d+$/)) {
          const targetBoxId = Number(textFilterDebounced)
          result.push({
            id: 'text',
            label: `Box ${textFilterDebounced}`,
            filterFn: (box) => box.id === targetBoxId,
            onDelete: clearTextFilter,
          })
        } else {
          result.push({
            id: 'text',
            label: `Customer contains "${textFilterDebounced}"`,
            filterFn: ({ customerName }) => customerName.toLowerCase().includes(textFilterDebounced.toLowerCase()),
            onDelete: clearTextFilter,
          })
        }
      }

      result.push(...addedFilters)

      return result
    }, [boxStateNameFilter, textFilterDebounced, addedFilters])

    const rows = useMemo<BoxRow[]>(() => {
      const filtered = filters.reduce<Box[]>((acc, { filterFn }) => acc.filter(filterFn), boxes)
      return filtered.map((box) => {
        const filterId = `filter/${String(box.customerId)}`
        return {
          ...box,
          boxId: {
            label: box.id,
            onClick: () => navigate.boxDetail({ boxId: box.id }),
          },
          customer: {
            label: box.customerName,
            onFilter: () => {
              clearTextFilter()
              setAddedFilters([
                {
                  id: filterId,
                  label: box.customerName,
                  filterFn: ({ customerId }) => customerId === box.customerId,
                  onDelete: () => setAddedFilters((value) => value.filter(({ id }) => id !== filterId)),
                },
              ])
            },
            disabled: !!addedFilters.find(({ id }) => id === filterId),
          },
        }
      })
    }, [boxes, filters])

    // RENDER

    const boxStateOptions = useMemo(() => {
      return [
        { value: ANY_BOX_STATE, label: 'Any' },
        ...boxStates.filter(({ isActive }) => isActive).map(({ name }) => ({ value: name, label: name })),
      ]
    }, [boxStates])

    return (
      <>
        <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
          <div css={{ display: 'flex', gap: '0.5em' }}>
            <SelectField
              value={boxStateNameFilter}
              onChange={(value) => setBoxStateNameFilter(String(value))}
              options={boxStateOptions}
              label="Box State"
            />
            <TextFilterField value={textFilter} onChange={setTextFilter} label="Box ID or Customer" />
            {user.hasPermission(NAMCResource.submissions, GrantAction.createAny) && (
              <div css={{ marginLeft: 'auto' }}>
                <MuiButton
                  variant="outlined"
                  onClick={() => {
                    navigate.boxSubmission()
                  }}
                >
                  Create New Submission
                </MuiButton>
              </div>
            )}
          </div>
          <div css={{ minHeight: '2em', marginTop: '0.75em', display: 'flex' }}>
            <FilterChips filters={filters} joinLabel="and" />
            <DataGridRecordCount total={boxes.length} visible={rows.length} descriptor="box" isLoading={isLoading} />
          </div>
          <div css={{ flex: 1 }}>
            <DataGrid<BoxRow> rows={rows} columnDefs={privilegedColumnDefs} frameworkComponents={renderers} />
          </div>
        </div>
      </>
    )
  },
  Fallback
)
