/** @jsxImportSource @emotion/react */
import React, { useEffect, useMemo, useState } from 'react'
import * as turf from '@turf/turf'
import MuiButton from '@mui/material/Button'
import MuiSkeleton from '@mui/material/Skeleton'
import MuiDialog from '@mui/material/Dialog'
import MuiDialogActions from '@mui/material/DialogActions'
import MuiDialogContent from '@mui/material/DialogContent'
import MuiDialogTitle from '@mui/material/DialogTitle'
import { noop, uniq } from 'lodash'
import { useRecoilValue } from 'recoil'
// import log from 'loglevel'

import { DataGrid, DataGridRecordCount } from '../DataGrid'
import { pendingBoxColumnDefs } from './columnDefs'
import * as calls from '../../data/calls'
import * as selectors from '../../data/selectors'
import { mapboxToken } from '../../config'
import {
  Point,
  Site,
  ConfirmationDialog,
  Filter,
  FilterChips,
  ReadOnlyField,
  Title,
  formatUtcDateTimeToAppDate,
  pluralize,
  useNotifications,
  useApi,
  SelectField,
  appVersion,
  useUpdatableState,
  LinearProgress,
  getSortedSitesDistances,
  SitesMap,
  SitesMapSidePanel,
  useSitesMapController,
  PinMarker,
  TextField,
  useTextFilterField,
  CheckboxField,
} from '@namcbugdb/react-common'
import { Submission } from '../../types'

import { SiteDetailPanel, SITE_DETAIL_PANEL_TRANSITION_MS } from '../SiteDetailPanel'
import { SampleDetailPanel } from './SampleDetailPanel'

import { EditSiteDialog } from '../EditSiteDialog'
import { MatchedSiteLine } from './MatchedSiteLine'

import { ConfirmNavigateAway, useNavigate } from '../AppRouter'
import * as renderers from './renderers'

import { getAutoMatchStatus, getNextNewSiteId, isNewSite, makeSiteFromSample } from './util'

export const SAMPLE_PIN_COLOR = '#0055dd'
const SUGGESTED_FILTER_ID = 'suggested'

export interface PendingBoxReviewProps {
  submission: Submission
}

export interface ReviewableSample {
  sampleIndex: number
  isSelected: boolean
  customerSiteCode: string
  latitude: number
  longitude: number
  onEdit: () => void
  siteName: string
  databaseSiteId?: number
  suppressAutoMatch: boolean
  habitat: string
  ecosystem: string
  waterbodyName: string
  waterbodyType: string
}

interface AndFilter<T> extends Filter<T> {
  filterAllFn: (rows: T[]) => T[]
}

const sampleStatusOptions = [
  { value: 'any', label: 'Any' },
  { value: 'unmatched', label: 'Unmatched' },
  { value: 'matched', label: 'Matched' },
]

export const PendingBoxReview = ({ submission: submissionProp }: PendingBoxReviewProps): JSX.Element => {
  const api = useApi()
  const notifications = useNotifications()
  const deferredInvalidate = selectors.useDeferredInvalidate()
  const navigate = useNavigate()

  const allExistingSites = useRecoilValue(selectors.allSitesAtom)
  const allExistingSitesStatus = useRecoilValue(selectors.allSitesStatusAtom)
  const fetchSiteDetailBySiteIdOptimized = selectors.useFetchSiteDetailBySiteIdOptimized()
  const waterbodyTypes = useRecoilValue(selectors.selectWaterbodyTypes)

  // STATE

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [submission, setSubmission, updateSubmission] = useUpdatableState(
    {
      samples: submissionProp.samples,
      newSites: submissionProp.newSites ?? [],
      isDirty: false,
    },
    [submissionProp]
  )
  const { boxId, templateVersion, organizationName, createdDate } = submissionProp
  const { samples, newSites, isDirty } = submission

  const [selectedSampleIndex, setSelectedSampleIndex] = useState<number | undefined>(undefined)
  const [textFilter, setTextFilter, textFilterDebounced, resetTextFilter] = useTextFilterField(useState(''))
  const [suggestedSitesFilter, setSuggestedSitesFilter] = useState<AndFilter<Site> | null>(null)
  const [selectedSiteId, setSelectedSiteId] = useState<number | undefined>(undefined)
  const [sampleStatusFilter, setSampleStatusFilter] = useState<string>('any')
  const [isEditingSite, setIsEditingSite] = useState<boolean>(false)
  const [finalizing, setFinalizing] = useState<false | 'prompt' | 'finalizing'>(false)
  const [canDragMarker, setCanDragMarker] = useState<boolean>(false)
  const [isConfirmAutoCreateOpen, setIsConfirmAutoCreateOpen] = useState(false)

  // SITES

  const fetchAllExistingSitesOptimized = selectors.useFetchAllSitesOptimized()

  useEffect(() => {
    if (allExistingSitesStatus === selectors.FETCH_ALL_SITES_STATUS.unfetched) fetchAllExistingSitesOptimized()
  }, [allExistingSitesStatus])

  const allSites = useMemo(() => [...allExistingSites, ...newSites], [allExistingSites, newSites])

  const isFetching = allExistingSitesStatus !== selectors.FETCH_ALL_SITES_STATUS.fetched
  const allSitesNormalized = useMemo<Site[]>(
    () => allSites.map(({ name, ...rest }) => ({ name: name.toLowerCase(), ...rest })),
    [allSites]
  )

  // SAMPLES

  const sampleFilters = useMemo(() => {
    const result: Filter<ReviewableSample>[] = []

    switch (sampleStatusFilter) {
      case 'unmatched':
        result.push({
          id: 'matchStatus',
          label: 'Unmatched',
          filterFn: ({ databaseSiteId, sampleIndex }) => !databaseSiteId || sampleIndex === selectedSampleIndex,
          onDelete: () => {
            setSampleStatusFilter('any')
          },
        })
        break
      case 'matched':
        result.push({
          id: 'matchStatus',
          label: 'Matched',
          filterFn: ({ databaseSiteId, sampleIndex }) => !!databaseSiteId || sampleIndex === selectedSampleIndex,
          onDelete: () => {
            setSampleStatusFilter('any')
          },
        })
    }

    return result
  }, [sampleStatusFilter, selectedSampleIndex])

  const setSuggestedSitesFilterToSample = (sample: ReviewableSample) => {
    const { sampleIndex, customerSiteCode } = sample
    setSuggestedSitesFilter({
      id: SUGGESTED_FILTER_ID,
      label: `Suggested for Sample ${sampleIndex}`,
      filterFn: () => true,
      filterAllFn: (sites: Site[]) => {
        const customerSiteCodeLowercase = customerSiteCode.toLowerCase()
        const exactNameSite = sites.find(({ name }) => name.toLowerCase() === customerSiteCodeLowercase)
        if (exactNameSite) return [exactNameSite]

        const closeSitesDistances = getSortedSitesDistances(sites, sample)

        const closest = closeSitesDistances[0]

        const relativelyCloseDistance = Math.min(
          Math.max(closest.distance * 3, 0.1), // greater of 3 x closest or 100m
          5 // max-out at 5km
        )

        return closeSitesDistances.filter(({ distance }) => distance < relativelyCloseDistance).map(({ site }) => site)
      },
      onDelete: () => setSuggestedSitesFilter(null),
    })
  }

  const reviewableSamples = useMemo<ReviewableSample[]>(
    () =>
      samples.map((submissionSample) => {
        const {
          sampleIndex,
          siteId: customerSiteCode,
          latitude,
          longitude,
          habitat,
          ecosystem,
          nhdType: waterbodyType,
          waterbodyName,
          databaseSiteId,
          suppressAutoMatch,
        } = submissionSample
        if (typeof sampleIndex !== 'number' || sampleIndex === 0)
          throw new Error('sampleIndex is not a number or is zero (not allowed).')

        const matchedSite = databaseSiteId && allSites.find(({ id }) => id === databaseSiteId)

        return {
          isSelected: sampleIndex === selectedSampleIndex,
          sampleIndex,
          customerSiteCode: String(customerSiteCode),
          latitude: Number(latitude),
          longitude: Number(longitude),
          databaseSiteId: databaseSiteId ? Number(databaseSiteId) : undefined,
          suppressAutoMatch: !!suppressAutoMatch,
          onEdit: () => {
            setSelectedSampleIndex(sampleIndex)
          },
          siteName: matchedSite ? matchedSite.name : '',
          habitat: habitat ? String(habitat) : '',
          ecosystem: ecosystem ? String(ecosystem) : '',
          waterbodyType: waterbodyType ? String(waterbodyType) : '',
          waterbodyName: waterbodyName ? String(waterbodyName) : '',
        }
      }),
    [allSites, samples, selectedSampleIndex]
  )

  const filteredSamples = useMemo(
    () => sampleFilters.reduce<ReviewableSample[]>((acc, { filterFn }) => acc.filter(filterFn), reviewableSamples),
    [sampleFilters, reviewableSamples]
  )

  // SELECTED SAMPLE

  const selectedSample =
    selectedSampleIndex && reviewableSamples.find(({ sampleIndex }) => sampleIndex === selectedSampleIndex)

  useEffect(() => {
    if (!selectedSample) {
      setSuggestedSitesFilter(null)
      return
    }
    setSelectedSiteId(selectedSample.databaseSiteId)
    setSuggestedSitesFilterToSample(selectedSample)
  }, [selectedSampleIndex])

  // SELECTED & MATCHED SITE

  const sparseSitesDetail = useRecoilValue(selectors.sparseSitesDetailAtom)

  const selectedSite = useMemo(() => {
    // get best selected site data
    if (!selectedSiteId) return undefined
    return sparseSitesDetail.find(({ id }) => id === selectedSiteId) || allSites.find(({ id }) => id === selectedSiteId)
  }, [allSites, sparseSitesDetail, selectedSiteId])

  const matchedSite =
    selectedSample && selectedSample.databaseSiteId && allSites.find(({ id }) => id === selectedSample.databaseSiteId)

  useEffect(() => {
    if (!selectedSite || isNewSite(selectedSite)) return
    fetchSiteDetailBySiteIdOptimized(selectedSiteId)
  }, [selectedSiteId])

  // SITE AUTO-MATCHING

  useEffect(() => {
    if (isFetching) return // still awaiting all sites

    const autoMatchStatus = getAutoMatchStatus({ reviewableSamples, allSitesNormalized })
    const eligibleAutoMatches = autoMatchStatus.filter(({ status, suppressed }) => status === 'eligible' && !suppressed)
    const newMatchCount = eligibleAutoMatches.length

    if (newMatchCount === 0) return

    updateSubmission((draft) => {
      eligibleAutoMatches.forEach(({ sampleIndex, databaseSiteId }) => {
        const draftSample = draft.samples.find((sample) => sample.sampleIndex === sampleIndex)
        if (!draftSample) throw new Error('Sample not found.')
        draftSample.databaseSiteId = databaseSiteId
      })
      draft.isDirty = true
    })
    notifications.info(
      `Auto-matched ${newMatchCount} ${pluralize(newMatchCount, 'sample')} with ${pluralize(newMatchCount, 'site')}`
    )
  }, [reviewableSamples, allSitesNormalized])

  // SITES FILTERING

  const siteFilters = useMemo(() => {
    const result: AndFilter<Site>[] = []

    if (suggestedSitesFilter) result.push(suggestedSitesFilter)

    if (textFilterDebounced) {
      result.push({
        id: 'text',
        label: `Name contains "${textFilterDebounced}"`,
        filterFn: () => true,
        filterAllFn: (sites: Site[]) =>
          sites.filter(({ name }) => name.toLowerCase().includes(textFilterDebounced.toLowerCase())),
        onDelete: resetTextFilter,
      })
    }

    return result
  }, [suggestedSitesFilter, textFilterDebounced])

  const filteredSites = useMemo<Site[]>(() => {
    if (siteFilters.length === 0) return allSites
    const filtered = siteFilters.reduce<Site[]>((acc, filter) => uniq([...acc, ...filter.filterAllFn(allSites)]), [])
    return matchedSite ? uniq([...filtered, matchedSite]) : filtered // ensure matched site always included
  }, [allSites, siteFilters])

  // MAP CONTROLLER, FITTING

  const onSiteClick = (site: Site) => {
    if (matchedSite && matchedSite.id !== site.id) {
      notifications.info(
        'Cannot select site: sample already has a confirmed match. (To match to this site, unmatch existing site first.)'
      )
      return
    }
    setSelectedSiteId(site.id)
  }

  const controller = useSitesMapController({ sites: filteredSites, onSiteClick })

  useEffect(() => {
    const { isViewportReady, centerSite, fitSites } = controller
    if (siteFilters.length === 0) return // preserve viewed area when removing all filters
    if (!isViewportReady) return // viewport not ready, don't fit yet (desired behaviour for all-sites initial load)
    if (filteredSites.length === 0 && !selectedSample) return // nothing to fit

    if (filteredSites.length === 0 && selectedSample) {
      // center selected sample, zoom tighter than IDEAL_CLOSE_ZOOM
      centerSite(selectedSample, { zoom: 14, suppressZoomOut: true })
    } else if (filteredSites.length === 1 && !selectedSample) {
      // center single site, don't over-zoom
      centerSite(filteredSites[0])
    } else {
      const combinedPoints: Point[] = filteredSites
      if (selectedSample) combinedPoints.push(selectedSample)
      fitSites(combinedPoints) // fit all
    }
  }, [
    siteFilters,
    selectedSampleIndex, // do not use selectedSample so that site editing does not trigger effect
  ])

  // ACTIONS

  const handleSave = async () => {
    try {
      await calls.updateSampleSubmission(
        api,
        deferredInvalidate
      )({
        boxId,
        data: {
          templateVersion,
          appVersion,
          samples,
          newSites,
        },
      })
      updateSubmission((draft) => {
        draft.isDirty = false
      })
    } catch (err) {
      notifications.error(`Failed to save updated submission: ${err}`)
    }
  }

  const handleFinalize = async () => {
    try {
      setFinalizing('finalizing')
      await calls.finalizePendingBox(api, deferredInvalidate)({ boxId })
      navigate.boxDetail({ boxId })
    } catch (err) {
      setFinalizing('prompt')
      notifications.error(`Failed to finalize box submission: ${err}`)
    }
  }

  const handleMatch = () => {
    updateSubmission((draft) => {
      const draftSample = draft.samples.find(({ sampleIndex }) => sampleIndex === selectedSampleIndex)
      if (!draftSample || !selectedSite) throw new Error('Sample or Site not found.')
      draftSample.databaseSiteId = selectedSiteId
      draft.isDirty = true
    })
  }

  const handleUnmatch = () => {
    updateSubmission((draft) => {
      const draftSample = draft.samples.find(({ sampleIndex }) => sampleIndex === selectedSampleIndex)
      if (!draftSample) throw new Error('Sample not found.')
      draftSample.databaseSiteId = null
      draftSample.suppressAutoMatch = true
      draft.isDirty = true
    })
  }

  const handleNewSiteCreate = () => {
    const newSiteId = getNextNewSiteId(newSites)
    const fromSample = reviewableSamples.find(({ sampleIndex }) => sampleIndex === selectedSampleIndex)

    if (!fromSample) throw new Error('Sample not found.')
    const newSite = makeSiteFromSample({
      fromSample,
      newSiteId,
      allSitesNormalized,
      waterbodyTypes,
    })

    updateSubmission((draft) => {
      // update site
      const draftSample = draft.samples.find(({ sampleIndex }) => sampleIndex === selectedSampleIndex)
      if (!draftSample) throw new Error('Sample not found.')
      draftSample.databaseSiteId = newSite.id

      // add new site
      draft.newSites.push(newSite)

      draft.isDirty = true
    })

    setSelectedSiteId(newSiteId)
  }

  const handleAllRemainingSitesCreate = () => {
    let newSiteId = Math.min(0, ...newSites.map(({ id }) => id)) - 1
    const updatedAllSitesNormalized = [...allSitesNormalized]
    let createdCount = 0

    let autoMatchStatus = getAutoMatchStatus({
      reviewableSamples,
      allSitesNormalized,
    })

    updateSubmission((draft) => {
      reviewableSamples.forEach((fromSample) => {
        const { databaseSiteId, sampleIndex } = fromSample
        if (databaseSiteId) return // already has match

        const draftSample = draft.samples.find((sample) => sample.sampleIndex === sampleIndex)
        if (!draftSample) throw new Error('Sample not found.')

        draftSample.suppressAutoMatch = false // ensure sample prepared for auto-match

        const sampleAutoMatchStatus = autoMatchStatus.find(
          (autoMatchStatus) => autoMatchStatus.sampleIndex === sampleIndex
        )
        if (!sampleAutoMatchStatus) throw new Error('Could not determine sample auto-match status.')

        if (sampleAutoMatchStatus.status !== 'ineligible') return // either already matched, will be matched, or has ambiguous matches

        const newSite = makeSiteFromSample({
          fromSample,
          allSitesNormalized: updatedAllSitesNormalized,
          waterbodyTypes,
          newSiteId,
        })

        // refresh auto-match status
        updatedAllSitesNormalized.push({ ...newSite, name: newSite.name.toLowerCase() })
        autoMatchStatus = getAutoMatchStatus({
          reviewableSamples,
          allSitesNormalized: updatedAllSitesNormalized,
        })

        // decrement next site ID
        newSiteId -= 1

        // add new site
        draft.newSites.push(newSite)
        createdCount += 1
      })

      draft.isDirty = true
    })

    notifications.info(`Auto-created ${createdCount} ${pluralize(createdCount, 'site')}`)
  }

  const handleNewSiteEdit = () => {
    setIsEditingSite(true)
  }

  const handleNewSiteDiscard = () => {
    handleUnmatch()
    updateSubmission((draft) => {
      const index = draft.newSites.findIndex(({ id }) => id === selectedSiteId)
      if (index === -1) throw new Error('New site not found.')
      draft.newSites.splice(index, 1)
      draft.isDirty = true
    })
  }

  const handleNewSiteSave = async (updatedSite: Site) => {
    updateSubmission((draft) => {
      const index = draft.newSites.findIndex(({ id }) => id === updatedSite.id)
      if (index === -1) throw new Error('New site not found.')
      draft.newSites[index] = updatedSite
      draft.isDirty = true
    })
    setIsEditingSite(false)
  }

  const handleNewSiteDragged = async ({ latitude, longitude }: Point) => {
    updateSubmission((draft) => {
      const site = draft.newSites.find(({ id }) => id === selectedSiteId)
      if (!site) throw new Error('Site not found.')
      site.latitude = latitude
      site.longitude = longitude
      draft.isDirty = true
    })
  }

  useEffect(() => {
    setCanDragMarker(false) // reset on selection change
  }, [selectedSiteId])

  // RENDER

  const matchLabel = useMemo(() => {
    if (!matchedSite) return <strong>Unmatched</strong>
    return isNewSite(matchedSite) ? <strong>Matched New Site</strong> : <strong>Matched Site</strong>
  }, [matchedSite])

  const distanceLabel = useMemo(() => {
    if (!selectedSample || !selectedSite) return ''
    const distance = turf.distance(
      turf.point([selectedSample.longitude, selectedSample.latitude]),
      turf.point([selectedSite.longitude, selectedSite.latitude])
    )
    if (distance >= 1) return <strong>{distance.toFixed(1)} km</strong>
    return <strong>{(distance * 1000).toFixed(2)} m</strong>
  }, [selectedSample, selectedSite])

  const selectedSiteAddedSamplesCount =
    (selectedSite && reviewableSamples.filter(({ databaseSiteId }) => databaseSiteId === selectedSiteId).length) ?? 0

  const canFinalize = !isDirty && samples.every((sample) => sample.databaseSiteId)

  return (
    <>
      <div
        css={{
          height: '100%',
          display: 'flex',
          flexDirection: 'column',
        }}
      >
        <div css={{ display: 'flex', gap: '3rem' }}>
          <Title>Box {boxId} Submission Review</Title>
          <ReadOnlyField label="Customer" value={organizationName} />
          <ReadOnlyField label="Received Date" value={formatUtcDateTimeToAppDate(createdDate)} />
          <div css={{ marginLeft: 'auto', display: 'flex', gap: '0.5rem' }}>
            <MuiButton variant="outlined" onClick={handleSave} disabled={!isDirty}>
              Save Review Progress
            </MuiButton>
            <MuiButton variant="outlined" onClick={() => setFinalizing('prompt')} disabled={!canFinalize}>
              Finalize Review
            </MuiButton>
          </div>
        </div>
        <div css={{ flex: 1, display: 'flex', paddingTop: '1rem' }}>
          <div
            css={{
              minWidth: 500,
              flex: 0.4,
              padding: '0 2rem 0 0',
              display: 'flex',
              flexDirection: 'column',
            }}
          >
            <div css={{ display: 'flex', gap: '0.5em' }}>
              <SelectField
                value={sampleStatusFilter}
                onChange={(value) => setSampleStatusFilter(String(value))}
                options={sampleStatusOptions}
                label="Match Status"
              />
              <div css={{ marginLeft: 'auto' }}>
                <MuiButton
                  onClick={() => {
                    setIsConfirmAutoCreateOpen(true)
                  }}
                  variant="outlined"
                  disabled={isFetching || reviewableSamples.every(({ databaseSiteId }) => databaseSiteId)}
                >
                  Create Remaining Sites
                </MuiButton>
              </div>
            </div>
            <div css={{ minHeight: '2em', margin: '0.75em 0', display: 'flex' }}>
              <FilterChips filters={sampleFilters} joinLabel="or" />
              <DataGridRecordCount
                total={reviewableSamples.length}
                visible={filteredSamples.length}
                descriptor="sample"
              />
            </div>
            {isFetching ? (
              <MuiSkeleton variant="rectangular" height={600} />
            ) : (
              <div css={{ flex: 1 }}>
                <DataGrid
                  rowIdProperty="sampleIndex"
                  rows={filteredSamples}
                  columnDefs={pendingBoxColumnDefs}
                  frameworkComponents={renderers}
                />
              </div>
            )}
          </div>
          <div css={{ flex: 1, display: 'flex', flexDirection: 'column' }}>
            <div css={{ display: 'flex', gap: '0.5em' }}>
              <MuiButton
                onClick={() => {
                  if (!selectedSample) return
                  setSuggestedSitesFilterToSample(selectedSample)
                }}
                variant="outlined"
                disabled={!selectedSample || !!siteFilters.find(({ id }) => id === SUGGESTED_FILTER_ID)}
              >
                Suggested
              </MuiButton>
              <TextField
                value={textFilter}
                onChange={(value) => setTextFilter(value)}
                label="Site Name"
                css={{ width: '20em' }}
              />
            </div>
            <div css={{ minHeight: '2em', margin: '0.75em 0', display: 'flex' }}>
              <FilterChips filters={siteFilters} joinLabel="or" />
              <DataGridRecordCount
                total={allSites.length}
                visible={filteredSites.length}
                descriptor="site"
                isLoading={isFetching}
              />
            </div>
            <div css={{ flex: 1, display: 'flex' }}>
              <div css={{ flex: 1, position: 'relative' }}>
                <SitesMap controller={controller} mapboxToken={mapboxToken}>
                  {isFetching && (
                    <LinearProgress
                      delayMs={500}
                      css={{ position: 'absolute', width: '100%', height: '5px', opacity: 0.75, zIndex: 100 }}
                    />
                  )}
                  {selectedSample && <PinMarker site={selectedSample} color={SAMPLE_PIN_COLOR} largeDot />}
                  {selectedSite && (
                    <PinMarker site={selectedSite} onDragEnd={canDragMarker ? handleNewSiteDragged : undefined} />
                  )}
                  {matchedSite && <MatchedSiteLine from={selectedSample} to={matchedSite} />}
                </SitesMap>
              </div>
              <SitesMapSidePanel
                isOpen={!!selectedSample || !!selectedSite}
                transitionMs={SITE_DETAIL_PANEL_TRANSITION_MS}
                css={{
                  display: 'flex',
                  flexDirection: 'column',
                  '@media (max-height: 1150px)': { fontSize: '75%' },
                }}
              >
                <div css={{ display: 'flex', flexDirection: 'column', minHeight: '200px' }}>
                  {selectedSample ? (
                    <>
                      <div css={{ flex: 1 }}>
                        <SampleDetailPanel
                          sample={selectedSample}
                          onClose={() => {
                            setSelectedSampleIndex(undefined)
                            setSelectedSiteId(undefined)
                          }}
                        />
                      </div>
                      <div css={{ borderBottom: '1px solid lightgrey', height: '1px' }} />
                      <div
                        css={{
                          padding: '1em 0.5em',
                        }}
                      >
                        <div
                          css={{
                            display: 'grid',
                            gridTemplateColumns: '1fr 1fr',
                          }}
                        >
                          <ReadOnlyField label="Match Status" value={matchLabel} />
                          <ReadOnlyField label="Distance" value={distanceLabel} />
                        </div>
                        <div css={{ display: 'grid', gap: '0.5em', gridTemplateColumns: '1fr 1fr' }}>
                          {!selectedSite ? (
                            <MuiButton variant="contained" onClick={() => handleNewSiteCreate()}>
                              New Site
                            </MuiButton>
                          ) : (
                            <MuiButton
                              variant="contained"
                              disabled={!selectedSite || !!selectedSample.databaseSiteId}
                              onClick={handleMatch}
                            >
                              Match Site
                            </MuiButton>
                          )}
                          {isNewSite(selectedSite) && selectedSiteAddedSamplesCount === 1 ? (
                            <MuiButton
                              variant="outlined"
                              disabled={!selectedSample.databaseSiteId}
                              onClick={handleNewSiteDiscard}
                            >
                              Discard New Site
                            </MuiButton>
                          ) : (
                            <MuiButton
                              variant="outlined"
                              disabled={!selectedSample || !selectedSample.databaseSiteId}
                              onClick={handleUnmatch}
                            >
                              Unmatch Site
                            </MuiButton>
                          )}
                        </div>
                      </div>
                    </>
                  ) : (
                    <div css={{ padding: '1em' }}>No sample selected.</div>
                  )}
                </div>
                <div css={{ borderBottom: '1px solid lightgrey', height: '1px' }} />
                <div css={{ flex: 1 }}>
                  {selectedSite ? (
                    <SiteDetailPanel
                      site={selectedSite}
                      addSampleCount={selectedSiteAddedSamplesCount}
                      onClose={
                        matchedSite
                          ? undefined
                          : () => {
                              setSelectedSiteId(undefined)
                            }
                      }
                    >
                      {isNewSite(selectedSite) && (
                        <div css={{ marginLeft: '10px', display: 'flex', gap: '0.5em' }}>
                          <MuiButton onClick={handleNewSiteEdit} variant="outlined">
                            Edit Site
                          </MuiButton>
                          <CheckboxField
                            label="Enable draggable pin"
                            value={canDragMarker}
                            onChange={setCanDragMarker}
                          />
                        </div>
                      )}
                    </SiteDetailPanel>
                  ) : (
                    <div css={{ padding: '1em' }}>No site selected.</div>
                  )}
                </div>
              </SitesMapSidePanel>
            </div>
          </div>
        </div>
      </div>
      {selectedSite && (
        <EditSiteDialog
          isOpen={isEditingSite}
          site={selectedSite}
          allSitesNormalized={allSitesNormalized}
          onCancel={() => setIsEditingSite(false)}
          onSave={handleNewSiteSave}
        />
      )}
      <MuiDialog open={!!finalizing} onClose={finalizing === 'finalizing' ? noop : () => setFinalizing(false)}>
        <MuiDialogTitle>Confirm Finalize Review</MuiDialogTitle>
        <MuiDialogContent>
          <ul>
            <li>
              {reviewableSamples.length} sample {pluralize(reviewableSamples.length, 'record')} will be created
            </li>
            <li>
              {newSites.length} {pluralize(newSites.length, 'site')} will be created
            </li>
            <li>
              Box will be set to <em>Waiting</em>
            </li>
          </ul>
        </MuiDialogContent>
        <MuiDialogActions>
          <MuiButton onClick={() => setFinalizing(false)} disabled={finalizing === 'finalizing'}>
            Cancel
          </MuiButton>
          <MuiButton onClick={handleFinalize} disabled={finalizing === 'finalizing'}>
            Finalize
          </MuiButton>
        </MuiDialogActions>
      </MuiDialog>
      {isDirty && <ConfirmNavigateAway message="Some changes are not saved. Are you sure you want to leave?" />}
      {isConfirmAutoCreateOpen && (
        <ConfirmationDialog
          title="Confirm Site Auto-Creation"
          isOpen={isConfirmAutoCreateOpen}
          onClose={() => setIsConfirmAutoCreateOpen(false)}
          onConfirm={() => handleAllRemainingSitesCreate()}
        >
          Selecting this option will attempt to automatically generate a new official NAMC site for all sampled
          locations in this submission without a matched site. The submitted latitude and longitudes will be used for
          the locations. You can still interact with each submitted site location afterward and modify any of the site
          locations or matches. Are you sure that you want to generate all remaining sites?
        </ConfirmationDialog>
      )}
    </>
  )
}
