/** @jsxImportSource @emotion/react */
import React from 'react'
import { ICellRendererParams } from 'ag-grid-community'
import MuiIconButton from '@mui/material/IconButton'
import EditIcon from '@mui/icons-material/Edit'
import DeleteIcon from '@mui/icons-material/Delete'

import { TranslationGroupRow } from './TranslationDetail'
import { Chip, withTooltip } from '@namcbugdb/react-common'

interface CellRendererParams<T> extends ICellRendererParams {
  data: T
}

export const TaxaRenderer = ({ data }: CellRendererParams<TranslationGroupRow>): React.ReactNode => {
  const { taxa } = data
  return (
    <div css={{ display: 'flex', alignItems: 'center', height: '100%' }}>
      {taxa.map((taxon) => (
        <Chip
          chipColor="#eee"
          key={taxon.id}
          label={`${taxon.name} (${taxon.levelName})`}
          css={{ marginRight: '0.5rem' }}
        />
      ))}
    </div>
  )
}

export const ActionsRenderer = ({ data }: CellRendererParams<TranslationGroupRow>): React.ReactNode => {
  const { onEdit, onDelete, canOperate } = data
  return (
    <div css={{ display: 'flex', alignItems: 'center', height: '100%' }}>
      <MuiIconButton onClick={onEdit} disabled={!canOperate}>
        {withTooltip(<EditIcon />, 'Edit OTU Name', !canOperate)}
      </MuiIconButton>
      <MuiIconButton onClick={onDelete} disabled={!canOperate}>
        {withTooltip(<DeleteIcon />, 'Delete OTU Name', !canOperate)}
      </MuiIconButton>
    </div>
  )
}
