/** @jsxImportSource @emotion/react */
import React, { useImperativeHandle, useEffect, forwardRef, useState, useRef } from 'react'
import { ICellRendererParams, ICellEditorParams } from 'ag-grid-community'
import AddIcon from '@mui/icons-material/AddCircleOutline'
import DeleteIcon from '@mui/icons-material/Delete'
import FilterAltIcon from '@mui/icons-material/FilterAlt'
import MuiIconButton from '@mui/material/IconButton'
import CheckCircleOutlinedIcon from '@mui/icons-material/CheckCircleOutlined'
import ChangeCircleOutlinedIcon from '@mui/icons-material/ChangeCircleOutlined'
import CircleOutlinedIcon from '@mui/icons-material/CircleOutlined'
import ErrorOutlineIcon from '@mui/icons-material/ErrorOutline'

import { Badge, TargetEffortBadge, withTooltip } from '@namcbugdb/react-common'

import { OrganismsDataGridContext, OrganismRow } from './types'

interface CellRendererParams<T> extends ICellRendererParams {
  context: OrganismsDataGridContext
  data: OrganismRow
  value: T
}
interface CellEditorParams<T> extends ICellEditorParams {
  context: OrganismsDataGridContext
  data: OrganismRow
  value: T
}

export const NumberEditor = forwardRef(
  (
    {
      value: oldValue,
      data: { canEdit },
      precisionAllowed = 0,
      negativeAllowed = false,
    }: CellEditorParams<number> & { precisionAllowed: number; negativeAllowed?: boolean },
    ref
  ) => {
    const [value, setValue] = useState(oldValue || '')
    const inputRef = useRef<HTMLInputElement | null>(null)

    useEffect(() => {
      setTimeout(() => inputRef.current?.focus())
    }, [])

    useImperativeHandle(ref, () => {
      return {
        getValue: () => value || 0,
        isCancelBeforeStart: () => !canEdit,
        isCancelAfterEnd: () => false,
        isPopup: () => false,
      }
    })

    const handleChange = ({ target: { value: newValue } }: React.ChangeEvent<HTMLInputElement>) => {
      const newNumber = Number(newValue)
      if (Number(newNumber.toFixed(precisionAllowed)) !== newNumber) return
      if (!negativeAllowed && newNumber < 0) return
      setValue(newNumber)
    }

    return (
      <input
        // className="ag-input"
        type="number"
        ref={inputRef}
        value={value}
        onChange={handleChange}
        style={{ width: '100%', textAlign: 'right' }}
      />
    )
  }
)
NumberEditor.displayName = 'NumberEditor'

export const LifeStageEditor = forwardRef(
  ({ value: oldValue, data: { allowedLifeStages, canEdit } }: CellEditorParams<number>, ref) => {
    const [value, setValue] = useState(oldValue)
    const [done, setDone] = useState(false)

    const inputRef = useRef<HTMLSelectElement | null>(null)

    const handleChange = ({ target: { value: newValue } }: React.ChangeEvent<HTMLSelectElement>) => {
      setValue(Number(newValue))
      setDone(true)
    }
    useEffect(() => {
      if (!done) return
      // api.stopEditing();
    }, [done])

    useEffect(() => {
      setTimeout(() => inputRef.current?.focus())
    }, [])

    useImperativeHandle(ref, () => {
      return {
        getValue: () => Number(value),
        isCancelBeforeStart: () => !canEdit || value,
        isCancelAfterEnd: () => false,
        isPopup: () => false,
      }
    })

    const options = allowedLifeStages.map(({ id: value, name: label }) => ({ value, label }))

    if (!value) options.unshift({ value: -1, label: '' })

    return (
      <select value={value} onChange={handleChange} ref={inputRef} style={{ width: '100%' }}>
        {options.map(({ value, label }) => (
          <option key={value} value={value}>
            {label}
          </option>
        ))}
      </select>
    )
  }
)
LifeStageEditor.displayName = 'LifeStageEditor'

export const LifeStageRenderer = ({ context, value }: CellRendererParams<number>): React.ReactNode => {
  return context.lifeStages.find(({ id }) => value === id)?.name || ''
}

export const NumberRenderer = ({ value }: CellRendererParams<number>): React.ReactNode => {
  return value || ''
}

export const TaxonRenderer = ({ value, data: { isTargetEffort } }: CellRendererParams<string>): React.ReactNode => {
  return (
    <span>
      {value}
      {isTargetEffort && <TargetEffortBadge />}
    </span>
  )
}

export const NotesAndCommentsRenderer = ({
  data: { noteTypeIds, comments },
  context: { noteTypes },
}: CellRendererParams<undefined>): React.ReactNode => {
  return (
    <div>
      {noteTypeIds.map((noteTypeId) => {
        const noteType = noteTypes.find(({ id }) => id === noteTypeId)
        return (
          <Badge
            badgeColor="#cc7000"
            textColor="white"
            tooltip={noteType?.name || 'Unknown'}
            css={{ marginRight: '0.2em' }}
            key={noteTypeId}
            label={noteType?.abbreviation || '?'}
          />
        )
      })}
      <span>{comments}</span>
    </div>
  )
}

export const NotesAndCommentsEditor = forwardRef(
  (
    {
      data: { noteTypeIds: oldNoteTypeIds, comments: oldComments, canEdit },
      context: { noteTypes },
    }: CellEditorParams<undefined>,
    ref
  ) => {
    const [noteTypeIds, setNoteTypeIds] = useState(oldNoteTypeIds)
    const [comments, setComments] = useState(oldComments)

    const inputRef = useRef<HTMLTextAreaElement | null>(null)

    useEffect(() => {
      setTimeout(() => inputRef.current?.focus())
    }, [])

    useImperativeHandle(ref, () => {
      return {
        getValue: () => ({
          noteTypeIds,
          comments,
        }),
        isCancelBeforeStart: () => !canEdit,
        isCancelAfterEnd: () => false,
        isPopup: () => true,
      }
    })

    // TODO: limit to active note types

    return (
      <div css={{ minWidth: '20em' }}>
        <textarea
          ref={inputRef}
          value={comments}
          onChange={(event) => setComments(event.target.value)}
          css={{ width: '95%', height: '3em', fontFamily: 'Roboto' }}
        />
        <div>
          {noteTypes.map(({ id, name, abbreviation }) => (
            <div key={id} css={{ margin: '0.2em 0' }}>
              <input
                type="checkbox"
                checked={noteTypeIds.includes(id)}
                onChange={(e) => {
                  if (e.target.checked) {
                    setNoteTypeIds(() => [...noteTypeIds, id])
                  } else {
                    setNoteTypeIds(() => noteTypeIds.filter((noteTypeId) => noteTypeId !== id))
                  }
                }}
              />
              (<span css={{ fontWeight: 'bold' }}>{abbreviation}</span>) - {name}
            </div>
          ))}
        </div>
      </div>
    )
  }
)
NotesAndCommentsEditor.displayName = 'NotesAndCommentsEditor'

export const ActionsRenderer = ({
  data: organismRow,
  context: { actions, setDeletionCandidateOrganism },
}: CellRendererParams<OrganismRow>): React.ReactNode => {
  const { splitCount, bigRareCount, noteTypeIds, comments, canDuplicate, canFilter, canDelete, persistence } =
    organismRow
  return (
    <div css={{ display: 'flex', alignItems: 'center', height: '100%' }}>
      <MuiIconButton
        disabled={!canDuplicate}
        onClick={() => {
          actions.duplicateOrganism(organismRow)
        }}
      >
        {withTooltip(<AddIcon />, 'Create a similar organism record')}
      </MuiIconButton>
      <MuiIconButton
        disabled={!canFilter}
        onClick={() => {
          actions.createFilter(organismRow)
        }}
      >
        {withTooltip(<FilterAltIcon />, 'Filter by this taxonomy')}
      </MuiIconButton>
      <MuiIconButton
        disabled={!canDelete}
        onClick={() => {
          if (persistence !== 'saved' && !splitCount && !bigRareCount && noteTypeIds.length === 0 && !comments) {
            actions.deleteOrganism(organismRow)
          } else {
            setDeletionCandidateOrganism(organismRow)
          }
        }}
      >
        {withTooltip(<DeleteIcon />, 'Delete this organism record')}
      </MuiIconButton>
    </div>
  )
}

export const PersistenceRenderer = ({ value }: CellRendererParams<OrganismRow['persistence']>): React.ReactNode => {
  return (
    <div css={{ display: 'flex', alignItems: 'center', height: '100%' }}>
      {(() => {
        switch (value) {
          case 'unsaved':
            return withTooltip(<CircleOutlinedIcon />, 'Record is not ready to be saved to database.')
          case 'saving':
            return withTooltip(<ChangeCircleOutlinedIcon />, 'Record is saving to database...')
          case 'saved':
            return withTooltip(<CheckCircleOutlinedIcon />, 'Record is saved in database.')
          case 'error':
            return withTooltip(
              <ErrorOutlineIcon css={{ path: { fill: 'red' } }} />,
              'Record had an error saving or deleting.'
            )
        }
        return null
      })()}
    </div>
  )
}
