import gql from 'graphql-tag'
import { Submission } from '../../types'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

interface UpdatedSampleSubmission {
  boxId: number
  data: Pick<Submission, 'templateVersion' | 'appVersion' | 'samples' | 'newSites'>
}

export const updateSampleSubmissionMutation = gql`
  mutation ($boxId: Int!, $data: JSONObject!) {
    updateSampleSubmission(boxId: $boxId, data: $data)
  }
`

export const updateSampleSubmission =
  (api: Api, invalidate: InvalidateFn) =>
  async (submission: UpdatedSampleSubmission): Promise<true> => {
    const data = await api.mutation<{
      updateSampleSubmission: number | null // TODO: what?
    }>(updateSampleSubmissionMutation, { ...submission, data: JSON.stringify(submission.data) })

    if (!data.updateSampleSubmission) throw new ApiError('submission did not update')

    invalidate('boxes')
    return true
  }
