/** @jsxImportSource @emotion/react */
import React from 'react'
import { ICellRendererParams } from 'ag-grid-community'
import { ReviewableSample, SAMPLE_PIN_COLOR } from './PendingBoxReview'
import MuiIconButton from '@mui/material/IconButton'
import EditIcon from '@mui/icons-material/Edit'

import { withTooltip, PinSvg } from '@namcbugdb/react-common'

interface CellRendererParams<T> extends ICellRendererParams {
  data: T
}

export const SampleIndexRenderer = ({ data }: CellRendererParams<ReviewableSample>): React.ReactNode => {
  const { sampleIndex, isSelected } = data
  return (
    <div css={{ display: 'flex', alignItems: 'center' }}>
      {isSelected && (
        <div css={{ height: 30, width: 15 }}>
          <PinSvg color={SAMPLE_PIN_COLOR} largeDot />
        </div>
      )}
      <div css={{ marginLeft: 'auto' }}>{sampleIndex}</div>
    </div>
  )
}

export const ActionsRenderer = ({ data }: CellRendererParams<ReviewableSample>): React.ReactNode => {
  const { onEdit } = data
  return (
    <div css={{ display: 'flex', alignItems: 'center', height: '100%' }}>
      <MuiIconButton onClick={onEdit}>{withTooltip(<EditIcon />, 'Edit sample match')}</MuiIconButton>
    </div>
  )
}
