/** @jsxImportSource @emotion/react */
import React, { useState } from 'react'
import { useRecoilValue } from 'recoil'
import { GrantAction, NAMCResource } from '@namcbugdb/common'
import MuiButton from '@mui/material/Button'

import {
  ConfirmationDialog,
  MyUser,
  Predictor,
  Title,
  useApi,
  useNotifications,
  useUpdatableState,
  trimAllStrings,
  ReadOnlyField,
  TextField,
  AutocompleteField,
  CheckboxField,
  Menu,
  Divider,
  NumberField,
} from '@namcbugdb/react-common'

import { ConfirmNavigateAway, useNavigate } from '../AppRouter'
import * as calls from '../../data/calls'
import * as selectors from '../../data/selectors'

export interface PredictorDetailProps {
  predictor: Predictor | null
}

interface Fields {
  abbreviation: string
  calculation: string
  calculationScript: string
  cellRepresents: string
  cellSize: string
  description: string
  geometryFilePath: string
  inputType: string
  isGee: string
  isTemporal: boolean
  isText: string
  maxValue: number | null
  minValue: number | null
  name: string
  notes: string
  smallShedException: string
  source: string
  transformation: string
  typeId: number | null
  units: string
}

type FieldErrors = Partial<Record<keyof Fields, boolean>> | undefined

interface State {
  editMode: 'stopped' | 'started' | 'stopping' | 'deleting'
  isDirty: boolean
  values: Fields
  lastValues?: Fields
}

const getInitialState = ({ predictor }: Pick<PredictorDetailProps, 'predictor'> & { user: MyUser }): State => {
  const {
    abbreviation = '',
    calculation = '',
    calculationScript = '',
    cellRepresents = '',
    cellSize = '',
    description = '',
    geometryFilePath = '',
    inputType = '',
    isGee = '',
    isTemporal = false,
    isText = '',
    maxValue = null,
    minValue = null,
    name = '',
    notes = '',
    smallShedException = '',
    source = '',
    transformation = '',
    typeId = null,
    units = '',
  } = predictor ?? {}
  return {
    editMode: predictor ? 'stopped' : 'started',
    isDirty: false,
    values: {
      abbreviation,
      calculation,
      calculationScript,
      cellRepresents,
      cellSize,
      description,
      geometryFilePath,
      inputType,
      isGee,
      isTemporal,
      isText,
      maxValue,
      minValue,
      name,
      notes,
      smallShedException,
      source,
      transformation,
      typeId,
      units,
    },
  }
}

export const PredictorDetail = ({ predictor }: PredictorDetailProps): JSX.Element => {
  const api = useApi()
  const invalidate = selectors.useInvalidate()
  const notifications = useNotifications()
  const navigate = useNavigate()
  const user = useRecoilValue(selectors.selectMyUser)

  const isNewPredictor = !predictor

  // LOOKUP OPTIONS

  const predictorTypeOptions = selectors.lookupListToOptions(useRecoilValue(selectors.selectPredictorTypes))

  // STATE

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [state, setState, updateState] = useUpdatableState<State>(getInitialState({ predictor, user }), [predictor])
  const [isConfirmDeleteOpen, setIsConfirmDeleteOpen] = useState(false)

  const { values, editMode, isDirty } = state

  const errors: FieldErrors = (() => {
    const result: FieldErrors = {
      name: !values.name.trim(),
      abbreviation: !values.abbreviation.trim(),
      typeId: !values.typeId,
    }
    if (!Object.values(result).some((value) => value)) return undefined
    return result
  })()

  const isStopped = editMode === 'stopped'
  const isEditing = editMode === 'started'
  const isSaving = editMode === 'stopping'
  const isDeleting = editMode === 'deleting'

  // HANDLERS

  const setFieldValue = (fieldName: keyof Fields, value: unknown) => {
    updateState((draft) => {
      Object.assign(draft.values, { [fieldName]: value })
      draft.isDirty = true
    })
  }
  const makeFieldChangeHandler = (fieldName: keyof Fields) => (value: unknown) => setFieldValue(fieldName, value)

  const handleEdit = () => {
    updateState((draft) => {
      draft.editMode = 'started'
      draft.lastValues = draft.values
    })
  }

  const handleCancel = () => {
    updateState((draft) => {
      if (!draft.lastValues) throw new Error('no values to revert to')
      draft.editMode = 'stopped'
      draft.values = draft.lastValues
      draft.isDirty = false
    })
  }

  const handleSave = async () => {
    try {
      updateState((draft) => {
        draft.editMode = 'stopping'
      })
      const { typeId, ...restPredictorInput } = trimAllStrings(values)
      if (!typeId) throw new Error('No predictor type.')
      if (isNewPredictor) {
        // new predictor
        const predictorId = await calls.createPredictor(api, invalidate)({ typeId, ...restPredictorInput })
        updateState((draft) => {
          draft.editMode = 'stopped'
          draft.isDirty = false
        })
        window.setTimeout(() => {
          navigate.predictorDetail({ predictorId }, true) // let state update
        })
      } else {
        // existing predictor
        await calls.updatePredictor(
          api,
          invalidate
        )({
          ...restPredictorInput,
          typeId,
          id: predictor.id,
        })
        updateState((draft) => {
          draft.editMode = 'stopped'
          draft.isDirty = false
        })
      }
    } catch (err) {
      switch ((err as Error).message) {
        case 'duplicate key value violates unique constraint "predictors_predictor_name_key"':
          notifications.error(`Failed to save: name "${values.name}" already in use`)
          break
        case 'duplicate key value violates unique constraint "predictors_abbreviation_key"':
          notifications.error(`Failed to save: abbreviation "${values.abbreviation}" already in use`)
          break
        default:
          notifications.error(`Failed to save: ${err}`)
      }
      updateState((draft) => {
        draft.editMode = 'started'
      })
    }
  }

  const handleDelete = async () => {
    try {
      if (!predictor) throw new Error('No predictor.')
      updateState((draft) => {
        draft.editMode = 'deleting'
      })
      await calls.deletePredictor(api, invalidate)(predictor)
      notifications.info(`Deleted predictor ${predictor.name}`)
      navigate.predictors()
    } catch (err) {
      notifications.error(`Failed to delete: ${err}`)
      updateState((draft) => {
        draft.editMode = 'stopped'
      })
    }
  }

  // RENDER

  return (
    <>
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        <div css={{ display: 'flex', alignItems: 'center', minHeight: '3rem', marginBottom: '1rem', gap: '1rem' }}>
          {!isEditing ? (
            <Title>{values.name}</Title>
          ) : (
            <div css={{ flex: 1 }}>
              <TextField
                label="Predictor Name"
                value={values.name}
                onChange={(value) => setFieldValue('name', value)}
                error={errors?.name}
                InputProps={{ sx: { fontWeight: '700' } }} // roughly match <Title>
                maxLength={255}
                fullWidth
              />
            </div>
          )}
          <div css={{ display: 'flex', gap: '0.5em', marginLeft: 'auto' }}>
            {(isStopped || isDeleting) && user.hasPermission(NAMCResource.models, GrantAction.updateAny) && (
              <Menu
                label="Actions"
                disabled={isDeleting}
                items={[
                  {
                    key: 'edit',
                    label: 'Edit Predictor',
                    onClick: handleEdit,
                  },
                  Divider,
                  {
                    key: 'delete',
                    label: 'Delete Predictor',
                    onClick: () => {
                      setIsConfirmDeleteOpen(true)
                    },
                    disabled: !user.hasPermission(NAMCResource.models, GrantAction.deleteAny),
                  },
                ]}
              />
            )}
            {(isEditing || isSaving) && (
              <>
                {!isNewPredictor && (
                  <MuiButton onClick={handleCancel} variant="outlined" disabled={isSaving}>
                    Cancel
                  </MuiButton>
                )}
                <MuiButton onClick={handleSave} variant="contained" disabled={!isDirty || isSaving || !!errors}>
                  Save
                </MuiButton>
              </>
            )}
          </div>
        </div>
        <div
          css={{
            display: 'grid',
            gap: '0.75em',
            gridTemplateColumns: 'repeat(3, 1fr)',
            gridTemplateAreas: `
              "abbreviation           typeId            id"
              "units                  source            inputType"
              "isTemporal             isText            isGee"
              "cellRepresents         cellSize          smallShedException" 
              "transformation         minValue          maxValue"
              "calculationScript      geometryFilePath  ."
              "calculation            description       notes"
            `,
          }}
        >
          <ReadOnlyField gridArea="id" value={predictor?.id ?? 'TBD'} label="Predictor ID" />
          <TextField
            gridArea="abbreviation"
            value={values.abbreviation}
            onChange={makeFieldChangeHandler('abbreviation')}
            label="Abbreviation"
            readOnly={!isEditing}
            error={errors?.abbreviation}
            maxLength={50}
          />
          <AutocompleteField
            gridArea="typeId"
            label="Type"
            value={values.typeId}
            onChange={makeFieldChangeHandler('typeId')}
            readOnly={!isEditing}
            error={errors?.typeId}
            options={predictorTypeOptions}
          />
          <TextField
            gridArea="units"
            value={values.units}
            onChange={makeFieldChangeHandler('units')}
            label="Units"
            readOnly={!isEditing}
            error={errors?.units}
            maxLength={255}
          />
          <TextField
            gridArea="source"
            value={values.source}
            onChange={makeFieldChangeHandler('source')}
            label="Source"
            readOnly={!isEditing}
            error={errors?.source}
            maxLength={255}
          />
          <TextField
            gridArea="inputType"
            value={values.inputType}
            onChange={makeFieldChangeHandler('inputType')}
            label="Input Type"
            readOnly={!isEditing}
            error={errors?.inputType}
            maxLength={255}
          />
          <CheckboxField
            gridArea="isTemporal"
            value={values.isTemporal}
            onChange={makeFieldChangeHandler('isTemporal')}
            label="Is Temporal"
            readOnly={!isEditing}
          />
          <TextField
            gridArea="isText"
            value={values.isText}
            onChange={makeFieldChangeHandler('isText')}
            label="Is Text"
            readOnly={!isEditing}
            error={errors?.isText}
            maxLength={255}
          />
          <TextField
            gridArea="isGee"
            value={values.isGee}
            onChange={makeFieldChangeHandler('isGee')}
            label="Is GEE"
            readOnly={!isEditing}
            error={errors?.isGee}
            maxLength={255}
          />
          <TextField
            gridArea="cellRepresents"
            value={values.cellRepresents}
            onChange={makeFieldChangeHandler('cellRepresents')}
            label="Cell Represents"
            readOnly={!isEditing}
            error={errors?.cellRepresents}
            maxLength={255}
          />
          <TextField
            gridArea="cellSize"
            value={values.cellSize}
            onChange={makeFieldChangeHandler('cellSize')}
            label="Cell Size"
            readOnly={!isEditing}
            error={errors?.cellSize}
            maxLength={255}
          />
          <TextField
            gridArea="smallShedException"
            value={values.smallShedException}
            onChange={makeFieldChangeHandler('smallShedException')}
            label="Small Watershed Exception"
            readOnly={!isEditing}
            error={errors?.smallShedException}
            maxLength={255}
          />
          <TextField
            gridArea="transformation"
            value={values.transformation}
            onChange={makeFieldChangeHandler('transformation')}
            label="Transformation"
            readOnly={!isEditing}
            error={errors?.transformation}
            maxLength={255}
          />
          <NumberField
            gridArea="minValue"
            value={values.minValue}
            onChange={makeFieldChangeHandler('minValue')}
            label="Minimum Value"
            readOnly={!isEditing}
            error={errors?.minValue}
          />
          <NumberField
            gridArea="maxValue"
            value={values.maxValue}
            onChange={makeFieldChangeHandler('maxValue')}
            label="Maximum Value"
            readOnly={!isEditing}
            error={errors?.maxValue}
          />
          <TextField
            gridArea="calculationScript"
            value={values.calculationScript}
            onChange={makeFieldChangeHandler('calculationScript')}
            label="Calculation Script"
            readOnly={!isEditing}
            error={errors?.calculationScript}
            maxLength={255}
          />
          <TextField
            gridArea="geometryFilePath"
            value={values.geometryFilePath}
            onChange={makeFieldChangeHandler('geometryFilePath')}
            label="Geometry File Path"
            readOnly={!isEditing}
            error={errors?.geometryFilePath}
            maxLength={255}
          />
          <TextField
            gridArea="calculation"
            value={values.calculation}
            onChange={makeFieldChangeHandler('calculation')}
            label="Calculation"
            readOnly={!isEditing}
            error={errors?.calculation}
            multiline
            minRows={2}
          />
          <TextField
            gridArea="description"
            value={values.description}
            onChange={makeFieldChangeHandler('description')}
            label="Description"
            readOnly={!isEditing}
            error={errors?.description}
            multiline
            minRows={2}
          />
          <TextField
            gridArea="notes"
            value={values.notes}
            onChange={makeFieldChangeHandler('notes')}
            label="Notes"
            readOnly={!isEditing}
            error={errors?.notes}
            multiline
            minRows={2}
          />
        </div>
      </div>
      <ConfirmationDialog
        title="Confirm Delete Predictor"
        isOpen={isConfirmDeleteOpen}
        onClose={() => {
          setIsConfirmDeleteOpen(false)
        }}
        onConfirm={handleDelete}
      >
        Are you sure you want to delete this predictor?
      </ConfirmationDialog>
      {isDirty && <ConfirmNavigateAway message="Some changes are not saved. Are you sure you want to leave?" />}
    </>
  )
}
