import gql from 'graphql-tag'
import { PaginatedRecords, Sample as GraphSample } from '@namcbugdb/common'

import { Api } from '@namcbugdb/react-common'
import { GeoJsonPolygonCoordinates, MinimalSample } from '@namcbugdb/react-common'
import { transformMinimalSample } from './fetchMinimalSamplesByIds'

export const fetchMinimalSamplesByPolygonQuery = gql`
  query ($polygon: String, $offset: Int) {
    samples(limit: 1000, offset: $offset, polygon: $polygon) {
      records {
        sampleId
        sampleDate
        sampleType
        sampleMethod
        area
      }
      nextOffset
    }
  }
`

export const fetchMinimalSamplesByPolygon =
  (api: Api) =>
  async (coordinates: GeoJsonPolygonCoordinates): Promise<MinimalSample[]> => {
    const stringifiedGeoJson = JSON.stringify({
      type: 'Polygon',
      coordinates,
    })

    const data = await api.queryAllPages<{ samples: PaginatedRecords<GraphSample> }>(
      fetchMinimalSamplesByPolygonQuery,
      { polygon: stringifiedGeoJson },
      { dataProperty: 'samples' }
    )
    return data.samples.records.map(transformMinimalSample)
  }
