import gql from 'graphql-tag'
import { Predictor as GraphPredictor } from '@namcbugdb/common'

import { Api, deNull } from '@namcbugdb/react-common'
import { Predictor } from '@namcbugdb/react-common'

export const fetchPredictorsQuery = gql`
  query ($offset: Int!) {
    predictors(limit: 500, offset: $offset) {
      records {
        abbreviation
        calculation
        calculationScript
        cellRepresents
        cellSize
        description
        geometryFilePath
        inputType
        isGee
        isTemporal
        isText
        maxValue
        minValue
        modelCount
        notes
        predictorId # -> id
        predictorName # -> name
        predictorType # -> type
        predictorTypeId # -> type
        smallShedException
        source
        transformation
        units
      }
      nextOffset
    }
  }
`

export const fetchPredictors = (api: Api) => async (): Promise<Predictor[]> => {
  const data = await api.queryAllPages<{ predictors: { records: GraphPredictor[] } }>(
    fetchPredictorsQuery,
    {},
    { dataProperty: 'predictors' }
  )

  return data.predictors.records.map(
    ({ predictorId: id, predictorName: name, predictorType: type, predictorTypeId: typeId, ...rest }) =>
      deNull(
        {
          id,
          name,
          type,
          typeId,
          ...rest,
        },
        {
          calculation: '',
          calculationScript: '',
          cellRepresents: '',
          cellSize: '',
          description: '',
          geometryFilePath: '',
          inputType: '',
          isGee: '',
          isText: '',
          notes: '',
          smallShedException: '',
          source: '',
          transformation: '',
        }
      )
  )
}
