import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

export interface ModelInput {
  name: string // -> modelName
  abbreviation: string
  typeId: number | null // -> modelTypeId
  translationId: number | null
  extentDescription: string
  codeFunction: string
  codeFunction2: string
  platform: string
  referenceSites: number | null
  groupCount: number | null
  minimumCount: number | null
  oeMean: number | null
  oeStdev: number | null
  taxonomicEffort: string
  fixedCount: number | null
  description: string
  predictorIds: number[]
  metricIds: number[]
  isActive: boolean
}

export const createModelMutation = gql`
  mutation ($model: ModelInput!) {
    createModel(model: $model)
  }
`

export const createModel =
  (api: Api, invalidate: InvalidateFn) =>
  async (model: ModelInput): Promise<number> => {
    const { name: modelName, typeId: modelTypeId, taxonomicEffort, ...restModel } = model
    const data = await api.mutation<{
      createModel: number | null // NOTE: new model ID
    }>(createModelMutation, {
      model: { modelName, modelTypeId, taxonomicEffort: taxonomicEffort || null, ...restModel },
    })

    const id = data.createModel
    if (!id) throw new ApiError('model did not create')

    invalidate('models')
    return id
  }
