import { atom, selectorFamily, useRecoilState, useSetRecoilState } from 'recoil'

import {
  Site,
  useNotifications,
  useApi,
  makeOnPageHandler,
  useAccumulateApiItems,
  apiState,
} from '@namcbugdb/react-common'
import { fetchSitesByProjectId, fetchSitesByBoxId, fetchSiteDetailById, fetchSitesPage } from '../calls'
import { refreshOn } from './invalidate'

export enum FETCH_ALL_SITES_STATUS {
  unfetched = 0,
  fetching = 1,
  fetched = 2,
}

export const allSitesStatusAtom = atom<FETCH_ALL_SITES_STATUS>({
  key: 'sites/allSitesStatusAtom',
  default: FETCH_ALL_SITES_STATUS.unfetched,
})

export const allSitesAtom = atom<Site[]>({
  key: 'sites/allSitesAtom',
  default: [],
})

export const sparseSitesDetailAtom = atom<Site[]>({
  key: 'SitesListing/sparseSitesDetailAtom',
  default: [],
})

export const selectSitesBySingleSiteId = selectorFamily<Site[], number>({
  key: 'selectors/selectSitesBySiteId',
  get:
    (id) =>
    async ({ get }) => {
      get(refreshOn('sites'))

      const api = get(apiState)
      const site = await fetchSiteDetailById(api)(id)
      return site ? [site] : []
    },
})

export const useFetchAllSitesOptimized = (): (() => void) => {
  const [allSitesStatus, setAllSitesStatus] = useRecoilState(allSitesStatusAtom)

  const setAllSites = useSetRecoilState(allSitesAtom)
  const accumulateAllSites = useAccumulateApiItems(fetchSitesPage, 5000, makeOnPageHandler(setAllSites))

  return () => {
    if (allSitesStatus === FETCH_ALL_SITES_STATUS.unfetched) {
      setAllSitesStatus(FETCH_ALL_SITES_STATUS.fetching)
      Promise.all([
        accumulateAllSites(0, 10000),
        accumulateAllSites(10000, 20000),
        accumulateAllSites(20000, 30000),
        accumulateAllSites(30000, Infinity),
      ]).then(() => {
        setAllSitesStatus(FETCH_ALL_SITES_STATUS.fetched)
      })
    }
  }
}

export const useFetchSiteDetailBySiteIdOptimized = (): ((siteId: number | null | undefined) => void) => {
  const api = useApi()
  const notifications = useNotifications()
  const [sparseSitesDetail, setSparseSitesDetail] = useRecoilState(sparseSitesDetailAtom)

  return async (siteId: number | null | undefined) => {
    if (!siteId || sparseSitesDetail.find((siteDetail) => siteDetail.id === siteId)) return

    try {
      const siteDetail = await fetchSiteDetailById(api)(siteId)
      if (!siteDetail) return
      setSparseSitesDetail((existing) => {
        return [...existing, siteDetail] // TODO: handle replacement in case
      })
    } catch (err) {
      notifications.error(`Failed to retrieve site: ${err}`)
    }
  }
}

export const selectSitesByBoxId = selectorFamily<Site[], number>({
  key: 'selectSitesByBoxId',
  get:
    (id) =>
    async ({ get }) => {
      get(refreshOn('sites'))
      get(refreshOn('boxSamples'))

      const api = get(apiState)
      return fetchSitesByBoxId(api)(id)
    },
})

export const selectSitesByProjectId = selectorFamily<Site[], number>({
  key: 'selectSitesByProjectId',
  get:
    (id) =>
    async ({ get }) => {
      get(refreshOn('sites'))
      get(refreshOn('projectSamples'))

      const api = get(apiState)
      return fetchSitesByProjectId(api)(id)
    },
})
