import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'
import { OrganizationInput } from './createOrganization'

export interface UserInput {
  firstName: string
  lastName: string
  phone: string
  affiliationId?: number | null
  roleIds?: number[] // only for updateUser
}

export const createOrganizationWithUserMutation = gql`
  mutation ($organization: OrganizationInput!, $user: IndividualInput!) {
    createUser(organization: $organization, user: $user)
  }
`

interface createOrganizationWithUserParams {
  organization: OrganizationInput
  user: UserInput
}

export const createOrganizationWithUser =
  (api: Api, invalidate: InvalidateFn) =>
  async ({ organization, user }: createOrganizationWithUserParams): Promise<number> => {
    const { name: organizationName, typeId: organizationTypeId, ...restOrganization } = organization
    const data = await api.mutation<{
      createUser: number | null // NOTE: new user ID
    }>(createOrganizationWithUserMutation, {
      organization: { organizationName, organizationTypeId, ...restOrganization },
      user,
    })

    const id = data.createUser
    if (!id) throw new ApiError('user/organization did not create')

    invalidate('organizations')
    invalidate('users')
    invalidate('myUser')

    return id
  }
