/** @jsxImportSource @emotion/react */
import React, { useMemo, useState } from 'react'
import { atom, useRecoilState, useRecoilValue } from 'recoil'
import MuiButton from '@mui/material/Button'
import AddIcon from '@mui/icons-material/AddCircleOutline'
import { GrantAction, NAMCResource } from '@namcbugdb/common'
// import log from 'loglevel'

import {
  ConfirmationDialog,
  useNotifications,
  Taxon,
  Translation,
  TranslationGroup,
  Filter,
  FilterChips,
  withSuspense,
  useUpdatableState,
  useApi,
  Title,
  ReadOnlyField,
  trimAllStrings,
  TextField,
  useTextFilterField,
  Divider,
  Menu,
  CheckboxField,
} from '@namcbugdb/react-common'
import * as calls from '../../data/calls'
import * as selectors from '../../data/selectors'
import { DataGrid, DataGridRecordCount } from '../DataGrid'
import { translationDetailColumnDefs } from './columnDefs'
import * as renderers from './renderers'
import { EditTranslationGroupDialog } from './EditTranslationGroupDialog'
import { AccordionPanel } from '@namcbugdb/react-common'
import { Fallback } from './Fallback'
import { ConfirmNavigateAway, useNavigate } from '../AppRouter'

export interface TranslationDetailProps {
  translation: Translation | null
}

interface Fields {
  name: string
  isActive: boolean
  description: string
}

type FieldErrors = Partial<Record<keyof Fields, boolean>> | undefined

interface State {
  editMode: 'stopped' | 'started' | 'stopping' | 'deleting'
  isDirty: boolean
  values: Fields
  lastValues?: Fields
}

const getInitialState = ({ translation }: Pick<TranslationDetailProps, 'translation'>): State => {
  const { name = '', isActive = true, description = '' } = translation ?? {}
  return {
    editMode: translation ? 'stopped' : 'started',
    isDirty: false,
    values: {
      name,
      isActive,
      description,
    },
  }
}

export interface TranslationGroupRow extends TranslationGroup {
  taxa: Taxon[]
  onEdit: () => void
  onDelete: () => void
  canOperate: boolean
}

const isExpandedState = atom<boolean>({
  key: 'TranslationDetail/isExpandedState',
  default: true,
})

export const TranslationDetail = withSuspense(
  ({ translation }: TranslationDetailProps): JSX.Element => {
    const api = useApi()
    const notifications = useNotifications()
    const navigate = useNavigate()
    const user = useRecoilValue(selectors.selectMyUser)
    const invalidate = selectors.useInvalidate()
    const deferredInvalidate = selectors.useDeferredInvalidate()
    const allTaxa = useRecoilValue(selectors.selectTaxa)

    const isNewTranslation = !translation

    // STATE

    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const [state, setState, updateState] = useUpdatableState<State>(getInitialState({ translation }), [translation])
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const [groups, setGroups, updateGroups] = useUpdatableState(translation?.groups ?? [], [translation])
    const [textFilter, setTextFilter, textFilterDebounced, clearTextFilter] = useTextFilterField(useState(''))
    const [isEditOpen, setIsEditOpen] = useState(false)
    const [isConfirmDeleteTranslationOpen, setIsConfirmDeleteTranslationOpen] = useState(false)
    const [isConfirmDeleteGroupOpen, setIsConfirmDeleteGroupOpen] = useState(false)
    const [targetGroupId, setTargetGroupId] = useState<number>()
    const [isPanelExpanded, setIsPanelExpanded] = useRecoilState(isExpandedState)

    const { values, editMode, isDirty } = state

    const errors: FieldErrors = (() => {
      const result: FieldErrors = {
        name: !values.name.trim(),
      }
      if (!Object.values(result).some((value) => value)) return undefined
      return result
    })()

    const isStopped = editMode === 'stopped'
    const isEditing = editMode === 'started'
    const isSaving = editMode === 'stopping'
    const isDeleting = editMode === 'deleting'

    // HANDLERS

    const setFieldValue = (fieldName: keyof Fields, value: unknown) => {
      updateState((draft) => {
        Object.assign(draft.values, { [fieldName]: value })
        draft.isDirty = true
      })
    }
    const makeFieldChangeHandler = (fieldName: keyof Fields) => (value: unknown) => setFieldValue(fieldName, value)

    const handleEdit = () => {
      updateState((draft) => {
        draft.editMode = 'started'
        draft.lastValues = draft.values
      })
    }

    const handleCancel = () => {
      updateState((draft) => {
        if (!draft.lastValues) throw new Error('no values to revert to')
        draft.editMode = 'stopped'
        draft.values = draft.lastValues
        draft.isDirty = false
      })
    }

    const handleSaveTranslation = async () => {
      try {
        updateState((draft) => {
          draft.editMode = 'stopping'
        })
        const translationInput = trimAllStrings(values)
        if (isNewTranslation) {
          // new translation
          const translationId = await calls.createTranslation(api, invalidate)(translationInput)
          updateState((draft) => {
            draft.editMode = 'stopped'
            draft.isDirty = false
          })
          window.setTimeout(() => {
            navigate.translationDetail({ translationId }, true) // let state update
          })
        } else {
          // existing translation
          await calls.updateTranslation(
            api,
            invalidate
          )({
            ...translationInput,
            id: translation.id,
          })
          updateState((draft) => {
            draft.editMode = 'stopped'
            draft.isDirty = false
          })
        }
      } catch (err) {
        switch ((err as Error).message) {
          case 'duplicate key value violates unique constraint "translations_translation_name_key"':
            notifications.error(`Failed to save: name "${values.name}" already in use`)
            break
          default:
            notifications.error(`Failed to save: ${err}`)
        }
        updateState((draft) => {
          draft.editMode = 'started'
        })
      }
    }

    const handleDeleteTranslation = async () => {
      try {
        if (!translation) throw new Error('No translation.')
        updateState((draft) => {
          draft.editMode = 'deleting'
        })
        await calls.deleteTranslation(api, invalidate)(translation)
        notifications.info(`Deleted translation ${translation.name}`)
        navigate.translations()
      } catch (err) {
        notifications.error(`Failed to delete: ${err}`)
        updateState((draft) => {
          draft.editMode = 'stopped'
        })
      }
    }

    const handleAddGroup = () => {
      setIsEditOpen(true)
      setTargetGroupId(undefined)
    }

    const handleUpdatedGroup = (updatedGroup: TranslationGroup) => {
      updateGroups((draft) => {
        const index = draft.findIndex(({ id }) => id === updatedGroup.id)
        if (index === -1) {
          // new group
          draft.push(updatedGroup)
        } else {
          // updated group
          draft[index] = updatedGroup
        }
      })
    }

    const handleDeleteGroup = async () => {
      try {
        if (!targetGroupId) throw new Error('No group.')
        await calls.deleteTranslationGroup(api, deferredInvalidate)({ id: targetGroupId })
        updateGroups((draft) => {
          const index = draft.findIndex(({ id }) => id === targetGroupId)
          if (index === -1) return // should never occur
          draft.splice(index, 1)
        })
      } catch (err) {
        notifications.error(`Failed to delete OTU Name: ${err}`)
      }
    }

    // FILTERS

    const filters = useMemo(() => {
      const result: Filter<TranslationGroup>[] = []

      if (textFilterDebounced) {
        const textFilterDebouncedLowercase = textFilterDebounced.toLowerCase()
        result.push({
          id: 'text',
          label: `OTU Name contains "${textFilterDebounced}"`,
          filterFn: ({ translationScientificName }) =>
            translationScientificName.toLowerCase().includes(textFilterDebouncedLowercase),
          onDelete: clearTextFilter,
        })
      }

      return result
    }, [textFilterDebounced])

    const rows = useMemo<TranslationGroupRow[]>(() => {
      const filtered = filters.reduce<TranslationGroup[]>((acc, { filterFn }) => acc.filter(filterFn), groups)

      return filtered.map((translationGroup) => {
        const taxa = translationGroup.taxonomyIds.map((taxonId) => {
          const taxon = allTaxa.find(({ id }) => id === taxonId)
          if (!taxon) throw new Error('Taxon not found')
          return taxon
        })

        const onEdit = () => {
          setIsEditOpen(true)
          setTargetGroupId(translationGroup.id)
        }

        const onDelete = () => {
          setIsConfirmDeleteGroupOpen(true)
          setTargetGroupId(translationGroup.id)
        }

        const canOperate = isStopped

        return { ...translationGroup, taxa, onEdit, onDelete, canOperate }
      })
    }, [groups, allTaxa, filters, isStopped])

    // RENDER

    return (
      <>
        <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
          <AccordionPanel
            value={isPanelExpanded}
            onChange={setIsPanelExpanded}
            locked={isEditing}
            summary={
              !isEditing ? (
                <Title>{values.name}</Title>
              ) : (
                <div css={{ flex: 1 }}>
                  <TextField
                    label="Translation Name"
                    value={values.name}
                    onChange={makeFieldChangeHandler('name')}
                    error={errors?.name}
                    InputProps={{ sx: { fontWeight: '700' } }} // roughly match <Title>
                    css={{ width: '30em' }}
                    maxLength={255}
                  />
                </div>
              )
            }
            actions={
              <div css={{ display: 'flex', gap: '0.5em' }}>
                {(isStopped || isDeleting) && user.hasPermission(NAMCResource.translations, GrantAction.updateAny) && (
                  <Menu
                    label="Actions"
                    disabled={isDeleting}
                    items={[
                      {
                        key: 'edit',
                        label: 'Edit Translation',
                        onClick: handleEdit,
                      },
                      Divider,
                      {
                        key: 'delete',
                        label: 'Delete Translation',
                        onClick: () => {
                          setIsConfirmDeleteTranslationOpen(true)
                        },
                        disabled: !user.hasPermission(NAMCResource.translations, GrantAction.deleteAny),
                      },
                    ]}
                  ></Menu>
                )}
                {(isEditing || isSaving) && (
                  <>
                    {!isNewTranslation && (
                      <MuiButton onClick={handleCancel} variant="outlined" disabled={isSaving}>
                        Cancel
                      </MuiButton>
                    )}
                    <MuiButton
                      onClick={handleSaveTranslation}
                      variant="contained"
                      disabled={!isDirty || isSaving || !!errors}
                    >
                      Save
                    </MuiButton>
                  </>
                )}
              </div>
            }
          >
            <div
              css={{
                display: 'grid',
                gap: '0.75em',
                gridTemplateColumns: '1fr 1fr 4fr',
                gridTemplateAreas: `
                  "id     isActive     description"
                  ".      .     description"
                `,
              }}
            >
              <ReadOnlyField gridArea="id" value={translation?.id ?? 'TBD'} label="Translation ID" />
              <CheckboxField
                gridArea="isActive"
                value={!values.isActive}
                onChange={(value) => setFieldValue('isActive', !value)}
                label="Active"
                readOnly={!isEditing}
              />
              <TextField
                gridArea="description"
                value={values.description}
                onChange={(value) => setFieldValue('description', value)}
                label="Description"
                readOnly={!isEditing}
                multiline
                minRows={3}
              />
            </div>
          </AccordionPanel>
          <div css={{ display: 'flex' }}>
            <TextField
              value={textFilter}
              onChange={setTextFilter}
              label="OTU Name"
              css={{ width: '25em' }}
              disabled={!isStopped}
            />
            <div css={{ marginLeft: 'auto' }}>
              <MuiButton onClick={handleAddGroup} variant="outlined" startIcon={<AddIcon />} disabled={!isStopped}>
                Add OTU Name
              </MuiButton>
            </div>
          </div>
          <div css={{ minHeight: '2em', marginTop: '0.75em', display: 'flex' }}>
            <FilterChips filters={filters} joinLabel="and" />
            <DataGridRecordCount total={groups.length} visible={rows.length} descriptor="OTU Name" />
          </div>
          <div css={{ flex: 1 }}>
            <DataGrid<TranslationGroupRow>
              columnDefs={translationDetailColumnDefs}
              rows={rows}
              frameworkComponents={renderers}
            />
          </div>
        </div>
        {translation?.id && (
          <EditTranslationGroupDialog
            isOpen={isEditOpen}
            translationId={translation.id}
            targetGroupId={targetGroupId}
            onClose={() => setIsEditOpen(false)}
            onUpdate={handleUpdatedGroup}
            groups={groups}
            taxa={allTaxa}
          />
        )}
        <ConfirmationDialog
          title="Confirm Delete Translation"
          isOpen={isConfirmDeleteTranslationOpen}
          onClose={() => {
            setIsConfirmDeleteTranslationOpen(false)
          }}
          onConfirm={handleDeleteTranslation}
        >
          Are you sure you want to delete this translation?
        </ConfirmationDialog>
        <ConfirmationDialog
          title="Confirm Delete OTU Name"
          isOpen={isConfirmDeleteGroupOpen}
          onClose={() => {
            setIsConfirmDeleteGroupOpen(false)
          }}
          onConfirm={handleDeleteGroup}
        >
          Are you sure you want to delete {groups.find(({ id }) => id === targetGroupId)?.translationScientificName}?
        </ConfirmationDialog>
        {isDirty && <ConfirmNavigateAway message="Some changes are not saved. Are you sure you want to leave?" />}
      </>
    )
  },
  () => <Fallback />
)
