/** @jsxImportSource @emotion/react */
import React, { useMemo } from 'react'
import { atom, useRecoilState, useRecoilValue } from 'recoil'
import MuiButton from '@mui/material/Button'

import * as selectors from '../../data/selectors'
import { Model, withSuspense, Filter, FilterChips, TextFilterField, useTextFilterField } from '@namcbugdb/react-common'

import { DataGrid, renderers, DataGridRecordCount } from '../DataGrid'
import { modelsColumnDefs } from './columnDefs'
import { useNavigate } from '../AppRouter'
import { GrantAction, NAMCResource } from '@namcbugdb/common'
import { Fallback } from './Fallback'

export interface ModelRow extends Model {
  title: {
    label: string
    onClick: () => void
    disabled?: boolean
  }
}

const textFilterState = atom({
  key: 'ModelsListing/textFilterState',
  default: '',
})

export const ModelsListing = withSuspense(
  () => {
    const navigate = useNavigate()
    const user = useRecoilValue(selectors.selectMyUser)
    const models = useRecoilValue(selectors.selectModels)

    // STATE AND REFS

    const [textFilter, setTextFilter, textFilterDebounced, clearTextFilter] = useTextFilterField(
      useRecoilState(textFilterState)
    )

    // FILTERS

    const filters = useMemo(() => {
      const result: Filter<Model>[] = []

      if (textFilterDebounced) {
        result.push({
          id: 'text',
          label: `Name/Abbreviation contains "${textFilterDebounced}"`,
          filterFn: ({ name, abbreviation }) =>
            name.toLowerCase().includes(textFilterDebounced.toLowerCase()) ||
            abbreviation.toLowerCase().includes(textFilterDebounced.toLowerCase()),
          onDelete: clearTextFilter,
        })
      }

      return result
    }, [textFilterDebounced])

    const rows = useMemo<ModelRow[]>(() => {
      const filtered = filters.reduce<Model[]>((acc, { filterFn }) => acc.filter(filterFn), models)
      return filtered.map((model) => ({
        ...model,
        title: {
          label: String(model.id),
          onClick: () => {
            navigate.modelDetail({ modelId: model.id })
          },
        },
      }))
    }, [filters])

    // RENDER

    return (
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        <div css={{ display: 'flex', gap: '0.5em' }}>
          <TextFilterField value={textFilter} onChange={setTextFilter} label="Model Name or Abbreviation" />
          {user.hasPermission(NAMCResource.models, GrantAction.createAny) && (
            <div css={{ marginLeft: 'auto' }}>
              <MuiButton
                variant="outlined"
                onClick={() => {
                  navigate.modelCreate()
                }}
              >
                Create New Model
              </MuiButton>
            </div>
          )}
        </div>
        <div css={{ minHeight: '2em', marginTop: '0.75em', display: 'flex' }}>
          <FilterChips filters={filters} joinLabel="and" />
          <DataGridRecordCount total={models.length} visible={rows.length} descriptor="model" />
        </div>
        <div css={{ flex: 1 }}>
          <DataGrid<ModelRow> rows={rows} columnDefs={modelsColumnDefs} frameworkComponents={renderers} />
        </div>
      </div>
    )
  },
  () => <Fallback />
)
