import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

export const startTaxonomyIdentificationMutation = gql`
  mutation ($sampleId: Int!) {
    taxIdentificationStart(sampleId: $sampleId)
  }
`

export const startTaxonomyIdentification =
  (api: Api, invalidate: InvalidateFn) =>
  async ({ sampleId }: { sampleId: number }): Promise<true> => {
    const data = await api.mutation<{
      taxIdentificationStart: number // NOTE this is records affected, 1 = success, 0 = failure
    }>(startTaxonomyIdentificationMutation, { sampleId })

    if (!data.taxIdentificationStart) throw new ApiError('taxonomy identification did not start')

    invalidate('boxes')
    invalidate('samples')
    return true
  }
