import gql from 'graphql-tag'
import { Site as GraphSite } from '@namcbugdb/common'

import { Api } from '@namcbugdb/react-common'
import { Site } from '@namcbugdb/react-common'
import { transformSite } from '.'

export const fetchSitesPageQuery = gql`
  query ($offset: Int, $limit: Int) {
    mapSites(offset: $offset, limit: $limit) {
      records {
        siteId
        siteName
        longitude
        latitude
        usStateId
      }
      nextOffset
    }
  }
`

export const fetchSitesPage =
  (api: Api) =>
  async (offset: number, limit: number): Promise<{ records: Site[]; nextOffset: number }> => {
    const data = await api.query<{ mapSites: { records: GraphSite[]; nextOffset: number } }>(fetchSitesPageQuery, {
      offset,
      limit,
    })
    const records = data.mapSites.records.map(transformSite)
    const { nextOffset } = data.mapSites

    return { records, nextOffset }
  }
