import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

export interface TranslationInput {
  name: string // -> translationName
  isActive: boolean
  description: string
}

export const createTranslationMutation = gql`
  mutation ($translation: TranslationInput!) {
    insertTranslation(translation: $translation)
  }
`

export const createTranslation =
  (api: Api, invalidate: InvalidateFn) =>
  async (translation: TranslationInput): Promise<number> => {
    const { name: translationName, ...restTranslation } = translation
    const data = await api.mutation<{
      insertTranslation: number | null // NOTE: new translation ID
    }>(createTranslationMutation, {
      translation: { translationName, ...restTranslation },
    })

    const id = data.insertTranslation
    if (!id) throw new ApiError('translation did not create')

    invalidate('translations')
    return id
  }
