/** @jsxImportSource @emotion/react */
import React, { useMemo, useRef } from 'react'
import { atom, useRecoilState, useRecoilValue, selector } from 'recoil'
import MuiStepper from '@mui/material/Stepper'
import MuiStep from '@mui/material/Step'
import MuiStepLabel from '@mui/material/StepLabel'
import MuiButton from '@mui/material/Button'
import MuiRadio from '@mui/material/Radio'
import MuiRadioGroup from '@mui/material/RadioGroup'
import MuiFormControlLabel from '@mui/material/FormControlLabel'
import MuiFormControl from '@mui/material/FormControl'
import MuiFormLabel from '@mui/material/FormLabel'
import MuiLinearProgress from '@mui/material/LinearProgress'
import KeyboardArrowLeftIcon from '@mui/icons-material/KeyboardArrowLeft'
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight'
import SendIcon from '@mui/icons-material/Send'
import ErrorOutlineIcon from '@mui/icons-material/ErrorOutline'
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline'
import { GrantAction, NAMCResource } from '@namcbugdb/common'

import {
  MyUser,
  ReadOnlyField,
  useApi,
  useNotifications,
  withSuspense,
  AutocompleteField,
  useUpdatableState,
  TextField,
  appVersion,
  isStagingApi,
} from '@namcbugdb/react-common'

import { DataGrid } from '../DataGrid'
import { FileProblem, ProcessFileResult, PreparationPage, FinishedPage } from '../../lib/submissions'
import { problemsColumnDefs } from './columnDefs'
import { UploadFileBox } from './UploadFileBox'
import { useInvalidate } from '../../data/selectors'
import { createOrganization, createSampleSubmission } from '../../data/calls'
import {
  selectDefaultOrganizationTypeId,
  selectMyUser,
  selectOrganizations,
  selectUsCountryId,
  selectUsers,
  selectUsStates,
} from '../../data/selectors'
import { Fallback } from './Fallback'
import { useNavigate } from '../AppRouter'
import { StepPage } from './StepPage'

const TEMPLATE_URL = `${process.env.PUBLIC_URL}/namc_sample_upload_template.xlsx` // this file must be in react/app/public

export type ProcessingStatus = 'empty' | 'uploading' | 'processing' | 'processed' | 'submitting' | 'submitted'

export interface ProblemRow extends FileProblem {
  id: string
}

enum STEP {
  prepare = 0,
  upload = 1,
  review = 2,
  finished = 3,
}

const stepTitles = ['Download Submission Template', 'Upload Template', 'Review', 'Finished']

const getProblemRowId = (() => {
  const idDict = {}
  let nextId = 1
  return (problem: FileProblem) => {
    const key = JSON.stringify(problem)
    if (!idDict[key]) idDict[key] = nextId++
    return idDict[key]
  }
})()

const selectOrganizationOptions = selector({
  key: 'CustomerSubmission/selectOrganizationOptions',
  get: async ({ get }) => {
    const organizations = get(selectOrganizations)
    return organizations.map(({ id: value, name: label }) => ({ value, label }))
  },
})

const selectUserOptions = selector({
  key: 'CustomerSubmission/selectUserOptions',
  get: async ({ get }) => {
    const users = get(selectUsers)
    return users.map(({ id: value, fullName, organizationName }) => ({
      value,
      label: `${fullName} (${organizationName})`,
    }))
  },
})

const selectUsStateOptions = selector({
  key: 'CustomerSubmission/selectUsStateOptions',
  get: async ({ get }) => {
    const usStates = get(selectUsStates)
    return usStates.map(({ id: value, abbreviation: label }) => ({ value, label }))
  },
})

const expandJsonState = atom({
  key: 'CustomerSubmission/expandJsonState',
  default: false,
})

export const emptyProcessResult = { samples: [], info: {}, problems: [], templateVersion: null }

interface Fields {
  existingOrganizationId: number | null
  submitterId: number | null
  organizationMode: 'existing' | 'new'
  organizationName: string
  address1: string
  address2: string
  city: string
  usStateId: number | null
  zipCode: string
  phone: string
}

type FieldErrors = Partial<Record<keyof Fields, boolean>> | undefined

export interface State {
  // current
  processingStatus: ProcessingStatus
  processResult: ProcessFileResult
  filename: string
  processingToken: number
  errorMessage: string
  boxId?: number
  createdOrganizationId?: number

  // for revert
  lastProcessingStatus: ProcessingStatus
  lastFilename: string

  // field values
  values: Fields
}

const getInitialState = ({ user }: { user: MyUser }): State => ({
  processingStatus: 'empty',
  processResult: emptyProcessResult,
  filename: '',
  processingToken: 0,
  errorMessage: '',
  lastProcessingStatus: 'empty',
  lastFilename: '',
  values: {
    existingOrganizationId: user.affiliationId,
    submitterId: user.id,
    organizationMode: 'existing',
    organizationName: '',
    address1: '',
    address2: '',
    city: '',
    usStateId: null,
    zipCode: '',
    phone: '',
  },
})

const MessageBlock = (props) => (
  <div css={{ margin: '1em 0', fontWeight: 'bold', display: 'flex', alignItems: 'center' }} {...props} />
)

export const CustomerSubmission = withSuspense(
  (): JSX.Element => {
    const [expandJson, setExpandJson] = useRecoilState(expandJsonState)
    const user = useRecoilValue(selectMyUser)
    const countryId = useRecoilValue(selectUsCountryId)
    const organizationTypeId = useRecoilValue(selectDefaultOrganizationTypeId)

    const navigate = useNavigate()
    const api = useApi()
    const invalidate = useInvalidate()
    const notifications = useNotifications()
    const downloadTemplateLinkRef = useRef<HTMLAnchorElement | null>(null)

    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const [state, setState, updateState] = useUpdatableState<State>(getInitialState({ user }), [])
    const [activeStep, setActiveStep] = React.useState<STEP>(STEP.prepare)

    const { processingStatus, processingToken, processResult, errorMessage, filename, boxId, values } = state
    const errors: FieldErrors = (() => {
      let errors: FieldErrors = {}
      switch (values.organizationMode) {
        case 'existing':
          errors = {
            existingOrganizationId: !values.existingOrganizationId,
          }
          break
        case 'new': {
          errors = {
            organizationName: values.organizationName.trim().length < 3,
            address1: !values.address1.trim(),
            city: !values.city.trim(),
            zipCode: !values.zipCode.trim().match(/^[\d-]{5,}$/),
            usStateId: !values.usStateId,
            phone: !values.phone,
          }
          break
        }
        default:
      }
      if (!Object.values(errors).some((value) => value)) return undefined
      return errors
    })()

    const { samples, info, problems, templateVersion } = processResult

    const setFieldValue = (fieldName: keyof Fields, value: unknown) => {
      updateState((draft) => {
        Object.assign(draft.values, { [fieldName]: value })
      })
    }

    const handleSubmit = async () => {
      try {
        updateState((draft) => {
          draft.processingStatus = 'submitting'
        })
        let customerId: number | null
        switch (values.organizationMode) {
          case 'new': {
            // create organization
            if (typeof values.usStateId !== 'number') throw new Error('missing state')
            const newOrganizationId = await createOrganization(
              api,
              invalidate
            )({
              name: values.organizationName.trim(),
              typeId: organizationTypeId,
              address1: values.address1.trim(),
              address2: values.address2.trim(),
              city: values.city.trim(),
              phone: values.phone.trim(),
              usStateId: values.usStateId,
              countryId,
              zipCode: values.zipCode.trim(),
            })
            updateState((draft) => {
              draft.createdOrganizationId = newOrganizationId
            })
            customerId = newOrganizationId
            break
          }
          case 'existing': {
            if (!values.existingOrganizationId) throw new Error('missing customer ID')
            customerId = values.existingOrganizationId
            break
          }
        }
        const { submitterId } = values
        if (!submitterId) throw new Error('missing submitter ID')
        if (!templateVersion) throw new Error('parsing did not return template version')
        const newBoxId = await createSampleSubmission(
          api,
          invalidate
        )({
          customerId,
          submitterId,
          data: { appVersion: appVersion ?? 'unknown', templateVersion, samples },
        })
        updateState((draft) => {
          draft.processingStatus = 'submitted'
          draft.boxId = newBoxId
        })
        setActiveStep(STEP.finished)
      } catch (err) {
        updateState((draft) => {
          draft.processingStatus = 'processed'
        })
        notifications.error(`Failed to create submission: ${err}`)
        return
      }
    }

    const rows = useMemo(() => {
      return problems.map((problem) => ({ id: getProblemRowId(problem), ...problem }))
    }, [problems])

    const canSubmit = samples.length > 0 && problems.length === 0 && processingStatus === 'processed' && !errors

    const hasFatalProblem = processResult.problems.some(({ type }) => type === 'fatal')
    const hasOnlyRowProblems = !hasFatalProblem && processResult.problems.length > 0
    const hasNoSamples = processingStatus === 'processed' && processResult.samples.length === 0

    const uploadAttentionMessage = (() => {
      if (errorMessage) return <span>Error reading file. Did you choose the correct file? ({errorMessage})</span>
      if (hasFatalProblem)
        return (
          <span>Error processing file. Did you choose the correct file? ({processResult.problems[0].message})</span>
        )
      if (hasOnlyRowProblems)
        return <span>Correct the following {processResult.problems.length} problems and try uploading again:</span>
      if (hasNoSamples) return <span>File does not contain any samples.</span>
      return null
    })()

    // PERMISSIONS

    const canChangeUser = user.hasPermission(NAMCResource.individuals, GrantAction.readAny)
    const canChangeOrganization =
      user.hasPermission(NAMCResource.organizations, GrantAction.readAny) &&
      user.hasPermission(NAMCResource.organizations, GrantAction.createAny)

    // RENDER

    return (
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column', maxWidth: '70rem' }}>
        <MuiStepper activeStep={activeStep}>
          {stepTitles.map((stepTitle) => (
            <MuiStep key={stepTitle}>
              <MuiStepLabel>{stepTitle}</MuiStepLabel>
            </MuiStep>
          ))}
        </MuiStepper>
        <div css={{ display: 'flex', alignItems: 'center', margin: '2em 0' }}>
          <div>
            <MuiButton
              variant="outlined"
              onClick={() => setActiveStep((s) => s - 1)}
              disabled={
                activeStep === STEP.prepare || activeStep === STEP.finished || processingStatus === 'submitting'
              }
            >
              <KeyboardArrowLeftIcon />
              Back
            </MuiButton>
          </div>
          <div css={{ flex: 1, textAlign: 'center', fontSize: '125%' }}>
            Step {activeStep + 1}: {stepTitles[activeStep]}
          </div>
          <div>
            {activeStep < STEP.review && (
              <MuiButton
                variant="outlined"
                onClick={() => setActiveStep((s) => s + 1)}
                disabled={activeStep === STEP.upload && !canSubmit}
              >
                Next
                <KeyboardArrowRightIcon />
              </MuiButton>
            )}
            {activeStep >= STEP.review && (
              <MuiButton
                variant={activeStep === STEP.finished ? 'outlined' : 'contained'}
                onClick={handleSubmit}
                disabled={!canSubmit || activeStep === STEP.finished}
              >
                Submit&nbsp;
                <SendIcon />
              </MuiButton>
            )}
          </div>
        </div>
        <div css={{ flex: 1, position: 'relative' }}>
          {/* PREPARE */}
          <StepPage active={activeStep === STEP.prepare}>
            <a
              ref={downloadTemplateLinkRef}
              href={TEMPLATE_URL}
              css={{ visibility: 'hidden', position: 'absolute' }}
              download
            ></a>
            <PreparationPage
              onDownloadTemplate={() => {
                downloadTemplateLinkRef.current?.click()
              }}
            />
          </StepPage>
          {/* UPLOAD */}
          <StepPage
            active={activeStep === STEP.upload}
            css={{
              display: 'flex',
              flexDirection: 'column',
            }}
          >
            <UploadFileBox
              updateState={updateState}
              processingToken={processingToken}
              processingStatus={processingStatus}
              filename={filename}
            />
            {uploadAttentionMessage && (
              <MessageBlock>
                <ErrorOutlineIcon />
                &nbsp;{uploadAttentionMessage}
              </MessageBlock>
            )}
            {canSubmit && (
              <MessageBlock>
                <CheckCircleOutlineIcon />
                &nbsp;Ready to review. Click Next to continue.
              </MessageBlock>
            )}
            {hasOnlyRowProblems && (
              <div css={{ flex: 1 }}>
                <DataGrid<ProblemRow> columnDefs={problemsColumnDefs} rows={rows} />
              </div>
            )}
          </StepPage>
          {/* REVIEW */}
          <StepPage active={activeStep === STEP.review && processingStatus !== 'submitting'}>
            <div css={{ display: 'flex', gap: '1em' }}>
              <div css={{ minWidth: '20em', flex: 1 }}>
                <MuiFormControl component="fieldset" css={{ width: '100%' }}>
                  <MuiFormLabel component="legend">User</MuiFormLabel>
                  {canChangeUser ? (
                    <AutocompleteField
                      optionsSelector={selectUserOptions}
                      valueLabel={user.fullName}
                      value={values.submitterId}
                      onChange={(value) => setFieldValue('submitterId', value)}
                    />
                  ) : (
                    <ReadOnlyField value={user.fullName} />
                  )}
                </MuiFormControl>
                <div css={{ paddingTop: '1em' }}>
                  <MuiFormControl component="fieldset" css={{ width: '100%' }}>
                    <MuiFormLabel component="legend">Organization</MuiFormLabel>
                    {canChangeOrganization && (
                      <MuiRadioGroup
                        row
                        value={values.organizationMode}
                        onChange={(_, newValue) => setFieldValue('organizationMode', newValue)}
                      >
                        <MuiFormControlLabel value="existing" control={<MuiRadio />} label="Existing" />
                        <MuiFormControlLabel value="new" control={<MuiRadio />} label="New" />
                      </MuiRadioGroup>
                    )}
                  </MuiFormControl>
                </div>
                {values.organizationMode === 'existing' &&
                  (canChangeOrganization ? (
                    <AutocompleteField
                      optionsSelector={selectOrganizationOptions}
                      valueLabel={user.organizationName}
                      value={values.existingOrganizationId}
                      onChange={(value) => setFieldValue('existingOrganizationId', value)}
                      error={errors?.existingOrganizationId}
                    />
                  ) : (
                    <ReadOnlyField value={user.organizationName} />
                  ))}
                {values.organizationMode === 'new' && (
                  <div
                    css={{
                      display: 'grid',
                      gap: '0.75em',
                      gridTemplateColumns: '1fr 1fr',
                      gridTemplateAreas: `
                        "organizationName organizationName"
                        "address1         address1"
                        "address2         address2"
                        "city             city"
                        "usStateId        zipCode"
                        "phone            ."
                      `,
                    }}
                  >
                    <TextField
                      gridArea="organizationName"
                      value={values.organizationName}
                      onChange={(value) => setFieldValue('organizationName', value)}
                      label="Organization Name"
                      error={errors?.organizationName}
                    />
                    <TextField
                      gridArea="address1"
                      value={values.address1}
                      onChange={(value) => setFieldValue('address1', value)}
                      label="Address 1"
                      error={errors?.address1}
                    />
                    <TextField
                      gridArea="address2"
                      value={values.address2}
                      onChange={(value) => setFieldValue('address2', value)}
                      label="Address 2"
                      error={errors?.address2}
                    />
                    <TextField
                      gridArea="city"
                      value={values.city}
                      onChange={(value) => setFieldValue('city', value)}
                      label="City"
                      error={errors?.city}
                    />
                    <AutocompleteField
                      gridArea="usStateId"
                      value={values.usStateId}
                      valueLabel="State"
                      onChange={(value) => setFieldValue('usStateId', value)}
                      optionsSelector={selectUsStateOptions}
                      label="State"
                      error={errors?.usStateId}
                      isClearable
                    />
                    <TextField
                      gridArea="zipCode"
                      value={values.zipCode}
                      onChange={(value) => setFieldValue('zipCode', value)}
                      label="Zip"
                      error={errors?.zipCode}
                    />
                    <TextField
                      gridArea="phone"
                      value={values.phone}
                      onChange={(value) => setFieldValue('phone', value)}
                      label="Phone"
                      error={errors?.phone}
                    />
                  </div>
                )}
              </div>
              <div css={{ flex: 1 }}>
                <MuiFormLabel component="legend">Submission Summary</MuiFormLabel>
                <table
                  css={{
                    borderCollapse: 'collapse',
                    th: { fontWeight: 'normal' },
                    td: { border: '1px solid grey' },
                    'th,td': { padding: '0.75em' },
                  }}
                >
                  <thead>
                    <tr>
                      <th>Sample Type</th>
                      <th># Samples</th>
                    </tr>
                  </thead>
                  <tbody>
                    {Object.entries(info).map(([wsName, sampleCount]) => (
                      <tr key={wsName}>
                        <td>{wsName}</td>
                        <td css={{ textAlign: 'right' }}>{sampleCount}</td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
            {canSubmit && (
              <MessageBlock>
                <CheckCircleOutlineIcon />
                &nbsp;Ready to submit. Click Submit to finish.
              </MessageBlock>
            )}
            {errors && (
              <MessageBlock>
                <ErrorOutlineIcon />
                &nbsp;Correct the above address issues.
              </MessageBlock>
            )}
            {isStagingApi && (
              <div>
                <MuiButton
                  onClick={() => {
                    setExpandJson((v) => !v)
                  }}
                >
                  {expandJson ? 'Hide Sample Data' : 'Show Sample Data'}
                </MuiButton>
                {expandJson && <pre>{JSON.stringify(samples, null, 2)}</pre>}
              </div>
            )}
          </StepPage>
          <StepPage active={activeStep === STEP.review && processingStatus === 'submitting'}>
            <MuiLinearProgress />
          </StepPage>
          {/* FINISHED */}
          <StepPage active={activeStep === STEP.finished}>
            {boxId && (
              <FinishedPage
                boxId={boxId}
                onGoToBoxes={() => navigate.boxes()}
                onGoToBoxDetail={() => navigate.boxDetail({ boxId })}
                onGoToChainOfCustody={() => navigate.chainOfCustodyForm({ boxId })}
              />
            )}
          </StepPage>
        </div>
      </div>
    )
  },
  () => <Fallback />
)
