import gql from 'graphql-tag'
import { PaginatedRecords, Individual as GraphIndividual } from '@namcbugdb/common'

import { Api } from '@namcbugdb/react-common'
import { User } from '@namcbugdb/react-common'

export const fetchUsersQuery = gql`
  query ($offset: Int) {
    users(limit: 1000, offset: $offset) {
      records {
        id
        firstName
        lastName
        email
        phone
        organizationName
        affiliationId
        roleIds
      }
      nextOffset
    }
  }
`

export const fetchUsers = (api: Api) => async (): Promise<User[]> => {
  const data = await api.queryAllPages<{ users: PaginatedRecords<GraphIndividual> }>(
    fetchUsersQuery,
    {},
    { dataProperty: 'users' }
  )
  const { records } = data.users

  return records.map((user) => ({
    fullName: `${user.firstName} ${user.lastName}`,
    ...user,
    roleIds: user.roleIds || [],
    organizationName: user.organizationName || '',
  }))
}
