import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

interface MergeBoxesParams {
  sourceBoxId: number
  targetBoxId: number
}

export const mergeBoxesMutation = gql`
  mutation ($sourceBoxId: Int!, $targetBoxId: Int!) {
    mergeBoxes(sourceBoxId: $sourceBoxId, targetBoxId: $targetBoxId)
  }
`

export const mergeBoxes =
  (api: Api, invalidate: InvalidateFn) =>
  async (params: MergeBoxesParams): Promise<true> => {
    const data = await api.mutation<{
      mergeBoxes: number // NOTE: TODO what is return?
    }>(mergeBoxesMutation, { ...params })

    if (!data.mergeBoxes) throw new ApiError('boxes did not merge')

    invalidate('boxes')
    invalidate('samples')
    invalidate('boxSamples')
    return true
  }
