import gql from 'graphql-tag'
import { PaginatedRecords, Sample as GraphSample } from '@namcbugdb/common'

import { Api } from '@namcbugdb/react-common'
import { MinimalSample } from '@namcbugdb/react-common'

export const fetchMinimalSamplesByIdsQuery = gql`
  query ($sampleIds: [Int], $offset: Int) {
    samples(limit: 1000, offset: $offset, sampleIds: $sampleIds) {
      records {
        sampleId
        sampleDate
        sampleType
        sampleMethod
        area
      }
      nextOffset
    }
  }
`

export const transformMinimalSample = ({
  sampleId: id,
  sampleType: type,
  sampleMethod: method,
  ...restSample
}: GraphSample): MinimalSample => ({
  id,
  type,
  method,
  ...restSample,
})

export const fetchMinimalSamplesByIds =
  (api: Api) =>
  async (sampleIds: number[]): Promise<MinimalSample[]> => {
    const data = await api.queryAllPages<{
      samples: PaginatedRecords<GraphSample>
    }>(fetchMinimalSamplesByIdsQuery, { sampleIds }, { dataProperty: 'samples' })

    return data.samples.records.map(transformMinimalSample)
  }
