import { ColDef } from 'ag-grid-community'
import { columnDef } from '../DataGrid'

export const makeBoxTaxonomyColumnDefs = (taxonomistNames: string[]): ColDef[] => [
  columnDef.textColumn({
    field: 'order',
    minWidth: 160,
    flex: 1,
  }),
  columnDef.textColumn({
    field: 'family',
    minWidth: 160,
    flex: 1,
  }),
  columnDef.textColumn({
    headerName: 'Scientific Name',
    field: 'name',
    minWidth: 160,
    flex: 1,
  }),
  ...taxonomistNames.map((name) =>
    columnDef.numberColumn({
      headerName: name,
      field: `taxonomistCounts.${name}`,
      minWidth: 160,
      flex: 1,
    })
  ),
  columnDef.numberColumn({
    headerName: 'Total Sample Count',
    field: 'sampleCount',
    minWidth: 160,
  }),
  columnDef.numberColumn({
    headerName: 'Total Split Count',
    field: 'totalCount',
    minWidth: 160,
  }),
]
