import { ColDef } from 'ag-grid-community'

import { OrganismRow, OrganismValueSetterParams, OrganismCellClassParams } from './types'

interface RowNode {
  data: OrganismRow
}

const singleValueSetter = ({
  colDef: { field },
  newValue: value,
  data,
  context: { actions },
}: OrganismValueSetterParams) => {
  actions.updateOrganism({ ...data, [field]: value })
  return false
}

const notesAndCommentsValueSetter = ({
  newValue: { noteTypeIds, comments },
  data,
  context: { actions },
}: OrganismValueSetterParams) => {
  actions.updateOrganism({ ...data, noteTypeIds, comments })
  return false
}

export const organismsColumnDefs = [
  {
    headerName: '',
    field: 'persistence',
    width: 70,
    hide: true,
    cellRenderer: 'PersistenceRenderer',
  },
  {
    headerName: 'Taxon',
    field: 'taxonName',
    width: 300,
    cellRenderer: 'TaxonRenderer',
  },
  {
    field: 'levelName',
    headerName: 'Level',
    width: 150,
    comparator: (valueA, valueB, nodeA: RowNode, nodeB: RowNode) =>
      nodeA.data.levelRankOrder - nodeB.data.levelRankOrder,
  },
  {
    field: 'lifeStageId',
    headerName: 'Life Stage',
    width: 150,
    editable: true,
    cellEditor: 'LifeStageEditor',
    cellRenderer: 'LifeStageRenderer',
    valueSetter: singleValueSetter,
    cellClassRules: {
      'DataGrid__cell--error': ({ value }: OrganismCellClassParams) => !value,
    },
    comparator: (valueA, valueB, nodeA: RowNode, nodeB: RowNode) =>
      nodeA.data.lifeStageRankOrder - nodeB.data.lifeStageRankOrder,
  },
  {
    field: 'splitCount',
    width: 150,
    editable: true,
    cellEditor: 'NumberEditor',
    cellRenderer: 'NumberRenderer',
    type: 'numericColumn',
    valueSetter: singleValueSetter,
    cellClassRules: {
      'DataGrid__cell--error': ({ data, value }: OrganismCellClassParams) => data.persistence === 'unsaved' && value,
    },
  },
  {
    field: 'bigRareCount',
    width: 150,
    editable: true,
    cellEditor: 'NumberEditor',
    cellRenderer: 'NumberRenderer',
    type: 'numericColumn',
    valueSetter: singleValueSetter,
    cellClassRules: {
      'DataGrid__cell--error': ({ data, value }: OrganismCellClassParams) => data.persistence === 'unsaved' && value,
    },
  },
  {
    field: 'bugSize',
    headerName: 'Size',
    width: 150,
    editable: true,
    cellEditor: 'NumberEditor',
    cellEditorParams: {
      precisionAllowed: 1,
    },
    cellRenderer: 'NumberRenderer',
    type: 'numericColumn',
    valueSetter: singleValueSetter,
    cellClassRules: {
      'DataGrid__cell--error': (params) => !params.value,
    },
  },
  {
    colId: 'notesAndComments',
    headerName: 'Notes/Comments',
    width: 150,
    editable: true,
    cellRenderer: 'NotesAndCommentsRenderer',
    cellEditor: 'NotesAndCommentsEditor',
    valueSetter: notesAndCommentsValueSetter,
    flex: 1,
    cellClassRules: {
      'DataGrid__cell--error': ({ data }: OrganismCellClassParams) =>
        data.persistence === 'unsaved' && (data.noteTypeIds.length > 0 || data.comments),
    },
  },
  {
    colId: 'actions',
    width: 150,
    cellRenderer: 'ActionsRenderer',
    pinned: 'right',
  },
] as ColDef[]
