import { ColDef } from 'ag-grid-community'

import { columnDef } from '../DataGrid'
import { ProblemRow } from './CustomerSubmission'

const columnNumberToLetters = (columnNumber) => {
  let col = columnNumber - 1
  let letters = ''
  while (col >= 0) {
    letters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'[col % 26] + letters
    col = Math.floor(col / 26) - 1
  }
  return letters
}

export const problemsColumnDefs = [
  // columnDef.numberColumn({
  //   field: 'id',
  //   width: 100,
  // }),
  columnDef.textColumn({
    headerName: 'Worksheet',
    field: 'wsName',
    width: 150,
  }),
  columnDef.textColumn({
    headerName: 'Cell',
    colId: 'cell',
    valueGetter: ({ data }: { data: ProblemRow }): string | null => {
      const { columnNumber, rowNumber } = data
      if (!columnNumber || !rowNumber) return null
      return `${columnNumberToLetters(columnNumber)}${rowNumber}`
    },
    width: 100,
  }),
  columnDef.textColumn({
    headerName: 'Problem',
    field: 'message',
    flex: 1,
    minWidth: 150,
  }),
  columnDef.numberColumn({
    headerName: 'Column #',
    field: 'columnNumber',
    width: 100,
  }),
  columnDef.numberColumn({
    headerName: 'Row #',
    field: 'rowNumber',
    width: 100,
  }),
] as ColDef[]
