import gql from 'graphql-tag'
import { PaginatedRecords, Sample as GraphSample } from '@namcbugdb/common'

import { Api } from '@namcbugdb/react-common'
import { SourceSample } from '@namcbugdb/react-common'

export const fetchBoxSamplesByBoxIdQuery = gql`
  query ($boxId: Int!, $offset: Int) {
    samples(limit: 1000, offset: $offset, boxId: $boxId) {
      records {
        sampleId
        sampleDate
        usState
        sampleType
        sampleMethod
        sorterName
        sortStartDate
        sortEndDate
        idErName
        idStartDate
        idEndDate
        customerSiteCode
        visitId
        siteName
        siteId
        area
      }
      nextOffset
    }
  }
`

export const transformSourceSample = (sample: GraphSample): SourceSample => {
  // Note: SourceSamples are also returned by project samples; code does not have a better home

  const {
    sampleId: id,
    sampleType: type,
    sampleMethod: method,
    idErName: identifierName,
    idStartDate: identificationStartDate,
    idEndDate: identificationEndDate,
    ...restSample
  } = sample

  return {
    ...restSample,
    id,
    type,
    method,
    identifierName,
    identificationStartDate,
    identificationEndDate,
  }
}

export const fetchBoxSamplesByBoxId =
  (api: Api) =>
  async (boxId: number): Promise<SourceSample[]> => {
    const data = await api.queryAllPages<{
      samples: PaginatedRecords<GraphSample>
    }>(fetchBoxSamplesByBoxIdQuery, { boxId }, { dataProperty: 'samples' })

    return data.samples.records.map(transformSourceSample)
  }
