import gql from 'graphql-tag'
import { Site as GraphSite } from '@namcbugdb/common'

import { Api } from '@namcbugdb/react-common'
import { Site } from '@namcbugdb/react-common'

export const fetchSitesQuery = gql`
  query ($offset: Int) {
    mapSites(limit: 10000, offset: $offset) {
      records {
        siteId
        siteName
        longitude
        latitude
        usStateId
      }
      nextOffset
    }
  }
`

export const transformSite = ({ siteId: id, siteName: name, ...rest }: GraphSite): Site => ({
  id,
  name,
  ...rest,
})

export const fetchSites = (api: Api) => async (): Promise<Site[]> => {
  const data = await api.queryAllPages<{ mapSites: { records: GraphSite[] } }>(
    fetchSitesQuery,
    {},
    {
      dataProperty: 'mapSites',
    }
  )
  const sites = data.mapSites.records

  return sites.map(transformSite)
}
