import React from 'react'
import { useRecoilValue } from 'recoil'
import * as selectors from '../../data/selectors'
import { withSuspense } from '@namcbugdb/react-common'
import { ProjectDetail as Component } from './ProjectDetail'
import { Fallback } from './Fallback'

export type ProjectDetailProps =
  | {
      projectId: string | number
      isNew?: never
    }
  | {
      projectId?: never
      isNew: true // unconsumed; used to positively differentiate modes
    }

export const ProjectDetail = withSuspense(({ projectId }: ProjectDetailProps): JSX.Element => {
  const project = useRecoilValue(projectId ? selectors.selectProjectById(Number(projectId)) : selectors.selectNull)

  if (projectId && !project) return <h2>Project {projectId} not found.</h2>

  return <Component project={project} />
}, Fallback)
