import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

export interface SiteInput {
  name: string // -> siteName
  latitude: number
  longitude: number
  waterbodyName: string | null
  waterbodyTypeId: number | null
  waterbodyCode: string | null
  description: string | null
  isReference: boolean
}

export const createSiteMutation = gql`
  mutation ($site: SiteInput!) {
    createSite(site: $site)
  }
`

export const createSite =
  (api: Api, invalidate: InvalidateFn) =>
  async (site: SiteInput): Promise<number> => {
    const { name: siteName, ...restSite } = site
    const data = await api.mutation<{
      createSite: number | null // NOTE: new site ID
    }>(createSiteMutation, { site: { siteName, ...restSite } })

    const id = data.createSite
    if (!id) throw new ApiError('site did not create')

    invalidate('sites')
    return id
  }
