/** @jsxImportSource @emotion/react */
import React, { useMemo, useRef, useState, useEffect, useCallback } from 'react'
import { useRecoilValue } from 'recoil'
import { useHotkeys } from 'react-hotkeys-hook'
import { sumBy, uniqBy } from 'lodash'
import { GrantAction, NAMCResource } from '@namcbugdb/common'
import AddIcon from '@mui/icons-material/AddCircleOutline'
import FilterAltIcon from '@mui/icons-material/FilterAlt'

import { DataGrid, DataGridApis, DataGridSummaryArea } from '../DataGrid'
import {
  ConfirmationDialog,
  TaxonomyField,
  TaxonomyFieldOption,
  TaxonomyFieldActionButton,
  Taxon,
  Sample,
  Organism,
  withSuspense,
  FilterChips,
  pluralize,
} from '@namcbugdb/react-common'
import * as selectors from '../../data/selectors'

import { OrganismsDataGridContext, OrganismRow } from './types'
import { useComponentController } from './componentController'
import { organismsColumnDefs } from './columnDefs'
import { SampleInfoPanel } from '../SampleInfoPanel'
import { TimeEntryDialog } from './TimeEntryDialog'
import { ToggleEditModeButton } from './ToggleEditModeButton'
import { ConfirmNavigateAway } from '../AppRouter'
import * as renderers from './renderers'

import { Fallback } from './Fallback'

export interface SampleTaxonomyIdentificationProps {
  sample: Sample
  sampleOrganisms: Organism[]
}

const gridOptions = {
  stopEditingWhenCellsLoseFocus: true,
  suppressCellSelection: false,
}

export const SampleTaxonomyIdentification = withSuspense(
  ({ sample, sampleOrganisms }: SampleTaxonomyIdentificationProps): JSX.Element => {
    const taxa = useRecoilValue(selectors.selectTaxa)
    const lifeStages = useRecoilValue(selectors.selectLifeStages)
    const noteTypes = useRecoilValue(selectors.selectNoteTypes)
    const user = useRecoilValue(selectors.selectMyUser)

    // STATE AND REFS

    const [state, actions] = useComponentController()
    const { organisms, organismFilters, editMode, scrollToRowId } = state
    const { measurements } = state.sample || {}
    const isEditing = editMode === 'started'

    const [deletionCanddateOrganism, setDeletionCandidateOrganism] = useState<OrganismRow | null>(null)
    const [gridApis, setGridApis] = useState<DataGridApis>()

    const inputRef = useRef<HTMLInputElement | null>(null)

    // EFFECTS

    useEffect(() => {
      actions.reset({ sample, organisms: sampleOrganisms, user })
    }, [sample, sampleOrganisms, user])

    useEffect(() => {
      if (!gridApis) return
      gridApis.columnApi.setColumnVisible('bugSize', !!measurements)
    }, [gridApis, measurements])

    useEffect(() => {
      if (!gridApis) return
      gridApis.columnApi.setColumnVisible('persistence', isEditing)
    }, [gridApis, isEditing])

    useEffect(() => {
      if (!gridApis || !scrollToRowId) return
      window.setTimeout(() => {
        gridApis.api.ensureNodeVisible(({ data: { rowId } }) => rowId === scrollToRowId)
      })
      actions.setScrollToRowId(null)
    }, [gridApis, scrollToRowId, actions.setScrollToRowId])

    // DATA GRID CONTEXT

    const context = useMemo<OrganismsDataGridContext>(
      () => ({
        lifeStages,
        noteTypes,
        actions,
        setDeletionCandidateOrganism,
      }),
      [lifeStages, noteTypes, actions]
    )

    // ROWS

    const getTaxonById = useCallback((taxonId: number) => taxa.find(({ id }) => id === taxonId), [taxa])

    const organismFilterTaxons = useMemo<Taxon[]>(
      () =>
        organismFilters.reduce<Taxon[]>((acc, { taxonId }) => {
          const taxon = getTaxonById(taxonId)
          if (!taxon) return acc
          return [...acc, taxon]
        }, []),
      [organismFilters, getTaxonById]
    )

    const filterTaxonIds = useMemo<number[] | null>(() => {
      if (organismFilterTaxons.length === 0) return null
      return organismFilterTaxons.reduce(
        (acc, { id, descendantIds }) => [...acc, id, ...(descendantIds || [])],
        [] as number[]
      )
    }, [organismFilterTaxons])

    const rows = useMemo<OrganismRow[]>(() => {
      const filteredOrganisms = filterTaxonIds
        ? organisms.filter(({ taxonId }) => filterTaxonIds.includes(taxonId))
        : organisms

      const activeLifeStages = lifeStages.filter(({ isActive }) => isActive)

      return filteredOrganisms
        .filter((organism) => organism.persistence !== 'deleting')
        .map((organism) => {
          const taxon = getTaxonById(organism.taxonId)

          // computed
          const taxonName = taxon?.name || '?'
          const levelName = taxon?.levelName || '?'
          const levelRankOrder = taxon?.levelRankOrder ?? -1
          const lifeStageRankOrder = lifeStages.find(({ id }) => id === organism.lifeStageId)?.rankOrder ?? 0
          const isTargetEffort = taxon?.isTargetEffort ?? false

          // row permissions/options
          const canDuplicate =
            isEditing &&
            (sample.measurements ||
              organisms.filter(({ taxonId }) => taxonId === organism.taxonId).length < activeLifeStages.length)
          const canFilter = !organismFilters.find(({ taxonId }) => taxonId === organism.taxonId)
          const canDelete = isEditing
          const canEdit = isEditing
          const usedLifeStageIds = organisms
            .filter(({ id, taxonId }) => taxonId === organism.taxonId && id !== organism.id)
            .map(({ lifeStageId }) => lifeStageId)
          const allowedLifeStages = activeLifeStages.filter(({ id }) => !usedLifeStageIds.includes(id))

          return {
            ...organism,
            taxonName,
            levelName,
            levelRankOrder,
            lifeStageRankOrder,
            isTargetEffort,
            canDuplicate,
            canFilter,
            canDelete,
            canEdit,
            allowedLifeStages,
          }
        })
    }, [getTaxonById, filterTaxonIds, organisms, organismFilters, lifeStages, isEditing])

    // UI OPTIONS

    const taxonOptions = useMemo<TaxonomyFieldOption[]>(() => {
      const organismTaxonIds = organisms.map(({ taxonId }) => taxonId)
      const added = taxa
        .filter((taxon) => organismTaxonIds.includes(taxon.id))
        .map((taxon) => ({ ...taxon, status: 'in' as const }))
      const available = taxa
        .filter((taxon) => !organismTaxonIds.includes(taxon.id))
        .map((taxon) => ({ ...taxon, status: 'notIn' as const }))

      const onAddOrganism = (taxon) => {
        actions.createOrganism({
          taxonId: taxon.id,
        })
        if (filterTaxonIds && !filterTaxonIds.includes(taxon.id)) actions.createFilter({ taxonId: taxon.id })
      }
      const onAddFilter = (taxon) => actions.createFilter({ taxonId: taxon.id })

      return [...added, ...available].map((taxonOption) => {
        const onChoose = isEditing ? () => onAddOrganism(taxonOption) : () => onAddFilter(taxonOption)
        const optionActions = (
          <div css={{ display: 'grid', gridGap: '0.25em', gridTemplateColumns: 'auto auto' }}>
            <TaxonomyFieldActionButton
              onClick={() => onAddOrganism(taxonOption)}
              isDefault={isEditing}
              disabled={!isEditing}
              startIcon={<AddIcon />}
            >
              Add Organism
            </TaxonomyFieldActionButton>
            <TaxonomyFieldActionButton
              onClick={() => onAddFilter(taxonOption)}
              isDefault={!isEditing}
              startIcon={<FilterAltIcon />}
            >
              Add Filter
            </TaxonomyFieldActionButton>
          </div>
        )

        return {
          ...taxonOption,
          actions: optionActions,
          onChoose,
        }
      })
    }, [taxa, organisms, isEditing])

    // HOTKEYS

    useHotkeys('/', (e) => {
      e.preventDefault()
      e.stopPropagation()
      inputRef.current?.focus()
    })

    // RENDER

    const canIdentify = user.hasPermission(NAMCResource.organisms, GrantAction.createAny)
    const canLeave = rows.every(({ persistence }) => persistence === 'unsaved' || persistence === 'saved')

    const filteredOrganismSum = sumBy(rows, ({ splitCount }) => splitCount)
    const filteredTaxonCount = uniqBy(rows, ({ taxonId }) => taxonId).length
    const filteredRowCount = rows.length

    return (
      <>
        <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
          {state.sample && (
            <SampleInfoPanel
              sample={state.sample}
              actions={
                canIdentify && (
                  <ToggleEditModeButton
                    userId={user.id}
                    sample={state.sample}
                    actions={actions}
                    editMode={state.editMode}
                    canLeave={canLeave}
                  />
                )
              }
            />
          )}
          <div>
            <TaxonomyField inputRef={inputRef} options={taxonOptions} />
          </div>
          <div css={{ minHeight: '2em', marginTop: '0.75em', display: 'flex' }}>
            <FilterChips
              filters={organismFilters.map(({ taxonId }) => {
                const taxon = getTaxonById(taxonId)
                return {
                  id: String(taxonId),
                  filterFn: () => true, // this component uses a pre-standardization filter object; this is just to satisfy FilterChips
                  label: `${taxon?.levelName}: ${taxon?.name}`,
                  onDelete: () => actions.deleteFilter({ taxonId }),
                }
              })}
              joinLabel="or"
            />
            <DataGridSummaryArea>
              {`${filteredOrganismSum} ${pluralize(
                filteredOrganismSum,
                'organism'
              )}, across ${filteredTaxonCount} ${pluralize(
                filteredTaxonCount,
                'taxon'
              )} and ${filteredRowCount} ${pluralize(filteredRowCount, 'record')}${
                organisms.length === filteredRowCount ? '' : ' (filtered)'
              }`}
            </DataGridSummaryArea>
          </div>
          <div css={{ flex: 1 }}>
            <DataGrid<OrganismRow>
              rows={rows}
              rowIdProperty="rowId"
              columnDefs={organismsColumnDefs}
              frameworkComponents={renderers}
              context={context}
              gridOptions={gridOptions}
              onGridApisReady={setGridApis}
            />
          </div>
        </div>
        <ConfirmationDialog
          title="Confirm Delete Organism"
          isOpen={!!deletionCanddateOrganism}
          onClose={() => setDeletionCandidateOrganism(null)}
          onConfirm={() => {
            if (!deletionCanddateOrganism) return
            actions.deleteOrganism(deletionCanddateOrganism)
          }}
          confirmLabel="Delete"
        ></ConfirmationDialog>
        <TimeEntryDialog
          isOpen={editMode === 'stoppingPrompt'}
          onConfirm={({ time }) => {
            actions.finishTaxonomyIdentification({ time })
          }}
        />
        {!canLeave && (
          <ConfirmNavigateAway message="Some data hasn't finished saving or deleting. Are you sure you want to leave?" />
        )}
      </>
    )
  },
  Fallback
)
