/** @jsxImportSource @emotion/react */
import React, { useMemo } from 'react'
import MuiButton from '@mui/material/Button'
import MuiDialog from '@mui/material/Dialog'
import MuiDialogTitle from '@mui/material/DialogTitle'
import MuiDialogActions from '@mui/material/DialogActions'
import MuiDialogContent from '@mui/material/DialogContent'
import MuiFormControl from '@mui/material/FormControl'
import MuiInputLabel from '@mui/material/InputLabel'
import AddIcon from '@mui/icons-material/AddCircleOutline'
// import log from 'loglevel'

import * as calls from '../../data/calls'
import * as selectors from '../../data/selectors'
import {
  Taxon,
  TranslationGroup,
  withSuspense,
  useUpdatableState,
  useNotifications,
  Chip,
  useApi,
  TaxonomyFieldOption,
  TaxonomyField,
  TaxonomyFieldActionButton,
  TextField,
  CheckboxField,
} from '@namcbugdb/react-common'

export interface EditTranslationGroupDialogProps {
  isOpen: boolean
  translationId: number
  targetGroupId?: number
  onClose: () => void
  onUpdate: (updatedTranslationGroup: TranslationGroup) => void
  groups: TranslationGroup[]
  taxa: Taxon[]
}

interface Fields {
  translationScientificName: string
  isFinal: boolean
  taxonomyIds: number[]
}

type FieldErrors = Partial<Record<keyof Fields, boolean>> | undefined

interface State {
  editMode: 'started' | 'stopping'
  isDirty: boolean
  values: Fields
}

const getInitialState = ({ group }: { group?: TranslationGroup }): State => {
  return {
    editMode: 'started',
    isDirty: false,
    values: {
      translationScientificName: group?.translationScientificName || '',
      isFinal: group?.isFinal || true,
      taxonomyIds: group?.taxonomyIds || [],
    },
  }
}

export const EditTranslationGroupDialog = withSuspense(
  ({
    isOpen,
    onClose,
    onUpdate,
    translationId,
    targetGroupId,
    groups,
    taxa,
  }: EditTranslationGroupDialogProps): JSX.Element => {
    const api = useApi()
    const notifications = useNotifications()
    const deferredInvalidate = selectors.useDeferredInvalidate()

    // STATE

    const group = groups.find(({ id }) => id === targetGroupId)

    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const [state, setState, updateState] = useUpdatableState<State>(getInitialState({ group }), [isOpen])

    const { editMode, values, isDirty } = state
    const { translationScientificName, isFinal, taxonomyIds } = values
    const checkName = translationScientificName.trim().toLowerCase()

    const disallowNamesLowercase = useMemo(
      () =>
        groups
          .filter(({ id }) => id !== group?.id)
          .map(({ translationScientificName }) => translationScientificName.toLowerCase()),
      [groups, checkName]
    )

    const errors: FieldErrors = (() => {
      const result = {
        translationScientificName:
          !translationScientificName || !!disallowNamesLowercase.find((name) => name === checkName),
        taxonomyIds: taxonomyIds.length === 0,
      }
      if (!Object.values(result).some((value) => value)) return undefined
      return result
    })()

    const isSaving = editMode === 'stopping'

    // HANLDERS

    const setFieldValue = (fieldName: keyof Fields, value: unknown) => {
      updateState((draft) => {
        Object.assign(draft.values, { [fieldName]: value })
        draft.isDirty = true
      })
    }
    const makeFieldChangeHandler = (fieldName: keyof Fields) => (value: unknown) => setFieldValue(fieldName, value)

    const handleSave = async () => {
      try {
        updateState((draft) => {
          draft.editMode = 'stopping'
        })
        if (group?.id) {
          // update
          const input = { id: group.id, ...values }
          await calls.updateTranslationGroup(api, deferredInvalidate)(input)
          onUpdate(input)
        } else {
          // create
          const translationGroupId = await calls.createTranslationGroup(
            api,
            deferredInvalidate
          )({ translationId, ...values })
          onUpdate({ id: translationGroupId, ...values })
        }
        onClose()
      } catch (err) {
        notifications.error(`Failed to update OTU Name: ${err}`)
        updateState((draft) => {
          draft.editMode = 'started'
        })
      }
    }

    const handleAddTaxonomyId = (addId: number) => {
      setFieldValue('taxonomyIds', [...taxonomyIds, addId])
    }

    const handleRemoveTaxonomyId = (removeId: number) => {
      setFieldValue(
        'taxonomyIds',
        taxonomyIds.filter((id) => id !== removeId)
      )
    }

    // RENDER

    const selectedTaxa: Taxon[] = taxonomyIds.map((targetId) => {
      const taxon = taxa.find(({ id }) => id === targetId)
      if (!taxon) throw new Error('Taxon not found.')
      return taxon
    })

    const taxaOptions: TaxonomyFieldOption[] = useMemo(() => {
      const allGroupsTaxonomyIds = groups
        .filter(({ id }) => id !== group?.id)
        .reduce<number[]>((acc, group) => [...acc, ...group.taxonomyIds], [])
        .concat(...taxonomyIds)
      return taxa.map((taxon) => {
        const canAdd = !allGroupsTaxonomyIds.includes(taxon.id)
        const onChoose = () => {
          if (!canAdd) return
          handleAddTaxonomyId(taxon.id)
        }
        const actions = (
          <TaxonomyFieldActionButton onClick={onChoose} isDefault disabled={!canAdd} startIcon={<AddIcon />}>
            Add Taxon
          </TaxonomyFieldActionButton>
        )
        return {
          ...taxon,
          status: 'in',
          actions,
          onChoose,
        }
      })
    }, [taxa, groups, taxonomyIds])

    return (
      <MuiDialog open={isOpen} onClose={onClose} fullWidth maxWidth="lg">
        <MuiDialogTitle>{group ? 'Edit' : 'Create'} OTU Name</MuiDialogTitle>
        <MuiDialogContent>
          <div
            css={{
              marginTop: '1em',
              display: 'grid',
              gridTemplateColumns: '1fr auto',
              gap: '1em',
              gridTemplateAreas: `
                "translationScientificName isFinal"
                "taxonomyIds taxonomyIds"
                "addTaxon addTaxon"
            `,
            }}
          >
            <TextField
              gridArea="translationScientificName"
              label="OTU Name"
              value={translationScientificName}
              onChange={makeFieldChangeHandler('translationScientificName')}
              error={errors?.translationScientificName}
              fullWidth
              disabled={isSaving}
            />
            <CheckboxField
              gridArea="isFinal"
              label="Final"
              value={isFinal}
              onChange={makeFieldChangeHandler('isFinal')}
              disabled={isSaving}
            />
            <MuiFormControl css={{ gridArea: 'taxonomyIds' }}>
              <MuiInputLabel shrink error={errors?.taxonomyIds}>
                Taxa
              </MuiInputLabel>
              <div
                css={{
                  display: 'flex',
                  flexWrap: 'wrap',
                  gap: '0.5rem',
                  minHeight: '32px',
                  padding: '8.5px',
                }}
              >
                {errors?.taxonomyIds && <span css={{ color: '#d32f2f', margin: '7px' }}>—</span>}
                {selectedTaxa.map((taxon) => (
                  <Chip
                    chipColor="#eee"
                    key={taxon.id}
                    label={`${taxon.name} (${taxon.levelName})`}
                    onDelete={
                      isSaving
                        ? undefined
                        : () => {
                            handleRemoveTaxonomyId(taxon.id)
                          }
                    }
                  />
                ))}
              </div>
            </MuiFormControl>
            <div css={{ gridArea: 'addTaxon' }}>
              <TaxonomyField options={taxaOptions} label="Add Taxa" ungrouped />
            </div>
          </div>
        </MuiDialogContent>
        <MuiDialogActions>
          <MuiButton onClick={onClose} disabled={isSaving}>
            Cancel
          </MuiButton>
          <MuiButton onClick={handleSave} disabled={!isDirty || !!errors || isSaving}>
            Save
          </MuiButton>
        </MuiDialogActions>
      </MuiDialog>
    )
  },
  () => null
)
