/** @jsxImportSource @emotion/react */
import React, { useEffect, useState } from 'react'
import MuiSkeleton from '@mui/material/Skeleton'
import MuiButton from '@mui/material/Button'
import MuiDialog from '@mui/material/Dialog'
import MuiDialogActions from '@mui/material/DialogActions'
import MuiDialogContent from '@mui/material/DialogContent'
import * as calls from '../../data/calls'
import * as selectors from '../../data/selectors'
import {
  withSuspense,
  pluralize,
  useApi,
  useNotifications,
  Title,
  ReadOnlyField,
  AutocompleteField,
  AutocompleteFieldProps,
  AutocompleteOption,
} from '@namcbugdb/react-common'

import { useRecoilValue } from 'recoil'

import { useNavigate } from '../AppRouter'

import { sortBy } from 'lodash'

export interface MergeBoxDialogProps {
  boxId: number
  isOpen: boolean
  onClose: () => void
}

const SelectOtherBoxField = withSuspense(
  ({
    boxId,
    ...restProps
  }: Pick<AutocompleteFieldProps<number>, 'label' | 'value' | 'onChange'> & { boxId: number }): JSX.Element => {
    const boxes = useRecoilValue(selectors.selectBoxes)
    const options: AutocompleteOption<number>[] = sortBy(boxes, (box) => box.id)
      .filter(({ id }) => id !== boxId)
      .reverse()
      .map(({ id, alias, sampleCount, boxState }) => ({
        value: id,
        label: `Box ${id}${alias ? `: ${alias}` : ''} (${boxState}, ${sampleCount} samples)`,
      }))

    return <AutocompleteField {...restProps} options={options} />
  },
  (props) => <ReadOnlyField {...props} value={<MuiSkeleton />} />
)

export const MergeBoxDialog = withSuspense(
  ({ boxId, isOpen, onClose }: MergeBoxDialogProps): JSX.Element => {
    const api = useApi()
    const invalidate = selectors.useInvalidate()
    const notifications = useNotifications()
    const navigate = useNavigate()

    const box = useRecoilValue(selectors.selectBoxById(boxId))
    const sampleCount = box?.sampleCount

    const [targetBoxId, setTargetBoxId] = useState<number | null>(null)
    const [canOperate, setCanOperate] = useState(false)
    const [isOperating, setIsOperating] = useState(false)

    useEffect(() => {
      if (!isOpen) return
      setTargetBoxId(null)
      setCanOperate(false)
    }, [isOpen])

    useEffect(() => {
      if (!targetBoxId) return
      setCanOperate(false)
      const timer = window.setTimeout(() => {
        setCanOperate(true)
      }, 3000)
      return () => window.clearTimeout(timer)
    }, [targetBoxId])

    const handleClose = () => {
      if (isOperating) return // do not close when operating (may have been invoked by escape key)
      onClose()
    }

    const handleMerge = async () => {
      if (!targetBoxId) throw new Error('No target box.')
      try {
        setIsOperating(true)
        await calls.mergeBoxes(api, invalidate)({ sourceBoxId: boxId, targetBoxId })
        navigate.boxDetail({ boxId: targetBoxId }, true)
        notifications.info(`Merged Box ${boxId} into Box ${targetBoxId}`)
      } catch (err) {
        notifications.error(`Failed to merge boxes: ${err}`)
      } finally {
        setIsOperating(false)
      }
    }

    return (
      <MuiDialog open={isOpen} fullWidth maxWidth="sm" onClose={handleClose}>
        <MuiDialogContent>
          <Title>Merge Box {boxId} Into Another Box</Title>
          <div css={{ marginTop: '1em' }}>
            <SelectOtherBoxField boxId={boxId} label="Target Box" value={targetBoxId} onChange={setTargetBoxId} />
          </div>
          {targetBoxId &&
            (canOperate ? (
              <ul>
                <li>
                  {sampleCount} {pluralize(sampleCount, 'sample')} will be transferred from Box {boxId} to Box{' '}
                  {targetBoxId}
                </li>
                <li>Box {boxId} will be deleted</li>
              </ul>
            ) : (
              <ul>
                <li>
                  <MuiSkeleton width="20em" />
                </li>
                <li>
                  <MuiSkeleton width="10em" />
                </li>
              </ul>
            ))}
        </MuiDialogContent>
        <MuiDialogActions>
          <MuiButton onClick={handleClose} disabled={isOperating}>
            Cancel
          </MuiButton>
          <MuiButton onClick={handleMerge} disabled={isOperating || !canOperate}>
            Transfer {sampleCount} {pluralize(sampleCount, 'Sample')} And Delete Box
          </MuiButton>
        </MuiDialogActions>
      </MuiDialog>
    )
  },
  () => null
)
