import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

import { TranslationInput } from './createTranslation'

interface ExistingTranslationInput extends TranslationInput {
  id: number
}

export const updateTranslationMutation = gql`
  mutation ($id: Int!, $translation: TranslationInput!) {
    updateTranslation(id: $id, translation: $translation)
  }
`

export const updateTranslation =
  (api: Api, invalidate: InvalidateFn) =>
  async (translation: ExistingTranslationInput): Promise<true> => {
    const { id, name: translationName, ...restTranslation } = translation
    const data = await api.mutation<{
      updateTranslation: number // NOTE: id of translation
    }>(updateTranslationMutation, {
      id,
      translation: {
        translationName,
        ...restTranslation,
      },
    })

    if (!data.updateTranslation) throw new ApiError('translation did not update')

    invalidate('translations')
    return true
  }
