import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { Translation } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

export const deleteTranslationMutation = gql`
  mutation ($translationId: Int!) {
    deleteTranslation(id: $translationId)
  }
`

export const deleteTranslation =
  (api: Api, invalidate: InvalidateFn) =>
  async (translation: Translation): Promise<true> => {
    const data = await api.mutation<{ deleteTranslation: number }>(deleteTranslationMutation, {
      translationId: translation.id,
    })

    if (!data.deleteTranslation) throw new ApiError('translation did not delete')

    invalidate('translations')
    return true
  }
