import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

export const deleteTranslationGroupMutation = gql`
  mutation ($id: Int!) {
    deleteTranslationGroup(id: $id)
  }
`

export const deleteTranslationGroup =
  (api: Api, invalidate: InvalidateFn) =>
  async (translationGroup: { id: number }): Promise<true> => {
    const data = await api.mutation<{ deleteTranslationGroup: number }>(deleteTranslationGroupMutation, {
      id: translationGroup.id,
    })

    if (!data.deleteTranslationGroup) throw new ApiError('translation group did not delete')

    invalidate('translations')
    return true
  }
