import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

export interface TranslationGroupInput {
  translationId: number
  translationScientificName: string
  isFinal: boolean
  taxonomyIds: number[]
}

export const createTranslationGroupMutation = gql`
  mutation ($translationId: Int!, $translationGroup: TranslationGroupInput!) {
    createTranslationGroup(translationId: $translationId, translationGroup: $translationGroup)
  }
`

export const createTranslationGroup =
  (api: Api, invalidate: InvalidateFn) =>
  async (translationInput: TranslationGroupInput): Promise<number> => {
    const { translationId, ...translationGroup } = translationInput
    const data = await api.mutation<{
      createTranslationGroup: number | null // NOTE: new translationGroup ID
    }>(createTranslationGroupMutation, { translationId, translationGroup })

    const id = data.createTranslationGroup
    if (!id) throw new ApiError('translation group did not create')

    invalidate('translations')
    return id
  }
