/** @jsxImportSource @emotion/react */
import React from 'react'

import { formatNumber, pluralize, LinearProgress } from '@namcbugdb/react-common'

export const DataGridSummaryArea = ({
  children,
  className,
}: {
  children: React.ReactNode
  className?: string
}): JSX.Element => (
  <div
    className={className}
    css={{
      fontSize: '0.8125rem' /* match chip */,
      display: 'flex',
      alignItems: 'center',
      position: 'relative',
    }}
  >
    {children}
  </div>
)

interface DataGridRecordCountProps {
  className?: string
  descriptor: string
  isLoading?: boolean
  total: number
  visible: number
}

export const DataGridRecordCount = ({
  className,
  descriptor,
  isLoading,
  total,
  visible,
}: DataGridRecordCountProps): JSX.Element => (
  <DataGridSummaryArea className={className}>
    {isLoading && <LinearProgress css={{ position: 'absolute', width: '100%', bottom: 5 }} />}
    {total === visible
      ? `${formatNumber(total)} ${pluralize(total, descriptor)}`
      : `${formatNumber(visible)} of ${formatNumber(total)} ${pluralize(total, descriptor)} shown`}
  </DataGridSummaryArea>
)

interface DataGridHeadingProps {
  children?: React.ReactNode
}

export const DataGridHeading = ({ children }: DataGridHeadingProps): JSX.Element => (
  <div css={{ fontWeight: '500', marginLeft: '1em' }}>{children}</div>
)
