import gql from 'graphql-tag'
import { PaginatedRecords, BoxTaxa as GraphBoxTaxa } from '@namcbugdb/common'

import { Api } from '@namcbugdb/react-common'
import { BoxTaxon } from '@namcbugdb/react-common'

export const fetchBoxTaxaByBoxIdQuery = gql`
  query ($offset: Int, $boxId: Int!) {
    boxTaxa(boxId: $boxId, limit: 1000, offset: $offset) {
      records {
        order
        family
        taxonomyId
        scientificName
        taxonomistCounts
        sampleCount
        totalCount
      }
      nextOffset
    }
  }
`

export const fetchBoxTaxaByBoxId =
  (api: Api) =>
  async (boxId: number): Promise<BoxTaxon[]> => {
    const data = await api.queryAllPages<{
      boxTaxa: PaginatedRecords<GraphBoxTaxa>
    }>(fetchBoxTaxaByBoxIdQuery, { boxId }, { dataProperty: 'boxTaxa' })

    return data.boxTaxa.records.map(({ taxonomyId: id, scientificName: name, taxonomistCounts, ...rest }) => ({
      ...rest,
      id,
      name,
      taxonomistCounts: taxonomistCounts || {},
    }))
  }
