/** @jsxImportSource @emotion/react */
import React, { Suspense } from 'react'
import { useRecoilValue } from 'recoil'
import AccountCircleIcon from '@mui/icons-material/AccountCircle'
import MuiSkeleton from '@mui/material/Skeleton'

import * as selectors from '../../data/selectors'

const Connector = (): JSX.Element => {
  const user = useRecoilValue(selectors.selectMyUser)

  return (
    <>
      <AccountCircleIcon /> {user.fullName}
    </>
  )
}

const Fallback = (): JSX.Element => (
  <>
    <MuiSkeleton variant="circular" width={25} />
    <MuiSkeleton variant="text" width={120} />
  </>
)

export const UserName = (): JSX.Element => (
  <div css={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '3em' }}>
    <Suspense fallback={<Fallback />}>
      <Connector />
    </Suspense>
  </div>
)
