import gql from 'graphql-tag'

import { Api } from '@namcbugdb/react-common'
import { ReportDataRow } from '@namcbugdb/react-common'

export const fetchReportDataByReportIdQuery = gql`
  query ($reportId: Int!, $offset: Int!) {
    reportData(reportId: $reportId, limit: 500, offset: $offset) {
      records
      nextOffset
    }
  }
`

export const fetchReportDataByReportId =
  (api: Api) =>
  async (reportId: number): Promise<ReportDataRow[]> => {
    const data = await api.queryAllPages<{ reportData: { records: { jsonData: ReportDataRow }[] } }>(
      fetchReportDataByReportIdQuery,
      { reportId },
      { dataProperty: 'reportData' }
    )

    return data.reportData.records.map(({ jsonData }) => jsonData)
  }
