import gql from 'graphql-tag'
import axios from 'axios'
import {
  BoxInfo as GraphBoxInfo,
  Sample as GraphSample,
  SampleTaxa as GraphSampleTaxa,
  MetricResult as GraphMetricResult,
} from '@namcbugdb/common'

import { Api } from '@namcbugdb/react-common'

export const fetchCustomerBoxReportByBoxIdQuery1 = gql`
  query ($boxId: Int!) {
    boxInfo(boxId: $boxId) {
      submittedBy
      submitterEmail
      customer {
        organizationName
        address1
        address2
        city
        usState
        zipCode
        phone
      }
    }
  }
`

export const fetchCustomerBoxReportByBoxIdQuery2 = gql`
  query ($boxId: Int!, $offset: Int!) {
    samples(boxId: $boxId, offset: $offset, limit: 1000) {
      records {
        sampleId
        usState
        visitId
        customerSiteCode
        sampleDate
        sampleLatitude
        sampleLongitude
        siteName
        siteLatitude
        siteLongitude
        waterbodyName
        sampleType
        sampleMethod
        habitatName
        area
        fieldSplit
        labSplit
        jarCount
        qualitative
      }
      nextOffset
    }
  }
`

export const fetchCustomerBoxReportByBoxIdQuery3 = gql`
  query ($boxId: Int!, $offset: Int!) {
    sampleTaxa(boxId: $boxId, offset: $offset, limit: 1000) {
      records {
        sampleId
        class
        order
        family
        genus
        species
        scientificName
        splitCount
        bigRareCount
      }
      nextOffset
    }
  }
`

export const fetchCustomerBoxReportByBoxIdQuery4 = gql`
  query ($boxId: Int!, $offset: Int!) {
    reportMetrics(reportId: 1, boxId: $boxId, limit: 1000, offset: $offset) {
      records {
        sampleId
        metricName
        metricValue
        metricGroupId
      }
      nextOffset
    }
  }
`

export const fetchCustomerBoxReportByBoxId =
  (api: Api) =>
  async (
    boxId: number
  ): Promise<{
    boxInfo: GraphBoxInfo
    samples: GraphSample[]
    sampleTaxa: GraphSampleTaxa[]
    reportMetrics: GraphMetricResult[]
    template: Blob
  }> => {
    const [
      { boxInfo },
      {
        samples: { records: samples },
      },
      {
        sampleTaxa: { records: sampleTaxa },
      },
      {
        reportMetrics: { records: reportMetrics },
      },
      template,
    ] = await Promise.all([
      // query 1: boxInfo
      api.query<{
        boxInfo: GraphBoxInfo
      }>(fetchCustomerBoxReportByBoxIdQuery1, { boxId }),

      // query 2: samples
      api.queryAllPages<{ samples: { records: GraphSample[] } }>(
        fetchCustomerBoxReportByBoxIdQuery2,
        { boxId },
        { dataProperty: 'samples' }
      ),

      // query 3: sampleTaxa
      api.queryAllPages<{ sampleTaxa: { records: GraphSampleTaxa[] } }>(
        fetchCustomerBoxReportByBoxIdQuery3,
        { boxId },
        { dataProperty: 'sampleTaxa' }
      ),

      // query 4: reportMetrics
      api.queryAllPages<{ reportMetrics: { records: GraphMetricResult[] } }>(
        fetchCustomerBoxReportByBoxIdQuery4,
        { boxId },
        { dataProperty: 'reportMetrics' }
      ),

      // fetch: Excel file
      axios
        .get(`${process.env.PUBLIC_URL}/namc_customer_report.xlsx`, { responseType: 'blob' })
        .then(({ data }) => data),
    ])

    return {
      boxInfo,
      samples,
      sampleTaxa,
      reportMetrics,
      template,
    }
  }
