import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

export interface TaxonInput {
  name: string // -> scientificName
  levelId: number
  parentId: number
  isTargetEffort: boolean
  synonyms: string[]
  notes: string
}

export const createTaxonMutation = gql`
  mutation ($taxon: TaxonomyInput!) {
    createTaxonomy(taxonomy: $taxon)
  }
`

export const createTaxon =
  (api: Api, invalidate: InvalidateFn) =>
  async (taxon: TaxonInput): Promise<number> => {
    const { name: scientificName, ...restTaxon } = taxon
    const data = await api.mutation<{
      createTaxonomy: number // NOTE: count of records changed
    }>(createTaxonMutation, {
      taxon: {
        scientificName,
        ...restTaxon,
      },
    })

    const id = data.createTaxonomy
    if (!id) throw new ApiError('taxon did not create')

    invalidate('taxa')
    return id
  }
