/** @jsxImportSource @emotion/react */
import React from 'react'
import { useRecoilValue } from 'recoil'
import MuiSkeleton from '@mui/material/Skeleton'
import { uniq } from 'lodash'

import * as selectors from '../../data/selectors'
import {
  ReadOnlyField,
  withSuspense,
  AutocompleteArrayField,
  AutocompleteArrayFieldProps,
} from '@namcbugdb/react-common'

export type RolesFieldProps = Omit<AutocompleteArrayFieldProps, 'options'> & { hideRoleLevel?: number }

export const RolesField = withSuspense<RolesFieldProps>(
  ({ value, onChange, hideRoleLevel = -1, ...restProps }): JSX.Element => {
    const roles = useRecoilValue(selectors.selectRoles)

    const hiddenIds = roles.filter(({ level }) => level <= hideRoleLevel).map(({ id }) => id)

    const options = roles
      .filter(({ id }) => !hiddenIds.includes(id))
      .map(({ id: value, name: label }) => ({
        value,
        label,
      }))

    const displayValue = value.filter((id) => !hiddenIds.includes(Number(id)))
    const hiddenValue = value.filter((id) => hiddenIds.includes(Number(id)))

    const handleChange = (value) => onChange(uniq([...value, ...hiddenValue]))

    return <AutocompleteArrayField value={displayValue} onChange={handleChange} {...restProps} options={options} />
  },
  (props) => <ReadOnlyField {...props} value={<MuiSkeleton />} />
)
