import React, { useEffect } from 'react'
import { Prompt, useHistory } from 'react-router-dom'

// Note: Prompt is fairly janky (e.g. changes URL anyway: https://github.com/remix-run/react-router/issues/5405); use React Router v6 when this behaviour supported

export const ConfirmNavigateAway = ({ message }: { message: string }): JSX.Element | null => {
  const history = useHistory()

  useEffect(() => {
    const listener = (e: BeforeUnloadEvent) => {
      e.preventDefault()
      e.returnValue = ''
    }
    window.addEventListener('beforeunload', listener)
    return () => {
      window.removeEventListener('beforeunload', listener)
    }
  }, [])

  if (!history) return null // we are not in React Router and Prompt will fail

  return <Prompt when={true} message={message} />
}
