import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

import { PredictorInput } from './createPredictor'

interface ExistingPredictorInput extends PredictorInput {
  id: number
}

export const updatePredictorMutation = gql`
  mutation ($id: Int!, $predictor: PredictorInput!) {
    updatePredictor(id: $id, predictor: $predictor)
  }
`

export const updatePredictor =
  (api: Api, invalidate: InvalidateFn) =>
  async (predictor: ExistingPredictorInput): Promise<true> => {
    const { id, name: predictorName, typeId: predictorTypeId, ...restPredictor } = predictor
    const data = await api.mutation<{
      updatePredictor: number // NOTE: id of predictor
    }>(updatePredictorMutation, {
      id,
      predictor: {
        predictorName,
        predictorTypeId,
        ...restPredictor,
      },
    })

    if (!data.updatePredictor) throw new ApiError('predictor did not update')

    invalidate('predictors')
    return true
  }
