import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

import { MetricInput } from './createMetric'

interface ExistingMetricInput extends MetricInput {
  id: number
}

export const updateMetricMutation = gql`
  mutation ($id: Int!, $metric: MetricInput!) {
    updateMetric(id: $id, metric: $metric)
  }
`

export const updateMetric =
  (api: Api, invalidate: InvalidateFn) =>
  async (metric: ExistingMetricInput): Promise<true> => {
    const { id, name: metricName, groupId: metricGroupId, ...restMetric } = metric
    const data = await api.mutation<{
      updateMetric: number // NOTE: id of metric
    }>(updateMetricMutation, {
      id,
      metric: {
        metricName,
        metricGroupId,
        ...restMetric,
      },
    })

    if (!data.updateMetric) throw new ApiError('metric did not update')

    invalidate('metrics')
    return true
  }
