import gql from 'graphql-tag'
import { PaginatedRecords, Sample as GraphSample } from '@namcbugdb/common'

import { Api } from '@namcbugdb/react-common'
import { MinimalSample } from '@namcbugdb/react-common'
import { transformMinimalSample } from './fetchMinimalSamplesByIds'

export const fetchMinimalSamplesByOrganizationIdQuery = gql`
  query ($organizationId: Int, $offset: Int) {
    samples(limit: 1000, offset: $offset, entityId: $organizationId) {
      records {
        sampleId
        sampleDate
        sampleType
        sampleMethod
        area
      }
      nextOffset
    }
  }
`

export const fetchMinimalSamplesByOrganizationId =
  (api: Api) =>
  async (organizationId: number): Promise<MinimalSample[]> => {
    const data = await api.queryAllPages<{
      samples: PaginatedRecords<GraphSample>
    }>(fetchMinimalSamplesByOrganizationIdQuery, { organizationId }, { dataProperty: 'samples' })

    return data.samples.records.map(transformMinimalSample)
  }
