import log from 'loglevel'

let domain = process.env.REACT_APP_DOMAIN
if (process.env.NODE_ENV && process.env.NODE_ENV === 'development') {
  domain = 'localhost'
  // basename = ''
}
log.debug('config: ', { domain })

export const endpoints = {
  graphql: `${process.env.REACT_APP_GRAPHQL}` as string,
  public_graphql: `${process.env.REACT_APP_PUBLIC_GRAPHQL}` as string,
}

export const mapboxToken = process.env.REACT_APP_MAPBOX_TOKEN as string

/**
 * Here are the App defaults we might want to change
 */
export const appDefaults = {
  apiTimeout: 20000, // ms timeout for API Queries
}

export const redirectUrl = new URL(
  process.env.PUBLIC_URL.endsWith('/') ? process.env.PUBLIC_URL : process.env.PUBLIC_URL + '/',
  window.location.origin
)

log.debug('RedirectUrl', { redirectUrl, publicUrl: process.env.PUBLIC_URL })
