import React from 'react'
import MuiSkeleton from '@mui/material/Skeleton'
import { useRecoilValue } from 'recoil'

import * as selectors from '../../data/selectors'
import { ReadOnlyField, withSuspense, ProgressField } from '@namcbugdb/react-common'

export const IdentificationProgressField = withSuspense<{
  boxId: number
  gridArea: string
  value: number
}>(
  ({ boxId, value, ...restProps }) => {
    const boxSamples = useRecoilValue(selectors.selectBoxSamplesByBoxId(boxId))
    const doneCount = boxSamples.filter((boxSample) => boxSample.identificationEndDate).length
    const totalCount = boxSamples.length
    return (
      <ProgressField
        {...restProps}
        label="Identification"
        value={value}
        doneCount={doneCount}
        totalCount={totalCount}
      />
    )
  },
  (props): JSX.Element => <ReadOnlyField {...props} label="Identification" value={<MuiSkeleton />} minWidth={120} />
)
