import { useReducer, useMemo } from 'react'
import produce from 'immer'
import { uniqueId } from 'lodash'
import log from 'loglevel'

import { MyUser, Organism, Sample, useApi, useNotifications } from '@namcbugdb/react-common'
import * as calls from '../../data/calls'
import * as selectors from '../../data/selectors'

import { LocalOrganism, OrganismFilter, ControllerActions, ReducerAction, ReducerState } from './types'

const generateRowId = () => `row/${uniqueId()}`

const initialState = {
  sample: undefined,
  organisms: [],
  measurements: false,
  organismFilters: [],
  editMode: 'stopped',
  scrollToRowId: null,
} as ReducerState

const reducer = (state: ReducerState, action: ReducerAction): ReducerState => {
  log.debug('dispatched:', action)

  if (action.type === 'reset') {
    const {
      payload: { sample, organisms, user },
    } = action
    return {
      ...initialState,
      sample,
      organisms: organisms.map((organism) => ({ ...organism, rowId: generateRowId(), persistence: 'saved' })),
      editMode: sample.identifierId === user.id && !sample.identificationEndDate ? 'started' : 'stopped',
      measurements: !!sample.measurements,
    }
  }

  const newState = produce(state, (draft) => {
    switch (action.type) {
      case 'createLocalOrganism': {
        const { payload: organism } = action
        draft.organisms.push({
          ...organism,
          persistence: 'unsaved',
        })
        draft.scrollToRowId = organism.rowId
        break
      }
      case 'updateLocalOrganism': {
        const {
          payload: { rowId, fields },
        } = action
        const organism = draft.organisms.find((organism) => organism.rowId === rowId)
        if (!organism) return
        Object.assign(organism, fields)
        break
      }
      case 'deleteLocalOrganism': {
        const {
          payload: { rowId },
        } = action
        draft.organisms = draft.organisms.filter((organism) => organism.rowId !== rowId)
        break
      }
      case 'createFilter': {
        const {
          payload: { taxonId },
        } = action
        if (state.organismFilters.find((existingFilter) => existingFilter.taxonId === taxonId)) return
        draft.organismFilters.push({ taxonId })
        break
      }
      case 'deleteFilter': {
        const {
          payload: { taxonId },
        } = action
        draft.organismFilters = draft.organismFilters.filter((organismFilter) => organismFilter.taxonId !== taxonId)
        break
      }
      case 'setEditMode': {
        const editMode = action.payload
        draft.editMode = editMode
        if (editMode === 'stopped')
          draft.organisms = draft.organisms.filter((organism) => organism.persistence !== 'unsaved')
        break
      }
      case 'setScrollToRowId': {
        draft.scrollToRowId = action.payload
      }
    }
  })
  return newState
}

export const useComponentController = (): [ReducerState, ControllerActions] => {
  const api = useApi()
  const deferredInvalidate = selectors.useDeferredInvalidate()
  const notifications = useNotifications()

  const upsertOrganismIfSavable = async (organism: LocalOrganism) => {
    if (organism.lifeStageId) {
      dispatch({ type: 'updateLocalOrganism', payload: { rowId: organism.rowId, fields: { persistence: 'saving' } } })
      try {
        const id = await calls.upsertOrganism(api, deferredInvalidate)(organism)
        dispatch({
          type: 'updateLocalOrganism',
          payload: { rowId: organism.rowId, fields: { id, persistence: 'saved' } },
        })
      } catch (err) {
        dispatch({ type: 'updateLocalOrganism', payload: { rowId: organism.rowId, fields: { persistence: 'error' } } })
        notifications.error(`Failed to save organism record: ${err}`)
      }
    }
  }

  const [state, dispatch] = useReducer(reducer, initialState)

  const actions = useMemo(
    () => ({
      createOrganism: async ({ taxonId }: Pick<LocalOrganism, 'taxonId'>) => {
        if (!state.sample) throw new Error('No sample')
        const rowId = generateRowId()
        const newOrganism: LocalOrganism = {
          rowId,
          taxonId,
          sampleId: state.sample.id,
          splitCount: 0,
          bigRareCount: null,
          noteTypeIds: [],
          comments: '',
        }
        dispatch({ type: 'createLocalOrganism', payload: newOrganism })
        await upsertOrganismIfSavable(newOrganism)
      },
      duplicateOrganism: async (organism: LocalOrganism) => {
        if (!state.sample) throw new Error('No sample')
        const rowId = generateRowId()
        const newOrganism: LocalOrganism = {
          rowId,
          taxonId: organism.taxonId,
          sampleId: state.sample.id,
          splitCount: 0,
          bigRareCount: null,
          noteTypeIds: [],
          comments: '',
        }
        dispatch({ type: 'createLocalOrganism', payload: newOrganism })
        await upsertOrganismIfSavable(newOrganism)
      },
      updateOrganism: async (organism: LocalOrganism) => {
        dispatch({ type: 'updateLocalOrganism', payload: { rowId: organism.rowId, fields: organism } })
        await upsertOrganismIfSavable(organism)
      },
      deleteOrganism: async (organism: LocalOrganism) => {
        try {
          if (organism.persistence !== 'unsaved') {
            dispatch({
              type: 'updateLocalOrganism',
              payload: { rowId: organism.rowId, fields: { persistence: 'deleting' } },
            })
            await calls.deleteOrganism(api, deferredInvalidate)(organism as Organism)
          }
          dispatch({ type: 'deleteLocalOrganism', payload: organism })
        } catch (err) {
          dispatch({
            type: 'updateLocalOrganism',
            payload: { rowId: organism.rowId, fields: { persistence: 'error' } },
          })
          notifications.error(`Failed to delete organism record: ${err}`)
        }
      },
      createFilter: (filter: OrganismFilter) => dispatch({ type: 'createFilter', payload: filter }),
      deleteFilter: (filter: OrganismFilter) => dispatch({ type: 'deleteFilter', payload: filter }),
      reset: async (payload: { sample: Sample; organisms: Organism[]; user: MyUser }) => {
        dispatch({ type: 'reset', payload })
      },
      startTaxonomyIdentification: async () => {
        try {
          if (!state.sample) throw new Error('No sample')
          dispatch({ type: 'setEditMode', payload: 'starting' })
          await calls.startTaxonomyIdentification(api, deferredInvalidate)({ sampleId: state.sample.id })
          dispatch({ type: 'setEditMode', payload: 'started' })
        } catch (err) {
          dispatch({ type: 'setEditMode', payload: 'stopped' })
          notifications.error(`Failed to start taxonomy identification: ${err}`)
        }
      },
      stopTaxonomyIdentification: () => {
        dispatch({ type: 'setEditMode', payload: 'stoppingPrompt' })
      },
      finishTaxonomyIdentification: async ({ time }: { time: number }) => {
        try {
          if (!state.sample) throw new Error('No sample')
          dispatch({ type: 'setEditMode', payload: 'stoppingSaving' })
          await calls.stopTaxonomyIdentification(api, deferredInvalidate)({ sampleId: state.sample.id, time })
          dispatch({ type: 'setEditMode', payload: 'stopped' })
        } catch (err) {
          dispatch({ type: 'setEditMode', payload: 'started' })
          notifications.error(`Failed to stop taxonomy identification: ${err}`)
        }
      },
      setScrollToRowId: (rowId: LocalOrganism['rowId'] | null) => {
        dispatch({ type: 'setScrollToRowId', payload: rowId })
      },
    }),
    [api, state.sample] // Note: memoizing here isn't desirable, except we don't want new grid context every time :-/
  )
  return [state, actions]
}
