import { selector } from 'recoil'

import {
  lookupListToNames,
  selectDesignTypes,
  selectEcosystems,
  selectEffortTypes,
  selectFishDietSpecies,
  selectHabitats,
  selectSampleMethods,
  selectTowTypeNames,
  selectWaterbodyTypes,
} from '../../../data/selectors'
import { CellProcessor, SampleProcessor, StringValueList } from '../types'
import {
  divideBy100, // divides number by 100 (for converting whole number percentage to fractional percentage)
  isAcceptableDate, // ensures Excel date value on/after 2000-01-01
  isInList, // ensures string value is in list values
  isNumber, // ensures Excel numeric value
  isNumberBetween, // ensures Excel numeric value between min and max params
  // isPercent, // ensures Excel numeric value between 0 and 1
  isPositiveNumber, // ensures Excel numeric value greater than 0
  isReadable, // ensures readable value (e.g. not link, forumla, etc.)
  isRequired, // ensures cell has a value (other "is" validators ignore no value!)
  isWholeNumber, // ensures Excel numeric value integer
  toShortDateString, // ensures Excel date, then converts to string in 'YYYY-MM-DD' format
  toString, // converts any cell value to string
  trimIfString, // trims any string
} from '../processing/cellProcessors'

import {
  // isAcceptableLatitudeAndLongitude, // ensures latitude and longitude are in a US state (via API)
  isRequiredIf, // ensures value is present if callback condition is met (callback is passed the sample data object)
  setLiteralValue, // directly sets field value to string, number, or boolean parameter
  setRowNumber, // directly sets field value to worksheet row number
  setSampleNumber, // directly sets field value to 1-based incrementing sample number
  // setTemplateVersion, // directly sets field value to detected Excel template version
  split, // replaces a field value with two new fields based on a separator
} from '../processing/sampleProcessors'

export type SheetSchema = Record<string, (CellProcessor | SampleProcessor)[]>

/* 
  acceptedTemplateVersions
  - currently-accepted template versions
  - files uploaded with a version not in array will throw an error
*/
export const acceptedTemplateVersions = [5]

/* 
  lookupsWsName
  - name of the control worksheet which must contain named ranges "FirstDataRow" and "TemplateVersion"
*/
export const lookupsWsName = 'Lookups'

/* 
  everyCellProcessors
  - validations/transforms applied to every cell before those in schemasByWsName
*/
export const everyCellProcessors = [isReadable, trimIfString]

/*
  schemasByWsName
  - must contain keys matching names of every worksheet; any other worksheets found (besides Lookups) will throw as unrecognized
  - any columns found that are not in worksheet schema will throw as unrecognized
  - processors from cellProcessors.ts are applied left-to-right; empty array simply recognizes/allows field without any processing
  - processors from sampleProcessors.ts are applied AFTER any from cellProcessors.ts
  - processParsedSample is performed on sample after this schema applied, and may further validate/transform sample
  - the "ALL" schema is applied to every worksheet, but will be overridden if a same-named column is in the specific worksheet schema
*/
export const schemasByWsName: Record<string, SheetSchema> = {
  ALL: {
    sampleIndex: [setSampleNumber],
    rowNumber: [setRowNumber],
  },
  Benthic: {
    sampleType: [setLiteralValue('benthic')],
    siteId: [isRequired, toString],
    visitId: [toString],
    sampleDate: [isRequired, isAcceptableDate, toShortDateString],
    jarCount: [isRequired, isWholeNumber, isNumberBetween(1, 100)],
    fieldSplit: [isRequired, isNumberBetween(0, 100), divideBy100],
    latitude: [isRequired, isNumber],
    longitude: [isRequired, isNumber],
    waterbodyName: [isRequired],
    ecosystemHabitat: [isRequired, isInList('ecosystemHabitats'), split(':', 'ecosystem', 'habitat')],
    sampleDesign: [isRequired, isInList('designTypes')],
    sampleEffortStandardization: [isRequired, isInList('effortTypes')],
    areaTime: [
      isPositiveNumber,
      isRequiredIf(({ sampleEffortStandardization }) => sampleEffortStandardization === 'Not Standardized'),
    ],
    samplerType: [isRequired, isInList('samplerTypes')],
    meshSize: [isPositiveNumber, isWholeNumber],
    nhdType: [isInList('nhdTypes')],
    nhdId: [],
    notes: [toString],
  },
  'Fish Diet': {
    sampleType: [setLiteralValue('fishDiet')],
    siteId: [isRequired, toString],
    visitId: [toString],
    sampleDate: [isRequired, isAcceptableDate, toShortDateString],
    jarCount: [isRequired, isWholeNumber, isNumberBetween(1, 100)],
    fieldSplit: [isRequired, isNumberBetween(0, 100), divideBy100],
    latitude: [isRequired, isNumber],
    longitude: [isRequired, isNumber],
    waterbodyName: [isRequired],
    ecosystem: [isRequired, isInList('ecosystems')],
    samplerType: [isRequired, isInList('samplerTypes')],
    fishSpecies: [isRequired, isInList('fishDietSpecies')],
    fishLength: [isRequired, isPositiveNumber],
    fishMass: [isRequired, isPositiveNumber],
    nhdType: [isInList('nhdTypes')],
    nhdId: [],
    notes: [toString],
  },
  Zooplankton: {
    sampleType: [setLiteralValue('zooplankton')],
    siteId: [isRequired, toString],
    visitId: [toString],
    sampleDate: [isRequired, isAcceptableDate, toShortDateString],
    jarCount: [isRequired, isWholeNumber, isNumberBetween(1, 100)],
    fieldSplit: [isRequired, isNumberBetween(0, 100), divideBy100],
    latitude: [isRequired, isNumber],
    longitude: [isRequired, isNumber],
    waterbodyName: [isRequired],
    ecosystemHabitat: [isRequired, isInList('ecosystemHabitats'), split(':', 'ecosystem', 'habitat')],
    sampleDesign: [isRequired, isInList('designTypes')],
    samplerType: [isRequired, isInList('samplerTypes')],
    meshSize: [isPositiveNumber, isWholeNumber],
    netArea: [isPositiveNumber],
    towLength: [isPositiveNumber],
    towType: [isInList('towTypes')],
    nhdType: [isInList('nhdTypes')],
    nhdId: [],
    notes: [toString],
  },
  Drift: {
    sampleType: [setLiteralValue('drift')],
    siteId: [isRequired, toString],
    visitId: [toString],
    sampleDate: [isRequired, isAcceptableDate, toShortDateString],
    jarCount: [isRequired, isWholeNumber, isNumberBetween(1, 100)],
    fieldSplit: [isRequired, isNumberBetween(0, 100), divideBy100],
    latitude: [isRequired, isNumber],
    longitude: [isRequired, isNumber],
    waterbodyName: [isRequired],
    ecosystemHabitat: [isRequired, isInList('ecosystemHabitats'), split(':', 'ecosystem', 'habitat')],
    samplerType: [isRequired, isInList('samplerTypes')],
    meshSize: [isPositiveNumber, isWholeNumber],
    netArea: [isPositiveNumber],
    duration: [isPositiveNumber],
    velocity: [isPositiveNumber],
    nhdType: [isInList('nhdTypes')],
    nhdId: [],
    notes: [toString],
  },
  'Water Quality': {
    sampleType: [setLiteralValue('waterQuality')],
    siteId: [isRequired, toString],
    visitId: [toString],
    sampleDate: [isRequired, isAcceptableDate, toShortDateString],
    jarCount: [isRequired, isWholeNumber, isNumberBetween(1, 100)],
    samplerType: [isRequired, isInList('samplerTypes')],
    waterbodyName: [isRequired],
    ecosystem: [isRequired, isInList('ecosystems')],
    latitude: [isRequired, isNumber],
    longitude: [isRequired, isNumber],
    nhdType: [isInList('nhdTypes')],
    nhdId: [],
    notes: [toString],
  },
}

/*
  ListName and selectLists
  - ListName defines all available list names for isInList
  - selectLists is a Recoil selector which produces descriptor (used in problem message) and allowed values for each list
  - TypeScript will catch omissions, however the completeness of the value lists is the responsibility of database and below selector code
 */
export type ListName =
  | 'sampleMethods'
  | 'towTypes'
  | 'fishDietSpecies'
  | 'designTypes'
  | 'effortTypes'
  | 'samplerTypes'
  | 'ecosystems'
  | 'habitats'
  | 'ecosystemHabitats'
  | 'nhdTypes'

export const selectLists = selector<Record<ListName, StringValueList>>({
  key: 'submissions/selectLists',
  get: async ({ get }) => {
    const ecosystemNames = lookupListToNames(get(selectEcosystems))
    const habitatNames = lookupListToNames(get(selectHabitats))

    return {
      sampleMethods: {
        descriptor: 'sample method',
        values: lookupListToNames(get(selectSampleMethods)),
      },
      towTypes: {
        descriptor: 'tow type',
        values: get(selectTowTypeNames),
      },
      fishDietSpecies: {
        descriptor: 'fish diet species',
        values: lookupListToNames(get(selectFishDietSpecies)),
      },
      designTypes: {
        descriptor: 'sample design type',
        values: lookupListToNames(get(selectDesignTypes)),
      },
      effortTypes: {
        descriptor: 'sample effort type',
        values: lookupListToNames(get(selectEffortTypes)),
      },
      samplerTypes: {
        descriptor: 'sampler type',
        values: lookupListToNames(get(selectSampleMethods)),
      },
      ecosystems: {
        descriptor: 'ecosystem',
        values: ecosystemNames,
      },
      habitats: {
        descriptor: 'habitat',
        values: habitatNames,
      },
      ecosystemHabitats: {
        descriptor: 'ecosystem/habitat',
        values: ecosystemNames.reduce<string[]>(
          (acc, ecosystemName) => [...acc, ...habitatNames.map((habitatName) => `${ecosystemName}: ${habitatName}`)],
          []
        ),
      },
      nhdTypes: {
        descriptor: 'NHD type',
        values: lookupListToNames(get(selectWaterbodyTypes)),
      },
    }
  },
})
