import React from 'react'
import MuiButton from '@mui/material/Button'
import EditIcon from '@mui/icons-material/Edit'
import StopIcon from '@mui/icons-material/Stop'

import { Sample, withTooltip } from '@namcbugdb/react-common'

import { ControllerState, ControllerActions } from './componentController'

interface ToggleEditModeButtonProps {
  userId: number
  sample: Sample
  editMode: ControllerState['editMode']
  canStopOwn: boolean
  canStopOther: boolean
  actions: ControllerActions
}

export const ToggleEditModeButton = ({
  userId,
  sample,
  editMode,
  canStopOwn,
  canStopOther,
  actions,
}: ToggleEditModeButtonProps): JSX.Element | null => {
  const isStartedByOther = sample.sortStartDate && !sample.sortEndDate && sample.sorterId !== userId

  const error = (() => {
    if (sample.boxState !== 'Active')
      return 'The sorting for this sample cannot be edited because the box it is associated with is not currently active.'
    if (isStartedByOther && !canStopOther) return 'Sorting for this sample is already underway by another user.'
  })()

  const isActive = (editMode !== 'stopped' && editMode !== 'starting') || isStartedByOther
  const disabled =
    !!error || editMode === 'starting' || editMode === 'stopping' || (editMode === 'started' && !canStopOwn)

  return withTooltip(
    <MuiButton
      disabled={disabled}
      onClick={() => {
        if (editMode === 'started' || isStartedByOther) {
          actions.stopSort()
        } else {
          actions.startSort()
        }
      }}
      startIcon={isActive ? <StopIcon /> : <EditIcon />}
      variant={isActive ? 'contained' : 'outlined'}
    >
      {isActive ? 'Stop Sorting' : 'Start Sorting'}
    </MuiButton>,
    error,
    disabled
  )
}
