import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

interface ReconcileParams {
  boxId: number
  fromTaxonomyId: number
  toTaxonomyId: number
}

export const reconcileBoxTaxonomyMutation = gql`
  mutation ($boxId: Int!, $fromTaxonomyId: Int!, $toTaxonomyId: Int!) {
    taxReconciliation(boxId: $boxId, fromTaxonomyId: $fromTaxonomyId, toTaxonomyId: $toTaxonomyId)
  }
`

export const reconcileBoxTaxonomy =
  (api: Api, invalidate: InvalidateFn) =>
  async (params: ReconcileParams): Promise<true> => {
    const data = await api.mutation<{
      taxReconciliation: number
    }>(reconcileBoxTaxonomyMutation, { ...params })

    if (!data.taxReconciliation) throw new ApiError('taxonomy did not reconcile')

    invalidate('boxTaxa')
    return true
  }
