/** @jsxImportSource @emotion/react */
import React, { useMemo } from 'react'
import { atom, selector, useRecoilState, useRecoilValue } from 'recoil'

import * as selectors from '../../data/selectors'
import {
  Organization,
  withSuspense,
  Filter,
  FilterChips,
  TextFilterField,
  useTextFilterField,
  AutocompleteField,
} from '@namcbugdb/react-common'

import { DataGrid, renderers, DataGridRecordCount } from '../DataGrid'
import { organizationsColumnDefs } from './columnDefs'
import { noop } from 'lodash'
import { Fallback } from './Fallback'

export interface OrganizationRow extends Organization {
  title: {
    label: string
    onClick: () => void
    disabled: boolean
  }
  parentName: string
  filterableUsState: {
    label: string
    onFilter: () => void
    disabled: boolean
  }
}

const ANY_US_STATE = 0

const selectUsStateOptions = selector({
  key: 'OrganizationsListing/selectUsStateOptions',
  get: async ({ get }) => {
    const usStates = get(selectors.selectUsStates)
    return [{ value: ANY_US_STATE, label: 'Any' }, ...usStates.map(({ id: value, name: label }) => ({ value, label }))]
  },
})

const usStateFilterIdAtom = atom<number>({
  key: 'OrganizationsListing/usStateFilterIdAtom',
  default: ANY_US_STATE,
})

const textFilterAtom = atom({
  key: 'OrganizationsListing/textFilterState',
  default: '',
})

export const OrganizationsListing = withSuspense(
  () => {
    const organizations = useRecoilValue(selectors.selectOrganizations)
    const usStateOptions = useRecoilValue(selectUsStateOptions)

    // STATE AND REFS

    const [textFilter, setTextFilter, textFilterDebounced, clearTextFilter] = useTextFilterField(
      useRecoilState(textFilterAtom)
    )
    const [usStateIdFilter, setUsStateIdFilter] = useRecoilState(usStateFilterIdAtom)

    // FILTERS

    const filters = useMemo(() => {
      const result: Filter<OrganizationRow>[] = []

      if (usStateIdFilter !== ANY_US_STATE) {
        const usStateAbbreviation = usStateOptions.find(({ value }) => value === usStateIdFilter)?.label ?? '?'
        result.push({
          id: 'state',
          label: `State: ${usStateAbbreviation}`,
          filterFn: ({ usStateId }) => usStateId === usStateIdFilter,
          onDelete: () => {
            setUsStateIdFilter(ANY_US_STATE)
          },
        })
      }

      if (textFilterDebounced) {
        const textFilterDebouncedLowercase = textFilterDebounced.toLowerCase()
        if (textFilterDebounced.match(/^\d+$/)) {
          const targetBoxId = Number(textFilterDebounced)
          result.push({
            id: 'text',
            label: `Organization ${textFilterDebounced}`,
            filterFn: (box) => box.id === targetBoxId,
            onDelete: clearTextFilter,
          })
        } else {
          result.push({
            id: 'text',
            label: `Organization/Parent Name or City contains "${textFilterDebounced}"`,
            filterFn: ({ name, parentName, city }) =>
              name.toLowerCase().includes(textFilterDebouncedLowercase) ||
              parentName.toLowerCase().includes(textFilterDebouncedLowercase) ||
              city.toLowerCase().includes(textFilterDebouncedLowercase),
            onDelete: clearTextFilter,
          })
        }
      }

      return result
    }, [usStateIdFilter, textFilterDebounced])

    const unfilteredRows = useMemo<OrganizationRow[]>(() => {
      return organizations.map((organization) => {
        return {
          ...organization,
          title: {
            label: organization.name,
            onClick: noop,
            disabled: true,
          },
          parentName: organizations.find(({ id }) => id === organization.parentId)?.name || '',
          filterableUsState: {
            label: organization.usState,
            onFilter: () => {
              setUsStateIdFilter(organization.usStateId ?? ANY_US_STATE)
            },
            disabled: organization.usStateId === usStateIdFilter || !organization.usStateId,
          },
        }
      })
    }, [organizations, filters])

    const rows = useMemo(
      () => filters.reduce((acc, { filterFn }) => acc.filter(filterFn), unfilteredRows),
      [unfilteredRows, filters]
    )

    // RENDER

    return (
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        <div css={{ display: 'flex', gap: '0.5em' }}>
          <AutocompleteField
            value={usStateIdFilter}
            onChange={(value) => setUsStateIdFilter(Number(value))}
            options={usStateOptions}
            label="State"
            css={{ width: '10em' }}
          />
          <TextFilterField value={textFilter} onChange={setTextFilter} label="Organization/Parent Name or City" />
        </div>
        <div css={{ minHeight: '2em', marginTop: '0.75em', display: 'flex' }}>
          <FilterChips filters={filters} joinLabel="and" />
          <DataGridRecordCount total={organizations.length} visible={rows.length} descriptor="organization" />
        </div>
        <div css={{ flex: 1 }}>
          <DataGrid<OrganizationRow> rows={rows} columnDefs={organizationsColumnDefs} frameworkComponents={renderers} />
        </div>
      </div>
    )
  },
  () => <Fallback />
)
