import gql from 'graphql-tag'
import { Report as GraphReport } from '@namcbugdb/common'

import { Api, deNull } from '@namcbugdb/react-common'
import { Report } from '@namcbugdb/react-common'

export const fetchReportsQuery = gql`
  query ($offset: Int!) {
    reports(limit: 500, offset: $offset) {
      records {
        reportId
        reportName
        description
        isActive
      }
      nextOffset
    }
  }
`

export const fetchReports = (api: Api) => async (): Promise<Report[]> => {
  const data = await api.queryAllPages<{ reports: { records: GraphReport[] } }>(
    fetchReportsQuery,
    {},
    { dataProperty: 'reports' }
  )

  return data.reports.records
    .filter(({ isActive }) => isActive)
    .map(({ reportId: id, reportName: name, ...rest }) =>
      deNull(
        { id, name, ...rest },
        {
          name: '',
          description: '',
        }
      )
    )
}
