import React from 'react'
import { useRecoilValue } from 'recoil'
import * as selectors from '../../data/selectors'
import { withSuspense } from '@namcbugdb/react-common'
import { PendingBoxReview as Component } from './PendingBoxReview'
import { Fallback } from './Fallback'

export interface PendingBoxReviewProps {
  boxId: string | number
}

export const PendingBoxReview = withSuspense(
  ({ boxId }: PendingBoxReviewProps): JSX.Element => {
    const submission = useRecoilValue(selectors.selectLatestSubmissionByBoxId(Number(boxId)))

    if (!submission || submission.boxStateId !== selectors.BOX_STATE_ID.pending)
      return <h2>Box {boxId} not found or it is not in Pending state.</h2>

    return <Component submission={submission} />
  },
  () => <Fallback />
)
