import gql from 'graphql-tag'
import { Project as GraphProject } from '@namcbugdb/common'

import { Api, deNull } from '@namcbugdb/react-common'
import { Project } from '@namcbugdb/react-common'

export const fetchProjectsQuery = gql`
  query ($offset: Int!) {
    projects(limit: 500, offset: $offset) {
      records {
        projectId
        projectName
        contactId
        contactName
        description
        sampleCount
        modelIds
        createdDate
        updatedDate
        isPrivate
      }
      nextOffset
    }
  }
`

export const fetchProjects = (api: Api) => async (): Promise<Project[]> => {
  const data = await api.queryAllPages<{ projects: { records: GraphProject[] } }>(
    fetchProjectsQuery,
    {},
    { dataProperty: 'projects' }
  )

  return data.projects.records.map(({ projectId: id, projectName: name, ...rest }) =>
    deNull(
      {
        id,
        name,
        ...rest,
      },
      { contactName: '', description: '' }
    )
  )
}
