/** @jsxImportSource @emotion/react */
import React, { useState } from 'react'
import MuiSkeleton from '@mui/material/Skeleton'
import MuiButton from '@mui/material/Button'
import MuiDialog from '@mui/material/Dialog'
import MuiDialogActions from '@mui/material/DialogActions'
import MuiDialogContent from '@mui/material/DialogContent'
import { SelectSamples } from '../SelectSamples'
import * as calls from '../../data/calls'
import * as selectors from '../../data/selectors'
import { withSuspense, pluralize, MinimalSample, useApi, useNotifications } from '@namcbugdb/react-common'

import { useRecoilValue } from 'recoil'
import { useNavigate } from '../AppRouter'

export interface SplitBoxDialogProps {
  boxId: number
  isOpen: boolean
  onClose: () => void
}

export const SplitBoxDialog = withSuspense(
  ({ boxId, isOpen, onClose }: SplitBoxDialogProps): JSX.Element => {
    const api = useApi()
    const invalidate = selectors.useInvalidate()
    const notifications = useNotifications()
    const navigate = useNavigate()

    const samples = useRecoilValue(selectors.selectBoxSamplesByBoxId(boxId))
    const [isOperating, setIsOperating] = useState(false)
    const [newBoxSamples, setNewBoxSamples] = useState<MinimalSample[]>([])

    const handleClose = () => {
      if (isOperating) return // do not close when operating (may have been invoked by escape key)
      onClose()
    }

    const handleSplit = async () => {
      try {
        setIsOperating(true)
        const newBoxId = await calls.splitBox(api, invalidate)({ boxId, sampleIds: newBoxSamples.map(({ id }) => id) })
        navigate.boxDetail({ boxId: newBoxId })
        notifications.info(`Created Box ${newBoxId} with samples transferred from Box ${boxId}`)
      } catch (err) {
        notifications.error(`Failed to split box: ${err}`)
        setIsOperating(false)
      }
    }

    return (
      <MuiDialog open={isOpen} fullWidth maxWidth="lg" onClose={handleClose}>
        <MuiDialogContent>
          <SelectSamples
            initialMode="split"
            boxId={boxId}
            samples={samples}
            onOperatingChange={setIsOperating}
            onNewBoxSamplesChange={setNewBoxSamples}
          />
        </MuiDialogContent>
        <MuiDialogActions>
          <MuiButton onClick={handleClose} disabled={isOperating}>
            Cancel
          </MuiButton>
          <MuiButton onClick={handleSplit} disabled={isOperating || newBoxSamples.length === 0}>
            Create Box From {newBoxSamples.length} {pluralize(newBoxSamples.length, 'sample')}
          </MuiButton>
        </MuiDialogActions>
      </MuiDialog>
    )
  },
  ({ isOpen, onClose }) => (
    <MuiDialog open={isOpen} fullWidth maxWidth="lg" onClose={onClose}>
      <MuiDialogContent>
        <MuiSkeleton variant="rectangular" height="20rem" />
      </MuiDialogContent>
      <MuiDialogActions>
        <MuiButton onClick={onClose}>Cancel</MuiButton>
      </MuiDialogActions>
    </MuiDialog>
  )
)
