/** @jsxImportSource @emotion/react */
import React from 'react'
import { ICellRendererParams } from 'ag-grid-community'
import CheckBoxOutlinedIcon from '@mui/icons-material/CheckBoxOutlined'
import CheckBoxOutlineBlankOutlinedIcon from '@mui/icons-material/CheckBoxOutlineBlankOutlined'
import MuiIconButton from '@mui/material/IconButton'
import FilterAltIcon from '@mui/icons-material/FilterAlt'

import { withTooltip } from '@namcbugdb/react-common'
import { noop } from 'lodash'

interface CellRendererParams<T> extends ICellRendererParams {
  value: T
}

const LinkRenderer = ({
  value: { label, onClick, disabled },
}: CellRendererParams<{
  label: string | number
  onClick: () => void
  disabled?: boolean
}>): React.ReactNode => (
  <span onClick={disabled ? noop : onClick} className={disabled ? undefined : 'link'}>
    {label}
  </span>
)

const ProgressRenderer = ({ value }: CellRendererParams<number>): React.ReactNode => {
  const percent = `${Math.floor(value * 100)}%`
  return (
    <div css={{ height: '100%', width: '100%', position: 'relative' }}>
      <div css={{ position: 'absolute', background: 'lightgrey', width: '100%', height: '50%', top: '25%' }} />
      <div css={{ position: 'absolute', background: '#42d64c', width: percent, height: '50%', top: '25%' }} />
      <div css={{ position: 'absolute', width: '100%', textAlign: 'center' }}>{percent}</div>
    </div>
  )
}

const CheckRenderer = ({ value }: CellRendererParams<boolean>): React.ReactNode => (
  <div css={{ display: 'flex', alignItems: 'center', height: '100%' }}>
    {value ? <CheckBoxOutlinedIcon /> : <CheckBoxOutlineBlankOutlinedIcon />}
  </div>
)

const FilterableTextRenderer = ({
  value: { label, onFilter, disabled },
  filterTooltip,
}: CellRendererParams<{
  label: string | number
  onFilter: () => void
  disabled?: boolean
}> & { filterTooltip: string }): React.ReactNode => {
  return (
    <div>
      <div css={{ position: 'absolute', right: 0 }}>
        <MuiIconButton
          onClick={() => {
            onFilter()
          }}
          disabled={disabled}
        >
          {withTooltip(<FilterAltIcon />, filterTooltip, disabled)}
        </MuiIconButton>
      </div>
      {label}
    </div>
  )
}

const ActionableTextRenderer = ({
  value: { label, onAction, disabled },
  tooltip,
  Icon,
}: CellRendererParams<{
  label: string | number
  onAction: () => void
  disabled?: boolean
}> & { tooltip?: string; Icon: React.FC }): React.ReactNode => {
  return (
    <div>
      <div css={{ position: 'absolute', right: 0 }}>
        <MuiIconButton
          onClick={() => {
            onAction()
          }}
          disabled={disabled}
        >
          {withTooltip(<Icon />, tooltip, disabled)}
        </MuiIconButton>
      </div>
      {label}
    </div>
  )
}

export const renderers = {
  LinkRenderer,
  ProgressRenderer,
  CheckRenderer,
  FilterableTextRenderer,
  ActionableTextRenderer,
}
