/** @jsxImportSource @emotion/react */
import React, { useEffect } from 'react'

import { ChainOfCustodyForm } from '../../data/submissions/components/ChainOfCustodyForm'
import { Submission } from '../../types'

export interface ChainOfCustodyProps {
  submission: Submission
  autoPrint?: boolean
}

export const ChainOfCustody = ({ submission, autoPrint }: ChainOfCustodyProps): JSX.Element => {
  useEffect(() => {
    if (!autoPrint) return
    window.setTimeout(() => {
      window.print()
    }, 500)
  }, [])

  return <ChainOfCustodyForm submission={submission} />
}
