import gql from 'graphql-tag'
import { Site as GraphSite } from '@namcbugdb/common'

import { Api } from '@namcbugdb/react-common'
import { Site } from '@namcbugdb/react-common'
import { transformSite } from './fetchSites'

export const fetchSitesByProjectIdQuery = gql`
  query ($projectId: Int, $offset: Int) {
    sites(projectIds: [$projectId], limit: 200, offset: $offset) {
      records {
        siteId
        siteName
        longitude
        latitude
        usStateId
      }
      nextOffset
    }
  }
`

export const fetchSitesByProjectId =
  (api: Api) =>
  async (projectId: number): Promise<Site[]> => {
    const data = await api.queryAllPages<{ sites: { records: GraphSite[] } }>(
      fetchSitesByProjectIdQuery,
      { projectId },
      { dataProperty: 'sites' }
    )
    const sites = data.sites.records

    return sites.map(transformSite)
  }
