import gql from 'graphql-tag'
import { Individual as GraphIndividual } from '@namcbugdb/common'
import { sortBy } from 'lodash'

import { Api } from '@namcbugdb/react-common'
import { User } from '@namcbugdb/react-common'

export const fetchSorterUsersQuery = gql`
  query {
    sorters {
      id
      firstName
      lastName
    }
  }
`

export const fetchSorterUsers = (api: Api) => async (): Promise<User[]> => {
  const data = await api.query<{
    sorters: GraphIndividual[]
  }>(fetchSorterUsersQuery)
  const { sorters } = data

  return sortBy(
    sorters.map((user) => ({ ...user, fullName: `${user.firstName} ${user.lastName}` })),
    ({ fullName }) => fullName
  )
}
