import { GrantAction, NAMCResource } from '@namcbugdb/common'
import { selector, selectorFamily } from 'recoil'
import { MyUser, User, apiState } from '@namcbugdb/react-common'
import { fetchMyUser, fetchSorterUsers, fetchUserById, fetchUsers } from '../calls'
import { refreshOn } from './invalidate'

export const selectHasUser = selector<boolean>({
  key: 'selectors/selectHasUser',
  get: async ({ get }) => {
    const user = get(selectMyUser)
    return !user.virtual
  },
})

export const selectMyUser = selector<MyUser>({
  key: 'selectors/selectMyUser',
  get: async ({ get }) => {
    get(refreshOn('myUser'))

    const api = get(apiState)
    const myUser = await fetchMyUser(api)()

    if (!myUser)
      return {
        id: -1,
        affiliationId: -1,
        email: '',
        phone: '',
        firstName: '',
        lastName: '',
        fullName: 'New User',
        hasPermission: () => false,
        organizationName: '',
        virtual: true,
        roleIds: [],
      }

    const hasPermission = (resource: NAMCResource, grantAction: GrantAction) => {
      const explicitPermission = !!myUser.permissions[grantAction]?.includes(resource)
      const implicitPermission = !!myUser.permissions[grantAction.replace('Own', 'Any')]?.includes(resource) // if I have Any, implicitly have Own
      return explicitPermission || implicitPermission
    }
    return { ...myUser, hasPermission }
  },
})

export const selectSorterUsers = selector<User[]>({
  key: 'selectors/sorterUsers',
  get: async ({ get }) => {
    get(refreshOn('users'))

    const api = get(apiState)
    return fetchSorterUsers(api)()
  },
})

export const selectUserById = selectorFamily<User | null, number>({
  key: 'selectors/selectUserById',
  get:
    (id) =>
    async ({ get }) => {
      get(refreshOn('users'))

      const api = get(apiState)
      return fetchUserById(api)(id)
    },
})

export const selectUsers = selector<User[]>({
  key: 'selectors/selectUsers',
  get: async ({ get }) => {
    get(refreshOn('users'))

    const api = get(apiState)
    return fetchUsers(api)()
  },
})
