import gql from 'graphql-tag'
import { Lookups } from '@namcbugdb/common'

import { Api } from '@namcbugdb/react-common'

export const fetchLookupsQuery = gql`
  query {
    lookups {
      taxa {
        taxaLevels
        lifeStages
        fishDietSpecies
      }
      sample {
        noteTypes
        boxStates
        sampleMethods
        sampleTypes
        towTypes
        effortTypes
        designTypes
      }
      geo {
        systems
        habitats
        ecosystems
        waterbodyTypes
        states
        countries
        modelTypes
        predictorTypes
      }
      entity {
        organizationTypes
        roles
      }
      metric {
        metricGroups
        calculations
        metricReports
        rarefactionTypes
      }
    }
  }
`

export const fetchLookups = (api: Api) => async (): Promise<Lookups> => {
  const data = await api.query<{
    lookups: Lookups
  }>(fetchLookupsQuery)
  return data.lookups
}
