import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { Site } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

export const deleteSiteMutation = gql`
  mutation ($siteId: Int!) {
    deleteSite(id: $siteId)
  }
`

export const deleteSite =
  (api: Api, invalidate: InvalidateFn) =>
  async (site: Site): Promise<true> => {
    const data = await api.mutation<{ deleteSite: number }>(deleteSiteMutation, {
      siteId: site.id,
    })

    if (!data.deleteSite) throw new ApiError('site did not delete')

    invalidate('sites')
    return true
  }
