import { round } from 'lodash'

import { SampleSortingProps, LAB_SPLIT_PERCENT_PRECISION } from './SampleSorting'

import * as calls from '../../data/calls'
import * as selectors from '../../data/selectors'
import { Sample, useApi, useNotifications, useUpdatableState, getCurrentDateTime } from '@namcbugdb/react-common'

interface Fields {
  sorterId: number | ''
  labSplitPercent: number | null
  sorterCount: number | null
  sortTime: number | null
  elutriation: boolean
}

type FieldErrors = Partial<Record<keyof Fields, boolean>> | undefined

export type ControllerState = {
  sample?: Sample
  values: Fields
  errors: FieldErrors
  editMode: 'stopped' | 'starting' | 'started' | 'stopping'
  persistence?: 'unsaved' | 'dirty' | 'saving' | 'saved'
}

export interface ControllerActions {
  reset: () => void
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  setFieldValue: (fieldName: keyof Fields, value: any) => void
  startSort: () => void
  stopSort: () => void
  saveSort: () => void
}

const initialState = {
  values: { sorterId: '', labSplitPercent: null, sorterCount: null, sortTime: null, sortDate: '', elutriation: false },
  editMode: 'stopped',
  persistence: 'unsaved',
  errors: undefined,
} as ControllerState

const getFieldErrors = (values: Fields): FieldErrors => {
  const errors = {
    sorterId: !values.sorterId,
    labSplitPercent: values.labSplitPercent === null,
    sorterCount: values.sorterCount === null,
    sortTime: values.sortTime === null,
  }
  if (!Object.values(errors).some((value) => value)) return undefined
  return errors
}

export const useComponentController = ({
  sample,
  user,
  sorterUsers,
}: SampleSortingProps): [ControllerState, ControllerActions] => {
  const api = useApi()
  const deferredInvalidate = selectors.useDeferredInvalidate()
  const notifications = useNotifications()

  const [state, setState, updateState] = useUpdatableState<ControllerState>(
    initialState,
    [] // reset action is called in component; normally this would be `[sample]` to ensure reset occurs when sample changes without unmount
  )

  const actions = {
    reset: () => {
      const { sorterId, sorterCount, sortTime, labSplit, elutriation } = sample
      const values: Fields = {
        sorterId: sorterId || '',
        sorterCount,
        sortTime,
        labSplitPercent: labSplit && round(labSplit * 100, LAB_SPLIT_PERCENT_PRECISION),
        elutriation: !!elutriation,
      }

      setState(() => ({
        ...initialState,
        sample,
        userId: user.id,
        values,
        persistence: getFieldErrors(values) ? 'unsaved' : 'saved',
        editMode: sample.sorterId === user.id && !sample.sortEndDate ? 'started' : 'stopped',
      }))
    },
    setFieldValue: async (fieldName: keyof Fields, value: unknown) => {
      if (!state.sample) throw new Error('No sample')
      updateState((draft) => {
        Object.assign(draft.values, { [fieldName]: value })
        draft.persistence = 'dirty'
      })
    },
    saveSort: async () => {
      try {
        const { sorterId, labSplitPercent, sorterCount, sortTime, elutriation } = state.values
        if (
          !state.sample?.id ||
          sorterId === '' ||
          labSplitPercent === null ||
          sorterCount === null ||
          sortTime === null
        )
          throw new Error('Missing values.')
        const updatedSample = {
          sampleId: state.sample.id,
          sorterId,
          labSplit: labSplitPercent / 100,
          sorterCount,
          sortTime,
          elutriation,
        }
        updateState((draft) => {
          draft.persistence = 'saving'
        })
        await calls.updateLabSort(api, deferredInvalidate)(updatedSample)
        const sorterName =
          sorterUsers.find(({ id }) => id === updatedSample.sorterId)?.fullName ?? state.sample.sorterName
        updateState((draft) => {
          draft.persistence = 'saved'
          Object.assign(draft.sample, { ...updatedSample, sorterName })
        })
      } catch (err) {
        notifications.error(`Failed to update Lab Sort: ${err}`)
        updateState((draft) => {
          draft.persistence = 'dirty'
        })
      }
    },
    startSort: async () => {
      if (!state.sample) throw new Error('No sample')
      try {
        updateState((draft) => {
          draft.editMode = 'starting'
        })
        await calls.startSort(api, deferredInvalidate)({ sampleId: state.sample.id })
        updateState((draft) => {
          if (!draft.sample) throw new Error('No sample.')
          draft.editMode = 'started'
          draft.values.sorterId = user.id
          draft.sample.sorterId = user.id
          draft.sample.sorterName = user.fullName
        })
      } catch (err) {
        notifications.error(`Failed to start Lab Sort: ${err}`)
        updateState((draft) => {
          draft.editMode = 'stopped'
        })
      }
    },
    stopSort: async () => {
      if (!state.sample) throw new Error('No sample')
      try {
        updateState((draft) => {
          draft.editMode = 'stopping'
        })
        await calls.stopSort(api, deferredInvalidate)({ sampleId: state.sample.id })
        const todayDate = getCurrentDateTime()
        updateState((draft) => {
          if (!draft.sample) throw new Error('No sample.')
          draft.editMode = 'stopped'
          draft.sample.sortEndDate = todayDate
        })
      } catch (err) {
        notifications.error(`Failed to stop Lab Sort: ${err}`)
        updateState((draft) => {
          draft.editMode = 'started'
        })
      }
    },
  }

  const errors = state.editMode === 'stopped' ? undefined : getFieldErrors(state.values)

  return [{ ...state, errors }, actions]
}
