/** @jsxImportSource @emotion/react */
import React, { useState, useRef, useEffect } from 'react'
import MuiButton from '@mui/material/Button'
import MuiDialog from '@mui/material/Dialog'
import MuiDialogActions from '@mui/material/DialogActions'
import MuiDialogContent from '@mui/material/DialogContent'
import MuiDialogTitle from '@mui/material/DialogTitle'

import { NumberField } from '@namcbugdb/react-common'

interface TimeEntryDialogProps {
  isOpen: boolean
  onConfirm: (metrics: { time: number }) => void
}

export const TimeEntryDialog = (props: TimeEntryDialogProps): JSX.Element => {
  const { isOpen } = props
  return (
    <MuiDialog open={isOpen} disableEscapeKeyDown>
      <DialogBody {...props} />
    </MuiDialog>
  )
}

const DialogBody = ({ onConfirm }: TimeEntryDialogProps): JSX.Element => {
  const [time, setTime] = useState<number | null>(null)
  const inputRef = useRef<HTMLInputElement | null>(null)

  const hasErrors = time === null

  const handleConfirm = () => {
    if (time !== null) onConfirm({ time })
  }

  useEffect(() => {
    inputRef.current?.focus()
  }, [inputRef.current])

  return (
    <>
      <MuiDialogTitle>Metrics</MuiDialogTitle>
      <MuiDialogContent>
        <div css={{ padding: '0.5em' }}>
          <NumberField
            inputRef={inputRef}
            label="Hours"
            value={time}
            onChange={setTime}
            error={hasErrors}
            min={0}
            onKeyDown={(e) => {
              if (e.key !== 'Enter') return
              e.preventDefault()
              handleConfirm()
            }}
          />
        </div>
      </MuiDialogContent>
      <MuiDialogActions>
        <MuiButton disabled={time === null} onClick={handleConfirm}>
          OK
        </MuiButton>
      </MuiDialogActions>
    </>
  )
}
