import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

export const stopTaxonomyIdentificationMutation = gql`
  mutation ($sampleId: Int!, $time: Float!) {
    taxIdentificationStop(sampleId: $sampleId, time: $time)
  }
`

export const stopTaxonomyIdentification =
  (api: Api, invalidate: InvalidateFn) =>
  async ({ sampleId, time }: { sampleId: number; time: number }): Promise<true> => {
    const data = await api.mutation<{
      taxIdentificationStop: number // NOTE this is records affected, 1 = success, 0 = failure
    }>(stopTaxonomyIdentificationMutation, { sampleId, time })

    if (!data.taxIdentificationStop) throw new ApiError('taxonomy identification did not stop')

    invalidate('boxes')
    invalidate('samples')
    return true
  }
