import gql from 'graphql-tag'
import { USState as GraphUSState } from '@namcbugdb/common' // TODO: no type available yet

import { Api } from '@namcbugdb/react-common'

export interface fetchUsStateAtCoordinatesParams {
  latitude: number
  longitude: number
}

export const fetchUsStateAtCoordinatesQuery = gql`
  query ($latitude: Float!, $longitude: Float!) {
    getUSState(latitude: $latitude, longitude: $longitude) {
      id
      name
      abbreviation
    }
  }
`

export const fetchUsStateAtCoordinates =
  (api: Api) =>
  async (params: fetchUsStateAtCoordinatesParams): Promise<GraphUSState | null> => {
    const data = await api.query<{ getUSState: GraphUSState }>(fetchUsStateAtCoordinatesQuery, { ...params })
    return data.getUSState
  }
