import gql from 'graphql-tag'
import { Individual as GraphIndividual } from '@namcbugdb/common'

import { Api, deNull } from '@namcbugdb/react-common'
import { User } from '@namcbugdb/react-common'

export const fetchUserByIdQuery = gql`
  query ($userId: Int!) {
    user(id: $userId) {
      id
      firstName
      lastName
      email
      phone
      affiliationId
      organizationName
      roleIds
    }
  }
`

export const fetchUserById =
  (api: Api) =>
  async (userId: number): Promise<User | null> => {
    const data = await api.query<{ user: GraphIndividual }>(fetchUserByIdQuery, { userId })
    const { user } = data
    if (!user) return null
    const fullName = `${user.firstName} ${user.lastName}`
    return deNull(
      { fullName, ...data.user },
      {
        // id,
        fullName: '',
        firstName: '',
        lastName: '',
        email: '',
        phone: '',
        // affiliationId,
        organizationName: '',
        roleIds: [],
      }
    )
  }
