import gql from 'graphql-tag'
import { PaginatedRecords, Sample as GraphSample } from '@namcbugdb/common'

import { Api } from '@namcbugdb/react-common'
import { MinimalSample } from '@namcbugdb/react-common'
import { transformMinimalSample } from './fetchMinimalSamplesByIds'

export const fetchMinimalSamplesBySiteIdQuery = gql`
  query ($siteId: Int, $offset: Int) {
    samples(limit: 1000, offset: $offset, siteId: $siteId) {
      records {
        sampleId
        sampleDate
        sampleType
        sampleMethod
        area
      }
      nextOffset
    }
  }
`

export const fetchMinimalSamplesBySiteId =
  (api: Api) =>
  async (siteId: number): Promise<MinimalSample[]> => {
    const data = await api.queryAllPages<{
      samples: PaginatedRecords<GraphSample>
    }>(fetchMinimalSamplesBySiteIdQuery, { siteId }, { dataProperty: 'samples' })

    return data.samples.records.map(transformMinimalSample)
  }
