import gql from 'graphql-tag'
import { PaginatedRecords, Sample as GraphSample } from '@namcbugdb/common'

import { Api } from '@namcbugdb/react-common'
import { MinimalSample } from '@namcbugdb/react-common'
import { transformMinimalSample } from './fetchMinimalSamplesByIds'

export const fetchMinimalSamplesByTaxonIdQuery = gql`
  query ($taxonId: Int, $offset: Int) {
    samples(limit: 1000, offset: $offset, taxonomyId: $taxonId) {
      records {
        sampleId
        sampleDate
        sampleType
        sampleMethod
        area
      }
      nextOffset
    }
  }
`

export const fetchMinimalSamplesByTaxonId =
  (api: Api) =>
  async (taxonId: number): Promise<MinimalSample[]> => {
    const data = await api.queryAllPages<{
      samples: PaginatedRecords<GraphSample>
    }>(fetchMinimalSamplesByTaxonIdQuery, { taxonId }, { dataProperty: 'samples' })

    return data.samples.records.map(transformMinimalSample)
  }
