import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

import { ModelInput } from './createModel'

interface ExistingModelInput extends ModelInput {
  id: number
}

export const updateModelMutation = gql`
  mutation ($id: Int!, $model: ModelInput!) {
    updateModel(id: $id, model: $model)
  }
`

export const updateModel =
  (api: Api, invalidate: InvalidateFn) =>
  async (model: ExistingModelInput): Promise<true> => {
    const { id, name: modelName, typeId: modelTypeId, taxonomicEffort, ...restModel } = model
    const data = await api.mutation<{
      updateModel: number // NOTE: id of model
    }>(updateModelMutation, {
      id,
      model: {
        modelName,
        modelTypeId,
        taxonomicEffort: taxonomicEffort || null,
        ...restModel,
      },
    })

    if (!data.updateModel) throw new ApiError('model did not update')

    invalidate('models')
    return true
  }
