import { countBy } from 'lodash'
import { selector, selectorFamily } from 'recoil'
import { USState as GraphUSState } from '@namcbugdb/common'
// import log from 'loglevel'

import {
  Attribute,
  Box,
  Metric,
  Model,
  Organization,
  Predictor,
  Project,
  Report,
  Translation,
  apiState,
} from '@namcbugdb/react-common'
import { Submission } from '../../types'
import { refreshOn } from './invalidate'
import {
  fetchActiveBoxes,
  fetchAttributes,
  fetchBoxDetailById,
  fetchBoxes,
  fetchLatestSubmissionByBoxId,
  fetchMetrics,
  fetchModelById,
  fetchModels,
  fetchOrganizationById,
  fetchOrganizations,
  fetchPredictors,
  fetchProjects,
  fetchReports,
  fetchTranslationGroupsByTranslationId,
  fetchTranslations,
  fetchUserBoxes,
  fetchUsStateAtCoordinates,
} from '../calls'

export const selectBoxes = selector<Box[]>({
  key: 'selectors/boxes',
  get: async ({ get }) => {
    get(refreshOn('boxes'))
    get(refreshOn('boxSamples'))

    const api = get(apiState)
    return fetchBoxes(api)()
  },
})

