export const selectOrganizations = selector<Organization[]>({
  key: 'selectors/organizations',
  get: async ({ get }) => {
    get(refreshOn('organizations'))

    const api = get(apiState)
    const organizations = await fetchOrganizations(api)()
    const nameCount = countBy(organizations, 'name')
    return organizations.map(({ id, name, ...rest }) => ({
      id,
      name: nameCount[name] > 1 ? `${name} [${id}]` : name, // add ID where needed to disambiguate
      ...rest,
    }))
  },
})

