export const selectTranslationById = selectorFamily<Translation | null, number>({
  key: 'selectors/selectTranslationById',
  get:
    (id) =>
    async ({ get }) => {
      get(refreshOn('translations'))

      const translations = get(selectTranslations)
      const api = get(apiState)

      const translation = translations.find(({ id: translationId }) => translationId === id)
      if (!translation) return null

      const groups = await fetchTranslationGroupsByTranslationId(api)(id)

      return { ...translation, groups }
    },
})

