export const selectProjectById = selectorFamily<Project | null, number>({
  key: 'selectors/selectProjectById',
  get:
    (id) =>
    async ({ get }) => {
      const projects = get(selectProjects)

      return projects.find((project) => project.id === id) ?? null
    },
})

