import { sortBy } from 'lodash'
import { selector, selectorFamily } from 'recoil'

import { BoxTaxon, Taxon, apiState } from '@namcbugdb/react-common'
import { fetchBoxTaxaByBoxId, fetchTaxa } from '../calls'
import { refreshOn } from './invalidate'
import { selectTaxaLevels } from './lookups'

export const withDescendantIds = (taxa: Taxon[]): Taxon[] => {
  const highestLevelRankOrder = taxa.reduce(
    (highest: number, { levelRankOrder }) => Math.max(highest, levelRankOrder),
    0
  )

  const result = taxa.map((taxon) => ({ ...taxon })) // copy array

  let currentLevel = highestLevelRankOrder

  while (currentLevel > 0) {
    // eslint-disable-next-line no-loop-func
    result.forEach((taxon) => {
      if (taxon.levelRankOrder !== currentLevel) return

      const childTaxons = result.filter(({ parentId }) => parentId === taxon.id)
      taxon.childIds = childTaxons.map(({ id }) => id)
      taxon.descendantIds = childTaxons.reduce(
        (acc, { id, descendantIds }) => [...acc, id, ...(descendantIds || [])],
        [] as number[]
      )
    })
    currentLevel -= 1
  }
  return result
}

export const selectTaxa = selector<Taxon[]>({
  key: 'taxa',
  get: async ({ get }) => {
    get(refreshOn('taxa'))

    const api = get(apiState)
    const [taxaLevels, taxa] = await Promise.all([get(selectTaxaLevels), fetchTaxa(api)()])

    return withDescendantIds(
      sortBy(
        taxa.map((taxon) => ({
          ...taxon,
          levelRankOrder: taxaLevels.find(({ id }) => id === taxon.levelId)?.rankOrder ?? -1,
        })),
        (taxon) => taxon.name
      )
    )
  },
})

export const selectBoxTaxaByBoxId = selectorFamily<BoxTaxon[], number>({
  key: 'selectors/selectBoxTaxaByBoxId',
  get:
    (boxId) =>
    async ({ get }) => {
      get(refreshOn('taxa'))
      get(refreshOn('boxTaxa'))

      const api = get(apiState)
      return fetchBoxTaxaByBoxId(api)(boxId)
    },
})
