import React from 'react'
import { useRecoilValue } from 'recoil'

import * as selectors from '../../data/selectors'
import { withSuspense } from '@namcbugdb/react-common'

import { BoxDetail as Component } from './BoxDetail'
import { Fallback } from './Fallback'

export interface BoxDetailProps {
  boxId: string | number
}

export const BoxDetail = withSuspense(({ boxId }: BoxDetailProps): JSX.Element => {
  const box = useRecoilValue(selectors.selectBoxById(Number(boxId)))
  const user = useRecoilValue(selectors.selectMyUser)

  if (box === null) return <h2>Box {boxId} not found.</h2>

  return <Component box={box} user={user} />
}, Fallback)
