import React from 'react'
import MuiButton from '@mui/material/Button'
import EditIcon from '@mui/icons-material/Edit'
import StopIcon from '@mui/icons-material/Stop'

import { Sample, withTooltip } from '@namcbugdb/react-common'
import { ControllerActions, ReducerState } from './types'

interface ToggleEditModeButtonProps {
  userId: number
  sample: Sample
  editMode: ReducerState['editMode']
  actions: ControllerActions
  canLeave: boolean
}

export const ToggleEditModeButton = ({
  userId,
  sample,
  editMode,
  actions,
  canLeave,
}: ToggleEditModeButtonProps): JSX.Element | null => {
  const error = (() => {
    if (sample.boxState !== 'Active')
      return 'The taxa for this sample cannot be edited because the box it is associated with is not currently active.'
    if (!sample.sortEndDate) return 'The taxa for this sample cannot be edited because the box has not been sorted.'
    if (sample.identificationStartDate && !sample.identificationEndDate && sample.identifierId !== userId)
      return 'Taxonomic identification for this sample is already underway by another user.'
  })()

  if (error)
    return withTooltip(
      <MuiButton disabled variant="outlined" startIcon={<EditIcon />}>
        Start Identification
      </MuiButton>,
      error,
      true
    )

  const isActive = editMode !== 'stopped' && editMode !== 'starting'

  return (
    <MuiButton
      disabled={(editMode !== 'started' && editMode !== 'stopped') || !canLeave}
      onClick={() => {
        if (editMode === 'started') {
          actions.stopTaxonomyIdentification()
        } else {
          actions.startTaxonomyIdentification()
        }
      }}
      startIcon={isActive ? <StopIcon /> : <EditIcon />}
      variant={isActive ? 'contained' : 'outlined'}
    >
      {isActive ? 'Stop Identification' : 'Start Identification'}
    </MuiButton>
  )
}
