import gql from 'graphql-tag'
import { Site as GraphSite } from '@namcbugdb/common'

import { Api, deNull } from '@namcbugdb/react-common'
import { Site } from '@namcbugdb/react-common'
import { transformSite } from './fetchSites'

export const fetchSiteDetailByIdQuery = gql`
  query ($siteId: Int!) {
    siteInfo(siteId: $siteId) {
      siteId
      siteName
      latitude
      longitude
      usStateId
      sampleCount
      waterbodyName
      waterbodyTypeId
      waterbodyCode
      description
      isReference
      createdDate
      updatedDate
    }
  }
`

export const fetchSiteDetailById =
  (api: Api) =>
  async (siteId: number): Promise<Site | null> => {
    const data = await api.query<{ siteInfo: GraphSite }>(fetchSiteDetailByIdQuery, { siteId })
    const { siteInfo } = data
    if (!siteInfo) return null
    return deNull(transformSite(siteInfo), {
      waterbodyName: '',
      waterbodyCode: '',
      description: '',
      isReference: false,
    })
  }
