import { WebMercatorViewport } from 'react-map-gl'
import { LngLatBounds, LngLatLike } from 'mapbox-gl'

import { Point, MAX_ZOOM } from '@namcbugdb/react-common'

export const calculateFit = (
  points: Point[],
  {
    height,
    width,
    padding = 50,
    maxZoom = MAX_ZOOM,
  }: { height: number; width: number; padding?: number; maxZoom?: number }
): { longitude: number; latitude: number; zoom: number } | undefined => {
  if (points.length === 0) return undefined // guard against no points

  const updatedViewport = new WebMercatorViewport({ width, height })
  const allPointsLngLat = points.map(({ longitude, latitude }) => [longitude, latitude]) as LngLatLike[]
  const updatedBounds = new LngLatBounds([allPointsLngLat[0], allPointsLngLat[0]])
  allPointsLngLat.forEach((pointLngLat) => updatedBounds.extend(pointLngLat))
  const sw = updatedBounds.getSouthWest()
  const ne = updatedBounds.getNorthEast()

  const { longitude, latitude, zoom } = updatedViewport.fitBounds(
    [
      [sw.lng, sw.lat],
      [ne.lng, ne.lat],
    ],
    { padding, maxZoom }
  )

  return { longitude, latitude, zoom }
}
