import gql from 'graphql-tag'
import { Profile as GraphProfile } from '@namcbugdb/common'

import { Api } from '@namcbugdb/react-common'
import { User, UserPermissions } from '@namcbugdb/react-common'

export const fetchMyUserQuery = gql`
  query {
    myProfile {
      id
      firstName
      lastName
      email
      phone
      permissions
      affiliationId
      organizationName
      roleIds
    }
  }
`

export const fetchMyUser = (api: Api) => async (): Promise<(User & { permissions: UserPermissions }) | null> => {
  const data = await api.query<{
    myProfile: GraphProfile | null
  }>(fetchMyUserQuery)
  const { myProfile } = data

  if (!myProfile) return null

  return {
    ...myProfile,
    permissions: myProfile.permissions || {},
    fullName: `${myProfile.firstName} ${myProfile.lastName}`,
  }
}
