import { ReviewableSample } from './PendingBoxReview'
import { Site, getDistance, getSortedSitesDistances } from '@namcbugdb/react-common'
import { Lookup } from '@namcbugdb/common'

export const getNextNewSiteId = (newSites: Site[]): number => Math.min(0, ...newSites.map(({ id }) => id)) - 1

export const isNewSite = (site: { id: number } | undefined): boolean => !!site && site.id < 0

type MatchStatus =
  | 'matched' // already matched
  | 'eligible' // can be matched with single eligible site
  | 'ambiguous' // can't be matched due to too many eligible sites
  | 'ineligible' // can't be matched due to no available sites

export const getAutoMatchStatus = ({
  reviewableSamples,
  allSitesNormalized,
}: {
  reviewableSamples: ReviewableSample[]
  allSitesNormalized: Site[]
}): {
  sampleIndex: number
  status: MatchStatus

  databaseSiteId: number | null
  suppressed?: boolean
}[] => {
  return reviewableSamples.map((sample) => {
    const { sampleIndex, suppressAutoMatch: suppressed } = sample
    let status: MatchStatus = 'ineligible'
    let databaseSiteId: number | null = null

    if (sample.databaseSiteId) {
      status = 'matched'
      databaseSiteId = sample.databaseSiteId
    } else {
      const siteWithMatchingName = allSitesNormalized.find(({ name }) => name === sample.customerSiteCode.toLowerCase())
      const suitableNamedSiteId =
        siteWithMatchingName && getDistance(siteWithMatchingName, sample) < 0.01 && siteWithMatchingName.id // has one < 10m

      const closeSitesWithDistances = getSortedSitesDistances(allSitesNormalized, sample, 0.01) // close sites (prefiltered for coordinate diff < 0.01 degrees)
      const closeSiteIds = closeSitesWithDistances
        .filter(({ distance }) => distance < 0.005)
        .map(({ site: { id } }) => id) // < 5m

      if (suitableNamedSiteId) {
        if (closeSiteIds.length === 0 || closeSiteIds[0] === suitableNamedSiteId) {
          status = 'eligible'
          databaseSiteId = suitableNamedSiteId
        } else {
          status = 'ambiguous'
        }
      } else if (closeSiteIds.length === 1) {
        status = 'eligible'
        databaseSiteId = closeSiteIds[0]
      } else if (closeSiteIds.length > 1) {
        status = 'ambiguous'
      }
    }

    return { sampleIndex, status, databaseSiteId, suppressed }
  })
}

export const makeSiteFromSample = ({
  fromSample,
  newSiteId,
  allSitesNormalized,
  waterbodyTypes,
}: {
  fromSample: ReviewableSample
  newSiteId: number
  allSitesNormalized: Site[]
  waterbodyTypes: Lookup[]
}): Site => {
  const name = (() => {
    let candidateName = ''
    let copyNumber = 0
    // eslint-disable-next-line no-constant-condition
    while (true) {
      candidateName = `${fromSample.customerSiteCode}${copyNumber ? ` (${copyNumber})` : ''}`
      const candidateNameLowercase = candidateName.toLowerCase()
      if (!allSitesNormalized.find(({ name }) => name === candidateNameLowercase)) break
      copyNumber++
    }

    return candidateName
  })()

  return {
    id: newSiteId,
    name,
    latitude: fromSample.latitude,
    longitude: fromSample.longitude,
    waterbodyName: fromSample.waterbodyName,
    waterbodyTypeId: (waterbodyTypes.find(({ name }) => name === fromSample.waterbodyType) ?? { id: null }).id,
    waterbodyCode: '',
    description: '',
    isReference: false,
  }
}
