import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

interface AddProjectSamplesInput {
  projectId: number
  sampleIds: number[]
}

export const addProjectSamplesMutation = gql`
  mutation ($projectId: Int!, $sampleIds: [Int]) {
    addProjectSamples(projectId: $projectId, sampleIds: $sampleIds)
  }
`

export const addProjectSamples =
  (api: Api, invalidate: InvalidateFn) =>
  async (params: AddProjectSamplesInput): Promise<true> => {
    const data = await api.mutation<{
      addProjectSamples: number // NOTE: return type is number of samples after operation (0 is not indication of failure)
    }>(addProjectSamplesMutation, { ...params })

    if (!data.addProjectSamples) throw new ApiError('project samples did not update') // not possible to know if no records were added due to return type above

    invalidate('projectSamples')
    return true
  }
