import { selectorFamily } from 'recoil'

import {
  apiState,
  GeoJsonPolygonCoordinates,
  MinimalSample,
  ModelResult,
  Organism,
  Sample,
  SamplePredictorValue,
  SourceSample,
} from '@namcbugdb/react-common'

import { refreshOn } from './invalidate'
import {
  fetchBoxSamplesByBoxId,
  fetchMinimalSamplesByOrganizationId,
  fetchMinimalSamplesByPolygon,
  fetchMinimalSamplesBySiteId,
  fetchMinimalSamplesByTaxonId,
  fetchMinimalSamplesByUsStateId,
  fetchModelResultsBySampleId,
  fetchOrganismsBySampleId,
  fetchProjectSamplesByProjectId,
  fetchSampleById,
  fetchSamplePredictorValuesBySampleId,
} from '../calls'

export const selectBoxSamplesByBoxId = selectorFamily<SourceSample[], number>({
  key: 'selectors/boxSamplesByBoxId',
  get:
    (id) =>
    async ({ get }) => {
      get(refreshOn('samples'))
      get(refreshOn('boxSamples'))

      const api = get(apiState)
      return fetchBoxSamplesByBoxId(api)(id)
    },
})

