/** @jsxImportSource @emotion/react */
import React, { useEffect } from 'react'
import { HashRouter as ReactRouter } from 'react-router-dom'
import { RecoilRoot, useRecoilState } from 'recoil'
import { UseHostedReturn } from '@namcbugdb/react-auth'
import { SnackbarProvider } from 'notistack'

import { AppRouter } from './components/AppRouter'
import { GraphApi, apiState } from '@namcbugdb/react-common'
import { endpoints } from './config'

import './normalize.css'
import './App.css'

export interface AppProps {
  signOut: () => void
  auth: UseHostedReturn
}

const Component = ({ signOut, auth }: AppProps): JSX.Element => {
  const [api, setApi] = useRecoilState(apiState)

  useEffect(() => {
    const apiInstance = new GraphApi(endpoints.graphql, auth, signOut)
    setApi(apiInstance)
  }, [])

  if (!(api instanceof GraphApi)) return <div /> // wait until API is set

  return (
    <ReactRouter>
      <AppRouter />
    </ReactRouter>
  )
}

export const App = (props: AppProps): JSX.Element => {
  return (
    <RecoilRoot>
      <SnackbarProvider>
        <Component {...props} />
      </SnackbarProvider>
    </RecoilRoot>
  )
}
