import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { Project } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

export const deleteProjectMutation = gql`
  mutation ($projectId: Int!) {
    deleteProject(projectId: $projectId)
  }
`

export const deleteProject =
  (api: Api, invalidate: InvalidateFn) =>
  async (project: Project): Promise<true> => {
    const data = await api.mutation<{ deleteProject: number }>(deleteProjectMutation, {
      projectId: project.id,
    })

    if (!data.deleteProject) throw new ApiError('project did not delete')

    invalidate('projects')
    return true
  }
