/** @jsxImportSource @emotion/react */
import React, { useMemo } from 'react'
import { atom, useRecoilState, useRecoilValue } from 'recoil'
import MuiSkeleton from '@mui/material/Skeleton'
import MuiButton from '@mui/material/Button'
import { GrantAction, NAMCResource } from '@namcbugdb/common'

import * as selectors from '../../data/selectors'
import {
  Project,
  withSuspense,
  Filter,
  FilterChips,
  TextFilterField,
  useTextFilterField,
} from '@namcbugdb/react-common'
import { DataGrid, renderers, DataGridRecordCount } from '../DataGrid'

import { projectsColumnDefs } from './columnDefs'
import { useNavigate } from '../AppRouter'

export interface ProjectRow extends Project {
  title: {
    label: string
    onClick: () => void
  }
  owner: {
    label: string
    onFilter: () => void
    disabled: boolean
  }
}

const textFilterState = atom({
  key: 'ProjectsListing/textFilterState',
  default: '',
})

const addedFiltersState = atom<Filter<Project>[]>({
  key: 'ProjectsListing/addedFiltersState',
  default: [],
})

export const ProjectsListing = withSuspense(
  () => {
    const navigate = useNavigate()
    const user = useRecoilValue(selectors.selectMyUser)
    const projects = useRecoilValue(selectors.selectProjects)

    // STATE AND REFS

    const [textFilter, setTextFilter, textFilterDebounced, clearTextFilter] = useTextFilterField(
      useRecoilState(textFilterState)
    )
    const [addedFilters, setAddedFilters] = useRecoilState(addedFiltersState)

    // FILTERS

    const filters = useMemo(() => {
      const result: Filter<Project>[] = []

      if (textFilterDebounced) {
        if (textFilterDebounced.match(/^\d+$/)) {
          const targetBoxId = Number(textFilterDebounced)
          result.push({
            id: 'text',
            label: `Project ${textFilterDebounced}`,
            filterFn: (box) => box.id === targetBoxId,
            onDelete: clearTextFilter,
          })
        } else {
          result.push({
            id: 'text',
            label: `Title/Owner contains "${textFilterDebounced}"`,
            filterFn: ({ name, contactName }) =>
              name.toLowerCase().includes(textFilterDebounced.toLowerCase()) ||
              contactName.toLowerCase().includes(textFilterDebounced.toLowerCase()),
            onDelete: clearTextFilter,
          })
        }
      }

      result.push(...addedFilters)

      return result
    }, [textFilterDebounced, addedFilters])

    const rows = useMemo<ProjectRow[]>(() => {
      const filtered = filters.reduce<Project[]>((acc, { filterFn }) => acc.filter(filterFn), projects)
      return filtered.map((project) => {
        const filterId = `filter/${String(project.contactName)}`
        return {
          ...project,
          title: {
            label: project.name,
            onClick: () => navigate.projectDetail({ projectId: project.id }),
          },
          owner: {
            label: project.contactName,
            onFilter: () => {
              clearTextFilter()
              setAddedFilters([
                {
                  id: filterId,
                  label: project.contactName,
                  filterFn: ({ contactName }) => contactName === project.contactName,
                  onDelete: () => setAddedFilters((value) => value.filter(({ id }) => id !== filterId)),
                },
              ])
            },
            disabled: !!addedFilters.find(({ id }) => id === filterId) || !project.contactName,
          },
        }
      })
    }, [projects, filters])

    // RENDER

    return (
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        <div css={{ display: 'flex', gap: '0.5em' }}>
          <TextFilterField value={textFilter} onChange={setTextFilter} label="Project ID, Title, Owner" />
          {user.hasPermission(NAMCResource.projects, GrantAction.createAny) && (
            <div css={{ marginLeft: 'auto' }}>
              <MuiButton
                variant="outlined"
                onClick={() => {
                  navigate.projectCreate()
                }}
              >
                Create New Project
              </MuiButton>
            </div>
          )}
        </div>
        <div css={{ minHeight: '2em', marginTop: '0.75em', display: 'flex' }}>
          <FilterChips filters={filters} joinLabel="and" />
          <DataGridRecordCount total={projects.length} visible={rows.length} descriptor="project" />
        </div>
        <div css={{ flex: 1 }}>
          <DataGrid<Project> rows={rows} columnDefs={projectsColumnDefs} frameworkComponents={renderers} />
        </div>
      </div>
    )
  },
  () => <MuiSkeleton height="20em" />
)
