import gql from 'graphql-tag'
import { Box as GraphBox } from '@namcbugdb/common'
import { sortBy } from 'lodash'

import { Api } from '@namcbugdb/react-common'
import { Box } from '@namcbugdb/react-common'

export const fetchBoxesQuery = gql`
  query ($offset: Int) {
    boxes(limit: 1000, offset: $offset) {
      records {
        boxId
        customerName
        customerId
        createdDate
        boxReceivedDate
        submittedBy
        boxState
        sampleCount
        midges
        measurements
        sortComplete
        idComplete
        alias
      }
      nextOffset
    }
  }
`

export const transformBox = ({
  boxId: id,
  boxReceivedDate: receivedDate,
  submittedBy: submittedByName,
  idComplete: identificationComplete,
  ...rest
}: GraphBox): Box => ({
  id,
  receivedDate,
  submittedByName,
  identificationComplete,
  ...rest,
})

export const fetchBoxes = (api: Api) => async (): Promise<Box[]> => {
  const data = await api.queryAllPages<{ boxes: { records: GraphBox[] } }>(
    fetchBoxesQuery,
    {},
    { dataProperty: 'boxes' }
  )

  const boxes = (data.boxes.records ?? []).map(transformBox)

  return sortBy(boxes, ({ receivedDate }) => receivedDate).reverse()
}
