/** @jsxImportSource @emotion/react */
import React from 'react'

interface StepPageProps {
  active: boolean
  className?: string
  children: React.ReactNode
}

export const StepPage = ({ active, className, children, ...rest }: StepPageProps): JSX.Element => (
  <div
    className={className}
    css={{
      width: '100%',
      height: '100%',
      position: 'absolute',
      visibility: active ? 'visible' : 'hidden',
    }}
    {...rest}
  >
    {children}
  </div>
)
