/** @jsxImportSource @emotion/react */
import React, { useEffect } from 'react'
import { useRecoilValue, useSetRecoilState } from 'recoil'
import MuiIconButton from '@mui/material/IconButton'
import MenuIcon from '@mui/icons-material/Menu'

import * as selectors from '../../data/selectors'
import { QuickNavigateField } from '../QuickNavigateField'
import { isLocalhost, isStagingApi } from '@namcbugdb/react-common'

import { isNavigationOpenState } from '../SideNavBar'
import { withSuspense } from '@namcbugdb/react-common'

export const AppHeader = withSuspense(
  (): JSX.Element => {
    const hasUser = useRecoilValue(selectors.selectHasUser)
    const setIsNavigationOpen = useSetRecoilState(isNavigationOpenState)

    useEffect(() => {
      if (isLocalhost) {
        document.title = `${document.title} (local)`
      } else if (isStagingApi) {
        document.title = `${document.title} (staging)`
      }
    }, [])

    const conditionalVisibility = hasUser ? 'visible' : 'hidden'

    return (
      <header
        css={{
          height: '3em',
          background: '#064270',
          color: 'white',
          display: 'flex',
          alignItems: 'center',
          padding: '0 0.5em',
        }}
      >
        <MuiIconButton onClick={() => setIsNavigationOpen(true)}>
          <MenuIcon sx={{ fill: 'white' }} />
        </MuiIconButton>
        <h1 css={{ fontWeight: 500, letterSpacing: '0.05em', fontSize: '200%' }}>
          INSTAR
          {isStagingApi && <span css={{ color: 'yellow' }}> STAGING</span>}
        </h1>

        <div css={{ margin: '0 0.25em 0 auto', visibility: conditionalVisibility }}>
          <QuickNavigateField css={{ '.MuiInputBase-root': { background: 'white' } }} />
        </div>
      </header>
    )
  },
  () => null
)
