import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { Organization } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

export const deleteOrganizationMutation = gql`
  mutation ($organizationId: Int!) {
    deleteOrganization(id: $organizationId)
  }
`

export const deleteOrganization =
  (api: Api, invalidate: InvalidateFn) =>
  async (organization: Organization): Promise<true> => {
    const data = await api.mutation<{ deleteOrganization: number }>(deleteOrganizationMutation, {
      organizationId: organization.id,
    })

    if (!data.deleteOrganization) throw new ApiError('organization did not delete')

    invalidate('organizations')
    return true
  }
