/** @jsxImportSource @emotion/react */
import React, { useMemo } from 'react'
import MuiButton from '@mui/material/Button'
import { selector, atom, useRecoilState } from 'recoil'
import { NAMCResource, GrantAction } from '@namcbugdb/common'
import dayjs from 'dayjs'

import { ReadOnlyField, Title, useNotifications } from '@namcbugdb/react-common'

import { BoxSamples } from './BoxSamples'
import {
  AccordionPanel,
  Box,
  MyUser,
  useApi,
  formatUtcDateTimeToAppDate,
  pluralize,
  useUpdatableState,
  CheckboxField,
  TextField,
  AutocompleteField,
  Divider,
  Menu,
  MenuItem,
  DateField,
} from '@namcbugdb/react-common'
import * as calls from '../../data/calls'
import * as selectors from '../../data/selectors'
import { downloadCustomerBoxReport } from '../../lib/reports/downloadCustomerBoxReport'

import { SortingProgressField } from './SortingProgressField'
import { IdentificationProgressField } from './IdentificationProgressField'
import { ModelsField } from '../ModelsField'
import { ConfirmNavigateAway } from '../AppRouter'
import { BoxSitesMapPreview } from './BoxSitesMapPreview'
import { useNavigate } from '../AppRouter'
import { DataGridHeading } from '../DataGrid'

import { SplitBoxDialog } from './SplitBoxDialog'
import { MergeBoxDialog } from './MergeBoxDialog'

export interface BoxDetailProps {
  box: Box
  user: MyUser
}

const selectOrganizationOptions = selector({
  key: 'BoxDetail/selectOrganizationOptions',
  get: async ({ get }) => {
    const organizations = await get(selectors.selectOrganizations)
    return organizations.map(({ id: value, name: label }) => ({ value, label }))
  },
})

const selectBoxStateOptions = selector({
  key: 'BoxDetail/selectBoxStateOptions',
  get: async ({ get }) => {
    const boxStates = await get(selectors.selectBoxStates)
    return boxStates.map(({ id, name }) => ({ value: id, label: name }))
  },
})

const isExpandedState = atom<boolean>({
  key: 'BoxDetail/isExpandedState',
  default: true,
})

interface Fields {
  boxStateId: number
  measurements: boolean
  midges: boolean
  description: string
  customerId: number
  alias: string
  modelIds: number[]
  receivedDate: string | null
}

interface State {
  values: Fields
  editMode: 'stopped' | 'started' | 'stopping'
  isDirty: boolean
  isSplitDialogOpen: boolean
  isMergeDialogOpen: boolean
}

const getInitialState = ({ box }: { box: Box }): State => {
  const {
    boxStateId = -1,
    measurements,
    midges,
    description = '',
    customerId,
    alias,
    modelIds = [],
    receivedDate,
  } = box
  return {
    values: {
      boxStateId,
      measurements,
      midges,
      description,
      customerId,
      alias,
      modelIds,
      receivedDate,
    },
    editMode: 'stopped',
    isDirty: false,
    isSplitDialogOpen: false,
    isMergeDialogOpen: false,
  }
}

export const BoxDetail = ({ box, user }: BoxDetailProps): JSX.Element => {
  const api = useApi()
  const invalidate = selectors.useInvalidate()
  const navigate = useNavigate()
  const notifications = useNotifications()

  const [state, setState, updateState] = useUpdatableState(getInitialState({ box }), [box])

  const [isPanelExpanded, setIsPanelExpanded] = useRecoilState(isExpandedState)
  const {
    editMode,
    values: {
      boxStateId,
      measurements,
      midges,
      description,
      customerId,
      alias,
      modelIds,
      receivedDate: isoReceivedDate,
    },
    isDirty,
    isSplitDialogOpen,
    isMergeDialogOpen,
  } = state
  const receivedDate = isoReceivedDate && formatUtcDateTimeToAppDate(isoReceivedDate)

  const isStopped = editMode === 'stopped'
  const isEditing = editMode === 'started'
  const isSaving = editMode === 'stopping'

  // HANDLERS

  const setFieldValue = (fieldName: keyof Fields, value: unknown) => {
    updateState((draft) => {
      Object.assign(draft.values, { [fieldName]: value })
      draft.isDirty = true
    })
  }
  const makeFieldChangeHandler = (fieldName: keyof Fields) => (value: unknown) => setFieldValue(fieldName, value)

  const handleEdit = () => {
    setIsPanelExpanded(true)
    updateState((draft) => {
      draft.editMode = 'started'
    })
  }

  const handleSave = async () => {
    updateState((draft) => {
      draft.editMode = 'stopping'
    })
    try {
      await calls.updateBox(api, invalidate)({ boxId: box.id, ...state.values })
      updateState((draft) => {
        draft.editMode = 'stopped'
        draft.isDirty = false
      })
    } catch (err) {
      notifications.error(`Failed to save box: ${err}`)
      updateState((draft) => {
        draft.editMode = 'started'
      })
    }
  }

  const handleCancel = () => {
    setState(getInitialState({ box }))
  }

  // RENDER

  const actionsMenuItems = useMemo(() => {
    const items: MenuItem[] = [
      {
        key: 'taxonomy',
        label: 'Taxonomy',
        onClick: () => navigate.boxTaxonomy({ boxId: box.id }),
      },
      {
        key: 'customerReport',
        label: 'Download Customer Report',
        onClick: async () => {
          try {
            const cancelNotification1 = notifications.info('Still creating report...', 2500)
            const cancelNotification2 = notifications.info('Still creating report...', 30000)
            await downloadCustomerBoxReport({ boxId: box.id, api, filename: `NAMC_report_${box.id}.xlsx` })
            cancelNotification1()
            cancelNotification2()
          } catch (err) {
            notifications.error(`Failed to create report: ${err}`)
          }
        },
        disabled: box.boxStateId !== selectors.BOX_STATE_ID.complete,
      },
      Divider,
    ]
    if (user.hasPermission(NAMCResource.boxes, GrantAction.updateAny)) {
      items.push({
        key: 'edit',
        label: 'Edit Box',
        onClick: () => {
          handleEdit()
        },
      })
      if (box.boxStateId !== selectors.BOX_STATE_ID.pending) {
        items.push(
          {
            key: 'split',
            label: 'Split Box',
            onClick: () => {
              updateState((draft) => {
                draft.isSplitDialogOpen = true
              })
            },
          },
          {
            key: 'merge',
            label: 'Merge Box',
            onClick: () => {
              updateState((draft) => {
                draft.isMergeDialogOpen = true
              })
            },
          }
        )
      }
    }
    if (
      user.hasPermission(NAMCResource.submissions, GrantAction.updateAny) &&
      box.boxStateId === selectors.BOX_STATE_ID.pending
    ) {
      items.push({
        key: 'review',
        label: 'Review Box',
        onClick: () => {
          navigate.boxReview({ boxId: box.id })
        },
      })
    }

    return items
  }, [box.boxStateId])

  return (
    <>
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        <AccordionPanel
          value={isPanelExpanded}
          onChange={setIsPanelExpanded}
          locked={isEditing}
          summary={
            <Title>
              Box {box.id}{' '}
              <Title.Subtitle>
                ({box.boxState}, {box.sampleCount} {pluralize(box.sampleCount, 'sample')})
              </Title.Subtitle>
            </Title>
          }
          actions={
            <div css={{ display: 'flex', gap: '0.5em' }}>
              {isStopped && actionsMenuItems.length > 0 && <Menu label="Actions" items={actionsMenuItems} />}
              {(isEditing || isSaving) && (
                <>
                  <MuiButton
                    onClick={() => {
                      handleCancel()
                    }}
                    variant="outlined"
                    disabled={isSaving}
                  >
                    Cancel
                  </MuiButton>
                  <MuiButton
                    onClick={() => {
                      handleSave()
                    }}
                    variant="contained"
                    disabled={!isDirty || isSaving}
                  >
                    Save
                  </MuiButton>
                </>
              )}
            </div>
          }
        >
          <div
            css={{
              flex: 1,
              display: 'grid',
              gap: '0.75em',
              gridTemplateColumns: 'auto auto auto auto auto',
              gridTemplateAreas: `
              "boxState     customerId              sorting       identification     map"
              "measurements submittedByName         modelIds      modelIds           map"
              "midges       createdDate             modelIds      modelIds           map"
              "taxaQa       receivedDate            alias         alias              map"
              "sorterQa     processingCompleteDate  description   description        map"
              ".            .                       description   description        map"
            `,
            }}
          >
            <AutocompleteField
              label="Box State"
              optionsSelector={selectBoxStateOptions}
              value={boxStateId}
              valueLabel={box.boxState}
              onChange={makeFieldChangeHandler('boxStateId')}
              readOnly={!isEditing}
              gridArea="boxState"
            />
            <AutocompleteField
              gridArea="customerId"
              optionsSelector={selectOrganizationOptions}
              valueLabel={box.customerName}
              value={customerId}
              onChange={makeFieldChangeHandler('customerId')}
              label="Customer"
              readOnly={!isEditing}
              css={{ minWidth: '20em' }}
            />
            <ReadOnlyField gridArea="submittedByName" value={box.submittedByName} label="Submitted By" />
            <ReadOnlyField
              gridArea="createdDate"
              value={formatUtcDateTimeToAppDate(box.createdDate ?? null)}
              label="Submitted On"
            />
            <DateField
              gridArea="receivedDate"
              value={receivedDate}
              onChange={makeFieldChangeHandler('receivedDate')}
              label="Received On"
              readOnly={!isEditing}
              max={dayjs().format('YYYY-MM-DD')}
            />
            <ReadOnlyField
              gridArea="processingCompleteDate"
              value={formatUtcDateTimeToAppDate(box.processingCompleteDate ?? null)}
              label="Processing Completed On"
            />
            <CheckboxField
              label="Measurements"
              value={measurements}
              onChange={makeFieldChangeHandler('measurements')}
              readOnly={!isEditing}
              gridArea="measurements"
            />
            <CheckboxField
              label="Midges"
              value={midges}
              onChange={makeFieldChangeHandler('midges')}
              readOnly={!isEditing}
              gridArea="midges"
            />
            <ReadOnlyField label="Taxonomic QA" value={box.taxaQa} gridArea="taxaQa" />
            <ReadOnlyField label="Sorting QA" value={box.sorterQa} gridArea="sorterQa" />
            <ModelsField
              label="Models"
              value={modelIds}
              onChange={makeFieldChangeHandler('modelIds')}
              readOnly={!isEditing}
              gridArea="modelIds"
            />
            <TextField
              label="Alias"
              value={alias}
              onChange={makeFieldChangeHandler('alias')}
              readOnly={!isEditing}
              gridArea="alias"
            />
            <TextField
              label="Description"
              value={description}
              onChange={makeFieldChangeHandler('description')}
              readOnly={!isEditing}
              multiline
              minRows={3}
              gridArea="description"
            />
            <SortingProgressField boxId={box.id} value={box.sortComplete} gridArea="sorting" />
            <IdentificationProgressField boxId={box.id} value={box.identificationComplete} gridArea="identification" />
            {editMode !== 'started' && (
              <div css={{ gridArea: 'map' }}>
                <BoxSitesMapPreview boxId={box.id} />
              </div>
            )}
          </div>
        </AccordionPanel>
        <DataGridHeading>Samples</DataGridHeading>
        <div css={{ flex: 1, minHeight: '10em' }}>
          <BoxSamples boxId={box.id} />
        </div>
      </div>
      <SplitBoxDialog
        boxId={box.id}
        isOpen={isSplitDialogOpen}
        onClose={() =>
          updateState((draft) => {
            draft.isSplitDialogOpen = false
          })
        }
      />
      <MergeBoxDialog
        boxId={box.id}
        isOpen={isMergeDialogOpen}
        onClose={() =>
          updateState((draft) => {
            draft.isMergeDialogOpen = false
          })
        }
      />
      {isDirty && <ConfirmNavigateAway message="Some changes are not saved. Are you sure you want to leave?" />}
    </>
  )
}
