import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { Model } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

export const deleteModelMutation = gql`
  mutation ($modelId: Int!) {
    deleteModel(id: $modelId)
  }
`

export const deleteModel =
  (api: Api, invalidate: InvalidateFn) =>
  async (model: Model): Promise<true> => {
    const data = await api.mutation<{ deleteModel: number }>(deleteModelMutation, {
      modelId: model.id,
    })

    if (!data.deleteModel) throw new ApiError('model did not delete')

    invalidate('models')
    return true
  }
