/** @jsxImportSource @emotion/react */
import React from 'react'
import { useRecoilValue } from 'recoil'
import MuiSkeleton from '@mui/material/Skeleton'

import * as selectors from '../../data/selectors'
import { ModelResult, withSuspense } from '@namcbugdb/react-common'

import { DataGrid, DataGridHeading, DataGridRecordCount, renderers } from '../DataGrid'
import { sampleModelResultsColumnDefs } from './columnDefs'

export interface SampleModelResultsProps {
  sampleId: number
}

export const SampleModelResults = withSuspense(
  ({ sampleId }: SampleModelResultsProps): JSX.Element => {
    const rows = useRecoilValue(selectors.selectModelResultsBySampleId(sampleId))
    return (
      <div css={{ flex: rows.length ? 1 : 0, minHeight: '10rem', display: 'flex', flexDirection: 'column' }}>
        <div css={{ display: 'flex' }}>
          <DataGridHeading>Models</DataGridHeading>
          <DataGridRecordCount
            total={rows.length}
            visible={rows.length}
            descriptor="model result"
            css={{ marginLeft: 'auto' }}
          />
        </div>
        <div css={{ flex: 1 }}>
          <DataGrid<ModelResult>
            rowIdProperty="modelId"
            rows={rows}
            columnDefs={sampleModelResultsColumnDefs}
            frameworkComponents={renderers}
          />
        </div>
      </div>
    )
  },
  () => (
    <div css={{ minHeight: '10rem' }}>
      <MuiSkeleton variant="rectangular" height="10rem" />
    </div>
  )
)
