import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

export interface MetricInput {
  name: string // -> metricName
  abbreviation: string
  groupId: number | null // -> metricGroupId
  displayText: string
  isActive: boolean
  description: string
  calculationId: number
  reportIds: number[]
  rarefactionTypeId: number
  taxonomyIds: number[]
  attributeId: number | null
  attributeValues: string[]
}

export const createMetricMutation = gql`
  mutation ($metric: MetricInput!) {
    createMetric(metric: $metric)
  }
`

export const createMetric =
  (api: Api, invalidate: InvalidateFn) =>
  async (metric: MetricInput): Promise<number> => {
    const { name: metricName, groupId: metricGroupId, ...restMetric } = metric
    const data = await api.mutation<{
      createMetric: number | null // NOTE: new metric ID
    }>(createMetricMutation, { metric: { metricName, metricGroupId, ...restMetric } })

    const id = data.createMetric
    if (!id) throw new ApiError('metric did not create')

    invalidate('metrics')
    return id
  }
