import gql from 'graphql-tag'
import { PaginatedRecords, Taxonomy as GraphTaxonomy } from '@namcbugdb/common'

import { Api, deNull } from '@namcbugdb/react-common'
import { Taxon } from '@namcbugdb/react-common'

export const fetchTaxaQuery = gql`
  query ($offset: Int) {
    taxonomy(limit: 1000, offset: $offset, searchTerm: "%") {
      records {
        taxonomyId
        scientificName
        levelId
        levelName
        synonyms
        parentTaxonomyId
        notes
        isTargetEffort
        notes
      }
      nextOffset
    }
  }
`

export const fetchTaxa = (api: Api) => async (): Promise<Taxon[]> => {
  const data = await api.queryAllPages<{
    taxonomy: PaginatedRecords<GraphTaxonomy>
  }>(fetchTaxaQuery, {}, { dataProperty: 'taxonomy' })
  const { records } = data.taxonomy

  return records.map(
    ({ taxonomyId: id, scientificName: name, parentTaxonomyId: parentId, synonyms: synonymsJson, ...rest }) =>
      deNull(
        {
          id,
          name,
          levelRankOrder: 0, // reassigned in selector
          parentId,
          synonyms: synonymsJson ? JSON.parse(synonymsJson) : [],
          ...rest,
        },
        { notes: '' }
      )
  )
}
