/** @jsxImportSource @emotion/react */
import React from 'react'
import { Layer, Source } from 'react-map-gl'

import { Point, SITE_PIN_COLOR } from '@namcbugdb/react-common'

export interface MatchedSiteLineProps {
  from: Point
  to: Point
}

export const MatchedSiteLine = ({ from, to }: MatchedSiteLineProps): JSX.Element => {
  return (
    <>
      <Source
        id="MatchedSiteLine"
        type="geojson"
        data={{
          type: 'Feature',
          properties: {},
          geometry: {
            type: 'LineString',
            coordinates: [
              [from.longitude, from.latitude],
              [to.longitude, to.latitude],
            ],
          },
        }}
      />
      <Layer
        type="line"
        source="MatchedSiteLine"
        layout={{
          'line-join': 'round',
          'line-cap': 'round',
        }}
        paint={{
          'line-color': SITE_PIN_COLOR,
          'line-width': 2,
          'line-dasharray': [2, 2],
        }}
      />
    </>
  )
}
