import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

interface SampleInput {
  id: number

  // updatable fields
  area: number | null
  customerSiteCode: string
  designTypeId: number | null
  ecosystemId: number | null
  effortTypeId: number | null
  fieldNotes: string
  fieldSplit: number
  habitatId: number
  jarCount: number
  labNotes: string
  mesh: number | null
  sampleDate: string
  sampleLatitude: number | null
  sampleLongitude: number | null
  sampleMethodId: number
  siteId: number
  visitId: string
}

export const updateSampleMutation = gql`
  mutation ($id: Int!, $sample: SampleInput!) {
    updateSample(id: $id, sample: $sample)
  }
`

export const updateSample =
  (api: Api, invalidate: InvalidateFn) =>
  async (sample: SampleInput): Promise<true> => {
    const { id, ...restSample } = sample
    const data = await api.mutation<{
      updateSample: number // NOTE: records changed
    }>(updateSampleMutation, {
      id,
      sample: restSample,
    })

    if (!data.updateSample) throw new ApiError('sample did not update')

    invalidate('samples')
    invalidate('boxSamples')
    invalidate('projectSamples')
    return true
  }
