/** @jsxImportSource @emotion/react */
import React from 'react'
import { times } from 'lodash'
import dayjs from 'dayjs'
import { SerializedStyles } from '@emotion/react'

export const Document = ({
  children,
  styles,
}: {
  children: React.ReactNode
  styles: SerializedStyles
}): JSX.Element => (
  <div css={styles}>
    {/* below padding ensures borders print */}
    <div className="document" css={{ padding: '0 2px' }}>
      {children}
    </div>
  </div>
)

export const EmptyCell = ({ qty = 1 }: { qty: number }): JSX.Element => (
  <>
    {times(qty).map((i) => (
      <td key={i}></td>
    ))}
  </>
)

export const Table = ({ children, borders }: { children: React.ReactNode; borders?: boolean }): JSX.Element => (
  <table
    css={{
      'th,td': { minWidth: '1em', border: borders ? '1px solid black' : 'none', padding: '0.1em' },
      borderCollapse: 'collapse',
      width: '100%',
    }}
  >
    {children}
  </table>
)

export const PageBreak = (): JSX.Element => (
  <div css={{ height: '1px', pageBreakAfter: 'always', '@media screen': { borderBottom: '3px dashed green' } }} />
)

export const Line = (): JSX.Element => <hr css={{ borderBottom: '1px solid black', margin: '0.75em 0' }} />

export const longDate = (date: string | number | boolean | null | undefined): string => {
  if (typeof date !== 'string') return '?'
  return dayjs(date).format('MMMM D, YYYY')
}

export const shortDate = (date: string | number | boolean | null | undefined): string => {
  if (typeof date !== 'string') return '?'
  return dayjs(date).format('M/D/YY')
}
