import { ColDef } from 'ag-grid-community'
import { SamplePredictorValue } from '@namcbugdb/react-common'
import { columnDef } from '../DataGrid'

export const samplePredictorValuesColumnDefs = [
  columnDef.textColumn({
    field: 'predictorName',
    flex: 2,
    minWidth: 150,
  }),
  columnDef.booleanColumn({
    headerName: 'Type',
    field: 'isTemporal',
    valueLabels: ['Spatial', 'Temporal'],
    flex: 1,
    minWidth: 120,
  }),
  columnDef.textColumn({
    field: 'predictorValue',
    flex: 1,
    minWidth: 150,
  }),
  columnDef.dateColumn({
    headerName: 'Calculation Date',
    field: 'updatedDate',
    flex: 1,
    minWidth: 150,
  }),
  columnDef.textColumn({
    field: 'status',
    valueFormatter: ({ value }: { value: SamplePredictorValue['status'] }) => {
      switch (value) {
        case 'current':
          return 'Valid'
        case 'missing':
          return 'Missing'
        case 'expired':
          return 'Expired'
      }
    },
    flex: 1,
    minWidth: 120,
  }),
] as ColDef[]

export const sampleModelResultsColumnDefs = [
  columnDef.textColumn({
    field: 'modelName',
    flex: 3,
    minWidth: 150,
  }),
  columnDef.numberColumn({
    headerName: 'Fixed Count',
    field: 'fixedCount',
    flex: 1,
    minWidth: 120,
  }),
  columnDef.numberColumn({
    field: 'modelResult',
    flex: 1,
    minWidth: 120,
  }),
  columnDef.textColumn({
    field: 'condition',
    flex: 1,
    minWidth: 150,
  }),
  columnDef.textColumn({
    field: 'notes',
    flex: 3,
    minWidth: 150,
  }),
  columnDef.dateColumn({
    headerName: 'Calculation Date',
    field: 'updatedDate',
    flex: 1,
    minWidth: 150,
  }),
] as ColDef[]
