import gql from 'graphql-tag'
import { Model as GraphModel } from '@namcbugdb/common'

import { Api } from '@namcbugdb/react-common'
import { Model } from '@namcbugdb/react-common'

export const fetchModelsQuery = gql`
  query ($offset: Int!) {
    models(limit: 500, offset: $offset) {
      records {
        modelId
        modelName
        modelTypeName
        abbreviation
        translationName
        isActive
        fixedCount
        predictorCount
      }
      nextOffset
    }
  }
`

export const fetchModels = (api: Api) => async (): Promise<Model[]> => {
  const data = await api.queryAllPages<{ models: { records: GraphModel[] } }>(
    fetchModelsQuery,
    {},
    { dataProperty: 'models' }
  )

  return data.models.records.map(({ modelId: id, modelName: name, modelTypeName: typeName, ...rest }) => ({
    id,
    name,
    typeName,
    ...rest,
  }))
}
