import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

export const stopSortMutation = gql`
  mutation ($sampleId: Int!) {
    labStopSort(sampleId: $sampleId)
  }
`

export const stopSort =
  (api: Api, invalidate: InvalidateFn) =>
  async ({ sampleId }: { sampleId: number }): Promise<true> => {
    const data = await api.mutation<{
      labStopSort: number // NOTE this is records affected, 1 = success, 0 = failure
    }>(stopSortMutation, { sampleId })

    if (!data.labStopSort) throw new ApiError('sample sorting did not stop')

    invalidate('boxes')
    invalidate('samples')
    return true
  }
