/** @jsxImportSource @emotion/react */
import React, { useCallback, useEffect, useMemo, useState } from 'react'
import { atom, selector, useRecoilCallback, useRecoilState, useRecoilValue, useSetRecoilState } from 'recoil'
import { identity } from 'lodash'
import MuiButton from '@mui/material/Button'
import produce from 'immer'
// import log from 'loglevel'
import { GrantAction, NAMCResource } from '@namcbugdb/common'
import { mapboxToken } from '../../config'
import {
  ConfirmationDialog,
  Point,
  Site,
  Filter,
  FilterChips,
  SitesMap,
  SitesMapSidePanel,
  useSitesMapController,
  withSuspense,
  PinMarker,
  LinearProgress,
  useNotifications,
  useApi,
  AutocompleteField,
  TextField,
  useTextFilterField,
  Divider,
  Menu,
} from '@namcbugdb/react-common'
import * as calls from '../../data/calls'
import * as selectors from '../../data/selectors'

import { DataGridRecordCount } from '../DataGrid'

import { SiteDetailPanel, SITE_DETAIL_PANEL_TRANSITION_MS } from '../SiteDetailPanel'
import { ConfirmNavigateAway, useNavigate } from '../AppRouter'
import { Fallback } from './Fallback'
import { EditSiteDialog } from '../EditSiteDialog'

export interface SitesListingProps {
  siteId?: string | number
}

export const ANY_US_STATE = 0

// SELECTORS

const selectUsStateOptions = selector({
  key: 'SitesListing/selectUsStateOptions',
  get: async ({ get }) => {
    const usStates = get(selectors.selectUsStates)
    return [
      { value: ANY_US_STATE, label: 'Any' },
      ...usStates.map(({ id: value, abbreviation: label }) => ({ value, label })),
    ]
  },
})

// MODE & FILTER STATE

export type SitesListingSource = { type: 'box'; id: number } | { type: 'project'; id: number; name: string } | null

const sitesListingSourceAtom = atom<SitesListingSource>({
  key: 'SitesListing/sitesListingSourceAtom',
  default: null,
})

const usStateFilterIdAtom = atom<number>({
  key: 'SitesListing/usStateFilterIdAtom',
  default: ANY_US_STATE,
})

const textFilterAtom = atom<string>({
  key: 'SitesListing/textFilterAtom',
  default: '',
})

export const useSetSitesListingSource = (): ((sitesListingSource: SitesListingSource) => void) => {
  const setSitesListingSource = useSetRecoilState(sitesListingSourceAtom)
  const setUsStateFilter = useSetRecoilState(usStateFilterIdAtom)
  const setTextFilter = useSetRecoilState(textFilterAtom)
  return useCallback((sitesListingSource: SitesListingSource) => {
    setSitesListingSource(sitesListingSource)
    setUsStateFilter(ANY_US_STATE)
    setTextFilter('')
  }, [])
}

// COMPONENT

export const SitesListing = withSuspense(
  ({ siteId: siteIdUnsanitized }: SitesListingProps): JSX.Element => {
    const siteId = siteIdUnsanitized ? Number(siteIdUnsanitized) : null
    const isNewSite = siteIdUnsanitized === 'new'

    const navigate = useNavigate()
    const notifications = useNotifications()
    const user = useRecoilValue(selectors.selectMyUser)
    const api = useApi()
    const deferredInvalidate = selectors.useDeferredInvalidate()

    // CONTROLS STATE

    const usStateOptions = useRecoilValue(selectUsStateOptions)
    const [usStateIdFilter, setUsStateIdFilter] = useRecoilState(usStateFilterIdAtom)
    const [textFilter, setTextFilter, textFilterDebounced, resetTextFilter] = useTextFilterField(
      useRecoilState(textFilterAtom)
    )

    const [isEditOpen, setIsEditOpen] = useState(false)
    const [editTargetSite, setEditTargetSite] = useState<Site | null>(null)
    const [isSaving, setIsSaving] = useState(false)

    const [isMoving, setIsMoving] = useState(false)
    const [movingTargetPoint, setMovingTargetPoint] = useState<Point>({ latitude: 0, longitude: 0 })

    const [isConfirmDeleteOpen, setIsConfirmDeleteOpen] = useState(false)
    const [isDeleting, setIsDeleting] = useState(false)

    const isUiLocked = isMoving || isDeleting

    // DATA MANAGEMENT

    const [sitesListingSource, setSitesListingSource] = useRecoilState(sitesListingSourceAtom)
    const allSitesStatus = useRecoilValue(selectors.allSitesStatusAtom)
    const [allSites, setAllSites] = useRecoilState(selectors.allSitesAtom)
    const [sparseSitesDetail, setSparseSitesDetail] = useRecoilState(selectors.sparseSitesDetailAtom)

    const [sourceSites, setSourceSites] = useState<Site[]>([])
    const [detailPanelSite, setDetailPanelSite] = useState<Site | undefined>(undefined)

    const allSitesNormalized = useMemo(
      () => allSites.map((site) => ({ ...site, name: site.name.toLowerCase() })),
      [allSites]
    )

    const getBoxSitesByBoxId = useRecoilCallback<[number], Promise<Site[]>>(({ snapshot }) => async (id: number) => {
      return snapshot.getPromise(selectors.selectSitesByBoxId(id))
    })

    const getProjectSitesByProjectId = useRecoilCallback<[number], Promise<Site[]>>(
      ({ snapshot }) =>
        async (id: number) => {
          return snapshot.getPromise(selectors.selectSitesByProjectId(id))
        }
    )

    const fetchAllSitesOptimized = selectors.useFetchAllSitesOptimized()
    const fetchSiteDetailBySiteIdOptimized = selectors.useFetchSiteDetailBySiteIdOptimized()

    useEffect(() => {
      switch (sitesListingSource?.type) {
        case 'box': {
          // box mode: use selector
          getBoxSitesByBoxId(sitesListingSource.id).then(setSourceSites)
          break
        }
        case 'project': {
          // project mode: use selector
          getProjectSitesByProjectId(sitesListingSource.id).then(setSourceSites)
          break
        }
        default: {
          fetchAllSitesOptimized() // sites are progressively updated in next effect
        }
      }
    }, [sitesListingSource])

    useEffect(() => {
      if (!sitesListingSource) {
        // "all" mode, and allSites have changed: use all sites
        setSourceSites(allSites)
      }
    }, [sitesListingSource, allSites])

    useEffect(() => {
      fetchSiteDetailBySiteIdOptimized(siteId)
    }, [siteId])

    const isFetching = allSitesStatus === selectors.FETCH_ALL_SITES_STATUS.fetching

    const selectedSite = useMemo(() => {
      // get best selected site data
      if (!siteId) return undefined
      return (
        sparseSitesDetail.find((siteDetail) => siteDetail.id === siteId) ||
        sourceSites.find((site) => site.id === siteId)
      )
    }, [sourceSites, sparseSitesDetail, siteId])

    // FILTERING

    const filters = useMemo(() => {
      const result: Filter<Site>[] = []

      if (sitesListingSource) {
        const id = 'sitesListingSource'
        const filterFn = identity
        const onDelete = isUiLocked ? undefined : () => setSitesListingSource(null)

        switch (sitesListingSource.type) {
          case 'box': {
            result.push({ id, label: `Box ${sitesListingSource.id}`, filterFn, onDelete })
            break
          }
          case 'project': {
            result.push({ id, label: `Project ${sitesListingSource.name}`, filterFn, onDelete })
            break
          }
        }
      }

      if (usStateIdFilter !== ANY_US_STATE) {
        const usStateAbbreviation = usStateOptions.find(({ value }) => value === usStateIdFilter)?.label ?? '?'
        result.push({
          id: 'state',
          label: `State: ${usStateAbbreviation}`,
          filterFn: ({ usStateId }) => usStateId === usStateIdFilter,
          onDelete: isUiLocked
            ? undefined
            : () => {
                setUsStateIdFilter(ANY_US_STATE)
              },
        })
      }

      if (textFilterDebounced) {
        result.push({
          id: 'text',
          label: `Name contains "${textFilterDebounced}"`,
          filterFn: ({ name }) => name.toLowerCase().includes(textFilterDebounced.toLowerCase()),
          onDelete: isUiLocked ? undefined : resetTextFilter,
        })
      }

      return result
    }, [sitesListingSource, usStateIdFilter, textFilterDebounced, isUiLocked])

    const filteredSites = useMemo<Site[]>(() => {
      return filters.reduce<Site[]>((acc, { filterFn }) => acc.filter(filterFn), sourceSites)
    }, [sourceSites, filters])

    // MAP EVENTS AND CONTROLLER

    const onSiteClick = (site: Site) => {
      if (isUiLocked) {
        notifications.info('Cannot select another site while editing a site.')
        return
      }
      navigate.siteDetail({ siteId: site.id }, true)
    }

    const controller = useSitesMapController({ sites: filteredSites, onSiteClick })

    // SELECTION & FITTING

    useEffect(() => {
      // synchronize site detail panel with current selected site
      setDetailPanelSite(selectedSite)
    }, [selectedSite])

    useEffect(() => {
      // target site has changed
      if (!selectedSite) return // site isn't in data yet, must wait (not accounting for unusual case where ID doesn't exist)

      const { viewport, centerSite } = controller
      window.setTimeout(
        () => {
          centerSite(selectedSite, { zoom: Math.max(9, viewport.zoom || 0) }) // center selected, and zoom in (unless already zoomed closer)
        },
        detailPanelSite ? 0 : SITE_DETAIL_PANEL_TRANSITION_MS // if detail panel open, center immediately, if detail panel closed, wait for open to avoid map jank
      )
    }, [siteId, !!selectedSite])

    useEffect(() => {
      // filters changed, attempt to fit
      if (selectedSite) return // site selected, do not perform fit
      if (!controller.isViewportReady) return // viewport not ready, don't fit yet (desired behaviour for all-sites initial load)
      controller.smartFitAll() // fit filtered sites; if no sites, smartFitAll does nothing
    }, [filters])

    const [isInitialFitted, setIsInitalFitted] = useState(false)

    useEffect(() => {
      // if sitesListingSource in use, perform initial fit once
      if (!sitesListingSource || isInitialFitted) return // not doing or already done
      if (!controller.isViewportReady) return // viewport not ready so smartFitAll can't be run yet
      controller.smartFitAll() // fit sites
      setIsInitalFitted(true) // ensure runs only once
    }, [sitesListingSource, controller.isViewportReady])

    // SITE CRUD

    useEffect(() => {
      if (!isNewSite) return
      setEditTargetSite({
        id: -1,
        name: '',
        latitude: controller.viewport.latitude ?? 0,
        longitude: controller.viewport.longitude ?? 0,
        waterbodyCode: '',
        waterbodyTypeId: null,
        waterbodyName: '',
        description: '',
        isReference: false,
      })
      setIsEditOpen(true)
    }, [isNewSite])

    const isDetailSiteRetrieved = detailPanelSite && !!detailPanelSite.createdDate // heuristic

    const handleStartMoveSite = () => {
      if (!detailPanelSite) throw new Error('No site selected.')
      setIsMoving(true)
      setMovingTargetPoint(detailPanelSite)
    }

    const handleSiteMoved = (newPoint: Point) => {
      setMovingTargetPoint(newPoint)
    }

    const handleCancelMoveSite = () => {
      setIsMoving(false)
    }

    const handleSaveMovedSite = async () => {
      if (!detailPanelSite) throw new Error('No site selected.')
      await handleSaveSite({ ...detailPanelSite, ...movingTargetPoint })
      setIsMoving(false)
    }

    const handleSaveSite = async (values: Site) => {
      setIsSaving(true)
      try {
        const siteInput = {
          name: values.name.trim(),
          latitude: values.latitude,
          longitude: values.longitude,
          waterbodyName: values.waterbodyName?.trim() || null,
          waterbodyTypeId: values.waterbodyTypeId ?? null,
          waterbodyCode: values.waterbodyCode?.trim() || null,
          description: values.description?.trim() || null,
          isReference: values.isReference ?? false,
        }
        if (isNewSite) {
          const newSiteId = await calls.createSite(api, deferredInvalidate)(siteInput)
          setAllSites(
            produce((draft) => {
              draft.push({
                id: newSiteId,
                name: siteInput.name,
                latitude: siteInput.latitude,
                longitude: siteInput.longitude,
              })
            })
          )
          navigate.siteDetail({ siteId: newSiteId }, true)
          notifications.info(`Created site ${siteInput.name}`)
          setIsEditOpen(false)
        } else {
          await calls.updateSite(api, deferredInvalidate)({ ...siteInput, id: values.id })
          setAllSites(
            produce((draft) => {
              const targetSite = draft.find(({ id }) => id === values.id)
              if (!targetSite) throw new Error('Site not found.')
              Object.assign(targetSite, values)
              targetSite.usStateId = undefined
            })
          )
          setSparseSitesDetail(
            produce((draft) => {
              const targetSite = draft.find(({ id }) => id === values.id)
              if (!targetSite) throw new Error('Site not found.')
              Object.assign(targetSite, values)
              targetSite.usStateId = undefined
            })
          )
          notifications.info(`Saved site ${siteInput.name}`)
          setIsEditOpen(false)
        }
      } catch (err) {
        notifications.error(`Failed to save site: ${err}`)
      }
      setIsSaving(false)
    }

    const handleDeleteSite = async () => {
      if (!detailPanelSite) throw new Error('No site.')
      setIsDeleting(true)
      try {
        await calls.deleteSite(api, deferredInvalidate)(detailPanelSite)
        setAllSites(
          produce((draft) => {
            const targetSiteIndex = draft.findIndex(({ id }) => id === detailPanelSite.id)
            if (targetSiteIndex === -1) return
            draft.splice(targetSiteIndex, 1)
          })
        )
        setSparseSitesDetail(
          produce((draft) => {
            const targetSiteIndex = draft.findIndex(({ id }) => id === detailPanelSite.id)
            if (targetSiteIndex === -1) return
            draft.splice(targetSiteIndex, 1)
          })
        )
        notifications.info(`Deleted site ${detailPanelSite.name}`)
        navigate.sites(undefined, true)
      } catch (err) {
        notifications.error(`Failed to delete site: ${err}`)
      }
      setIsDeleting(false)
    }

    // RENDER

    return (
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        <div css={{ display: 'flex', gap: '0.5em' }}>
          <AutocompleteField
            value={usStateIdFilter}
            onChange={(value) => setUsStateIdFilter(Number(value))}
            options={usStateOptions}
            label="State"
            css={{ width: '10em' }}
            disabled={isUiLocked}
          />
          <TextField
            value={textFilter}
            onChange={(value) => setTextFilter(value)}
            label="Site Name"
            css={{ width: '20em' }}
            disabled={isUiLocked}
          />
          <div css={{ marginLeft: 'auto' }}>
            {user.hasPermission(NAMCResource.sites, GrantAction.createAny) && (
              <MuiButton variant="outlined" onClick={() => navigate.siteCreate()} disabled={isUiLocked}>
                Create New Site
              </MuiButton>
            )}
          </div>
        </div>
        <div css={{ minHeight: '2em', margin: '0.75em 0', display: 'flex' }}>
          <FilterChips filters={filters} joinLabel="and" />
          <DataGridRecordCount
            total={sourceSites.length}
            visible={filteredSites.length}
            descriptor="site"
            isLoading={isFetching}
          />
        </div>
        <div css={{ flex: 1, display: 'flex' }}>
          <div css={{ flex: 1, position: 'relative' }}>
            {isFetching && (
              <LinearProgress
                delayMs={500}
                css={{ position: 'absolute', width: '100%', height: '5px', opacity: 0.75, zIndex: 100 }}
              />
            )}
            <SitesMap controller={controller} mapboxToken={mapboxToken}>
              {selectedSite && (
                <PinMarker
                  site={isMoving ? { ...selectedSite, ...movingTargetPoint } : selectedSite}
                  onDragEnd={isMoving ? handleSiteMoved : undefined}
                />
              )}
            </SitesMap>
          </div>
          <SitesMapSidePanel isOpen={!!detailPanelSite} transitionMs={SITE_DETAIL_PANEL_TRANSITION_MS}>
            {detailPanelSite && (
              <SiteDetailPanel
                site={detailPanelSite}
                onClose={
                  isUiLocked
                    ? undefined
                    : () => {
                        navigate.sites(undefined, true)
                      }
                }
              >
                <div css={{ marginLeft: '0.8rem', display: 'flex', gap: '0.5rem' }}>
                  {!isUiLocked && user.hasPermission(NAMCResource.sites, GrantAction.updateAny) && (
                    <Menu
                      label="Actions"
                      disabled={!isDetailSiteRetrieved}
                      items={[
                        {
                          key: 'edit',
                          label: 'Edit Site',
                          onClick: () => {
                            setEditTargetSite(detailPanelSite)
                            setIsEditOpen(true)
                          },
                        },
                        {
                          key: 'drag',
                          label: 'Drag Site',
                          onClick: handleStartMoveSite,
                        },
                        Divider,
                        {
                          key: 'delete',
                          label: 'Delete Site',
                          onClick: () => {
                            setIsConfirmDeleteOpen(true)
                          },
                          disabled:
                            !user.hasPermission(NAMCResource.sites, GrantAction.deleteAny) ||
                            typeof detailPanelSite.sampleCount === 'undefined' ||
                            detailPanelSite.sampleCount > 0,
                        },
                      ]}
                    />
                  )}
                  {isMoving && (
                    <>
                      <MuiButton onClick={handleCancelMoveSite} variant="outlined" disabled={isSaving}>
                        Cancel
                      </MuiButton>
                      <MuiButton onClick={handleSaveMovedSite} variant="contained" disabled={isSaving}>
                        Save
                      </MuiButton>
                    </>
                  )}
                </div>
              </SiteDetailPanel>
            )}
          </SitesMapSidePanel>
        </div>
        <EditSiteDialog
          isOpen={isEditOpen}
          onCancel={() => {
            if (isNewSite) navigate.sites(undefined, true) // onClose only called
            setIsEditOpen(false)
          }}
          onSave={handleSaveSite}
          site={editTargetSite}
          allSitesNormalized={allSitesNormalized}
        />
        <ConfirmationDialog
          isOpen={isConfirmDeleteOpen}
          onClose={() => setIsConfirmDeleteOpen(false)}
          onConfirm={handleDeleteSite}
          title="Confirm Delete Site"
        >
          Are you sure you want to delete site {detailPanelSite?.name}?
        </ConfirmationDialog>
        {isMoving && (
          <ConfirmNavigateAway message="Site location has not been saved. Are you sure you want to leave?" />
        )}
      </div>
    )
  },
  () => <Fallback />
)
