/** @jsxImportSource @emotion/react */
import React, { useState } from 'react'
import { selector, useRecoilValue } from 'recoil'
import { GrantAction, NAMCResource } from '@namcbugdb/common'
import MuiButton from '@mui/material/Button'

import {
  ConfirmationDialog,
  Model,
  Title,
  ReadOnlyField,
  useApi,
  useNotifications,
  useUpdatableState,
  trimAllStrings,
  TextField,
  AutocompleteField,
  CheckboxField,
  Menu,
  Divider,
  NumberField,
} from '@namcbugdb/react-common'

import * as calls from '../../data/calls'
import * as selectors from '../../data/selectors'
import { ConfirmNavigateAway, useNavigate } from '../AppRouter'
import { PredictorsField } from '../PredictorsField'
import { MetricsField } from '../MetricsField'

export interface ModelDetailProps {
  model: Model | null
}

interface Fields {
  name: string
  abbreviation: string
  typeId: number | null
  translationId: number | null
  extentDescription: string
  codeFunction: string
  codeFunction2: string
  platform: string
  referenceSites: number | null
  groupCount: number | null
  minimumCount: number | null
  oeMean: number | null
  oeStdev: number | null
  taxonomicEffort: string
  fixedCount: number | null
  description: string
  predictorIds: number[]
  metricIds: number[]
  isActive: boolean
}

type FieldErrors = Partial<Record<keyof Fields, boolean>> | undefined

interface State {
  editMode: 'stopped' | 'started' | 'stopping' | 'deleting'
  isDirty: boolean
  values: Fields
  lastValues?: Fields
}

const getInitialState = ({ model }: Pick<ModelDetailProps, 'model'>): State => {
  const {
    name = '',
    abbreviation = '',
    typeId = null,
    translationId = null,
    extentDescription = '',
    codeFunction = '',
    codeFunction2 = '',
    platform = '',
    referenceSites = null,
    groupCount = null,
    minimumCount = null,
    oeMean = null,
    oeStdev = null,
    taxonomicEffort = '',
    fixedCount = null,
    description = '',
    predictorIds = [],
    metricIds = [],
    isActive = false,
  } = model ?? {}
  return {
    editMode: model ? 'stopped' : 'started',
    isDirty: false,
    values: {
      name,
      abbreviation,
      typeId,
      translationId,
      extentDescription,
      codeFunction,
      codeFunction2,
      platform,
      referenceSites,
      groupCount,
      minimumCount,
      oeMean,
      oeStdev,
      taxonomicEffort,
      fixedCount,
      description,
      predictorIds,
      metricIds,
      isActive,
    },
  }
}

const selectTranslationOptions = selector({
  key: 'ModelDetail/selectTranslationOptions',
  get: async ({ get }) => {
    const translations = await get(selectors.selectTranslations)
    return translations.map(({ id, name }) => ({ value: id, label: name }))
  },
})

export const ModelDetail = ({ model }: ModelDetailProps): JSX.Element => {
  const api = useApi()
  const invalidate = selectors.useInvalidate()
  const notifications = useNotifications()
  const navigate = useNavigate()
  const user = useRecoilValue(selectors.selectMyUser)

  const isNewModel = !model

  // LOOKUP OPTIONS

  const modelTypeOptions = selectors.lookupListToOptions(useRecoilValue(selectors.selectModelTypes))
  const taxonomicEffortOptions = [
    {
      value: 'Standard',
      label: 'Standard',
    },
    {
      value: 'Midges',
      label: 'Midges',
    },
  ]

  // STATE

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [state, setState, updateState] = useUpdatableState<State>(getInitialState({ model }), [model])
  const [isConfirmDeleteOpen, setIsConfirmDeleteOpen] = useState(false)

  const { values, editMode, isDirty } = state

  const errors: FieldErrors = (() => {
    const result: FieldErrors = {
      name: !values.name.trim(),
      abbreviation: !values.abbreviation.trim(),
      typeId: !values.typeId,
    }
    if (!Object.values(result).some((value) => value)) return undefined
    return result
  })()

  const isStopped = editMode === 'stopped'
  const isEditing = editMode === 'started'
  const isSaving = editMode === 'stopping'
  const isDeleting = editMode === 'deleting'

  // HANDLERS

  const setFieldValue = (fieldName: keyof Fields, value: unknown) => {
    updateState((draft) => {
      Object.assign(draft.values, { [fieldName]: value })
      draft.isDirty = true
    })
  }
  const makeFieldChangeHandler = (fieldName: keyof Fields) => (value: unknown) => setFieldValue(fieldName, value)

  const handleEdit = () => {
    updateState((draft) => {
      draft.editMode = 'started'
      draft.lastValues = draft.values
    })
  }

  const handleCancel = () => {
    updateState((draft) => {
      if (!draft.lastValues) throw new Error('no values to revert to')
      draft.editMode = 'stopped'
      draft.values = draft.lastValues
      draft.isDirty = false
    })
  }

  const handleSave = async () => {
    try {
      updateState((draft) => {
        draft.editMode = 'stopping'
      })
      const modelInput = trimAllStrings(values)
      if (isNewModel) {
        // new model
        const modelId = await calls.createModel(api, invalidate)(modelInput)
        updateState((draft) => {
          draft.editMode = 'stopped'
          draft.isDirty = false
        })
        window.setTimeout(() => {
          navigate.modelDetail({ modelId }, true) // let state update
        })
      } else {
        // existing model
        await calls.updateModel(
          api,
          invalidate
        )({
          ...modelInput,
          id: model.id,
        })
        updateState((draft) => {
          draft.editMode = 'stopped'
          draft.isDirty = false
        })
      }
    } catch (err) {
      switch ((err as Error).message) {
        case 'duplicate key value violates unique constraint "models_model_name_key"':
          notifications.error(`Failed to save: name "${values.name}" already in use`)
          break
        case 'duplicate key value violates unique constraint "models_abbreviation_key"':
          notifications.error(`Failed to save: abbreviation "${values.abbreviation}" already in use`)
          break
        default:
          notifications.error(`Failed to save: ${err}`)
      }
      updateState((draft) => {
        draft.editMode = 'started'
      })
    }
  }

  const handleDelete = async () => {
    try {
      if (!model) throw new Error('No model.')
      updateState((draft) => {
        draft.editMode = 'deleting'
      })
      await calls.deleteModel(api, invalidate)(model)
      notifications.info(`Deleted model ${model.name}`)
      navigate.models()
    } catch (err) {
      notifications.error(`Failed to delete: ${err}`)
      updateState((draft) => {
        draft.editMode = 'stopped'
      })
    }
  }

  // RENDER

  return (
    <>
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        <div css={{ display: 'flex', alignItems: 'center', minHeight: '3rem', marginBottom: '1rem', gap: '1rem' }}>
          {!isEditing ? (
            <Title>{values.name}</Title>
          ) : (
            <div css={{ flex: 1 }}>
              <TextField
                label="Model Name"
                value={values.name}
                onChange={(value) => setFieldValue('name', value)}
                error={errors?.name}
                InputProps={{ sx: { fontWeight: '700' } }} // roughly match <Title>
                maxLength={255}
                fullWidth
              />
            </div>
          )}
          <div css={{ display: 'flex', gap: '0.5em', marginLeft: 'auto' }}>
            {(isStopped || isDeleting) && user.hasPermission(NAMCResource.models, GrantAction.updateAny) && (
              <Menu
                label="Actions"
                disabled={isDeleting}
                items={[
                  {
                    key: 'edit',
                    label: 'Edit Model',
                    onClick: handleEdit,
                  },
                  Divider,
                  {
                    key: 'delete',
                    label: 'Delete Model',
                    onClick: () => {
                      setIsConfirmDeleteOpen(true)
                    },
                    disabled: !user.hasPermission(NAMCResource.models, GrantAction.deleteAny),
                  },
                ]}
              />
            )}
            {(isEditing || isSaving) && (
              <>
                {!isNewModel && (
                  <MuiButton onClick={handleCancel} variant="outlined" disabled={isSaving}>
                    Cancel
                  </MuiButton>
                )}
                <MuiButton onClick={handleSave} variant="contained" disabled={!isDirty || isSaving || !!errors}>
                  Save
                </MuiButton>
              </>
            )}
          </div>
        </div>
        <div
          css={{
            display: 'grid',
            gap: '0.75em',
            gridTemplateColumns: 'repeat(4, 1fr)',
            gridTemplateAreas: `
              "abbreviation     typeId           isActive            id"
              "oeMean           oeStdev          codeFunction        codeFunction2"
              "referenceSites   groupCount       minimumCount        fixedCount"
              "translationId    taxonomicEffort  platform            ."
              "predictorIds     predictorIds     predictorIds        predictorIds"
              "metricIds        metricIds        metricIds           metricIds"
              "description      description      extentDescription   extentDescription"
            `,
          }}
        >
          <ReadOnlyField gridArea="id" value={model?.id ?? 'TBD'} label="Model ID" />
          <TextField
            gridArea="abbreviation"
            value={values.abbreviation}
            onChange={makeFieldChangeHandler('abbreviation')}
            label="Abbreviation"
            readOnly={!isEditing}
            error={errors?.abbreviation}
            maxLength={50}
          />
          <AutocompleteField
            gridArea="typeId"
            label="Type"
            value={values.typeId}
            onChange={makeFieldChangeHandler('typeId')}
            readOnly={!isEditing}
            error={errors?.typeId}
            options={modelTypeOptions}
          />
          <CheckboxField
            gridArea="isActive"
            value={values.isActive}
            onChange={makeFieldChangeHandler('isActive')}
            label="Active"
            readOnly={!isEditing}
          />
          <NumberField
            gridArea="oeMean"
            value={values.oeMean}
            onChange={makeFieldChangeHandler('oeMean')}
            label="O2E Mean"
            readOnly={!isEditing}
            error={errors?.oeMean}
          />
          <NumberField
            gridArea="oeStdev"
            value={values.oeStdev}
            onChange={makeFieldChangeHandler('oeStdev')}
            label="O2E StDev"
            readOnly={!isEditing}
            error={errors?.oeStdev}
          />
          <TextField
            gridArea="codeFunction"
            value={values.codeFunction}
            onChange={makeFieldChangeHandler('codeFunction')}
            label="Code Function"
            readOnly={!isEditing}
            error={errors?.codeFunction}
            maxLength={255}
          />
          <TextField
            gridArea="codeFunction2"
            value={values.codeFunction2}
            onChange={makeFieldChangeHandler('codeFunction2')}
            label="Code Function 2"
            readOnly={!isEditing}
            error={errors?.codeFunction2}
            maxLength={255}
          />
          <NumberField
            gridArea="referenceSites"
            value={values.referenceSites}
            onChange={makeFieldChangeHandler('referenceSites')}
            label="Reference Sites"
            readOnly={!isEditing}
            error={errors?.referenceSites}
          />
          <NumberField
            gridArea="groupCount"
            value={values.groupCount}
            onChange={makeFieldChangeHandler('groupCount')}
            label="Group Count"
            readOnly={!isEditing}
            error={errors?.groupCount}
          />
          <NumberField
            gridArea="minimumCount"
            value={values.minimumCount}
            onChange={makeFieldChangeHandler('minimumCount')}
            label="Minimum Count"
            readOnly={!isEditing}
            error={errors?.minimumCount}
          />
          <NumberField
            gridArea="fixedCount"
            value={values.fixedCount}
            onChange={makeFieldChangeHandler('fixedCount')}
            label="Fixed Count"
            readOnly={!isEditing}
            error={errors?.fixedCount}
          />
          <AutocompleteField<number>
            gridArea="translationId"
            label="Translation"
            optionsSelector={selectTranslationOptions}
            valueLabel={model?.translationName ?? ''}
            value={values.translationId}
            onChange={makeFieldChangeHandler('translationId')}
            readOnly={!isEditing}
            isClearable
          />
          <AutocompleteField
            gridArea="taxonomicEffort"
            label="Taxonomic Effort"
            value={values.taxonomicEffort}
            onChange={makeFieldChangeHandler('taxonomicEffort')}
            readOnly={!isEditing}
            error={errors?.taxonomicEffort}
            options={taxonomicEffortOptions}
            isClearable
          />
          <TextField
            gridArea="platform"
            value={values.platform}
            onChange={makeFieldChangeHandler('platform')}
            label="Platform"
            readOnly={!isEditing}
            error={errors?.platform}
            maxLength={255}
          />
          <PredictorsField
            gridArea="predictorIds"
            label="Predictors"
            value={values.predictorIds}
            onChange={makeFieldChangeHandler('predictorIds')}
            readOnly={!isEditing}
            error={errors?.predictorIds}
          />
          <MetricsField
            gridArea="metricIds"
            label="Metrics"
            value={values.metricIds}
            onChange={makeFieldChangeHandler('metricIds')}
            readOnly={!isEditing}
            error={errors?.metricIds}
          />
          <TextField
            gridArea="description"
            value={values.description}
            onChange={makeFieldChangeHandler('description')}
            label="Description"
            readOnly={!isEditing}
            error={errors?.description}
            multiline
            minRows={2}
          />
          <TextField
            gridArea="extentDescription"
            value={values.extentDescription}
            onChange={makeFieldChangeHandler('extentDescription')}
            label="Extent Description"
            readOnly={!isEditing}
            error={errors?.extentDescription}
            multiline
            minRows={2}
          />
        </div>
      </div>
      <ConfirmationDialog
        title="Confirm Delete Model"
        isOpen={isConfirmDeleteOpen}
        onClose={() => {
          setIsConfirmDeleteOpen(false)
        }}
        onConfirm={handleDelete}
      >
        Are you sure you want to delete this model?
      </ConfirmationDialog>
      {isDirty && <ConfirmNavigateAway message="Some changes are not saved. Are you sure you want to leave?" />}
    </>
  )
}
