import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

import { SiteInput } from './createSite'

interface ExistingSiteInput extends SiteInput {
  id: number
}

export const updateSiteMutation = gql`
  mutation ($id: Int!, $site: SiteInput!) {
    updateSite(id: $id, site: $site)
  }
`

export const updateSite =
  (api: Api, invalidate: InvalidateFn) =>
  async (site: ExistingSiteInput): Promise<true> => {
    const { id, name: siteName, ...restSite } = site
    const data = await api.mutation<{
      updateSite: number // NOTE: id of site
    }>(updateSiteMutation, {
      id,
      site: {
        siteName,
        ...restSite,
      },
    })

    if (!data.updateSite) throw new ApiError('site did not update')

    invalidate('sites')
    return true
  }
