import dayjs from 'dayjs'
import Excel from 'exceljs'
import { sortBy, uniq } from 'lodash'

import { downloadWorkbook, Api, addSingleTableWorksheet } from '@namcbugdb/react-common'
import { fetchCustomerBoxReportByBoxId } from '../../data/calls'

const samplesColumnConfig: Partial<Excel.Column>[] = [
  { header: 'Sample ID', key: 'sampleId' },
  { header: 'State', key: 'usState' },
  { header: 'Visit ID', key: 'visitId' },
  { header: 'Site Code', key: 'siteIdentifier' },
  { header: 'Sample Date', key: 'sampleDate' },
  { header: 'Latitude', key: 'latitude' },
  { header: 'Longitude', key: 'longitude' },
  { header: 'Waterbody', key: 'waterbodyName' },
  { header: 'Sample Type', key: 'sampleType' },
  { header: 'Sampler Type', key: 'sampleMethod' },
  { header: 'Habitat', key: 'habitatName' },
  { header: 'Area', key: 'area' },
  { header: 'Field Split', key: 'fieldSplit' },
  { header: 'Lab Split', key: 'labSplit' },
  { header: 'Jar Count', key: 'jarCount' },
  { header: 'Qualitative', key: 'qualitative' },
]

const taxaColumnConfig: Partial<Excel.Column>[] = [
  { header: 'Sample ID', key: 'sampleId' },
  { header: 'Class', key: 'class' },
  { header: 'Order', key: 'order' },
  { header: 'Family', key: 'family' },
  { header: 'Genus', key: 'genus' },
  { header: 'Species', key: 'species' },
  { header: 'Scientific Name', key: 'scientificName' },
  { header: 'Split Count', key: 'splitCount' },
  { header: 'Big Rare Count', key: 'bigRareCount' },
]

export const downloadCustomerBoxReport = async ({
  boxId,
  api,
  filename,
}: {
  boxId: number
  api: Api
  filename: string
}): Promise<boolean> => {
  const {
    boxInfo: {
      submittedBy,
      submitterEmail,
      customer: { organizationName, address1, address2, city, usState, zipCode, phone },
    },
    samples,
    sampleTaxa,
    reportMetrics,
    template,
  } = await fetchCustomerBoxReportByBoxId(api)(boxId)

  // create workbook from blank

  const wb = new Excel.Workbook()

  await new Promise<void>((resolve) => {
    const reader = new FileReader()
    reader.onload = async () => {
      const buffer = reader.result
      if (!(buffer instanceof ArrayBuffer)) throw new Error('not an arraybuffer')
      await wb.xlsx.load(buffer)
      resolve()
    }
    reader.readAsArrayBuffer(template)
  })

  // Sheet 1: Summary

  const summaryWs = wb.worksheets[0]
  const findRangeByName = (name: string): string => {
    const found = wb.definedNames.model.find((range) => range.name === name)
    if (!found) throw new Error(`Named range "${name}" not found in workbook.`)
    return found.ranges[0].split('!')[1]
  }
  summaryWs.getCell(findRangeByName('ReportDate')).value = dayjs().format('YYYY-MM-DD')
  summaryWs.getCell(findRangeByName('SubmitterName')).value = submittedBy
  summaryWs.getCell(findRangeByName('CustomerName')).value = organizationName
  summaryWs.getCell(findRangeByName('Address1')).value = address1
  summaryWs.getCell(findRangeByName('Address2')).value = address2
  summaryWs.getCell(findRangeByName('CityStateZip')).value = `${city}, ${usState}  ${zipCode}`
  summaryWs.getCell(findRangeByName('Telephone')).value = phone
  summaryWs.getCell(findRangeByName('Email')).value = submitterEmail

  // Sheet 2: Samples

  const sampleRowData = samples.map(
    ({
      fieldSplit,
      labSplit,
      customerSiteCode,
      sampleLatitude,
      sampleLongitude,
      siteName,
      siteLatitude,
      siteLongitude,
      ...restSample
    }) => {
      const [latitude, longitude] =
        sampleLatitude && sampleLongitude ? [sampleLatitude, sampleLongitude] : [siteLatitude, siteLongitude]

      const siteIdentifier = customerSiteCode || siteName

      return {
        fieldSplit: fieldSplit * 100,
        labSplit: labSplit * 100,
        siteIdentifier,
        latitude,
        longitude,
        ...restSample,
      }
    }
  )

  addSingleTableWorksheet(wb, { wsName: 'Samples', columns: samplesColumnConfig, rows: sampleRowData })

  // Sheet 3: Taxa

  addSingleTableWorksheet(wb, { wsName: 'Taxa', columns: taxaColumnConfig, rows: sampleTaxa })

  // Sheet 4: Metrics

  const sampleIds = samples.map(({ sampleId }) => sampleId)
  const metricNames = uniq(reportMetrics.map(({ metricName }) => metricName))

  const metricsColumnConfig = [
    {
      header: 'Sample ID',
      key: 'sampleId',
    },
    ...sortBy(reportMetrics, ({ metricGroupId }) => metricGroupId || 0).map(({ metricName }) => ({
      header: metricName,
      key: metricName,
    })),
  ]

  const metricRowData = sampleIds.map((sampleId) => {
    const thisRow = { sampleId }
    metricNames.forEach((metricName) => {
      const value = reportMetrics.find(
        (reportMetric) => reportMetric.sampleId === sampleId && reportMetric.metricName === metricName
      )?.metricValue
      if (typeof value === 'undefined') return
      const valueAsNumber = Number(value)
      thisRow[metricName] = isNaN(valueAsNumber) ? value : valueAsNumber
    })
    return thisRow
  })

  addSingleTableWorksheet(wb, { wsName: 'Metrics', columns: metricsColumnConfig, rows: metricRowData })

  // download file

  downloadWorkbook(wb, filename)

  return true
}
