/** @jsxImportSource @emotion/react */
import React, { useEffect, useState } from 'react'
import { atom, useRecoilCallback, useRecoilState } from 'recoil'
import { identity, intersectionBy, noop } from 'lodash'
import produce from 'immer'
import MuiButton from '@mui/material/Button'
import AddCircleIcon from '@mui/icons-material/AddCircle'
import RemoveCircleIcon from '@mui/icons-material/RemoveCircle'
import MuiToggleButton from '@mui/material/ToggleButton'
import MuiToggleButtonGroup from '@mui/material/ToggleButtonGroup'
// import log from 'loglevel'

import * as calls from '../../data/calls'
import * as selectors from '../../data/selectors'
import {
  MinimalSample,
  GeoJsonPolygonCoordinates,
  Filter,
  FilterChips,
  Title,
  Chip,
  LinearProgress,
  pluralize,
  useApi,
  useNotifications,
  useUpdatableState,
  parseGeoJsonPolygon,
  CriterionOption,
  DateField,
  NumberField,
  TextField,
  AutocompleteField,
} from '@namcbugdb/react-common'
import {
  BoxCriterionField,
  GeoJsonCriterionField,
  MethodCriterionField,
  OrganizationCriterionField,
  ProjectCriterionField,
  SampleIdsCriterionField,
  SiteCriterionField,
  TaxonomyCriterionField,
  TypeCriterionField,
  UsStateCriterionField,
} from './CriterionFields'

const passAllFilterFn = () => true
const rejectAllFilterFn = () => false

export type SelectSamples = {
  onOperatingChange?: (operating: boolean) => void
  samples: MinimalSample[]
} & (
  | {
      initialMode: 'add' | 'remove'
      projectId: number
      projectName: string
      boxId?: never
      onNewBoxSamplesChange?: never
    }
  | {
      initialMode: 'split'
      projectId?: never
      projectName?: never
      boxId: number
      onNewBoxSamplesChange: (samples: MinimalSample[]) => void
    }
)

interface CriteriaFields {
  box: CriterionOption<number> | null
  taxon: CriterionOption<number> | null
  organization: CriterionOption<number> | null
  project: CriterionOption<number> | null
  usState: CriterionOption<number> | null
  site: CriterionOption<number> | null
  sampleIds: number[]
  fromDate: string | null
  toDate: string | null
  type: CriterionOption<string> | null
  method: CriterionOption<string> | null
  geoJson: string
  fromArea: number | null
  toArea: number | null
}

const getInitialCriteriaFields = (): CriteriaFields => ({
  box: null,
  taxon: null,
  organization: null,
  project: null,
  usState: null,
  site: null,
  sampleIds: [],
  fromDate: null,
  toDate: null,
  type: null,
  method: null,
  geoJson: '',
  fromArea: null,
  toArea: null,
})

type CriteriaFieldErrors = Partial<Record<keyof CriteriaFields, boolean>> | undefined

const minimalSamplesByIdAtom = atom<Record<number, MinimalSample | Promise<MinimalSample | undefined>>>({
  key: '',
  default: {},
})

export const SelectSamples = ({
  boxId: thisBoxId,
  initialMode,
  onOperatingChange,
  projectId: thisProjectId,
  projectName: thisProjectName,
  samples: initialSamples,
  onNewBoxSamplesChange,
}: SelectSamples): JSX.Element => {
  const api = useApi()
  const deferredInvalidate = selectors.useDeferredInvalidate()
  const notifications = useNotifications()

  // CRITERIA STATE

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [criteria, setCriteria, updateCriteria] = useUpdatableState<CriteriaFields>(getInitialCriteriaFields(), [])
  const makeSetFieldValue = (fieldName: keyof CriteriaFields) => (value: unknown) => {
    updateCriteria((draft) => {
      Object.assign(draft, { [fieldName]: value })
    })
  }
  const resetField = (fieldName: keyof CriteriaFields) => {
    updateCriteria((draft) => {
      Object.assign(draft, { [fieldName]: getInitialCriteriaFields()[fieldName] })
    })
  }
  const errors: CriteriaFieldErrors = (() => {
    const dateError = !!(criteria.fromDate && criteria.toDate && criteria.fromDate > criteria.toDate)
    const areaError = !!(criteria.fromArea && criteria.toArea && criteria.fromArea > criteria.toArea)
    const result: CriteriaFieldErrors = {
      fromDate: dateError,
      toDate: dateError,
      fromArea: areaError,
      toArea: areaError,
    }
    if (!Object.values(result).some((value) => value)) return undefined
    return result
  })()

  // OTHER STATE

  const [thisSourceSamples, setThisSourceSamples] = useState(initialSamples)
  const [candidateSamples, setCandidateSamples] = useState<MinimalSample[]>([])
  const [newBoxSamples, setNewBoxSamples] = useState<MinimalSample[]>([])
  const [resultsMessage, setResultsMessage] = useState<React.ReactNode>(null)
  const [displayFilters, setDisplayFilters] = useState<Filter<MinimalSample>[]>([])
  const [isOperating, setIsOperating] = useState(false)
  const [mode, setMode] = useState(initialMode)

  const isBoxSplitMode = mode === 'split'
  const isProjectAddMode = mode === 'add'
  const isProjectRemoveMode = mode === 'remove'
  const isProjectMode = isProjectAddMode || isProjectRemoveMode

  useEffect(() => {
    if (onOperatingChange) onOperatingChange(isOperating)
  }, [isOperating])

  // RECOIL

  const [minimalSamplesById, setMinimalSamplesById] = useRecoilState(minimalSamplesByIdAtom)

  const getSamplesByBoxId = useRecoilCallback<[number], Promise<MinimalSample[]>>(
    ({ snapshot }) =>
      async (boxId: number) =>
        snapshot.getPromise(selectors.selectBoxSamplesByBoxId(boxId)).catch((err) => {
          notifications.error(`Failed to retrieve box samples: ${err}`)
          return []
        })
  )

  const getSamplesByProjectId = useRecoilCallback<[number], Promise<MinimalSample[]>>(
    ({ snapshot }) =>
      async (projectId: number) =>
        snapshot.getPromise(selectors.selectProjectSamplesByProjectId(projectId)).catch((err) => {
          notifications.error(`Failed to retrieve project samples: ${err}`)
          return []
        })
  )

  const getSamplesByUsStateId = useRecoilCallback<[number], Promise<MinimalSample[]>>(
    ({ snapshot }) =>
      async (usStateId: number) =>
        snapshot.getPromise(selectors.selectMinimalSamplesByUsStateId(usStateId)).catch((err) => {
          notifications.error(`Failed to retrieve US State samples: ${err}`)
          return []
        })
  )

  const getSamplesBySiteId = useRecoilCallback<[number], Promise<MinimalSample[]>>(
    ({ snapshot }) =>
      async (siteId: number) =>
        snapshot.getPromise(selectors.selectMinimalSamplesBySiteId(siteId)).catch((err) => {
          notifications.error(`Failed to retrieve site samples: ${err}`)
          return []
        })
  )

  const getSamplesByTaxonId = useRecoilCallback<[number], Promise<MinimalSample[]>>(
    ({ snapshot }) =>
      async (taxonId: number) =>
        snapshot.getPromise(selectors.selectMinimalSamplesByTaxonId(taxonId)).catch((err) => {
          notifications.error(`Failed to retrieve taxon samples: ${err}`)
          return []
        })
  )

  const getSamplesByOrganizationId = useRecoilCallback<[number], Promise<MinimalSample[]>>(
    ({ snapshot }) =>
      async (organizationId: number) =>
        snapshot.getPromise(selectors.selectMinimalSamplesByOrganizationId(organizationId)).catch((err) => {
          notifications.error(`Failed to retrieve organization samples: ${err}`)
          return []
        })
  )

  const getSamplesByPolygon = useRecoilCallback<[GeoJsonPolygonCoordinates], Promise<MinimalSample[]>>(
    ({ snapshot }) =>
      async (polygon: GeoJsonPolygonCoordinates) =>
        snapshot.getPromise(selectors.selectMinimalSamplesByPolygon(polygon)).catch((err) => {
          notifications.error(`Failed to retrieve polygon samples: ${err}`)
          return []
        })
  )

  useEffect(() => {
    setMinimalSamplesById(
      produce((draft) => {
        initialSamples.forEach((sample) => {
          draft[sample.id] = sample
        })
      })
    )
  }, [initialSamples])

  // ADD/REMOVE HANDLERS

  const handleAddToProject = async () => {
    if (!thisProjectId || candidateSamples.length === 0) return
    const targetIds = candidateSamples.map(({ id }) => id)
    try {
      setIsOperating(true)
      while (targetIds.length > 0) {
        const sampleIds = targetIds.splice(0, 500) // add 500 per batch
        await calls.addProjectSamples(api, deferredInvalidate)({ projectId: thisProjectId, sampleIds })
        setThisSourceSamples((samples) => [...samples, ...candidateSamples.filter(({ id }) => sampleIds.includes(id))])
      }
      notifications.info(`Added ${candidateSamples.length} ${pluralize(candidateSamples.length, 'sample')} to project`)
    } catch (err) {
      notifications.error(`Failed to add samples to project: ${err}`)
    } finally {
      setIsOperating(false)
    }
  }

  const handleRemoveFromProject = async () => {
    if (!thisProjectId || candidateSamples.length === 0) return
    const targetIds = candidateSamples.map(({ id }) => id)
    try {
      setIsOperating(true)
      while (targetIds.length > 0) {
        const sampleIds = targetIds.splice(0, 500) // remove 500 per batch
        await calls.removeProjectSamples(api, deferredInvalidate)({ projectId: thisProjectId, sampleIds })
        setThisSourceSamples((samples) => samples.filter(({ id }) => !sampleIds.includes(id)))
      }
      notifications.info(
        `Removed ${candidateSamples.length} ${pluralize(candidateSamples.length, 'sample')} from project`
      )
    } catch (err) {
      notifications.error(`Failed to remove samples from project: ${err}`)
    } finally {
      setIsOperating(false)
    }
  }

  const handleAssignToNewBox = async () => {
    if (!thisBoxId || candidateSamples.length === 0 || !onNewBoxSamplesChange) return
    const targetIds = candidateSamples.map(({ id }) => id)
    setThisSourceSamples((samples) => samples.filter(({ id }) => !targetIds.includes(id)))
    const updatedNewBoxSamples = [...newBoxSamples, ...candidateSamples]
    setNewBoxSamples(updatedNewBoxSamples)
    onNewBoxSamplesChange(updatedNewBoxSamples)
  }

  // FETCHING AND FILTERS

  useEffect(() => {
    // setup
    const criteriaFilters: typeof displayFilters = []
    const thisSourceFilters: typeof displayFilters = []
    const sourcePromises: Promise<MinimalSample[]>[] = []
    const errorMessages: React.ReactNode[] = []
    let cancelled = false

    const setupIdBasedCriterion = (
      fieldName: keyof CriteriaFields,
      sampleGetter: (id: number) => Promise<MinimalSample[]>
    ) => {
      const criterion = criteria[fieldName] as CriterionOption<number> | null
      if (criterion) {
        criteriaFilters.push({
          id: fieldName,
          label: criterion.chipLabel,
          filterFn: passAllFilterFn,
          onDelete: () => resetField(fieldName),
        })
        sourcePromises.push(sampleGetter(criterion.id))
      }
    }

    // source: box
    setupIdBasedCriterion('box', getSamplesByBoxId)

    // source: project
    setupIdBasedCriterion('project', getSamplesByProjectId)

    // source: customer
    setupIdBasedCriterion('organization', getSamplesByOrganizationId)

    // source: site
    setupIdBasedCriterion('site', getSamplesBySiteId)

    // COMING: customer site code

    // source: US State
    setupIdBasedCriterion('usState', getSamplesByUsStateId)

    // COMING: waterbody name

    // source: GeoJSON polygon
    if (criteria.geoJson) {
      const { geoJson } = criteria
      try {
        const polygonCoordinates = parseGeoJsonPolygon(geoJson)
        criteriaFilters.push({
          id: 'geoJson',
          label: 'within polygon',
          filterFn: passAllFilterFn,
          onDelete: () => resetField('geoJson'),
        })
        sourcePromises.push(getSamplesByPolygon(polygonCoordinates))
      } catch (err) {
        criteriaFilters.push({
          id: 'geoJson',
          label: 'within polygon',
          filterFn: rejectAllFilterFn,
          onDelete: () => resetField('geoJson'),
          chipColor: 'error',
        })
        errorMessages.push(`GeoJSON Polygon error (${err})`)
      }
    }

    // source: taxon
    setupIdBasedCriterion('taxon', getSamplesByTaxonId)

    // source: sample IDs
    if (criteria.sampleIds.length > 0) {
      const { sampleIds } = criteria
      const onDelete = () => resetField('sampleIds')
      if (sampleIds.length === 1)
        criteriaFilters.push({
          id: 'sampleIds',
          label: `ID is ${sampleIds[0]}`,
          filterFn: passAllFilterFn,
          onDelete,
        })
      if (sampleIds.length > 1)
        criteriaFilters.push({
          id: 'sampleIds',
          label: `ID is in list (${sampleIds.length} samples)`,
          filterFn: passAllFilterFn,
          onDelete,
        })

      const usefulPromises: (MinimalSample | Promise<MinimalSample | undefined>)[] = []
      const fetchIds: number[] = []
      sampleIds.forEach((needId) => {
        if (minimalSamplesById[needId]) {
          usefulPromises.push(minimalSamplesById[needId])
        } else {
          fetchIds.push(needId)
        }
      })
      const fetchPromise = fetchIds.length > 0 ? calls.fetchMinimalSamplesByIds(api)(fetchIds) : Promise.resolve([])
      const newOrderedSamplePromises = fetchIds.map((fetchId) =>
        fetchPromise.then((fetched) => fetched.find(({ id }) => id === fetchId))
      )
      setMinimalSamplesById(
        produce((draft) => {
          fetchIds.forEach((fetchId, index) => {
            draft[fetchId] = newOrderedSamplePromises[index]
          })
        })
      )
      sourcePromises.push(
        Promise.all([...usefulPromises, ...newOrderedSamplePromises])
          .then((resolvedSamplesOrUndefineds) => resolvedSamplesOrUndefineds.filter(identity) as MinimalSample[])
          .catch((err) => {
            notifications.error(`Failed to retrieve samples by ID: ${err}`)
            setMinimalSamplesById(
              produce((draft) => {
                fetchIds.forEach((fetchId) => {
                  delete draft[fetchId] // clear promise so failure isn't permanent with the ID
                })
              })
            )
            return []
          })
      )
    }

    const isAddModeWithoutSource = isProjectAddMode && sourcePromises.length === 0

    // post-sources filter: sample type
    if (criteria.type) {
      const { type } = criteria
      criteriaFilters.push({
        id: 'type',
        label: type.chipLabel,
        filterFn: (sample) => sample.type === type.id,
        onDelete: () => resetField('type'),
        chipColor: isAddModeWithoutSource ? 'warn' : 'default',
      })
    }

    // post-sources filter: sample method
    if (criteria.method) {
      const { method } = criteria
      criteriaFilters.push({
        id: 'method',
        label: method.chipLabel,
        filterFn: (sample) => sample.method === method.id,
        onDelete: () => resetField('method'),
        chipColor: isAddModeWithoutSource ? 'warn' : 'default',
      })
    }

    // post-sources filter: date range
    if (criteria.fromDate || criteria.toDate) {
      const { fromDate, toDate } = criteria
      const common = {
        id: 'dateCriteria',
        onDelete: () => {
          resetField('fromDate')
          resetField('toDate')
        },
        chipColor: isAddModeWithoutSource ? 'warn' : 'default',
      }
      if (fromDate && !toDate) {
        criteriaFilters.push({
          ...common,
          label: `sampled ${fromDate} or later`,
          filterFn: ({ sampleDate }) => sampleDate >= fromDate,
        })
      } else if (!fromDate && toDate) {
        criteriaFilters.push({
          ...common,
          label: `sampled ${toDate} or earlier`,
          filterFn: ({ sampleDate }) => sampleDate <= toDate,
        })
      } else if (fromDate && fromDate === toDate) {
        criteriaFilters.push({
          ...common,
          label: `sampled on ${fromDate}`,
          filterFn: ({ sampleDate }) => sampleDate === fromDate,
        })
      } else if (fromDate && toDate) {
        if (fromDate > toDate) {
          criteriaFilters.push({
            ...common,
            label: `sampled between ${fromDate} and ${toDate}`,
            filterFn: rejectAllFilterFn,
            chipColor: 'error',
          })
          errorMessages.push('From Date cannot be after To Date')
        } else {
          criteriaFilters.push({
            ...common,
            label: `sampled between ${fromDate} and ${toDate}`,
            filterFn: ({ sampleDate }) => sampleDate >= fromDate && sampleDate <= toDate,
          })
        }
      }
    }

    // post-sources filter: area range
    if (criteria.fromArea || criteria.toArea) {
      const { fromArea, toArea } = criteria
      const common = {
        id: 'areaCriteria',
        onDelete: () => {
          resetField('fromArea')
          resetField('toArea')
        },
        chipColor: isAddModeWithoutSource ? 'warn' : 'default',
      }
      if (fromArea && !toArea) {
        criteriaFilters.push({
          ...common,
          label: `sampled area ${fromArea} m² or greater`,
          filterFn: ({ area }) => area >= fromArea,
        })
      } else if (!fromArea && toArea) {
        criteriaFilters.push({
          ...common,
          label: `sampled area ${toArea} m² or less`,
          filterFn: ({ area }) => area <= toArea,
        })
      } else if (fromArea && toArea) {
        if (fromArea > toArea) {
          criteriaFilters.push({
            ...common,
            label: `sampled area between ${fromArea} m² and ${toArea} m²`,
            filterFn: rejectAllFilterFn,
            chipColor: 'error',
          })
          errorMessages.push('Min Area cannot be greater than Max Area')
        } else {
          criteriaFilters.push({
            ...common,
            label: `sampled area between ${fromArea} m² and ${toArea} m²`,
            filterFn: ({ area }) => area >= fromArea && area <= toArea,
          })
        }
      }
    }

    // this project/box filter
    if (criteriaFilters.length > 0) {
      const thisSourceSampleIds = thisSourceSamples.map(({ id }) => id)
      if (isProjectAddMode) {
        thisSourceFilters.push({
          id: 'thisSource',
          label: 'not already in this project',
          filterFn: ({ id }) => !thisSourceSampleIds.includes(id),
        })
      } else if (isProjectRemoveMode) {
        thisSourceFilters.push({
          id: 'thisSource',
          label: 'currently in this project',
          filterFn: ({ id }) => thisSourceSampleIds.includes(id),
        })
      } else if (isBoxSplitMode) {
        thisSourceFilters.push(
          {
            id: 'thisSource',
            label: 'in this box',
            filterFn: ({ id }) => thisSourceSampleIds.includes(id),
          },
          {
            id: 'alreadySelected',
            label: 'not already selected',
            filterFn: passAllFilterFn,
          }
        )
      }
    }

    // set display filters
    setDisplayFilters([...criteriaFilters, ...thisSourceFilters])

    // clear matched samples
    setCandidateSamples([])

    // retrieve and filter data or display error
    if (errorMessages.length > 0) {
      // criteria error
      setResultsMessage(`Cannot match samples: ${errorMessages.join(', ')}.`)
    } else if (sourcePromises.length === 0) {
      // nothing to fetch
      if (criteriaFilters.length === 0) {
        // still waiting for any criterion
        setResultsMessage(null)
      } else if (isProjectAddMode) {
        // add mode with post-source filter(s) only; need more criteria
        setResultsMessage(
          'Add more criteria to match samples. (Type, Method, Date, and Area may only be used as supplemental criteria when adding samples.)'
        )
      } else {
        // remove mode with post-source filter(s) only; use current project/box samples as sole source
        const filteredSamples = criteriaFilters.reduce<MinimalSample[]>(
          (acc, { filterFn }) => acc.filter(filterFn),
          thisSourceSamples
        )
        setCandidateSamples(filteredSamples)
        const operableCount = filteredSamples.length
        if (isProjectMode) {
          setResultsMessage(
            <span>
              Matched <strong>{operableCount}</strong> {pluralize(operableCount, 'sample')}{' '}
              {isProjectAddMode ? 'not already' : 'currently'} in this project.
            </span>
          )
        } else {
          setResultsMessage(
            <span>
              Matched <strong>{operableCount}</strong> {pluralize(operableCount, 'sample')}.
              {operableCount === thisSourceSamples.length
                ? ' Cannot select samples as box would be left with 0 samples.'
                : ''}
            </span>
          )
        }
      }
    } else {
      // enter fetching mode
      setResultsMessage(<LinearProgress css={{ width: '25rem' }} />)

      // process sources and filters
      Promise.all(sourcePromises).then((sources) => {
        if (cancelled) return // do nothing if this effect has been superceded

        // intersect sources
        const [firstSource, ...restSources] = sources
        let filteredSamples = restSources.reduce(
          (acc, source) => intersectionBy(acc, source, ({ id }) => id),
          firstSource
        )

        // apply filters
        filteredSamples = criteriaFilters.reduce<MinimalSample[]>(
          (acc, { filterFn }) => acc.filter(filterFn),
          filteredSamples
        )
        const matchedCount = filteredSamples.length // get match count before source-based filter
        filteredSamples = thisSourceFilters.reduce<MinimalSample[]>(
          (acc, { filterFn }) => acc.filter(filterFn),
          filteredSamples
        )

        // set candidate samples and messaging
        setCandidateSamples(filteredSamples)
        const operableCount = filteredSamples.length
        if (isProjectMode) {
          setResultsMessage(
            <span>
              Matched {matchedCount} {pluralize(matchedCount, 'sample')} of which <strong>{operableCount}</strong>{' '}
              {pluralize(operableCount, 'is')} {isProjectAddMode ? 'not already' : 'currently'} in this project.
            </span>
          )
        } else {
          setResultsMessage(
            <span>
              Matched <strong>{operableCount}</strong> {pluralize(operableCount, 'sample')}.
              {operableCount === thisSourceSamples.length
                ? ' Cannot select samples as box would be left with 0 samples.'
                : ''}
            </span>
          )
        }
      })
    }

    // return cleanup function
    return () => {
      cancelled = true
    }
  }, [thisSourceSamples, mode, criteria])

  // RENDER

  return (
    <div>
      <div css={{ display: 'flex', alignItems: 'center', minHeight: '2.5em' }}>
        {isProjectMode && (
          <Title>
            Edit Samples in Project {thisProjectName}{' '}
            <Title.Subtitle>
              ({thisSourceSamples.length} {pluralize(thisSourceSamples.length, 'sample')})
            </Title.Subtitle>
          </Title>
        )}
        {isBoxSplitMode && newBoxSamples && (
          <Title>
            Split Box {thisBoxId}{' '}
            <Title.Subtitle>
              {`(${initialSamples.length} ${pluralize(initialSamples.length, 'sample')}`}
              {newBoxSamples.length > 0
                ? ` before split, ${initialSamples.length - newBoxSamples.length} ${pluralize(
                    initialSamples.length - newBoxSamples.length,
                    'sample'
                  )} after split)`
                : ')'}
            </Title.Subtitle>
          </Title>
        )}
        {isProjectMode && (
          <MuiToggleButtonGroup
            css={{ marginLeft: 'auto' }}
            value={mode}
            onChange={(_, newMode) => {
              if (!newMode) return
              setMode(newMode)
            }}
            exclusive
          >
            <MuiToggleButton size="small" value={'add'} key="add" css={{ width: '6em' }}>
              Add
            </MuiToggleButton>
            <MuiToggleButton size="small" value={'remove'} key="remove" css={{ width: '6em' }}>
              Remove
            </MuiToggleButton>
          </MuiToggleButtonGroup>
        )}
      </div>
      <div
        css={{
          marginTop: '1rem',
          display: 'grid',
          gap: '1rem',
          gridTemplateColumns: 'repeat(4, 1fr)',
          gridTemplateAreas: `
            "box           box           project       project"
            "organization  organization  organization  organization"
            "site          site          siteCode      siteCode"
            "usState       usState       waterbody     waterbody"
            "geoJson       geoJson       geoJson       geoJson"
            "taxon         taxon         taxon         taxon"
            "sampleIds     sampleIds     sampleIds     sampleIds"
            "type          type          method        method"
            "fromDate      toDate        fromArea      toArea"
          `,
        }}
      >
        {isProjectMode ? (
          <BoxCriterionField gridArea="box" value={criteria.box} onChange={makeSetFieldValue('box')} />
        ) : (
          <AutocompleteField gridArea="box" label="Box" value={null} onChange={noop} options={[]} disabled />
        )}
        <ProjectCriterionField
          gridArea="project"
          value={criteria.project}
          onChange={makeSetFieldValue('project')}
          thisProjectId={thisProjectId ?? 0}
        />
        {isProjectMode ? (
          <OrganizationCriterionField
            gridArea="organization"
            value={criteria.organization}
            onChange={makeSetFieldValue('organization')}
          />
        ) : (
          <AutocompleteField
            gridArea="organization"
            label="Customer"
            value={null}
            onChange={noop}
            options={[]}
            disabled
          />
        )}
        <SiteCriterionField gridArea="site" value={criteria.site} onChange={makeSetFieldValue('site')} />
        <TextField gridArea="siteCode" label="Customer Site Code" value="" onChange={noop} disabled />
        <UsStateCriterionField gridArea="usState" value={criteria.usState} onChange={makeSetFieldValue('usState')} />
        <TextField gridArea="waterbody" label="Waterbody Name" value="" onChange={noop} disabled />
        <GeoJsonCriterionField gridArea="geoJson" value={criteria.geoJson} onChange={makeSetFieldValue('geoJson')} />
        <TaxonomyCriterionField gridArea="taxon" value={criteria.taxon} onChange={makeSetFieldValue('taxon')} />
        <SampleIdsCriterionField
          gridArea="sampleIds"
          value={criteria.sampleIds}
          onChange={makeSetFieldValue('sampleIds')}
        />
        <TypeCriterionField gridArea="type" value={criteria.type} onChange={makeSetFieldValue('type')} />
        <MethodCriterionField gridArea="method" value={criteria.method} onChange={makeSetFieldValue('method')} />
        <DateField
          gridArea="fromDate"
          value={criteria.fromDate}
          onChange={makeSetFieldValue('fromDate')}
          label="From Date"
          error={errors?.fromDate}
        />
        <DateField
          gridArea="toDate"
          value={criteria.toDate}
          onChange={makeSetFieldValue('toDate')}
          label="To Date"
          error={errors?.toDate}
        />
        <NumberField
          gridArea="fromArea"
          value={criteria.fromArea}
          onChange={makeSetFieldValue('fromArea')}
          label="Min Area"
          error={errors?.fromArea}
          min={0}
          max={5}
        />
        <NumberField
          gridArea="toArea"
          value={criteria.toArea}
          onChange={makeSetFieldValue('toArea')}
          label="Max Area"
          error={errors?.toArea}
          min={0}
          max={5}
        />
      </div>
      <div
        css={{
          marginTop: '1rem',
          display: 'grid',
          gap: '1rem',
        }}
      >
        <FilterChips filters={displayFilters} joinLabel="and" noneNode={<Chip label="No criteria" />} />
        <div css={{ minHeight: '1.2rem', display: 'flex', alignItems: 'center' }}>{resultsMessage}</div>
        <div css={{ display: 'flex' }}></div>
        {isProjectMode && (
          <MuiButton
            variant="contained"
            disabled={candidateSamples.length === 0 || isOperating}
            onClick={isProjectAddMode ? handleAddToProject : handleRemoveFromProject}
            startIcon={isProjectAddMode ? <AddCircleIcon /> : <RemoveCircleIcon />}
          >
            {(() => {
              if (isProjectAddMode) {
                if (isOperating) return 'Adding samples to project...'
                if (candidateSamples.length === 0) return 'Add samples to project'
                return `Add ${candidateSamples.length} ${pluralize(candidateSamples.length, 'sample')} to project`
              } else {
                if (isOperating) return 'Removing samples from project...'
                if (candidateSamples.length === 0) return 'Remove samples from project'
                return `Remove ${candidateSamples.length} ${pluralize(candidateSamples.length, 'sample')} from project`
              }
            })()}
          </MuiButton>
        )}
        {isBoxSplitMode && (
          <MuiButton
            variant="contained"
            disabled={candidateSamples.length === 0 || candidateSamples.length === thisSourceSamples.length}
            onClick={handleAssignToNewBox}
          >
            {(() => {
              if (isOperating) return 'Creating box...'
              if (candidateSamples.length === 0) return 'Select samples for new box'
              return `Select ${candidateSamples.length} ${pluralize(candidateSamples.length, 'sample')} for new box`
            })()}
          </MuiButton>
        )}
      </div>
    </div>
  )
}
