import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { Metric } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

export const deleteMetricMutation = gql`
  mutation ($metricId: Int!) {
    deleteMetric(id: $metricId)
  }
`

export const deleteMetric =
  (api: Api, invalidate: InvalidateFn) =>
  async (metric: Metric): Promise<true> => {
    const data = await api.mutation<{ deleteMetric: number }>(deleteMetricMutation, {
      metricId: metric.id,
    })

    if (!data.deleteMetric) throw new ApiError('metric did not delete')

    invalidate('metrics')
    return true
  }
