/** @jsxImportSource @emotion/react */
import React, { useMemo } from 'react'
import { atom, useRecoilState, useRecoilValue } from 'recoil'
import { uniq } from 'lodash'
import MuiSkeleton from '@mui/material/Skeleton'

import * as calls from '../../data/calls'
import * as selectors from '../../data/selectors'
import {
  withSuspense,
  useApi,
  useNotifications,
  createSingleTableWorkbook,
  downloadWorkbook,
  downloadWorkbookAsCsv,
  Filter,
  FilterChips,
  Report,
  TextFilterField,
  useTextFilterField,
} from '@namcbugdb/react-common'
import { DataGrid, DataGridRecordCount, renderers } from '../DataGrid'
import { reportsColumnDefs } from './columnDefs'

export interface ReportRow extends Report {
  downloadXlsx: {
    label: string
    onClick: () => void
  }
  downloadCsv: {
    label: string
    onClick: () => void
  }
}

const textFilterState = atom({
  key: 'ReportsListing/textFilterState',
  default: '',
})

export const ReportsListing = withSuspense(
  () => {
    const api = useApi()
    const notifications = useNotifications()
    const reports = useRecoilValue(selectors.selectReports)

    // STATE AND REFS

    const [textFilter, setTextFilter, textFilterDebounced, clearTextFilter] = useTextFilterField(
      useRecoilState(textFilterState)
    )

    // FILTERS

    const filters = useMemo(() => {
      const result: Filter<Report>[] = []

      if (textFilterDebounced) {
        result.push({
          id: 'text',
          label: `Name/Description contains "${textFilterDebounced}"`,
          filterFn: ({ name, description }) =>
            name.toLowerCase().includes(textFilterDebounced.toLowerCase()) ||
            description.toLowerCase().includes(textFilterDebounced.toLowerCase()),
          onDelete: clearTextFilter,
        })
      }

      return result
    }, [textFilterDebounced])

    const rows = useMemo<ReportRow[]>(() => {
      const filtered = filters.reduce<Report[]>((acc, { filterFn }) => acc.filter(filterFn), reports)
      return filtered.map(({ id, name, description }) => {
        const createWorkbook = async () => {
          try {
            const cancelNotification = notifications.info('Still creating report...', 2500)
            const data = await calls.fetchReportDataByReportId(api)(id)
            cancelNotification()
            const columnNames = data.reduce<string[]>((acc, reportRow) => uniq([...acc, ...Object.keys(reportRow)]), [])
            return createSingleTableWorkbook({
              wsName: name,
              columns: columnNames.map((columnName) => ({
                key: columnName,
                header: columnName,
              })),
              rows: data,
            })
          } catch (err) {
            notifications.error(`Failed to create report: ${err}`)
          }
        }

        return {
          id,
          name,
          description,
          downloadXlsx: {
            label: 'Download XLSX',
            onClick: async () => {
              const wb = await createWorkbook()
              if (!wb) return
              downloadWorkbook(wb, `Report ${name}.xlsx`)
            },
          },
          downloadCsv: {
            label: 'Download CSV',
            onClick: async () => {
              const wb = await createWorkbook()
              if (!wb) return
              downloadWorkbookAsCsv(wb, `Report ${name}.csv`)
            },
          },
        }
      })
    }, [reports, filters])

    // RENDER

    return (
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        <div css={{ display: 'flex', gap: '0.5em' }}>
          <TextFilterField value={textFilter} onChange={setTextFilter} label="Report Name or Description" />
        </div>
        <div css={{ minHeight: '2em', marginTop: '0.75em', display: 'flex' }}>
          <FilterChips filters={filters} joinLabel="and" />
          <DataGridRecordCount total={reports.length} visible={rows.length} descriptor="report" />
        </div>
        <div css={{ flex: 1 }}>
          <DataGrid<ReportRow> rows={rows} columnDefs={reportsColumnDefs} frameworkComponents={renderers} />
        </div>
      </div>
    )
  },
  () => <MuiSkeleton variant="rectangular" height="20em" />
)
