import gql from 'graphql-tag'
import { Site as GraphSite } from '@namcbugdb/common'

import { Api } from '@namcbugdb/react-common'
import { Site } from '@namcbugdb/react-common'
import { transformSite } from './fetchSites'

export const fetchSitesByBoxIdQuery = gql`
  query ($boxId: Int, $offset: Int) {
    sites(boxIds: [$boxId], limit: 200, offset: $offset) {
      records {
        siteId
        siteName
        longitude
        latitude
        usStateId
      }
      nextOffset
    }
  }
`

export const fetchSitesByBoxId =
  (api: Api) =>
  async (boxId: number): Promise<Site[]> => {
    const data = await api.queryAllPages<{ sites: { records: GraphSite[] } }>(
      fetchSitesByBoxIdQuery,
      { boxId },
      { dataProperty: 'sites' }
    )
    const sites = data.sites.records

    return sites.map(transformSite)
  }
