import { SampleProcessor } from '../types'
import { fetchUsStateAtCoordinates } from '../../../data/calls'

export const setLiteralValue = (value: string | number | boolean): SampleProcessor => {
  const processor: SampleProcessor = async ({ sample, fieldName }) => {
    if (typeof fieldName !== 'string') return
    sample[fieldName] = value
  }
  processor.isSamplePostProcessor = true as const
  return processor
}

export const split = (separator: string, fieldName1: string, fieldName2: string): SampleProcessor => {
  const processor: SampleProcessor = async ({ sample, fieldName }) => {
    if (typeof fieldName !== 'string') return
    const fieldValue = sample[fieldName]
    if (typeof fieldValue !== 'string') return

    const [part1, part2] = fieldValue.split(separator)
    sample[fieldName1] = part1.trim()
    sample[fieldName2] = (part2 ?? '').trim()

    delete sample[fieldName]
  }
  processor.isSamplePostProcessor = true as const
  return processor
}

export const isRequiredIf = (callback: (sample) => boolean): SampleProcessor => {
  const processor: SampleProcessor = async ({ sample, fieldName, rowNumber, columnNumber, problems }) => {
    const value = sample[fieldName]
    if (callback(sample) && (value === undefined || value === null))
      problems.push({ type: 'row', rowNumber, columnNumber, message: 'Cell is required' })
  }
  processor.isSamplePostProcessor = true as const
  return processor
}

export const isAcceptableLatitudeAndLongitude: SampleProcessor = async ({ sample, config, rowNumber, problems }) => {
  const { latitude, longitude } = sample
  const { api } = config
  if (typeof latitude === 'number' && typeof longitude === 'number') {
    const usState = await fetchUsStateAtCoordinates(api)({ latitude, longitude }) // TODO: may want API-level caching here?
    if (!usState) {
      problems.push({ type: 'row', rowNumber, message: 'Latitude/Longitude is outside of accepted area.' })
    }
  }
}
isAcceptableLatitudeAndLongitude.isSamplePostProcessor = true as const

export const setRowNumber: SampleProcessor = async ({ sample, fieldName, rowNumber }) => {
  if (typeof fieldName !== 'string') return
  sample[fieldName] = rowNumber
}
setRowNumber.isSamplePostProcessor = true as const

export const setSampleNumber: SampleProcessor = async ({ sample, fieldName, sampleNumber }) => {
  if (typeof fieldName !== 'string') return
  sample[fieldName] = sampleNumber
}
setSampleNumber.isSamplePostProcessor = true as const

export const setTemplateVersion: SampleProcessor = async ({ sample, fieldName, templateVersion }) => {
  if (typeof fieldName !== 'string') return
  sample[fieldName] = templateVersion
}
setTemplateVersion.isSamplePostProcessor = true as const
