import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { UnpersistedOrganism, Organism } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

export const upsertOrganismMutation = gql`
  mutation (
    $sampleId: Int!
    $taxonId: Int!
    $lifeStageId: Int!
    $splitCount: Int!
    $bigRareCount: Int
    $comments: String
    $noteTypeIds: [Int]
  ) {
    createSampleTaxaInfo(
      sampleId: $sampleId
      taxonomyId: $taxonId
      lifeStageId: $lifeStageId
      splitCount: $splitCount
      comments: $comments
      noteTypeIds: $noteTypeIds
      bigRareCount: $bigRareCount
    )
  }
`

export const upsertOrganism =
  (api: Api, invalidate: InvalidateFn) =>
  async (organism: UnpersistedOrganism | Organism): Promise<number> => {
    const data = await api.mutation<{
      createSampleTaxaInfo: number
    }>(upsertOrganismMutation, { lifeStageId: -1, ...organism })

    const id = data.createSampleTaxaInfo
    if (!id) throw new ApiError('organism record did not create or update')

    invalidate('samples')
    return id
  }
