/** @jsxImportSource @emotion/react */
import React, { useState } from 'react'
import MuiButton from '@mui/material/Button'
import MuiDialog from '@mui/material/Dialog'
import MuiDialogActions from '@mui/material/DialogActions'
import MuiDialogContent from '@mui/material/DialogContent'
import MuiDialogTitle from '@mui/material/DialogTitle'

import { Taxon, TaxonomySelectField } from '@namcbugdb/react-common'

export interface AddTaxonDialogProps {
  isOpen: boolean
  taxa: Taxon[]
  onClose: () => void
  onAdd: (taxonId: number) => void
}

export const AddTaxonDialog = ({ isOpen, onClose, taxa, onAdd }: AddTaxonDialogProps): JSX.Element => {
  const [taxonId, setTaxonId] = useState<number | null>(null)

  const handleClose = () => {
    onClose()
    setTaxonId(null)
  }

  const handleConfirm = () => {
    if (!taxonId) return
    onAdd(taxonId)
    handleClose()
  }

  return (
    <MuiDialog open={isOpen} onClose={onClose} fullWidth maxWidth="md">
      <MuiDialogTitle>Add Taxonomy Filtering</MuiDialogTitle>
      <MuiDialogContent>
        <div css={{ padding: '1em 0' }}>
          <TaxonomySelectField label="Taxonomy" value={taxonId} onChange={setTaxonId} options={taxa} ungrouped />
        </div>
      </MuiDialogContent>
      <MuiDialogActions>
        <MuiButton onClick={onClose}>Cancel</MuiButton>
        <MuiButton onClick={handleConfirm} disabled={!taxonId}>
          Add
        </MuiButton>
      </MuiDialogActions>
    </MuiDialog>
  )
}
