import Excel from 'exceljs'

import {
  SubmissionSample,
  FileProblem,
  ProcessFileResult,
  ProcessorsConfig,
  CellProcessor,
  SampleProcessor,
} from '../types'
import { schemasByWsName, lookupsWsName, everyCellProcessors, acceptedTemplateVersions } from '../schema/templateSchema'
import { processParsedSample } from './processParsedSample'

export class KnownError extends Error {
  problem: FileProblem

  constructor(problem: FileProblem) {
    super()
    this.problem = problem
  }
}

const isCellProcessor = (processor: CellProcessor | SampleProcessor): processor is CellProcessor =>
  !(processor as SampleProcessor).isSamplePostProcessor

export const processFile = async (wb: Excel.Workbook, config: ProcessorsConfig): Promise<ProcessFileResult> => {
  const samples: SubmissionSample[] = []
  const problems: FileProblem[] = []
  const info = {}

  const promises: Promise<void>[] = []

  try {
    const lookupsWs = wb.worksheets.find((ws) => ws.name === lookupsWsName)
    if (!lookupsWs)
      throw new KnownError({
        type: 'fatal',
        message: `File does not appear to be a valid template.`,
      })

    const getNamedCellValues = (): Record<string, Excel.CellValue> => {
      const values = {}
      wb.definedNames.model.forEach(({ name, ranges }) => {
        const [range] = ranges
        const value = lookupsWs.getCell(range.split('!')[1]).value
        values[name] = value
      })
      return values
    }

    const { FirstDataRow: firstDataRowNumber, TemplateVersion: templateVersion } = getNamedCellValues()
    if (typeof firstDataRowNumber !== 'number')
      throw new KnownError({
        type: 'fatal',
        message: 'File does not appear to be a valid template: "FirstDataRow" is missing.',
      })
    if (typeof templateVersion !== 'number')
      throw new KnownError({
        type: 'fatal',
        message: 'File does not appear to be a valid template: "TemplateVersion" is missing.',
      })
    if (!acceptedTemplateVersions.includes(templateVersion))
      throw new KnownError({
        type: 'fatal',
        message: `File version of ${templateVersion} is not accepted. Accepted versions are: ${acceptedTemplateVersions
          .map((version) => `${version}`)
          .join(', ')}.`,
      })

    let sampleNumber = 1 // 1-based sample serial number (this is not zero-indexed for user-friendliness; also important this is non-zero where consumed later)

    wb.eachSheet((ws) => {
      const wsName = ws.name

      // skip Lookups
      if (wsName === lookupsWsName) return

      const columnKeys = (ws.getRow(firstDataRowNumber - 1).values as Excel.CellValue[]).map(String) // as per file design scheme

      // get worksheet schema
      const sheetSchema = schemasByWsName[wsName]
      if (!sheetSchema)
        throw new KnownError({
          wsName,
          type: 'fatal',
          message: `Unrecognized worksheet "${wsName}".`,
        })
      const universalSchema = schemasByWsName.ALL
      const schema = { ...universalSchema, ...sheetSchema }

      // process each worksheet row
      ws.eachRow((row, rowNumber) => {
        // skip top rows
        if (rowNumber < firstDataRowNumber) return

        try {
          // begin with row (sample)
          const sample: SubmissionSample = {}
          const sampleProblems: FileProblem[] = []

          // extract all cell values
          row.eachCell({ includeEmpty: true }, (cell, columnNumber) => {
            const key = columnKeys[columnNumber]

            if (!key && !cell.value) return // irrelevant cell

            if (!key)
              throw new KnownError({
                type: 'row',
                wsName,
                rowNumber,
                columnNumber,
                message: 'Unidentified cell: not in known column',
              })
            const processors = schema[key]
            if (!processors)
              throw new KnownError({
                type: 'row',
                wsName,
                rowNumber,
                columnNumber,
                message: 'Unexpected cell: column is not in schema',
              })

            // process/validate cell
            try {
              everyCellProcessors.filter(isCellProcessor).forEach((processorFn) => {
                processorFn(cell, config) // tests and/or mutates cell.value
              })

              processors.filter(isCellProcessor).forEach((processorFn) => {
                processorFn(cell, config) // tests and/or mutates cell.value
              })

              if (
                typeof cell.value === 'string' ||
                typeof cell.value === 'number' ||
                typeof cell.value === 'boolean' ||
                typeof cell.value === 'undefined' ||
                cell.value === null
              ) {
                sample[key] = cell.value
              } else {
                sample[key] = 'UNKNOWN' // TODO: handle this
              }
            } catch (err) {
              if (!(err instanceof KnownError)) throw err
              sampleProblems.push({ wsName, type: 'row', rowNumber, columnNumber, ...err.problem })
            }
          })

          // process parsed sample
          promises.push(
            processParsedSample({ sample, schema, config, rowNumber, sampleNumber, templateVersion, columnKeys }).then(
              (processedResult) => {
                // finish with row (sample)
                const finalProblems = [
                  ...sampleProblems,
                  ...processedResult.problems.map((problem) => ({ ...problem, wsName })),
                ]
                if (finalProblems.length > 0) {
                  problems.push(...finalProblems)
                } else {
                  info[wsName] = (info[wsName] || 0) + 1
                  samples.push(processedResult.sample)
                }
              }
            )
          )
        } catch (err) {
          if (!(err instanceof KnownError)) throw err
          problems.push(err.problem)
        }

        sampleNumber += 1
      })
    })

    // wait for all asyncs to return
    await Promise.all(promises)

    return { samples, info, problems, templateVersion }
  } catch (err) {
    if (err instanceof KnownError) return { samples: [], info: {}, problems: [err.problem], templateVersion: null }
    return {
      samples: [],
      info: {},
      problems: [{ type: 'fatal', message: `Fatal error while processing file: ${err}` }],
      templateVersion: null,
    }
  }
}
