import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { Predictor } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

export const deletePredictorMutation = gql`
  mutation ($predictorId: Int!) {
    deletePredictor(id: $predictorId)
  }
`

export const deletePredictor =
  (api: Api, invalidate: InvalidateFn) =>
  async (predictor: Predictor): Promise<true> => {
    const data = await api.mutation<{ deletePredictor: number }>(deletePredictorMutation, {
      predictorId: predictor.id,
    })

    if (!data.deletePredictor) throw new ApiError('predictor did not delete')

    invalidate('predictors')
    return true
  }
