import gql from 'graphql-tag'
import { PaginatedRecords, SampleTaxaInfo as GraphSampleTaxaInfo } from '@namcbugdb/common'

import { Api } from '@namcbugdb/react-common'
import { Organism } from '@namcbugdb/react-common'

export const fetchOrganismsBySampleIdQuery = gql`
  query ($sampleId: Int!, $offset: Int) {
    sampleTaxa(sampleIds: [$sampleId], limit: 1000, offset: $offset) {
      records {
        sampleId
        organismId
        lifeStageId
        taxonomyId
        levelId
        levelName
        splitCount
        bigRareCount
        noteTypeIds
        comments
      }
      nextOffset
    }
  }
`

export const fetchOrganismsBySampleId =
  (api: Api) =>
  async (sampleId: number): Promise<Organism[]> => {
    const data = await api.queryAllPages<{
      sampleTaxa: PaginatedRecords<GraphSampleTaxaInfo>
    }>(fetchOrganismsBySampleIdQuery, { sampleId }, { dataProperty: 'sampleTaxa' })

    return data.sampleTaxa.records.map((organism) => ({
      ...organism,
      id: organism.organismId,
      taxonId: organism.taxonomyId,
      noteTypeIds: organism.noteTypeIds.filter((noteTypeId) => noteTypeId !== null),
    }))
  }
