/** @jsxImportSource @emotion/react */
import React, { useMemo } from 'react'
import { atom, useRecoilState, useRecoilValue } from 'recoil'
import MuiButton from '@mui/material/Button'

import * as selectors from '../../data/selectors'
import {
  Predictor,
  withSuspense,
  Filter,
  FilterChips,
  TextFilterField,
  useTextFilterField,
} from '@namcbugdb/react-common'

import { DataGrid, renderers, DataGridRecordCount } from '../DataGrid'

import { predictorsColumnDefs } from './columnDefs'
import { useNavigate } from '../AppRouter'
import { GrantAction, NAMCResource } from '@namcbugdb/common'
import { Fallback } from './Fallback'

export interface PredictorRow extends Predictor {
  title: {
    label: string
    onClick: () => void
    disabled?: boolean
  }
}

const textFilterState = atom({
  key: 'PredictorsListing/textFilterState',
  default: '',
})

export const PredictorsListing = withSuspense(
  () => {
    const navigate = useNavigate()
    const user = useRecoilValue(selectors.selectMyUser)
    const predictors = useRecoilValue(selectors.selectPredictors)

    // STATE AND REFS

    const [textFilter, setTextFilter, textFilterDebounced, clearTextFilter] = useTextFilterField(
      useRecoilState(textFilterState)
    )

    // FILTERS

    const filters = useMemo(() => {
      const result: Filter<Predictor>[] = []

      if (textFilterDebounced) {
        result.push({
          id: 'text',
          label: `Name/Abbreviation contains "${textFilterDebounced}"`,
          filterFn: ({ name, abbreviation }) =>
            name.toLowerCase().includes(textFilterDebounced.toLowerCase()) ||
            abbreviation.toLowerCase().includes(textFilterDebounced.toLowerCase()),
          onDelete: clearTextFilter,
        })
      }

      return result
    }, [textFilterDebounced])

    const rows = useMemo<PredictorRow[]>(() => {
      const filtered = filters.reduce<Predictor[]>((acc, { filterFn }) => acc.filter(filterFn), predictors)
      return filtered.map((predictor) => ({
        ...predictor,
        title: {
          label: String(predictor.id),
          onClick: () => {
            navigate.predictorDetail({ predictorId: predictor.id })
          },
        },
      }))
    }, [filters])

    // RENDER

    return (
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        <div css={{ display: 'flex', gap: '0.5em' }}>
          <TextFilterField value={textFilter} onChange={setTextFilter} label="Predictor Name or Abbreviation" />
          {user.hasPermission(NAMCResource.models, GrantAction.createAny) && (
            <div css={{ marginLeft: 'auto' }}>
              <MuiButton
                variant="outlined"
                onClick={() => {
                  navigate.predictorCreate()
                }}
              >
                Create New Predictor
              </MuiButton>
            </div>
          )}
        </div>
        <div css={{ minHeight: '2em', marginTop: '0.75em', display: 'flex' }}>
          <FilterChips filters={filters} joinLabel="and" />
          <DataGridRecordCount total={predictors.length} visible={rows.length} descriptor="predictor" />
        </div>
        <div css={{ flex: 1 }}>
          <DataGrid<PredictorRow> rows={rows} columnDefs={predictorsColumnDefs} frameworkComponents={renderers} />
        </div>
      </div>
    )
  },
  () => <Fallback />
)
