import gql from 'graphql-tag'
import { PaginatedRecords, TranslationGroup as GraphTranslationGroup } from '@namcbugdb/common'

import { Api, deNull } from '@namcbugdb/react-common'
import { TranslationGroup } from '@namcbugdb/react-common'

export const fetchTranslationGroupsByTranslationIdQuery = gql`
  query ($translationId: Int!, $offset: Int!) {
    translationGroups(translationId: $translationId, offset: $offset, limit: 500) {
      records {
        translationGroupId
        translationScientificName
        isFinal
        taxonomyIds
      }
      nextOffset
    }
  }
`

const transformTranslationGroup = ({ translationGroupId: id, ...rest }: GraphTranslationGroup): TranslationGroup => ({
  id,
  ...rest,
})

export const fetchTranslationGroupsByTranslationId =
  (api: Api) =>
  async (translationId: number): Promise<TranslationGroup[]> => {
    const data = await api.queryAllPages<{
      translationGroups: PaginatedRecords<GraphTranslationGroup>
    }>(fetchTranslationGroupsByTranslationIdQuery, { translationId }, { dataProperty: 'translationGroups' })

    return data.translationGroups.records.map((translationGroup) =>
      deNull(transformTranslationGroup(translationGroup), { taxonomyIds: [] })
    )
  }
