import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { Sample } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

export const deleteSampleMutation = gql`
  mutation ($sampleId: Int!) {
    deleteSample(sampleId: $sampleId)
  }
`

export const deleteSample =
  (api: Api, invalidate: InvalidateFn) =>
  async (sample: Sample): Promise<true> => {
    const data = await api.mutation<{ deleteSample: number }>(deleteSampleMutation, {
      sampleId: sample.id,
    })

    if (!data.deleteSample) throw new ApiError('sample did not delete')

    invalidate('samples')
    invalidate('boxSamples')
    invalidate('projectSamples')
    return true
  }
