/** @jsxImportSource @emotion/react */
import React from 'react'
import { useRecoilValue } from 'recoil'
import MuiSkeleton from '@mui/material/Skeleton'

import * as selectors from '../../data/selectors'
import { SamplePredictorValue, withSuspense } from '@namcbugdb/react-common'

import { DataGrid, DataGridHeading, DataGridRecordCount, renderers } from '../DataGrid'

import { samplePredictorValuesColumnDefs } from './columnDefs'

export interface SamplePredictorValuesProps {
  sampleId: number
}

export const SamplePredictorValues = withSuspense(
  ({ sampleId }: SamplePredictorValuesProps): JSX.Element => {
    const rows = useRecoilValue(selectors.selectSamplePredictorValuesBySampleId(sampleId))
    return (
      <div css={{ flex: rows.length ? 1 : 0, minHeight: '10rem', display: 'flex', flexDirection: 'column' }}>
        <div css={{ display: 'flex' }}>
          <DataGridHeading>Predictors</DataGridHeading>
          <DataGridRecordCount
            total={rows.length}
            visible={rows.length}
            descriptor="sample predictor value"
            css={{ marginLeft: 'auto' }}
          />
        </div>
        <div css={{ flex: 1 }}>
          <DataGrid<SamplePredictorValue>
            rowIdProperty="predictorId"
            rows={rows}
            columnDefs={samplePredictorValuesColumnDefs}
            frameworkComponents={renderers}
          />
        </div>
      </div>
    )
  },
  () => (
    <div css={{ minHeight: '10rem' }}>
      <MuiSkeleton variant="rectangular" height="10rem" />
    </div>
  )
)
