import { SubmissionSample, FileProblem, ProcessorsConfig, CellProcessor, SampleProcessor } from '../types'
import { SheetSchema } from '../schema/templateSchema'

interface ProcessParsedSampleParams {
  sample: SubmissionSample
  schema: SheetSchema
  config: ProcessorsConfig
  rowNumber: number
  sampleNumber: number
  templateVersion: number
  columnKeys: string[]
}

const isSamplePostProcessor = (processor: CellProcessor | SampleProcessor): processor is SampleProcessor =>
  (processor as SampleProcessor).isSamplePostProcessor

export const processParsedSample = async ({
  sample: sourceSample,
  schema,
  config,
  rowNumber,
  sampleNumber,
  templateVersion,
  columnKeys,
}: ProcessParsedSampleParams): Promise<{ sample: SubmissionSample; problems: FileProblem[] }> => {
  const sample = { ...sourceSample }
  const problems: FileProblem[] = []

  const allPromises: Promise<void>[] = []

  // perform sample processors
  Object.entries(schema).forEach(([fieldName, processors]) => {
    processors.filter(isSamplePostProcessor).forEach((processor) => {
      allPromises.push(
        processor({
          sample,
          config,
          rowNumber,
          columnNumber: columnKeys.findIndex((columnKey) => columnKey === fieldName), // note that columnKeys are already [1] = column A
          sampleNumber,
          templateVersion,
          fieldName,
          problems,
        })
      )
    })
  })

  await Promise.all(allPromises)

  return { sample, problems }
}
