import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

export interface ProjectInput {
  name: string // -> projectName
  isPrivate: boolean
  description: string
  contactId: number
  modelIds: number[]
}

export const createProjectMutation = gql`
  mutation ($project: ProjectInput!) {
    createProject(project: $project)
  }
`

export const createProject =
  (api: Api, invalidate: InvalidateFn) =>
  async (project: ProjectInput): Promise<number> => {
    const { name: projectName, ...restProject } = project
    const data = await api.mutation<{
      createProject: number | null // NOTE: new project ID
    }>(createProjectMutation, { project: { projectName, ...restProject } })

    const id = data.createProject
    if (!id) throw new ApiError('project did not create')

    invalidate('projects')
    return id
  }
