/** @jsxImportSource @emotion/react */
import React from 'react'
import { useRecoilValue } from 'recoil'
import { useDropzone } from 'react-dropzone'
import MuiLinearProgress from '@mui/material/LinearProgress'
import UploadFileIcon from '@mui/icons-material/UploadFile'
import FilePresentIcon from '@mui/icons-material/FilePresent'
import MuiButton from '@mui/material/Button'
import Excel from 'exceljs'

import { Updater, useApi } from '@namcbugdb/react-common'
import { ProcessingStatus, emptyProcessResult, State } from './CustomerSubmission'
import { selectLists, processFile } from '../../data/submissions/index'

export interface UploadFileBoxProps {
  // onDrop: (files: File[]) => void
  updateState: (updater: Updater<State>) => void
  processingToken: number
  processingStatus: ProcessingStatus
  filename: string
}

export const UploadFileBox = ({
  updateState,
  processingToken,
  processingStatus,
  filename,
}: UploadFileBoxProps): JSX.Element => {
  const lists = useRecoilValue(selectLists)

  const api = useApi()

  const onDrop = (acceptedFiles: File[]) => {
    const handleError = (message: string) => {
      updateState((draft) => {
        draft.processingStatus = 'empty'
        draft.processResult = emptyProcessResult
        draft.filename = ''
        draft.errorMessage = message
      })
    }

    if (acceptedFiles.length > 1) {
      handleError('Only a single template file is allowed.')
      return
    }

    const file = acceptedFiles[0]
    const reader = new FileReader()
    const thisProcessingToken = processingToken + 1

    updateState((draft) => {
      draft.lastProcessingStatus = draft.processingStatus
      draft.processingStatus = 'uploading'
      draft.processingToken = thisProcessingToken
      draft.errorMessage = ''
    })

    reader.onerror = () => {
      handleError('Could not read template file.')
    }
    reader.onload = async () => {
      updateState((draft) => {
        draft.processingStatus = 'processing'
        draft.lastFilename = draft.filename
        draft.filename = file.name
      })
      try {
        const buffer = reader.result
        if (!(buffer instanceof ArrayBuffer)) throw new Error('ArrayBuffer error')
        const wb = new Excel.Workbook()
        await wb.xlsx.load(buffer)

        const processResult = await processFile(wb, { lists, api })

        updateState((draft) => {
          if (draft.processingToken !== thisProcessingToken) return // this processing is aborted/stale
          draft.processingStatus = 'processed'
          draft.processResult = processResult
        })
      } catch (err) {
        handleError('Could not parse template file.')
      }
    }
    try {
      reader.readAsArrayBuffer(file)
    } catch (err) {
      handleError('Could not read template file.')
    }
  }

  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    onDrop,
    accept: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
  })

  const handleAbort = () => {
    updateState((draft) => {
      draft.processingStatus = draft.lastProcessingStatus
      draft.filename = draft.lastFilename
      draft.processingToken++
    })
  }

  const isPerforming = processingStatus === 'uploading' || processingStatus === 'processing'

  return (
    <div
      {...(isPerforming ? {} : getRootProps())}
      css={{
        border: isDragActive ? '3px dashed blue' : '3px dashed lightgrey',
        minHeight: '6em',
        width: '100%',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        cursor: isPerforming ? 'default' : 'pointer',
        position: 'relative',
      }}
    >
      {isPerforming ? (
        <div css={{ width: '80%' }}>
          <MuiLinearProgress />
          <div css={{ textAlign: 'center' }}>
            <MuiButton onClick={handleAbort}>Cancel</MuiButton>
          </div>
        </div>
      ) : (
        <div css={{ display: 'flex', width: '80%', minHeight: '3em' }}>
          <div
            css={{
              flex: 1,
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              borderRight: '1px solid grey',
              paddingRight: '1em',
              textAlign: 'center',
            }}
          >
            {filename ? (
              <>
                <FilePresentIcon /> {filename}
              </>
            ) : (
              <>
                <UploadFileIcon /> No template
              </>
            )}
          </div>
          <div
            css={{
              flex: 1,
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              paddingLeft: '1em',
              textAlign: 'center',
            }}
          >
            <span>
              Drop here or&nbsp;<a>choose template</a>
            </span>
          </div>
        </div>
      )}
      <input {...getInputProps()} />
    </div>
  )
}
