import gql from 'graphql-tag'
import { Translation as GraphTranslation } from '@namcbugdb/common'

import { Api, deNull, Translation } from '@namcbugdb/react-common'

export const fetchTranslationsQuery = gql`
  query ($offset: Int!) {
    translations(limit: 500, offset: $offset) {
      records {
        translationId
        translationName
        isActive
        description
        taxaCount
        createdDate
        updatedDate
      }
      nextOffset
    }
  }
`

export const fetchTranslations = (api: Api) => async (): Promise<Translation[]> => {
  const data = await api.queryAllPages<{ translations: { records: GraphTranslation[] } }>(
    fetchTranslationsQuery,
    {},
    { dataProperty: 'translations' }
  )

  return data.translations.records.map(({ translationId: id, translationName: name, ...rest }) =>
    deNull(
      {
        id,
        name,
        ...rest,
      },
      { description: '' }
    )
  )
}
