import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

export const startSortMutation = gql`
  mutation ($sampleId: Int!) {
    labStartSort(sampleId: $sampleId)
  }
`

export const startSort =
  (api: Api, invalidate: InvalidateFn) =>
  async ({ sampleId }: { sampleId: number }): Promise<true> => {
    const data = await api.mutation<{
      labStartSort: number // NOTE this is records affected, 1 = success, 0 = failure
    }>(startSortMutation, { sampleId })

    if (!data.labStartSort) throw new ApiError('sample sorting did not start')

    invalidate('boxes')
    invalidate('samples')
    return true
  }
