import { ColDef, ValueFormatterParams } from 'ag-grid-community'

import { formatNumber, formatUtcDateTimeToAppDate } from '@namcbugdb/react-common'

export const comparator = {
  labelComparator: (valueA: { label: string | number }, valueB: { label: string | number }): number => {
    const labelA = valueA.label
    const labelB = valueB.label
    if (labelA == labelB) return 0
    return labelA > labelB ? 1 : -1
  },
}

export const columnDef = {
  linkColumn: (colDef: ColDef): ColDef => ({
    // value is expected to be { label: string, onClick: () => void }
    cellRenderer: 'LinkRenderer',
    comparator: comparator.labelComparator,
    ...colDef,
  }),
  textColumn: (colDef: ColDef): ColDef => colDef,
  textListColumn: (colDef: ColDef): ColDef => ({ valueFormatter: ({ value }) => value.join(', '), ...colDef }),
  filterableTextColumn: ({ filterTooltip, ...restColDef }: ColDef & { filterTooltip: string }): ColDef => ({
    // value is expected to be { label: string, onFilter: () => void, disabled?: boolean }
    cellRenderer: 'FilterableTextRenderer',
    cellRendererParams: { filterTooltip },
    comparator: comparator.labelComparator,
    ...restColDef,
  }),
  dateColumn: (colDef: ColDef): ColDef => ({
    valueFormatter: ({ value }) => formatUtcDateTimeToAppDate(value) ?? '',
    width: 120,
    ...colDef,
  }),
  progressColumn: (colDef: ColDef): ColDef => ({ cellRenderer: 'ProgressRenderer', ...colDef }),
  checkColumn: (colDef: ColDef): ColDef => ({ cellRenderer: 'CheckRenderer', ...colDef }),
  booleanColumn: ({ valueLabels, ...restColDef }: ColDef & { valueLabels: [string, string] }): ColDef => ({
    valueFormatter: ({ value }) => (value ? valueLabels[1] : valueLabels[0]),
    ...restColDef,
  }),
  numberColumn: (colDef: ColDef): ColDef => ({
    type: 'numericColumn',
    valueFormatter: ({ value }: ValueFormatterParams): string => value && formatNumber(value),
    ...colDef,
  }),
}
