/** @jsxImportSource @emotion/react */
import React from 'react'
import { atom, useRecoilState } from 'recoil'

import { SideNavBar as Component } from './SideNavBar'

export const isNavigationOpenState = atom<boolean>({
  key: 'isNavigationOpen',
  default: false,
})

export const SideNavBar = (): JSX.Element => {
  const [isOpen, setIsOpen] = useRecoilState(isNavigationOpenState)

  return (
    <Component
      isOpen={isOpen}
      onClose={() => {
        setIsOpen(false)
      }}
    />
  )
}
