/** @jsxImportSource @emotion/react */
import React, { useCallback, useEffect, useState } from 'react'
import { useRecoilValue } from 'recoil'
import MuiSkeleton from '@mui/material/Skeleton'
import MuiAutocomplete from '@mui/material/Autocomplete'
import MuiTextField from '@mui/material/TextField'

import * as selectors from '../../data/selectors'
import { ReadOnlyField, AutocompleteOption } from '@namcbugdb/react-common'

import { useNavigate } from '../AppRouter'

interface SiteSelectFieldProps {
  gridArea?: string
  label?: string
  onChange: (newValue: number) => void
  readOnly?: boolean
  value: number
  valueLabel: string
}

export const SiteSelectField = ({
  gridArea,
  label,
  onChange,
  readOnly,
  value,
  valueLabel,
}: SiteSelectFieldProps): JSX.Element => {
  const navigate = useNavigate()

  const allSites = useRecoilValue(selectors.allSitesAtom)
  const allSitesStatus = useRecoilValue(selectors.allSitesStatusAtom)
  const fetchAllSitesOptimized = selectors.useFetchAllSitesOptimized()
  const [text, setText] = useState('')
  const [options, setOptions] = useState<AutocompleteOption<number>[]>([])

  const getFilterSiteOptions = useCallback(
    (value: number) =>
      (options: AutocompleteOption<number>[], { inputValue }) => {
        if (inputValue.length < 3 && value) {
          const foundOption = options.find((option) => option.value === value)
          if (!foundOption) return []
          return [foundOption]
        }
        return options
          .filter(({ label }) => {
            return label.toLowerCase().includes(inputValue.toLowerCase())
          })
          .slice(0, 100) // 100 = max options to display
      },
    []
  )

  const handleTextChange = (e: React.SyntheticEvent, newValue: string) => {
    setText(newValue)
  }

  useEffect(() => {
    if (readOnly) return
    switch (allSitesStatus) {
      case selectors.FETCH_ALL_SITES_STATUS.unfetched: {
        fetchAllSitesOptimized()
        break
      }
      case selectors.FETCH_ALL_SITES_STATUS.fetched: {
        setOptions(allSites.map(({ id, name }) => ({ value: id, label: `${name} [${id}]` })))
      }
    }
  }, [allSitesStatus, readOnly])

  if (readOnly)
    return (
      <ReadOnlyField
        label="Site"
        gridArea={gridArea}
        value={
          <a
            onClick={() => {
              navigate.siteDetail({ siteId: value })
            }}
          >
            {valueLabel}
          </a>
        }
      />
    )

  if (options.length === 0) return <ReadOnlyField css={{ gridArea }} label={label} value={<MuiSkeleton />} />

  const isOptionsPresented = text.length >= 3

  return (
    <MuiAutocomplete
      inputValue={text}
      onInputChange={handleTextChange}
      options={isOptionsPresented ? options : []}
      filterOptions={getFilterSiteOptions(value)}
      noOptionsText={isOptionsPresented ? 'No sites found' : '...'}
      getOptionLabel={(option) => option.label}
      getOptionDisabled={(option) => !!option.disabled}
      renderInput={(params) => <MuiTextField {...params} label={label} />}
      onChange={(event, selected) => {
        onChange(selected?.value)
      }}
      size="small"
      value={options.find((option) => option.value === value)}
      css={{ gridArea }}
      disableClearable
    />
  )
}
