/** @jsxImportSource @emotion/react */
import React from 'react'

import * as selectors from '../../data/selectors'
import { SitesMapPreview } from '../SitesMapPreview'
import { mapboxToken } from '../../config'

export interface ProjectSitesMapPreviewProps {
  projectId: number
  projectName: string
}

export const ProjectSitesMapPreview = ({ projectId, projectName }: ProjectSitesMapPreviewProps): JSX.Element => {
  const sitesSelector = selectors.selectSitesByProjectId(projectId)
  return (
    <SitesMapPreview
      sitesSelector={sitesSelector}
      sitesListingSource={{ type: 'project', id: projectId, name: projectName }}
      mapboxToken={mapboxToken}
    />
  )
}
