import React from 'react'
import MuiSkeleton from '@mui/material/Skeleton'
import { useRecoilValue } from 'recoil'
import * as selectors from '../../data/selectors'
import { ReadOnlyField, withSuspense, ProgressField } from '@namcbugdb/react-common'

export const SortingProgressField = withSuspense<{
  boxId: number
  gridArea: string
  value: number
}>(
  ({ boxId, value, ...restProps }) => {
    const boxSamples = useRecoilValue(selectors.selectBoxSamplesByBoxId(boxId))
    const doneCount = boxSamples.filter((boxSample) => boxSample.sortEndDate).length
    const totalCount = boxSamples.length
    return <ProgressField {...restProps} label="Sorting" value={value} doneCount={doneCount} totalCount={totalCount} />
  },
  (props): JSX.Element => <ReadOnlyField {...props} label="Sorting" value={<MuiSkeleton />} minWidth={120} />
)
