/** @jsxImportSource @emotion/react */
import React, { useMemo } from 'react'
import MuiButton from '@mui/material/Button'
// import log from 'loglevel'

import * as selectors from '../../data/selectors'
import { DataGrid, renderers, DataGridRecordCount } from '../DataGrid'
import { Box, useApi, useNotifications } from '@namcbugdb/react-common'
import { downloadCustomerBoxReport } from '../../lib/reports/downloadCustomerBoxReport'
import { customerColumnDefs } from './columnDefs'
import { useNavigate } from '../AppRouter'

export interface CustomerBoxesListingProps {
  boxes: Box[]
}

export interface BoxRow extends Box {
  forms: {
    label: string
    onClick: () => void
  }
  reports: {
    label: string
    onClick: () => void
    disabled: boolean
  }
}

export const CustomerBoxesListing = ({ boxes }: CustomerBoxesListingProps): JSX.Element => {
  const api = useApi()
  const notifications = useNotifications()
  const navigate = useNavigate()

  // STATE AND REFS

  const rows = useMemo<BoxRow[]>(() => {
    return boxes.map((box) => {
      const canDownloadCustomerReport = box.boxStateId === selectors.BOX_STATE_ID.complete

      const handleDownloadCustomerReport = async () => {
        try {
          const cancelNotification1 = notifications.info('Still creating report...', 2500)
          const cancelNotification2 = notifications.info('Still creating report...', 30000)
          await downloadCustomerBoxReport({ boxId: box.id, api, filename: `NAMC_report_${box.id}.xlsx` })
          cancelNotification1()
          cancelNotification2()
        } catch (err) {
          notifications.error(`Failed to create report: ${err}`)
        }
      }

      return {
        ...box,
        forms: {
          label: box.submissionCount ? 'Chain of Custody' : '',
          onClick: () => {
            navigate.chainOfCustodyForm({ boxId: box.id })
          },
        },
        reports: {
          label: canDownloadCustomerReport ? 'Download NAMC Report' : 'In Progress',
          onClick: handleDownloadCustomerReport,
          disabled: !canDownloadCustomerReport,
        },
      }
    })
  }, [boxes])

  // RENDER

  return (
    <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
      <div css={{ minHeight: '2em', gap: '0.5em', display: 'flex' }}>
        <MuiButton
          variant="outlined"
          onClick={() => {
            navigate.boxSubmission()
          }}
        >
          Create New Submission
        </MuiButton>
        <DataGridRecordCount
          total={boxes.length}
          visible={rows.length}
          descriptor="submission"
          css={{ marginLeft: 'auto' }}
        />
      </div>
      <div css={{ flex: 1 }}>
        <DataGrid<BoxRow> rows={rows} columnDefs={customerColumnDefs} frameworkComponents={renderers} />
      </div>
    </div>
  )
}
