/** @jsxImportSource @emotion/react */
import React, { useMemo, useState } from 'react'
import { useRecoilValue } from 'recoil'
import { GrantAction, NAMCResource } from '@namcbugdb/common'
import MuiButton from '@mui/material/Button'
import MuiIconButton from '@mui/material/IconButton'
import AddCircleIcon from '@mui/icons-material/AddCircle'
import { identity } from 'lodash'

import {
  ConfirmationDialog,
  trimAllStrings,
  Chip,
  Metric,
  Title,
  ReadOnlyField,
  useApi,
  useNotifications,
  useUpdatableState,
  TextField,
  AutocompleteField,
  CheckboxField,
  Menu,
  Divider,
  AutocompleteArrayField,
} from '@namcbugdb/react-common'
import * as calls from '../../data/calls'
import * as selectors from '../../data/selectors'

import { ConfirmNavigateAway, useNavigate } from '../AppRouter'
import { AddTaxonDialog } from './AddTaxonDialog'

export interface MetricDetailProps {
  metric: Metric | null
}

interface Fields {
  name: string
  abbreviation: string
  groupId: number | null
  displayText: string
  isActive: boolean
  description: string
  calculationId: number | null
  reportIds: number[]
  rarefactionTypeId: number | null
  taxonomyIds: number[]
  attributeId: number | null
  attributeValuesText: string
}

type FieldErrors = Partial<Record<keyof Fields, boolean>> | undefined

interface State {
  editMode: 'stopped' | 'started' | 'stopping' | 'deleting'
  isDirty: boolean
  values: Fields
  lastValues?: Fields
}

const getInitialState = ({ metric }: Pick<MetricDetailProps, 'metric'>): State => {
  const {
    name = '',
    abbreviation = '',
    groupId = null,
    displayText = '',
    isActive = false,
    description = '',
    calculationId = null,
    reportIds = [],
    rarefactionTypeId = null,
    taxonomyIds = [],
    attributeId = null,
  } = metric ?? {}
  const attributeValuesText = metric?.attributeValues?.join(', ') ?? ''
  return {
    editMode: metric ? 'stopped' : 'started',
    isDirty: false,
    values: {
      name,
      abbreviation,
      groupId,
      displayText,
      isActive,
      description,
      calculationId,
      reportIds,
      rarefactionTypeId,
      taxonomyIds,
      attributeId,
      attributeValuesText,
    },
  }
}

export const MetricDetail = ({ metric }: MetricDetailProps): JSX.Element => {
  const api = useApi()
  const invalidate = selectors.useInvalidate()
  const notifications = useNotifications()
  const navigate = useNavigate()
  const user = useRecoilValue(selectors.selectMyUser)

  const isNewMetric = !metric

  // OPTIONS

  const metricGroupOptions = selectors.lookupListToOptions(useRecoilValue(selectors.selectMetricGroups))
  const calculationOptions = selectors.lookupListToOptions(useRecoilValue(selectors.selectCalculations))
  const metricReportOptions = selectors.lookupListToOptions(useRecoilValue(selectors.selectMetricReports))
  const rarefactionTypeOptions = selectors.lookupListToOptions(useRecoilValue(selectors.selectRarefactionTypes))
  const attributeOptions = selectors.lookupListToOptions(useRecoilValue(selectors.selectAttributes))
  const taxa = useRecoilValue(selectors.selectTaxa)

  // STATE

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [state, setState, updateState] = useUpdatableState<State>(getInitialState({ metric }), [metric])
  const [isConfirmDeleteOpen, setIsConfirmDeleteOpen] = useState(false)
  const [isAddTaxonOpen, setIsAddTaxonOpen] = useState(false)

  const { values, editMode, isDirty } = state
  const attributeValues = values.attributeValuesText
    .split(',')
    .map((attributeValue) => attributeValue.trim())
    .filter(identity)

  const errors: FieldErrors = (() => {
    const result: FieldErrors = {
      name: !values.name.trim(),
      abbreviation: !values.abbreviation.trim(),
      groupId: !values.groupId,
      calculationId: !values.calculationId,
      rarefactionTypeId: !values.rarefactionTypeId,
      attributeValuesText:
        (!!values.attributeId && attributeValues.length === 0) || (!values.attributeId && attributeValues.length > 0),
    }
    if (!Object.values(result).some((value) => value)) return undefined
    return result
  })()

  const isStopped = editMode === 'stopped'
  const isEditing = editMode === 'started'
  const isSaving = editMode === 'stopping'
  const isDeleting = editMode === 'deleting'

  // HANDLERS

  const setFieldValue = (fieldName: keyof Fields, value: unknown) => {
    updateState((draft) => {
      Object.assign(draft.values, { [fieldName]: value })
      draft.isDirty = true
    })
  }
  const makeFieldChangeHandler = (fieldName: keyof Fields) => (value: unknown) => setFieldValue(fieldName, value)

  const handleEdit = () => {
    updateState((draft) => {
      draft.editMode = 'started'
      draft.lastValues = draft.values
    })
  }

  const handleCancel = () => {
    updateState((draft) => {
      if (!draft.lastValues) throw new Error('no values to revert to')
      draft.editMode = 'stopped'
      draft.values = draft.lastValues
      draft.isDirty = false
    })
  }

  const handleSave = async () => {
    try {
      updateState((draft) => {
        draft.editMode = 'stopping'
      })
      const {
        // eslint-disable-next-line @typescript-eslint/no-unused-vars
        attributeValuesText, // discarded
        ...restInput
      } = values
      const { rarefactionTypeId, calculationId, ...restMetricInput } = trimAllStrings({ attributeValues, ...restInput })
      if (!rarefactionTypeId || !calculationId) throw new Error('Missing Rarefaction type or Calculation.')
      if (isNewMetric) {
        // new metric
        const metricId = await calls.createMetric(
          api,
          invalidate
        )({ rarefactionTypeId, calculationId, ...restMetricInput })
        updateState((draft) => {
          draft.editMode = 'stopped'
          draft.isDirty = false
        })
        window.setTimeout(() => {
          navigate.metricDetail({ metricId }, true) // let state update
        })
      } else {
        // existing metric
        await calls.updateMetric(
          api,
          invalidate
        )({
          ...restMetricInput,
          rarefactionTypeId,
          calculationId,
          id: metric.id,
        })
        updateState((draft) => {
          draft.editMode = 'stopped'
          draft.isDirty = false
        })
      }
    } catch (err) {
      switch ((err as Error).message) {
        case 'duplicate key value violates unique constraint "metrics_metric_name_key"':
          notifications.error(`Failed to save: name "${values.name}" already in use`)
          break
        case 'duplicate key value violates unique constraint "metrics_abbreviation_key"':
          notifications.error(`Failed to save: abbreviation "${values.abbreviation}" already in use`)
          break
        default:
          notifications.error(`Failed to save: ${err}`)
      }
      updateState((draft) => {
        draft.editMode = 'started'
      })
    }
  }

  const handleDelete = async () => {
    try {
      if (!metric) throw new Error('No metric.')
      updateState((draft) => {
        draft.editMode = 'deleting'
      })
      await calls.deleteMetric(api, invalidate)(metric)
      notifications.info(`Deleted metric ${metric.name}`)
      navigate.metrics()
    } catch (err) {
      notifications.error(`Failed to delete: ${err}`)
      updateState((draft) => {
        draft.editMode = 'stopped'
      })
    }
  }

  const handleAddTaxonId = (addTaxonId: number) => {
    setFieldValue('taxonomyIds', [...values.taxonomyIds, addTaxonId])
  }

  // RENDER

  const availableTaxa = useMemo(
    () => taxa.filter((taxon) => !values.taxonomyIds.includes(taxon.id)),
    [taxa, values.taxonomyIds]
  )

  return (
    <>
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        <div css={{ display: 'flex', alignItems: 'center', minHeight: '3rem', marginBottom: '1rem', gap: '1rem' }}>
          {!isEditing ? (
            <Title>{values.name}</Title>
          ) : (
            <div css={{ flex: 1 }}>
              <TextField
                label="Metric Name"
                value={values.name}
                onChange={(value) => setFieldValue('name', value)}
                error={errors?.name}
                InputProps={{ sx: { fontWeight: '700' } }} // roughly match <Title>
                maxLength={255}
                fullWidth
              />
            </div>
          )}
          <div css={{ display: 'flex', gap: '0.5em', marginLeft: 'auto' }}>
            {(isStopped || isDeleting) && user.hasPermission(NAMCResource.metrics, GrantAction.updateAny) && (
              <Menu
                label="Actions"
                disabled={isDeleting}
                items={[
                  {
                    key: 'edit',
                    label: 'Edit Metric',
                    onClick: handleEdit,
                  },
                  Divider,
                  {
                    key: 'delete',
                    label: 'Delete Metric',
                    onClick: () => {
                      setIsConfirmDeleteOpen(true)
                    },
                    disabled: !user.hasPermission(NAMCResource.metrics, GrantAction.deleteAny),
                  },
                ]}
              />
            )}
            {(isEditing || isSaving) && (
              <>
                {!isNewMetric && (
                  <MuiButton onClick={handleCancel} variant="outlined" disabled={isSaving}>
                    Cancel
                  </MuiButton>
                )}
                <MuiButton onClick={handleSave} variant="contained" disabled={!isDirty || isSaving || !!errors}>
                  Save
                </MuiButton>
              </>
            )}
          </div>
        </div>
        <div
          css={{
            display: 'grid',
            gap: '0.75em',
            gridTemplateColumns: 'repeat(3, 1fr)',
            gridTemplateAreas: `
              "abbreviation         groupId                 id"
              "rarefactionTypeId    calculationId           isActive"
              "displayText          reportIds               reportIds"
              "description          description             description"
              "taxonomyIds          attributeId             attributeValuesText"
            `,
          }}
        >
          <ReadOnlyField gridArea="id" value={metric?.id ?? 'TBD'} label="Metric ID" />
          <TextField
            gridArea="abbreviation"
            value={values.abbreviation}
            onChange={makeFieldChangeHandler('abbreviation')}
            label="Abbreviation"
            readOnly={!isEditing}
            error={errors?.abbreviation}
            maxLength={50}
          />
          <AutocompleteField<number>
            gridArea="groupId"
            label="Metric Group"
            value={values.groupId}
            onChange={makeFieldChangeHandler('groupId')}
            options={metricGroupOptions}
            readOnly={!isEditing}
            error={errors?.groupId}
          />
          <AutocompleteField<number>
            gridArea="rarefactionTypeId"
            label="Rarefaction"
            value={values.rarefactionTypeId}
            onChange={makeFieldChangeHandler('rarefactionTypeId')}
            options={rarefactionTypeOptions}
            readOnly={!isEditing}
            error={errors?.rarefactionTypeId}
          />
          <AutocompleteField<number>
            gridArea="calculationId"
            label="Calculation"
            value={values.calculationId}
            onChange={makeFieldChangeHandler('calculationId')}
            options={calculationOptions}
            readOnly={!isEditing}
            error={errors?.calculationId}
          />
          <CheckboxField
            gridArea="isActive"
            value={values.isActive}
            onChange={makeFieldChangeHandler('isActive')}
            label="Is Active"
            readOnly={!isEditing}
          />
          <TextField
            gridArea="displayText"
            value={values.displayText}
            onChange={makeFieldChangeHandler('displayText')}
            label="Display Text"
            readOnly={!isEditing}
            error={errors?.displayText}
            maxLength={255}
          />
          <AutocompleteArrayField
            gridArea="reportIds"
            label="Included In the Following Reports"
            value={values.reportIds}
            onChange={makeFieldChangeHandler('reportIds')}
            options={metricReportOptions}
            readOnly={!isEditing}
            error={errors?.reportIds}
          />
          <TextField
            gridArea="description"
            value={values.description}
            onChange={makeFieldChangeHandler('description')}
            label="Description"
            readOnly={!isEditing}
            error={errors?.description}
            multiline
            minRows={2}
          />
          <div css={{ gridArea: 'taxonomyIds' }}>
            <ReadOnlyField
              label="Taxonomy Filtering"
              value={
                <div css={{ flex: 1, display: 'flex', flexWrap: 'wrap', gap: '0.4em', alignItems: 'center' }}>
                  {values.taxonomyIds.length > 0
                    ? values.taxonomyIds.map((taxonId) => (
                        <Chip
                          key={taxonId}
                          label={taxa.find(({ id }) => id === taxonId)?.name ?? '?'}
                          onDelete={
                            isEditing
                              ? () => {
                                  setFieldValue(
                                    'taxonomyIds',
                                    values.taxonomyIds.filter((id) => id !== taxonId)
                                  )
                                }
                              : undefined
                          }
                        />
                      ))
                    : !isEditing && '—'}
                  {isEditing && (
                    <MuiIconButton
                      onClick={() => {
                        setIsAddTaxonOpen(true)
                      }}
                    >
                      <AddCircleIcon />
                    </MuiIconButton>
                  )}
                </div>
              }
            />
          </div>
          <AutocompleteField<number>
            gridArea="attributeId"
            label="Attribute Filter"
            value={values.attributeId}
            onChange={makeFieldChangeHandler('attributeId')}
            options={attributeOptions}
            readOnly={!isEditing}
            error={errors?.attributeId}
            isClearable
          />
          <TextField
            gridArea="attributeValuesText"
            value={values.attributeValuesText}
            onChange={makeFieldChangeHandler('attributeValuesText')}
            label="Attribute Values"
            readOnly={!isEditing}
            error={errors?.attributeValuesText}
            multiline
            minRows={2}
          />
        </div>
      </div>
      <ConfirmationDialog
        title="Confirm Delete Metric"
        isOpen={isConfirmDeleteOpen}
        onClose={() => {
          setIsConfirmDeleteOpen(false)
        }}
        onConfirm={handleDelete}
      >
        Are you sure you want to delete this metric?
      </ConfirmationDialog>
      <AddTaxonDialog
        isOpen={isAddTaxonOpen}
        taxa={availableTaxa}
        onClose={() => {
          setIsAddTaxonOpen(false)
        }}
        onAdd={handleAddTaxonId}
      />
      {isDirty && <ConfirmNavigateAway message="Some changes are not saved. Are you sure you want to leave?" />}
    </>
  )
}
