import React from 'react'
import { useRecoilValue } from 'recoil'
import * as selectors from '../../data/selectors'
import { withSuspense } from '@namcbugdb/react-common'
import { MetricDetail as Component } from './MetricDetail'
import { Fallback } from './Fallback'

export type MetricDetailProps =
  | {
      metricId: string | number
      isNew?: never
    }
  | {
      metricId?: never
      isNew: true // unconsumed; used to positively differentiate modes
    }

export const MetricDetail = withSuspense(({ metricId }: MetricDetailProps): JSX.Element => {
  const metric = useRecoilValue(metricId ? selectors.selectMetricById(Number(metricId)) : selectors.selectNull)

  if (metricId && !metric) return <h2>Metric {metricId} not found.</h2>

  return <Component metric={metric} />
}, Fallback)
