/** @jsxImportSource @emotion/react */
import React from 'react'
import MuiSkeleton from '@mui/material/Skeleton'

import { TaxonomySelectField, TaxonomySelectFieldProps } from '@namcbugdb/react-common'

export const BoxTaxonomySelectField = ({
  skeleton,
  ...props
}: TaxonomySelectFieldProps & { skeleton?: boolean }): JSX.Element => {
  if (skeleton) return <MuiSkeleton variant="rectangular" height={40} />
  return <TaxonomySelectField {...props} />
}
