import gql from 'graphql-tag'
import { BoxInfo as GraphBoxInfo } from '@namcbugdb/common'

import { Api } from '@namcbugdb/react-common'
import { Submission } from '../../types'

export const fetchLatestSubmissionByBoxIdQuery1 = gql`
  query ($boxId: Int!) {
    boxInfo(boxId: $boxId) {
      boxStateId
      customer {
        organizationName
        phone
      }
      latestSubmission {
        versionId
        submissionId
        submitter {
          firstName
          lastName
          email
          phone
        }
        createdDate
        updatedDate
      }
    }
  }
`

export const fetchLatestSubmissionByBoxIdQuery2 = gql`
  query ($submissionId: Int!) {
    submissionData(submissionId: $submissionId)
  }
`

export const fetchLatestSubmissionByBoxId =
  (api: Api) =>
  async (boxId: number): Promise<Submission | null> => {
    const data1 = await api.query<{ boxInfo: GraphBoxInfo }>(fetchLatestSubmissionByBoxIdQuery1, { boxId })

    const {
      boxStateId,
      customer: { organizationName, phone: organizationPhone },
      latestSubmission,
    } = data1.boxInfo
    if (!latestSubmission) return null
    const {
      versionId: version,
      submissionId,
      submitterId,
      submitter: { firstName, lastName, email: submitterEmail, phone: submitterPhone },
      createdDate,
      updatedDate,
    } = latestSubmission

    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    const data2 = await api.query<{ submissionData: any }>(fetchLatestSubmissionByBoxIdQuery2, {
      submissionId,
    })

    try {
      const { submissionData } = data2
      const submitterName = `${firstName} ${lastName}`

      if (typeof submissionData !== 'object') throw new Error()
      const { templateVersion, appVersion, samples, newSites } = submissionData
      if (typeof templateVersion !== 'number') throw new Error()
      if (typeof appVersion !== 'string') throw new Error()
      if (!Array.isArray(samples)) throw new Error()

      return {
        id: submissionId,
        boxId,
        boxStateId,
        organizationName,
        organizationPhone,
        submitterName,
        submitterEmail,
        submitterPhone,
        version,
        submitterId,
        createdDate,
        updatedDate,
        templateVersion,
        appVersion,
        samples,
        newSites,
      }
    } catch (err) {
      throw new Error('Could not parse submissionData')
    }
  }
