/** @jsxImportSource @emotion/react */
import React from 'react'
import { useRecoilValue } from 'recoil'
import MuiSkeleton from '@mui/material/Skeleton'

import * as selectors from '../../data/selectors'
import {
  ReadOnlyField,
  withSuspense,
  AutocompleteArrayField,
  AutocompleteArrayFieldProps,
} from '@namcbugdb/react-common'

export type ModelsFieldProps = Omit<AutocompleteArrayFieldProps, 'options'>

export const ModelsField = withSuspense<ModelsFieldProps>(
  (props): JSX.Element => {
    const models = useRecoilValue(selectors.selectModels)
    const options = models.map(({ id: value, abbreviation: label }) => ({ value, label }))

    return <AutocompleteArrayField {...props} options={options} />
  },
  (props) => <ReadOnlyField {...props} value={<MuiSkeleton />} />
)
