import gql from 'graphql-tag'
import { PaginatedRecords, Sample as GraphSample } from '@namcbugdb/common'

import { Api } from '@namcbugdb/react-common'
import { SourceSample } from '@namcbugdb/react-common'
import { transformSourceSample } from './fetchBoxSamplesByBoxId'

export const fetchProjectSamplesByProjectIdQuery = gql`
  query ($projectId: Int!, $offset: Int) {
    samples(limit: 1000, offset: $offset, projectId: $projectId) {
      records {
        sampleId
        sampleDate
        usState
        sampleType
        sampleMethod
        sorterName
        sortStartDate
        sortEndDate
        idErName
        idStartDate
        idEndDate
        customerSiteCode
        visitId
        siteName
        siteId
        area
      }
      nextOffset
    }
  }
`

export const fetchProjectSamplesByProjectId =
  (api: Api) =>
  async (projectId: number): Promise<SourceSample[]> => {
    const data = await api.queryAllPages<{
      samples: PaginatedRecords<GraphSample>
    }>(fetchProjectSamplesByProjectIdQuery, { projectId }, { dataProperty: 'samples' })

    return data.samples.records.map(transformSourceSample)
  }
