import gql from 'graphql-tag'
import { Organization as GraphOrganization } from '@namcbugdb/common'

import { Api, deNull } from '@namcbugdb/react-common'
import { Organization } from '@namcbugdb/react-common'
import { transformOrganization } from '.'

export const fetchOrganizationByIdQuery = gql`
  query ($organizationId: Int) {
    organizationsById(ids: [$organizationId]) {
      records {
        entityId
        parentEntityId
        organizationName
        organizationType
        organizationTypeId
        entityId
        address1
        address2
        city
        usState
        usStateId
        country
        countryId
        zipCode
        phone
        userCount
      }
    }
  }
`

export const fetchOrganizationById =
  (api: Api) =>
  async (organizationId: number): Promise<Organization | null> => {
    const data = await api.query<{ organizationsById: { records: GraphOrganization[] } }>(fetchOrganizationByIdQuery, {
      organizationId,
    })
    const organization = data.organizationsById.records[0]
    if (!organization) return null
    return deNull(transformOrganization(organization), {
      name: '',
      type: '',
      // typeId,
      // id,
      address1: '',
      address2: '',
      city: '',
      usState: '',
      // usStateId,
      country: '',
      // countryId,
      zipCode: '',
      phone: '',
      userCount: 0,
    })
  }
