import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'
import { TaxonInput } from './createTaxon'

interface ExistingTaxonInput extends TaxonInput {
  id: number
}

export const updateTaxonMutation = gql`
  mutation ($id: Int!, $taxon: TaxonomyInput!) {
    setTaxonomy(id: $id, taxonomy: $taxon)
  }
`

export const updateTaxon =
  (api: Api, invalidate: InvalidateFn) =>
  async (taxon: ExistingTaxonInput): Promise<true> => {
    const { id, name: scientificName, ...restTaxon } = taxon
    const data = await api.mutation<{
      setTaxonomy: number // NOTE: count of records changed
    }>(updateTaxonMutation, {
      id,
      taxon: {
        scientificName,
        ...restTaxon,
      },
    })

    if (!data.setTaxonomy) throw new ApiError('taxon did not update')

    invalidate('taxa')
    return true
  }
