import gql from 'graphql-tag'
import { ModelInfo as GraphModelInfo } from '@namcbugdb/common'

import { Api, deNull } from '@namcbugdb/react-common'
import { Model } from '@namcbugdb/react-common'

export const fetchModelByIdQuery = gql`
  query ($modelId: Int!) {
    modelInfo(modelId: $modelId) {
      modelId
      modelName
      modelTypeName
      abbreviation
      isActive
      fixedCount
      predictorCount

      modelTypeId
      translationId
      translationName
      extentDescription
      codeFunction
      codeFunction2
      platform
      referenceSites
      groupCount
      minimumCount
      oeMean
      oeStdev
      taxonomicEffort
      description
      predictorIds
      metricIds
    }
  }
`

export const fetchModelById =
  (api: Api) =>
  async (modelId: number): Promise<Model | null> => {
    const data = await api.query<{
      modelInfo: GraphModelInfo
    }>(fetchModelByIdQuery, { modelId })

    if (!data.modelInfo) return null

    const { modelId: id, modelName: name, modelTypeName: typeName, modelTypeId: typeId, ...rest } = data.modelInfo

    return deNull(
      {
        id,
        name,
        typeName,
        typeId,
        ...rest,
      },
      {
        codeFunction: '',
        codeFunction2: '',
        description: '',
        predictorIds: [],
        metricIds: [],
      }
    )
  }
