/** @jsxImportSource @emotion/react */
import React from 'react'
import { ICellRendererParams } from 'ag-grid-community'
import { TaxonRow } from './TaxonDetail'

import { TargetEffortBadge } from '@namcbugdb/react-common'

interface CellRendererParams<T> extends ICellRendererParams {
  data: T
}

export const TaxonNameRenderer = ({ data }: CellRendererParams<TaxonRow>): React.ReactNode => {
  const {
    name,
    hierarchy: { indent = 0, onClick, relationship },
    isTargetEffort,
  } = data
  return (
    <div css={{ display: 'flex', alignItems: 'baseline', paddingLeft: indent * 13 }}>
      <span>
        {relationship === 'Descendant' && '... '}
        {onClick ? <a onClick={onClick}>{name}</a> : <span>{name}</span>}
        {isTargetEffort && <TargetEffortBadge />}
      </span>
    </div>
  )
}
