/** @jsxImportSource @emotion/react */
import React from 'react'
import * as selectors from '../../data/selectors'
import { SitesMapPreview } from '../SitesMapPreview'
import { mapboxToken } from '../../config'

export interface BoxSitesMapPreviewProps {
  boxId: number
}

export const BoxSitesMapPreview = ({ boxId }: BoxSitesMapPreviewProps): JSX.Element => {
  const sitesSelector = selectors.selectSitesByBoxId(boxId)
  return (
    <SitesMapPreview
      sitesSelector={sitesSelector}
      sitesListingSource={{ type: 'box', id: boxId }}
      mapboxToken={mapboxToken}
    />
  )
}
