import React, { useMemo } from 'react'

import { DataGrid } from '../DataGrid'
import { samplesColumnDefs } from './columnDefs'

import { SourceSample, formatUtcDateTimeToAppDate } from '@namcbugdb/react-common'

import { useNavigate } from '../AppRouter'
import { renderers } from '../DataGrid'
import { SitesListingSource, useSetSitesListingSource } from '../SitesListing'

interface SamplesDataGridProps {
  samples: SourceSample[]
  sitesListingSource: SitesListingSource
}

interface SampleRow extends SourceSample {
  sampleId: {
    label: number
    onClick: () => void
  }
  site: {
    label: string
    onClick: () => void
  }
  sortState: string
  identificationState: string
}

export const SamplesDataGrid = ({ samples, sitesListingSource }: SamplesDataGridProps): JSX.Element => {
  const navigate = useNavigate()
  const setSitesListingSource = useSetSitesListingSource()

  const rows = useMemo<SampleRow[]>(
    () =>
      samples.map((sample) => {
        const { id } = sample
        const sampleId = {
          label: id,
          onClick: () => navigate.sampleDetail({ sampleId: id }),
        }

        const { siteId, siteName } = sample

        const { sortEndDate, sortStartDate, sorterName } = sample
        const sortState = (() => {
          if (sortEndDate) return `${sorterName} on ${formatUtcDateTimeToAppDate(sortEndDate)}`
          if (!sortStartDate) return 'Not sorted'
          return `Started by ${sorterName} on ${formatUtcDateTimeToAppDate(sortStartDate)}`
        })()

        const { identifierName, identificationEndDate, identificationStartDate } = sample
        const identificationState = (() => {
          if (identificationEndDate)
            return `${identifierName ?? 'Unknown'} on ${formatUtcDateTimeToAppDate(identificationEndDate)}`
          if (!identificationStartDate) return 'Not identified'
          return `Started by ${identifierName} on ${formatUtcDateTimeToAppDate(identificationStartDate)}`
        })()

        const site = {
          label: siteName,
          onClick: () => {
            setSitesListingSource(sitesListingSource)
            navigate.siteDetail({ siteId })
          },
        }

        return {
          ...sample,
          sampleId,
          site,
          sortState,
          identificationState,
        }
      }),
    [samples]
  )

  return <DataGrid<SampleRow> columnDefs={samplesColumnDefs} rows={rows} frameworkComponents={renderers} />
}
