import React from 'react'
import { useRecoilValue } from 'recoil'

import * as selectors from '../../data/selectors'
import { withSuspense } from '@namcbugdb/react-common'
import { UserDetail as Component } from './UserDetail'
import { Fallback } from './Fallback'

export const UserDetail = withSuspense(({ userId }: { userId: string | number }): JSX.Element => {
  const user = useRecoilValue(selectors.selectUserById(Number(userId)))
  const organization = useRecoilValue(selectors.selectOrganizationById(user?.affiliationId || 0)) // TODO: do this in another layer of component?

  if (!user) return <h2>User {userId} not found.</h2>
  if (!organization) return <h2>User {userId} organization not found.</h2>

  return <Component user={user} organization={organization} />
}, Fallback)

export const MyUserDetail = withSuspense((): JSX.Element => {
  const user = useRecoilValue(selectors.selectMyUser)
  const organization = useRecoilValue(selectors.selectOrganizationById(user.affiliationId)) ?? undefined

  return <Component user={user} organization={organization} isSelf />
}, Fallback)

export const NewUserDetail = withSuspense((): JSX.Element => {
  return <Component isSelf />
}, Fallback)
