import React from 'react'
import { useRecoilValue, waitForAny } from 'recoil'
import { GrantAction, NAMCResource } from '@namcbugdb/common'

import { PrivilegedBoxesListing } from './PrivilegedBoxesListing'
import { CustomerBoxesListing } from './CustomerBoxesListing'
import { Fallback } from './Fallback'
import * as selectors from '../../data/selectors'
import { withSuspense } from '@namcbugdb/react-common'

const CustomerConnector = (): JSX.Element => {
  const boxes = useRecoilValue(selectors.selectUserBoxes)

  return <CustomerBoxesListing boxes={boxes} />
}

const PrivilegedConnector = (): JSX.Element => {
  const boxStates = useRecoilValue(selectors.selectBoxStates)

  const [activeBoxesLoadable, boxesLoadable] = useRecoilValue(
    waitForAny([selectors.selectActiveBoxes, selectors.selectBoxes])
  )

  const isLoading = boxesLoadable.state !== 'hasValue'
  const boxes = isLoading ? activeBoxesLoadable.contents : boxesLoadable.contents

  return <PrivilegedBoxesListing boxes={boxes} boxStates={boxStates} isLoading={isLoading} />
}

export const BoxesListing = withSuspense((): JSX.Element => {
  const user = useRecoilValue(selectors.selectMyUser)

  if (user.hasPermission(NAMCResource.boxes, GrantAction.readAny)) return <PrivilegedConnector />
  return <CustomerConnector />
}, Fallback)
