/** @jsxImportSource @emotion/react */
import React, { useCallback, useEffect, useMemo, useRef, useState } from 'react'
import { selector, useRecoilValue } from 'recoil'
import { identity, sortBy } from 'lodash'
import MuiSkeleton from '@mui/material/Skeleton'
import MuiTextField from '@mui/material/TextField'
import MuiAutocomplete from '@mui/material/Autocomplete'
// import log from 'loglevel'

import * as selectors from '../../data/selectors'
import {
  TaxonomySelectField,
  withSuspense,
  ReadOnlyField,
  useDebounce,
  CriterionAutocomplete,
  CriterionFieldProps,
  CriterionOption,
  TextField,
} from '@namcbugdb/react-common'

const selectBoxOptions = selector<CriterionOption<number>[]>({
  key: 'SelectSamples/selectBoxOptions',
  get: ({ get }) => {
    const boxes = get(selectors.selectBoxes)
    return sortBy(boxes, ({ id }) => id)
      .reverse()
      .map(({ id, alias, sampleCount, boxState }) => ({
        id,
        optionLabel: `Box ${id}${alias ? `: ${alias}` : ''} (${boxState}, ${sampleCount} samples)`,
        chipLabel: `from box ${id}`,
        disabled: sampleCount === 0 || boxState === 'Pending',
      }))
  },
})

const selectUsStateOptions = selector<CriterionOption<number>[]>({
  key: 'SelectSamples/selectUsStateOptions',
  get: async ({ get }) => {
    const usStates = get(selectors.selectUsStates)
    return usStates.map(({ id, name }) => ({ id, optionLabel: name, chipLabel: `in ${name}` }))
  },
})

const selectOrganizationOptions = selector<CriterionOption<number>[]>({
  key: 'SelectSamples/selectOrganizationOptions',
  get: async ({ get }) => {
    const organizations = get(selectors.selectOrganizations)
    return sortBy(organizations, ({ name }) => name).map(({ id, name }) => ({
      id,
      optionLabel: name,
      chipLabel: `for ${name}`,
    }))
  },
})

