export const BoxCriterionField = withSuspense(
  (props: CriterionFieldProps<number>): JSX.Element => {
    const options = useRecoilValue(selectBoxOptions)
    return <CriterionAutocomplete {...props} options={options} label="Box" />
  },
  ({ gridArea }) => <ReadOnlyField css={{ gridArea }} value={<MuiSkeleton />} label="Box" />
)

