export const ProjectCriterionField = withSuspense(
  ({ thisProjectId, ...restProps }: CriterionFieldProps<number> & { thisProjectId: number }): JSX.Element => {
    const allProjectOptions = useRecoilValue(selectProjectOptions)
    const options = useMemo(
      () => allProjectOptions.map((option) => ({ ...option, disabled: option.id === thisProjectId })),
      [allProjectOptions]
    )
    return <CriterionAutocomplete {...restProps} options={options} label="Project" />
  },
  ({ gridArea }) => <ReadOnlyField css={{ gridArea }} value={<MuiSkeleton />} label="Project" />
)

export const TaxonomyCriterionField = withSuspense(
  ({ value, onChange, gridArea }: CriterionFieldProps<number>) => {
    const taxa = useRecoilValue(selectors.selectTaxa)
    const handleChange = (newId: number | null) => {
      onChange(
        newId === null
          ? null
          : {
              id: newId,
              optionLabel: '',
              chipLabel: `contains ${taxa.find((t) => t.id === newId)?.name}`,
            }
      )
    }
    return (
      <TaxonomySelectField
        label="Taxonomy"
        options={taxa}
        value={value && value.id}
        onChange={handleChange}
        gridArea={gridArea}
      />
    )
  },
  ({ gridArea }) => <ReadOnlyField gridArea={gridArea} label="Taxonomy" value={<MuiSkeleton />} />
)

interface SampleIdsFieldProps {
  value: number[]
  onChange: (sampleIds: number[]) => void
  gridArea: string
}

export const SampleIdsCriterionField = ({ value, onChange, gridArea }: SampleIdsFieldProps): JSX.Element => {
  const debounceTimerRef = useRef<NodeJS.Timeout>()

  const [text, setText] = useState('')
  const [isFocused, setIsFocused] = useState(false)

  const setValueFromText = () => {
    onChange(
      text
        .split(/[,\s]+/)
        .filter(identity)
        .map((v) => Number(v))
    )
  }

  useEffect(() => {
    if (debounceTimerRef.current) clearTimeout(debounceTimerRef.current)
    debounceTimerRef.current = setTimeout(() => {
      if (!isFocused) return
      setValueFromText()
    }, 1000)
  }, [text, isFocused])

  const handleFocus = () => {
    setIsFocused(true)
  }

  const handleBlur = () => {
    setIsFocused(false)
    setValueFromText()
  }

  useEffect(() => {
    if (isFocused) return
    setText(value.join(', '))
  }, [value])

  return (
    <TextField
      gridArea={gridArea}
      label="Sample IDs"
      value={text}
      onChange={(newValue) => {
        setText(newValue.replace(/[^0-9,\s]/g, '')) // only allow: 0-9 <comma> <whitespace>
      }}
      onFocus={handleFocus}
      onBlur={handleBlur}
      multiline
      minRows={2}
      maxRows={2}
    />
  )
}

