export const SiteCriterionField = ({ value, onChange, gridArea }: CriterionFieldProps<number>): JSX.Element => {
  const allSites = useRecoilValue(selectors.allSitesAtom)
  const allSitesStatus = useRecoilValue(selectors.allSitesStatusAtom)
  const fetchAllSitesOptimized = selectors.useFetchAllSitesOptimized()
  const [text, setText] = useState('')
  const [options, setOptions] = useState<CriterionOption<number>[]>([])

  const getFilterSiteOptions = useCallback(
    (value: CriterionOption<number> | null) => (options: CriterionOption<number>[], state) => {
      if (state.inputValue.length < 3 && value) return [value]
      return options
        .filter(({ optionLabel }) => {
          return optionLabel.toLowerCase().includes(state.inputValue.toLowerCase())
        })
        .slice(0, 100) // 100 = max options to display
    },
    []
  )

  const handleTextChange = (e: React.SyntheticEvent, newValue: string) => {
    setText(newValue)
  }

  useEffect(() => {
    switch (allSitesStatus) {
      case selectors.FETCH_ALL_SITES_STATUS.unfetched: {
        fetchAllSitesOptimized()
        break
      }
      case selectors.FETCH_ALL_SITES_STATUS.fetched: {
        setOptions(
          allSites.map(({ id, name }) => ({ id, optionLabel: `${name} [${id}]`, chipLabel: `from site ${name}` }))
        )
      }
    }
  }, [allSitesStatus])

  if (options.length === 0) return <ReadOnlyField css={{ gridArea }} label="Site" value={<MuiSkeleton />} />

  const isOptionsPresented = text.length >= 3

  return (
    <MuiAutocomplete
      inputValue={text}
      onInputChange={handleTextChange}
      options={isOptionsPresented || value ? options : []}
      filterOptions={getFilterSiteOptions(value)}
      noOptionsText={isOptionsPresented ? 'No sites found' : '...'}
      getOptionLabel={(option) => option.optionLabel}
      getOptionDisabled={(option) => !!option.disabled}
      renderInput={(params) => <MuiTextField {...params} label="Site" />}
      onChange={(event, selected) => {
        onChange(selected)
      }}
      size="small"
      value={value}
      css={{ gridArea }}
    />
  )
}

interface GeoJsonCriterionFieldProps {
  value: string
  onChange: (newValue: string) => void
  gridArea: string
}

export const GeoJsonCriterionField = ({ gridArea, value, onChange }: GeoJsonCriterionFieldProps): JSX.Element => {
  const [text, setText] = useState(value)
  const [debouncedText, setDebouncedTextImmediately] = useDebounce(text, 1000)

  useEffect(() => {
    onChange(debouncedText)
  }, [debouncedText])

  useEffect(() => {
    if (value === debouncedText) return
    // synchronize to incoming value prop
    setText(value)
    setDebouncedTextImmediately(value)
  }, [value])

  return (
    <TextField
      gridArea={gridArea}
      label="GeoJSON Polygon"
      value={text}
      onChange={setText}
      multiline
      minRows={2}
      maxRows={2}
    />
  )
}
