import gql from 'graphql-tag'
import { Box as GraphBox } from '@namcbugdb/common'
import { sortBy } from 'lodash'

import { Api } from '@namcbugdb/react-common'
import { Box } from '@namcbugdb/react-common'
import { transformBox } from './fetchBoxes'

export const fetchUserBoxesQuery = gql`
  query ($offset: Int) {
    userBoxes(limit: 1000, offset: $offset) {
      records {
        boxId
        customerName
        customerId
        createdDate
        boxReceivedDate
        submittedBy
        boxState
        boxStateId
        sampleCount
        midges
        measurements
        sortComplete
        idComplete
        alias
        submissionCount
      }
      nextOffset
    }
  }
`

export const fetchUserBoxes = (api: Api) => async (): Promise<Box[]> => {
  const data = await api.queryAllPages<{ userBoxes: { records: GraphBox[] } }>(
    fetchUserBoxesQuery,
    {},
    { dataProperty: 'userBoxes' }
  )

  const boxes = (data.userBoxes.records ?? []).map(transformBox)

  return sortBy(boxes, ({ id }) => id).reverse()
}
