import { sortBy } from 'lodash'
import { constSelector, selector } from 'recoil'
import { Lookups } from '@namcbugdb/common'

import { apiState } from '@namcbugdb/react-common'
import { fetchLookups } from '../calls'

export const lookupListToOptions = (lookup: { id: number; name: string }[]): { value: number; label: string }[] =>
  lookup.map(({ id: value, name: label }) => ({ value, label }))

export const lookupListToNames = (lookup: { name: string }[]): string[] => lookup.map(({ name }) => name)

export const selectLookups = selector<Lookups>({
  key: 'selectors/selectLookups',
  get: async ({ get }) => {
    const api = get(apiState)
    return fetchLookups(api)()
  },
})

export const selectTaxaLevels = selector({
  key: 'selectors/selectTaxaLevels',
  get: async ({ get }) => {
    const lookup = get(selectLookups).taxa?.taxaLevels
    if (!lookup) throw new Error('Missing lookup in query')
    return lookup
  },
})

export enum TAXA_LEVEL_ID {
  kingdom = 1,
  phylum = 5,
  class = 9,
  subclass = 10,
  order = 13,
  family = 19,
  genus = 23,
  species = 25,
}

