export const selectUsStates = selector({
  key: 'selectors/selectUsStates',
  get: async ({ get }) => {
    const lookup = get(selectLookups).geo?.states
    if (!lookup) throw new Error('Missing lookup in query')
    return sortBy(lookup, ({ name }) => name)
  },
})

export const selectUsCountryId = selector<number>({
  key: 'selectors/selectUsCountryId',
  get: async ({ get }) => {
    const countries = get(selectLookups).geo?.countries ?? []
    const usCountryId = countries.find((country) => country.name === 'United States')?.id
    if (!usCountryId) throw new Error('failed to determine United States country ID')
    return usCountryId
  },
})

