/** @jsxImportSource @emotion/react */
import React from 'react'
import { useRecoilValue } from 'recoil'
import MuiButton from '@mui/material/Button'
import MuiDialog from '@mui/material/Dialog'
import MuiDialogActions from '@mui/material/DialogActions'
import MuiDialogContent from '@mui/material/DialogContent'

import * as selectors from '../../data/selectors'
import {
  Site,
  ReadOnlyField,
  withSuspense,
  useNotifications,
  useUpdatableState,
  NumberField,
  TextField,
  AutocompleteField,
  CheckboxField,
  PinSvg,
} from '@namcbugdb/react-common'

export interface EditSiteDialogProps {
  allSitesNormalized: Site[]
  isOpen: boolean
  onCancel: () => void
  onSave: (updatedSite: Site) => Promise<void>
  site: Site | null
}

interface Fields {
  name: string
  latitude: number | null
  longitude: number | null
  waterbodyName: string
  waterbodyTypeId: number | null
  waterbodyCode: string
  description: string
  isReference: boolean
}

type FieldErrors = Partial<Record<keyof Fields, boolean>> | undefined

interface State {
  editMode: 'started' | 'stopping'
  isDirty: boolean
  values: Fields
}

const getInitialState = ({ site }: Pick<EditSiteDialogProps, 'site'>): State => {
  const {
    name = '',
    latitude = null,
    longitude = null,
    waterbodyName = '',
    waterbodyTypeId = null,
    waterbodyCode = '',
    description = '',
    isReference = false,
  } = site || {}
  return {
    editMode: 'started',
    isDirty: false,
    values: { name, latitude, longitude, waterbodyName, waterbodyTypeId, waterbodyCode, description, isReference },
  }
}

export const EditSiteDialog = withSuspense(
  ({ allSitesNormalized, isOpen, onCancel, onSave, site }: EditSiteDialogProps): JSX.Element => {
    const notifications = useNotifications()

    // OPTIONS

    const waterbodyTypeOptions = selectors.lookupListToOptions(useRecoilValue(selectors.selectWaterbodyTypes))

    // STATE

    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const [state, setState, updateState] = useUpdatableState<State>(getInitialState({ site }), [site])

    const {
      values: { name, latitude, longitude, waterbodyName, waterbodyTypeId, waterbodyCode, description, isReference },
      isDirty,
      editMode,
    } = state

    const errors: FieldErrors = (() => {
      const checkName = name.trim().toLowerCase()
      const result = {
        name:
          checkName !== site?.name.toLowerCase() &&
          (!checkName || !!allSitesNormalized.find(({ name }) => name === checkName)),
        latitude: !latitude,
        longitude: !longitude,
      }
      if (!Object.values(result).some((value) => value)) return undefined
      return result
    })()

    // HANLDERS

    const setFieldValue = (fieldName: keyof Fields, value: unknown) => {
      updateState((draft) => {
        Object.assign(draft.values, { [fieldName]: value })
        draft.isDirty = true
      })
    }
    const makeFieldChangeHandler = (fieldName: keyof Fields) => (value: unknown) => setFieldValue(fieldName, value)

    const handleSave = async () => {
      try {
        updateState((draft) => {
          draft.editMode = 'stopping'
        })
        await onSave({
          id: site?.id ?? 0,
          name: name.trim(),
          latitude: latitude ?? 0,
          longitude: longitude ?? 0,
          waterbodyName: waterbodyName.trim(),
          waterbodyTypeId,
          waterbodyCode: waterbodyCode.trim(),
          description: description.trim(),
          isReference,
        })
      } catch (err) {
        notifications.error(`Failed to save: ${err}`)
      }
      updateState((draft) => {
        draft.editMode = 'started'
      })
    }

    const isSaving = editMode === 'stopping'

    const handleCancel = () => {
      if (isSaving) return // deny close
      onCancel()
    }

    return (
      <MuiDialog open={isOpen || isSaving} onClose={handleCancel} fullWidth maxWidth="sm">
        <MuiDialogContent>
          <div
            css={{
              marginTop: '1em',
              display: 'grid',
              gridTemplateColumns: '1fr 1fr',
              gridTemplateRows: `
              auto
              1em
              auto
              auto
              1em
              auto
              auto
              auto
              `,
              gap: '0.5em',
              gridTemplateAreas: `
              "name          name"
              ".             ."
              "usState       id"
              "latitude      longitude"
              ".             ."
              "waterbodyName waterbodyType"
              "waterbodyCode isReference"
              "description   description"
            `,
            }}
          >
            <div css={{ gridArea: 'name', display: 'flex', alignItems: 'center' }}>
              <div
                css={{
                  width: 20,
                  marginRight: '0.5em',
                }}
              >
                <PinSvg />
              </div>
              <TextField
                label="Site Name"
                InputProps={{ sx: { fontWeight: 'bold', fontSize: '120%' } }}
                value={name}
                onChange={makeFieldChangeHandler('name')}
                error={errors?.name}
                fullWidth
                maxLength={50}
              />
            </div>
            <ReadOnlyField gridArea="id" label="ID" value={site && site.id > 0 ? site.id : 'TBD'} />
            <NumberField
              gridArea="latitude"
              label="Latitude"
              value={latitude}
              onChange={makeFieldChangeHandler('latitude')}
              step={0.0001}
              error={errors?.latitude}
              min={-90}
              max={90}
            />
            <NumberField
              gridArea="longitude"
              label="Longitude"
              value={longitude}
              onChange={makeFieldChangeHandler('longitude')}
              step={0.0001}
              error={errors?.longitude}
              min={-180}
              max={180}
            />
            <TextField
              gridArea="waterbodyName"
              label="Waterbody"
              value={waterbodyName}
              onChange={makeFieldChangeHandler('waterbodyName')}
              maxLength={255}
            />
            <AutocompleteField<number>
              gridArea="waterbodyType"
              label="Waterbody Type"
              value={waterbodyTypeId}
              onChange={makeFieldChangeHandler('waterbodyTypeId')}
              options={waterbodyTypeOptions}
              isClearable
            />
            <TextField
              gridArea="waterbodyCode"
              label="Waterbody Code"
              value={waterbodyCode}
              onChange={makeFieldChangeHandler('waterbodyCode')}
              maxLength={100}
            />
            <CheckboxField
              gridArea="isReference"
              label="Reference"
              value={isReference}
              onChange={makeFieldChangeHandler('isReference')}
            />
            <TextField
              gridArea="description"
              label="Description"
              value={description}
              onChange={makeFieldChangeHandler('description')}
              multiline
              minRows={2}
            />
          </div>
        </MuiDialogContent>
        <MuiDialogActions>
          <MuiButton onClick={handleCancel} disabled={isSaving}>
            Cancel
          </MuiButton>
          <MuiButton onClick={handleSave} disabled={!isDirty || !!errors || isSaving}>
            Save
          </MuiButton>
        </MuiDialogActions>
      </MuiDialog>
    )
  },
  () => null
)
