/** @jsxImportSource @emotion/react */
import React, { useEffect, useMemo, useState } from 'react'
import { atom, useRecoilState, useRecoilValue } from 'recoil'
import MuiButton from '@mui/material/Button'
import { identity, uniq } from 'lodash'

import * as calls from '../../data/calls'
import * as selectors from '../../data/selectors'
import {
  pluralize,
  Title,
  TargetEffortBadge,
  useUpdatableState,
  Taxon,
  ReadOnlyField,
  TaxonomySelectField,
  withSuspense,
  useApi,
  useNotifications,
  Menu,
  TextField,
  AutocompleteField,
  CheckboxField,
} from '@namcbugdb/react-common'

import { ConfirmNavigateAway, useNavigate } from '../AppRouter'
import { DataGrid, DataGridSummaryArea } from '../DataGrid'
import { taxonColumnDefs } from './columnDefs'
import * as renderers from './renderers'

import { GrantAction, NAMCResource } from '@namcbugdb/common'
import { Fallback } from './Fallback'

export interface TaxonRow extends Taxon {
  hierarchy: {
    relationship: 'Ancestor' | 'Parent' | '' | 'Child' | 'Descendant'
    indent?: number
    onClick?: () => void
  }
}

export type TaxonDetailProps =
  | {
      taxonId?: string | number
      isNew?: never
    }
  | {
      taxonId?: never
      isNew: true
    }

type Fields = {
  name: string
  synonymsText: string
  levelId: number | null
  isTargetEffort: boolean
  parentId: number | null
  notes: string
}

type FieldErrors = Partial<Record<keyof Fields, boolean>> | undefined

interface State {
  editMode: 'stopped' | 'started' | 'stopping'
  isDirty: boolean
  values: Fields
  lastValues?: Fields
}

const getInitialState = ({ taxon, isNew }: { taxon?: Taxon; isNew?: boolean }): State => {
  const { name = '', levelId = null, synonyms = [], isTargetEffort = false, parentId = null, notes = '' } = taxon ?? {}
  return {
    editMode: isNew ? 'started' : 'stopped',
    isDirty: false,
    values: { name, levelId, synonymsText: synonyms.join(', '), isTargetEffort, parentId, notes },
  }
}

const gridOptions = {
  defaultColDef: {
    sortable: false,
  },
  rowClassRules: {
    'DataGrid__row--target': (params: { data: TaxonRow }) => params.data.hierarchy.relationship === '',
  },
}

const showAllDescendantsAtom = atom({
  key: 'TaxonDetail/showAllDescendantsAtom',
  default: false,
})

export const TaxonDetail = withSuspense(
  ({ taxonId: taxonIdProp, isNew }: TaxonDetailProps): JSX.Element => {
    const api = useApi()
    const invalidate = selectors.useInvalidate()
    const [queueInvalidate, performInvalidate] = selectors.useManualInvalidate()
    const navigate = useNavigate()
    const notifications = useNotifications()
    const user = useRecoilValue(selectors.selectMyUser)

    const taxa = useRecoilValue(selectors.selectTaxa)
    const taxaLevels = useRecoilValue(selectors.selectTaxaLevels)

    // STATE

    const [taxonId, setTaxonId] = useState<number | null>(null)
    const [showAllDescendants, setShowAllDescendants] = useRecoilState(showAllDescendantsAtom)

    const taxon = useMemo(() => {
      return taxa.find(({ id }) => id === taxonId)
    }, [taxa, taxonId])

    const [state, setState, updateState] = useUpdatableState<State>(getInitialState({ taxon, isNew }), [taxon, isNew])
    const {
      values: { name, levelId, synonymsText, isTargetEffort, parentId, notes },
      editMode,
      isDirty,
    } = state

    const isStopped = editMode === 'stopped'
    const isEditing = editMode === 'started'
    const isSaving = editMode === 'stopping'

    const disallowNamesLowercase = useMemo(
      () => taxa.filter(({ id }) => id !== taxon?.id).map(({ name: existingName }) => existingName.toLowerCase()),
      [taxa, taxon]
    )

    const errors: FieldErrors = (() => {
      const checkName = name.trim().toLowerCase()
      const result = {
        name: !checkName || disallowNamesLowercase.includes(checkName),
        levelId: !levelId,
        parentId: !parentId,
      }
      if (!Object.values(result).some((value) => value)) return undefined
      return result
    })()

    // EFFECTS

    useEffect(() => {
      if (isNew) {
        setTaxonId(null)
        return
      }

      if (taxonIdProp) {
        setTaxonId(Number(taxonIdProp))
      } else {
        const animalia = taxa.find(({ name }) => name === 'Animalia')
        if (!animalia) throw new Error('Could not find "Animalia".')
        setTaxonId(animalia.id)
      }
      updateState((draft) => {
        draft.editMode = 'stopped'
      })
    }, [taxonIdProp, isNew])

    // HANDLERS

    const setFieldValue = (fieldName: keyof Fields, value: unknown) => {
      updateState((draft) => {
        Object.assign(draft.values, { [fieldName]: value })
        draft.isDirty = true
      })
    }
    const makeFieldChangeHandler = (fieldName: keyof Fields) => (value: unknown) => setFieldValue(fieldName, value)

    const handleEdit = () => {
      updateState((draft) => {
        draft.editMode = 'started'
      })
    }

    const handleSave = async () => {
      updateState((draft) => {
        draft.editMode = 'stopping'
      })
      try {
        if (!levelId || !parentId) throw new Error('Missing fields.')
        const taxonInput = {
          name: name.trim(),
          levelId,
          parentId,
          isTargetEffort,
          synonyms: uniq(
            synonymsText
              .split(/[,\n]/)
              .map((synonym) => synonym.trim())
              .filter(identity)
          ),
          notes: notes.trim(),
        }
        if (isNew) {
          // new taxon
          const newTaxonId = await calls.createTaxon(api, queueInvalidate)(taxonInput)
          window.setTimeout(() => {
            // wait for component to un-dirty before performing nav and refresh
            navigate.taxonDetail({ taxonId: newTaxonId }, true)
            performInvalidate()
          })
        } else {
          // existing taxon
          if (!taxonId) throw new Error('No taxon ID.')
          await calls.updateTaxon(api, invalidate)({ ...taxonInput, id: taxonId })
        }
        updateState((draft) => {
          draft.editMode = 'stopped'
          draft.isDirty = false
        })
      } catch (err) {
        notifications.error(`Failed to save taxon: ${err}`)
        updateState((draft) => {
          draft.editMode = 'started'
        })
      }
    }

    const handleCancel = () => {
      if (isNew) {
        navigate.taxa()
        return
      }
      setState(getInitialState({ taxon, isNew: false }))
    }

    const handleTaxonChange = (newTaxonId: number | null) => {
      if (!newTaxonId) {
        navigate.taxa()
        return
      }
      navigate.taxonDetail({ taxonId: newTaxonId })
    }

    // ROWS, RELATIONS, OPTIONS

    const rows = useMemo<TaxonRow[]>(() => {
      if (!taxon) return []

      const { childIds, descendantIds } = taxon
      if (!childIds || !descendantIds) throw new Error('Taxon missing childIds/descendantIds.')

      const result: TaxonRow[] = []

      let { parentId } = taxon
      let isDirectParent = true

      while (parentId) {
        const ancestorTaxon = taxa.find(({ id }) => id === parentId)
        if (!ancestorTaxon) throw new Error('Missing ancestor taxon')
        result.unshift({
          ...ancestorTaxon,
          hierarchy: {
            relationship: isDirectParent ? 'Parent' : 'Ancestor',
            onClick: isStopped ? () => navigate.taxonDetail({ taxonId: ancestorTaxon.id }) : undefined,
          },
        })
        parentId = ancestorTaxon.parentId
        isDirectParent = false
      }

      result.push({ ...taxon, hierarchy: { relationship: '' } })

      const descendingIndent = result.length

      const descendingIds = showAllDescendants ? descendantIds : childIds
      result.push(
        ...descendingIds.map((childId) => {
          {
            const childTaxon = taxa.find(({ id }) => id === childId)
            if (!childTaxon) throw new Error('Missing child taxon')
            return {
              ...childTaxon,
              hierarchy: {
                relationship:
                  !showAllDescendants || childIds.includes(childId) ? ('Child' as const) : ('Descendant' as const),
                onClick: isStopped ? () => navigate.taxonDetail({ taxonId: childTaxon.id }) : undefined,
              },
            }
          }
        })
      )

      return result.map((r, index) => ({
        ...r,
        hierarchy: { ...r.hierarchy, indent: Math.min(index, descendingIndent) },
      }))
    }, [taxa, taxon, showAllDescendants, isStopped])

    const eligibleParentTaxa = useMemo(() => {
      const selectedLevelRankOrder = taxaLevels.find(({ id }) => id === levelId)?.rankOrder || Infinity
      return taxa.filter(({ levelRankOrder }) => levelRankOrder < selectedLevelRankOrder)
    }, [taxaLevels, taxa, levelId])

    const taxaLevelOptions = useMemo(() => {
      const selectedParentLevelRankOrder = taxa.find(({ id }) => id === parentId)?.levelRankOrder ?? 0

      const lowestChildLevelRankOrder =
        (taxon?.childIds &&
          Math.min(
            ...taxa.filter(({ id }) => taxon.childIds?.includes(id)).map(({ levelRankOrder }) => levelRankOrder)
          )) ||
        Infinity

      const activeTaxaLevels = taxaLevels.filter(({ isActive }) => isActive)

      return activeTaxaLevels.map((level) => ({
        value: level.id,
        label: level.name,
        disabled: level.rankOrder <= selectedParentLevelRankOrder || level.rankOrder >= lowestChildLevelRankOrder,
      }))
    }, [taxaLevels, taxon, parentId])

    // RENDER

    const readOnlyParentName = useMemo(() => {
      return taxa.find(({ id }) => id === parentId)?.name
    }, [parentId])

    const totalAncestorCount = rows.filter(
      ({ hierarchy: { relationship } }) => relationship === 'Ancestor' || relationship === 'Parent'
    ).length
    const totalDescendantCount = rows.length - totalAncestorCount - 1

    return (
      <>
        <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
          <div css={{ margin: '0.5em 0 2em 0', display: 'flex', gap: '0.5em' }}>
            <TaxonomySelectField
              label="Taxon"
              options={taxa}
              value={taxonId}
              onChange={handleTaxonChange}
              disabled={!isStopped}
              disableClearable
              ungrouped
              css={{ flex: 1 }}
            />
            {user.hasPermission(NAMCResource.taxonomy, GrantAction.createAny) && (
              <MuiButton
                disabled={!isStopped}
                variant="outlined"
                onClick={() => {
                  navigate.taxonCreate()
                }}
              >
                Create New Taxon
              </MuiButton>
            )}
          </div>
          {taxon || isNew ? (
            <>
              <div
                css={{
                  display: 'grid',
                  gap: '1em',
                  gridTemplateColumns: '1fr 1fr 1fr auto',
                  gridTemplateRows: 'auto auto 6em',
                  gridTemplateAreas: `
                    "parentId       id            .               buttons"
                    "name           levelId       isTargetEffort  ."
                    "synonyms       synonyms      notes           notes" 
                  `,
                }}
              >
                {user.hasPermission(NAMCResource.taxonomy, GrantAction.updateAny) && (
                  <div css={{ gridArea: 'buttons', display: 'flex', gap: '0.5em' }}>
                    {isStopped ? (
                      <Menu
                        label="Actions"
                        items={[
                          {
                            key: 'edit',
                            label: 'Edit Taxon',
                            onClick: handleEdit,
                            disabled: taxon?.levelId === selectors.TAXA_LEVEL_ID.kingdom,
                          },
                        ]}
                      />
                    ) : (
                      <>
                        <MuiButton onClick={handleCancel} variant="outlined" disabled={isSaving}>
                          Cancel
                        </MuiButton>
                        <MuiButton onClick={handleSave} variant="contained" disabled={!isDirty || isSaving || !!errors}>
                          Save
                        </MuiButton>
                      </>
                    )}
                  </div>
                )}
                <ReadOnlyField gridArea="id" label="Taxonomy ID" value={taxon?.id ?? 'TBD'} />
                {isEditing ? (
                  <TextField
                    gridArea="name"
                    label="Scientific Name"
                    value={name}
                    onChange={makeFieldChangeHandler('name')}
                    error={errors?.name}
                    InputProps={{ sx: { fontWeight: '700' } }}
                  />
                ) : (
                  <ReadOnlyField
                    gridArea="name"
                    label="Scientific Name"
                    value={
                      <Title>
                        {name}
                        <span css={{ fontSize: '1rem' }}>{isTargetEffort && <TargetEffortBadge />}</span>
                      </Title>
                    }
                  />
                )}
                <TextField
                  gridArea="synonyms"
                  label="Synonyms"
                  value={synonymsText}
                  onChange={makeFieldChangeHandler('synonymsText')}
                  readOnly={!isEditing}
                  inputProps={{ spellCheck: false }}
                  multiline
                  minRows={3}
                  error={errors?.synonymsText}
                />
                <TextField
                  gridArea="notes"
                  label="Notes"
                  value={notes}
                  onChange={makeFieldChangeHandler('notes')}
                  readOnly={!isEditing}
                  multiline
                  minRows={3}
                  error={errors?.notes}
                />
                <AutocompleteField
                  gridArea="levelId"
                  label="Taxonomic Level"
                  value={levelId}
                  onChange={makeFieldChangeHandler('levelId')}
                  readOnly={!isEditing}
                  options={taxaLevelOptions}
                  error={errors?.levelId}
                />
                <CheckboxField
                  gridArea="isTargetEffort"
                  label="Is SAFIT II target effort"
                  value={isTargetEffort}
                  onChange={makeFieldChangeHandler('isTargetEffort')}
                  readOnly={!isEditing}
                />
                {isEditing ? (
                  <TaxonomySelectField
                    gridArea="parentId"
                    label="Parent"
                    options={eligibleParentTaxa}
                    value={parentId}
                    onChange={makeFieldChangeHandler('parentId')}
                    error={errors?.parentId}
                    disableClearable
                    ungrouped
                  />
                ) : (
                  <ReadOnlyField gridArea="parentId" label="Parent" value={readOnlyParentName} />
                )}
              </div>
              {!isNew && (
                <div css={{ flex: 1, display: 'flex', flexDirection: 'column' }}>
                  <div css={{ display: 'flex', alignItems: 'center' }}>
                    <div css={{ fontWeight: '500', marginLeft: '1em', marginRight: '4em' }}>Hierarchy</div>
                    <CheckboxField
                      value={showAllDescendants}
                      onChange={setShowAllDescendants}
                      label="Show all descendants"
                    />
                    <DataGridSummaryArea css={{ marginLeft: 'auto' }}>
                      Showing {totalAncestorCount} {pluralize(totalAncestorCount, 'ancestor')}
                      {parentId && ' (including parent)'}, {totalDescendantCount}{' '}
                      {showAllDescendants
                        ? `total ${pluralize(totalDescendantCount, 'descendant')}`
                        : `${pluralize(totalDescendantCount, 'child')}`}
                    </DataGridSummaryArea>
                  </div>
                  <div css={{ flex: 1 }}>
                    <DataGrid
                      columnDefs={taxonColumnDefs}
                      rows={rows}
                      frameworkComponents={renderers}
                      gridOptions={gridOptions}
                    />
                  </div>
                </div>
              )}
            </>
          ) : (
            <div>Select a taxon</div>
          )}
        </div>
        {isDirty && <ConfirmNavigateAway message="Some changes are not saved. Are you sure you want to leave?" />}
      </>
    )
  },
  () => <Fallback />
)
