import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

interface SplitBoxParams {
  boxId: number
  sampleIds: number[]
}

export const splitBoxMutation = gql`
  mutation ($boxId: Int!, $sampleIds: [Int]!) {
    splitBox(boxId: $boxId, sampleIds: $sampleIds)
  }
`

export const splitBox =
  (api: Api, invalidate: InvalidateFn) =>
  async (params: SplitBoxParams): Promise<number> => {
    const data = await api.mutation<{
      splitBox: number // NOTE: new box ID
    }>(splitBoxMutation, { ...params })

    if (!data.splitBox) throw new ApiError('box did not split')

    invalidate('boxes')
    invalidate('samples')
    invalidate('boxSamples')
    return data.splitBox
  }
