import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

interface LabSortSubmission {
  sampleId: number
  sorterId: number
  sortTime: number
  sorterCount: number
  elutriation: boolean
  labSplit: number
}

export const updateLabSortMutation = gql`
  mutation ($sampleId: Int!, $sorterId: Int!, $count: Int!, $elutriation: Boolean!, $time: Float!, $labSplit: Float!) {
    labSort(
      sampleId: $sampleId
      sorterId: $sorterId
      count: $count
      elutriation: $elutriation
      time: $time
      labSplit: $labSplit
    )
  }
`

export const updateLabSort =
  (api: Api, invalidate: InvalidateFn) =>
  async (submission: LabSortSubmission): Promise<true> => {
    const { sortTime: time, sorterCount: count, ...rest } = submission
    const variables = {
      time,
      count,
      ...rest,
    }
    const data = await api.mutation<{
      labSort: number // NOTE: count of records changed
    }>(updateLabSortMutation, variables)

    if (!data.labSort) throw new ApiError('sample sorting did not update')

    invalidate('boxes')
    invalidate('samples')
    return true
  }
