/** @jsxImportSource @emotion/react */
import React from 'react'
import { atom, useRecoilState } from 'recoil'
import MuiSkeleton from '@mui/material/Skeleton'
import { mapboxToken } from '../../config'

import { Sample, formatUtcDateTimeToAppDate, Title, ReadOnlyField } from '@namcbugdb/react-common'
import { AccordionPanel } from '@namcbugdb/react-common'
import { SitesMapPreview } from '../SitesMapPreview'
import { useNavigate } from '../AppRouter'
import * as selectors from '../../data/selectors'

const isExpandedState = atom<boolean>({
  key: 'SampleInfoPanel/isExpanded',
  default: true,
})

interface SampleInfoPanelProps {
  sample: Sample
  actions?: React.ReactNode
}

export const SampleInfoPanel = ({ sample, actions = null }: SampleInfoPanelProps): JSX.Element => {
  const navigate = useNavigate()
  const [isExpanded, setIsExpanded] = useRecoilState(isExpandedState)

  if (!sample) return <MuiSkeleton variant="rectangular" />

  const { id, boxId, sorterName = '', sampleDate, sortEndDate, customerName, siteName, siteId } = sample

  return (
    <AccordionPanel
      value={isExpanded}
      onChange={setIsExpanded}
      summary={
        <Title>
          Sample {id} <Title.Subtitle>(Box {boxId})</Title.Subtitle>
        </Title>
      }
      actions={actions}
    >
      <div
        css={{
          display: 'grid',
          gap: '0.75em',
          gridTemplateColumns: 'auto 1fr auto',
          gridTemplateAreas: `
            "boxId        customerName      map"
            "sampleDate   siteName          map"
            "sortEndDate  sorterName        map"
            ".            .                 map"
          `,
        }}
      >
        <div
          css={{
            gridArea: 'map',
            minHeight: '300px',
          }}
        >
          <SitesMapPreview
            sitesListingSource={null}
            sitesSelector={selectors.selectSitesBySingleSiteId(siteId)}
            mapboxToken={mapboxToken}
          />
        </div>
        <ReadOnlyField
          gridArea="boxId"
          label="Box ID"
          value={
            <a
              onClick={() => {
                navigate.boxDetail({ boxId })
              }}
            >
              {boxId}
            </a>
          }
        />
        <ReadOnlyField gridArea="customerName" label="Customer" value={customerName} />
        <ReadOnlyField gridArea="sampleDate" label="Sample Date" value={formatUtcDateTimeToAppDate(sampleDate)} />
        <ReadOnlyField gridArea="sorterName" label="Sorter" value={sorterName} />
        <ReadOnlyField gridArea="sortEndDate" label="Sort Date" value={formatUtcDateTimeToAppDate(sortEndDate)} />
        <ReadOnlyField
          gridArea="siteName"
          label="Site"
          value={
            <a
              onClick={() => {
                navigate.siteDetail({ siteId })
              }}
            >
              {siteName}
            </a>
          }
        />
      </div>
    </AccordionPanel>
  )
}
