import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

import { ProjectInput } from './createProject'

interface ExistingProjectInput extends ProjectInput {
  id: number
}

export const updateProjectMutation = gql`
  mutation ($id: Int!, $project: ProjectInput!) {
    updateProject(id: $id, project: $project)
  }
`

export const updateProject =
  (api: Api, invalidate: InvalidateFn) =>
  async (project: ExistingProjectInput): Promise<true> => {
    const { id, name: projectName, ...restProject } = project
    const data = await api.mutation<{
      updateProject: number // NOTE: id of project
    }>(updateProjectMutation, {
      id,
      project: {
        projectName,
        ...restProject,
      },
    })

    if (!data.updateProject) throw new ApiError('project did not update')

    invalidate('projects')
    return true
  }
