/** @jsxImportSource @emotion/react */
import React, { useState, useRef } from 'react'
import MuiTextField from '@mui/material/TextField'
import { useHotkeys } from 'react-hotkeys-hook'

import { useNavigate } from '../AppRouter'

export interface QuickNavigateFieldProps {
  className?: string
}

export const QuickNavigateField = ({ className }: QuickNavigateFieldProps): JSX.Element => {
  const navigate = useNavigate()

  const inputRef = useRef<HTMLInputElement | null>(null)
  const [value, setValue] = useState('')

  const handleKeyDown = ({ key }: React.KeyboardEvent<HTMLInputElement>): void => {
    switch (key) {
      case 'Enter': {
        const trimmedValue = value.trim()
        if (!trimmedValue.match(/^\d+$/)) return // accept numeric only
        setValue('')
        const targetId = Number(trimmedValue)
        if (targetId <= 9999) {
          navigate.boxDetail({ boxId: targetId })
        } else {
          navigate.sampleDetail({ sampleId: targetId })
        }
        break
      }
      case 'Escape': {
        setValue('')
        inputRef.current?.blur()
      }
    }
  }

  useHotkeys('cmd+o, ctrl+o', (e) => {
    e.preventDefault()
    e.stopPropagation()
    inputRef.current?.focus()
  })

  return (
    <MuiTextField
      className={className}
      size="small"
      value={value}
      onChange={({ target: { value: newValue } }) => setValue(newValue)}
      InputProps={{
        onKeyDown: handleKeyDown,
      }}
      inputRef={inputRef}
      placeholder="Box or Sample ID"
    />
  )
}
