import gql from 'graphql-tag'
import { PaginatedRecords, SamplePredictorValue as GraphSamplePredictorValue } from '@namcbugdb/common'
import { sortBy } from 'lodash'

import { Api } from '@namcbugdb/react-common'
import { SamplePredictorValue } from '@namcbugdb/react-common'

export const fetchSamplePredictorValuesBySampleIdQuery = gql`
  query ($sampleId: Int!, $offset: Int) {
    samplePredictorValues(sampleIds: [$sampleId], limit: 1000, offset: $offset) {
      records {
        predictorName
        predictorId
        abbreviation
        predictorValue
        isTemporal
        predictorValueUpdatedDate
        status
      }
      nextOffset
    }
  }
`

export const fetchSamplePredictorValuesBySampleId =
  (api: Api) =>
  async (sampleId: number): Promise<SamplePredictorValue[]> => {
    const data = await api.queryAllPages<{
      samplePredictorValues: PaginatedRecords<GraphSamplePredictorValue>
    }>(fetchSamplePredictorValuesBySampleIdQuery, { sampleId }, { dataProperty: 'samplePredictorValues' })

    return sortBy(
      data.samplePredictorValues.records.map(({ predictorValueUpdatedDate: updatedDate, status, ...rest }) => ({
        updatedDate,
        status: status as SamplePredictorValue['status'],
        ...rest,
      })),
      (record) => record.updatedDate || '0' // after reverse, final sort is reverse-chronological with blanks at bottom
    ).reverse()
  }
