/** @jsxImportSource @emotion/react */
import React, { useState } from 'react'
import { useRecoilValue } from 'recoil'
import MuiSkeleton from '@mui/material/Skeleton'
import MuiButton from '@mui/material/Button'
import MuiDialog from '@mui/material/Dialog'
import MuiDialogActions from '@mui/material/DialogActions'
import MuiDialogContent from '@mui/material/DialogContent'

import * as selectors from '../../data/selectors'
import { withSuspense, createSingleTableWorkbook, downloadWorkbook, SourceSample } from '@namcbugdb/react-common'

import { SamplesDataGrid, samplesDownloadColumns } from '../SamplesDataGrid'
import { DataGridRecordCount } from '../DataGrid'
import { SelectSamples } from '../SelectSamples'

interface ProjectDetailProps {
  projectId: number
  projectName: string
  canModify: boolean
}

export const ProjectSamples = withSuspense(
  ({ projectId, projectName, canModify }: ProjectDetailProps): JSX.Element => {
    const samples = useRecoilValue(selectors.selectProjectSamplesByProjectId(projectId))
    const [isOperating, setIsOperating] = useState(false)
    const [modifyMode, setModifyMode] = useState<'add' | 'remove' | null>(null)

    const handleCloseModify = () => {
      if (isOperating) return // do not close when operating (may have been invoked by escape key)
      setModifyMode(null)
    }

    const handleDownload = () => {
      const wb = createSingleTableWorkbook<SourceSample>({
        wsName: 'Samples',
        columns: samplesDownloadColumns,
        rows: samples,
      })
      downloadWorkbook(wb, `Project ${projectName} Samples.xlsx`)
    }

    return (
      <>
        <div css={{ minHeight: '2em', display: 'flex', alignItems: 'center', gap: '0.5rem' }}>
          <div css={{ fontWeight: '500', marginLeft: '1em', marginRight: 'auto' }}>Samples</div>
          <MuiButton
            css={{ width: '6em' }}
            variant="outlined"
            disabled={!canModify}
            onClick={() => {
              setModifyMode('add')
            }}
          >
            Add
          </MuiButton>
          <MuiButton
            css={{ width: '6em' }}
            variant="outlined"
            disabled={samples.length === 0 || !canModify}
            onClick={() => {
              setModifyMode('remove')
            }}
          >
            Remove
          </MuiButton>
          <MuiButton variant="outlined" disabled={samples.length === 0} onClick={handleDownload}>
            Download
          </MuiButton>
          <DataGridRecordCount total={samples.length} visible={samples.length} descriptor="sample" />
        </div>
        <div css={{ flex: 1, minHeight: '10em' }}>
          <SamplesDataGrid
            samples={samples}
            sitesListingSource={{ type: 'project', id: projectId, name: projectName }}
          />
        </div>
        <MuiDialog open={!!modifyMode} fullWidth maxWidth="lg" onClose={handleCloseModify}>
          <MuiDialogContent>
            <SelectSamples
              initialMode={modifyMode || 'add'}
              projectId={projectId}
              projectName={projectName}
              samples={samples}
              onOperatingChange={setIsOperating}
            />
          </MuiDialogContent>
          <MuiDialogActions>
            <MuiButton onClick={handleCloseModify} disabled={isOperating}>
              Close
            </MuiButton>
          </MuiDialogActions>
        </MuiDialog>
      </>
    )
  },
  () => <MuiSkeleton css={{ marginTop: '1rem' }} height="10rem" variant="rectangular" />
)
