import React from 'react'
import { useRecoilValue } from 'recoil'
import * as selectors from '../../data/selectors'
import { withSuspense } from '@namcbugdb/react-common'

import { ChainOfCustody as Component } from './ChainOfCustody'
import { Fallback } from './Fallback'

interface ChainOfCustodyProps {
  boxId: number
  autoPrint?: boolean
}

export const ChainOfCustody = withSuspense(({ boxId, autoPrint }: ChainOfCustodyProps): JSX.Element => {
  const submission = useRecoilValue(selectors.selectLatestSubmissionByBoxId(Number(boxId)))

  if (submission === null) return <h2>Submission for box {boxId} not found.</h2>

  return <Component submission={submission} autoPrint={autoPrint} />
}, Fallback)
