/** @jsxImportSource @emotion/react */
import React, { useMemo } from 'react'
import { atom, useRecoilState, useRecoilValue } from 'recoil'
import MuiSkeleton from '@mui/material/Skeleton'
import MuiButton from '@mui/material/Button'

import * as selectors from '../../data/selectors'
import {
  Translation,
  withSuspense,
  Filter,
  FilterChips,
  TextFilterField,
  useTextFilterField,
} from '@namcbugdb/react-common'
import { DataGrid, DataGridRecordCount, renderers } from '../DataGrid'

import { translationsColumnDefs } from './columnDefs'
import { useNavigate } from '../AppRouter'
import { GrantAction, NAMCResource } from '@namcbugdb/common'

interface TranslationRow extends Translation {
  title: {
    label: string
    onClick: () => void
  }
}

const textFilterState = atom({
  key: 'TranslationsListing/textFilterState',
  default: '',
})

export const TranslationsListing = withSuspense(
  () => {
    const navigate = useNavigate()
    const user = useRecoilValue(selectors.selectMyUser)
    const translations = useRecoilValue(selectors.selectTranslations)

    // STATE AND REFS

    const [textFilter, setTextFilter, textFilterDebounced, clearTextFilter] = useTextFilterField(
      useRecoilState(textFilterState)
    )

    // FILTERS

    const filters = useMemo(() => {
      const result: Filter<Translation>[] = []

      if (textFilterDebounced) {
        result.push({
          id: 'text',
          label: `Name contains "${textFilterDebounced}"`,
          filterFn: ({ name }) => name.toLowerCase().includes(textFilterDebounced.toLowerCase()),
          onDelete: clearTextFilter,
        })
      }

      return result
    }, [textFilterDebounced])

    const rows = useMemo<TranslationRow[]>(() => {
      const filtered = filters.reduce<Translation[]>((acc, { filterFn }) => acc.filter(filterFn), translations)
      return filtered.map((translation) => ({
        ...translation,
        title: {
          label: translation.name,
          onClick: () => {
            navigate.translationDetail({ translationId: translation.id })
          },
        },
      }))
    }, [translations, filters])

    // RENDER

    return (
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        <div css={{ display: 'flex', gap: '0.5em' }}>
          <TextFilterField value={textFilter} onChange={setTextFilter} label="Translation Name" />
          {user.hasPermission(NAMCResource.translations, GrantAction.createAny) && (
            <div css={{ marginLeft: 'auto' }}>
              <MuiButton
                variant="outlined"
                onClick={() => {
                  navigate.translationCreate()
                }}
              >
                Create New Translation
              </MuiButton>
            </div>
          )}
        </div>
        <div css={{ minHeight: '2em', marginTop: '0.75em', display: 'flex' }}>
          <FilterChips filters={filters} joinLabel="and" />
          <DataGridRecordCount total={translations.length} visible={rows.length} descriptor="translation" />
        </div>
        <div css={{ flex: 1 }}>
          <DataGrid<Translation> rows={rows} columnDefs={translationsColumnDefs} frameworkComponents={renderers} />
        </div>
      </div>
    )
  },
  () => <MuiSkeleton height="20em" />
)
