import { useEffect, useRef, useCallback } from 'react'
import { atomFamily, RecoilState, useRecoilCallback } from 'recoil'
import { allSitesAtom, allSitesStatusAtom, FETCH_ALL_SITES_STATUS, sparseSitesDetailAtom } from './sites'

type refreshFamily =
  | 'boxes'
  | 'boxSamples'
  | 'boxTaxa'
  | 'metrics'
  | 'models'
  | 'myUser'
  | 'organizations'
  | 'predictors'
  | 'projects'
  | 'projectSamples'
  | 'reports'
  | 'samples'
  | 'sites'
  | 'taxa'
  | 'translations'
  | 'users'

export type InvalidateFn = (family: refreshFamily, id?: number) => void

const refreshOnState = atomFamily<number, string>({
  key: 'refreshOnState',
  default: 0,
})

export const refreshOn = (family: refreshFamily, id?: number): RecoilState<number> => refreshOnState(`${family}:${id}`)

export const useInvalidate = (): InvalidateFn =>
  useRecoilCallback(
    ({ set }) =>
      (family: refreshFamily, id?: number) => {
        set(refreshOn(family, id), (v) => v + 1)
        if (family === 'sites') {
          set(allSitesStatusAtom, FETCH_ALL_SITES_STATUS.unfetched)
          set(allSitesAtom, [])
          set(sparseSitesDetailAtom, [])
        }
      },
    []
  )

export const useManualInvalidate = (): [InvalidateFn, () => void] => {
  const calls = useRef<[family: refreshFamily, id?: number][]>([]).current
  const invalidate = useInvalidate()

  const queueInvalidate = useCallback((family: refreshFamily, id?: number) => {
    calls.push([family, id])
  }, [])

  const performInvalidate = () => {
    while (calls.length > 0) {
      const call = calls.shift()
      if (!call) return
      invalidate(...call)
    }
  }

  return [queueInvalidate, performInvalidate]
}

export const useDeferredInvalidate = (): InvalidateFn => {
  const [queueInvalidate, performInvalidate] = useManualInvalidate()

  useEffect(
    () => () => {
      performInvalidate()
    },
    []
  )

  return queueInvalidate
}
