import gql from 'graphql-tag'
import { Organization as GraphOrganization } from '@namcbugdb/common'
import { sortBy } from 'lodash'

import { Api, deNull } from '@namcbugdb/react-common'
import { Organization } from '@namcbugdb/react-common'

export const fetchOrganizationsQuery = gql`
  query ($offset: Int!) {
    organizations(limit: 500, offset: $offset) {
      records {
        entityId
        organizationName
        parentEntityId
        city
        usState
        usStateId
        organizationType
        phone
      }
      nextOffset
    }
  }
`

export const transformOrganization = ({
  entityId: id,
  parentEntityId: parentId,
  organizationName: name,
  organizationType: type,
  organizationTypeId: typeId,
  city,
  ...restOrganization
}: GraphOrganization): Organization =>
  deNull(
    {
      id,
      parentId,
      name,
      type,
      typeId,
      city,
      ...restOrganization,
    },
    { city: '' }
  )

export const fetchOrganizations = (api: Api) => async (): Promise<Organization[]> => {
  const data = await api.queryAllPages<{ organizations: { records: GraphOrganization[] } }>(
    fetchOrganizationsQuery,
    {},
    { dataProperty: 'organizations' }
  )

  return sortBy(data.organizations.records.map(transformOrganization), (organization) => organization.name)
}
