/** @jsxImportSource @emotion/react */
import React from 'react'
import { useRecoilValue } from 'recoil'
import MuiIconButton from '@mui/material/IconButton'
import CloseIcon from '@mui/icons-material/Close'
import MuiSkeleton from '@mui/material/Skeleton'
import * as selectors from '../../data/selectors'
import { Site, ReadOnlyField, withSuspense, formatUtcDateTimeToAppDate, PinSvg } from '@namcbugdb/react-common'

import { UsStateAtPointField } from '../UsStateAtPointField'

export type SiteDetailPanelProps = {
  addSampleCount?: number // for submission review
  children?: React.ReactNode
  onClose?: () => void
  site: Site
}

export const SITE_DETAIL_PANEL_TRANSITION_MS = 200

export const SiteDetailPanel = withSuspense(
  ({ addSampleCount = 0, children, onClose, site }: SiteDetailPanelProps): JSX.Element => {
    const usStates = useRecoilValue(selectors.selectUsStates)
    const waterbodyTypes = useRecoilValue(selectors.selectWaterbodyTypes)

    const {
      id,
      name,
      usStateId,
      sampleCount = 0,
      latitude,
      longitude,
      waterbodyName,
      waterbodyTypeId,
      waterbodyCode,
      description,
      isReference,
      createdDate = '',
      updatedDate = '',
    } = site

    const waterbodyTypeName = waterbodyTypeId && (waterbodyTypes.find(({ id }) => id === waterbodyTypeId)?.name || '')

    const isNewSite = id < 0
    const isFetching = !isNewSite && typeof description === 'undefined' // heuristic: description is only fetched on site detail

    const formatDate = (dateString: string) => {
      if (isNewSite) return 'N/A'
      return formatUtcDateTimeToAppDate(dateString)
    }

    return (
      <div css={{ padding: '0.5em' }}>
        <div css={{ display: 'flex', alignItems: 'center', paddingLeft: '10px' }}>
          <div
            css={{
              width: 20,
              marginRight: '0.5em',
            }}
          >
            <PinSvg />
          </div>
          <div css={{ fontWeight: 'bold', fontSize: '120%', flex: 1, wordWrap: 'break-word', overflow: 'hidden' }}>
            {name}
          </div>

          <div css={{ marginLeft: 'auto', height: 40, width: 40 }}>
            {onClose && (
              <MuiIconButton onClick={onClose}>
                <CloseIcon />
              </MuiIconButton>
            )}
          </div>
        </div>
        <div
          css={{
            marginTop: '1em',
            display: 'grid',
            gridTemplateColumns: '1fr 1fr',
            gridTemplateRows: `
                  auto
                  auto
                  1em
                  auto
                  auto
                  auto
                  1em
                  auto
                  1em
                  auto
                  auto
                `,
            gap: '0.5em',
            gridTemplateAreas: `
                  "usState id"
                  "latitude longitude"
                  ". ."
                  "waterbodyName waterbodyType"
                  "waterbodyCode isReference"
                  "description   description"
                  ". ."
                  "sampleCount ."
                  ". ."
                  "createdDate updatedDate"
                  ". ."
                `,
          }}
        >
          {usStateId ? (
            <ReadOnlyField
              gridArea="usState"
              label="State"
              value={usStates.find(({ id }) => id === usStateId)?.abbreviation}
            />
          ) : (
            <UsStateAtPointField gridArea="usState" label="State" point={site} />
          )}
          <ReadOnlyField gridArea="id" label="ID" value={isNewSite ? 'TBD' : id} />
          <ReadOnlyField gridArea="latitude" label="Latitude" value={latitude} />
          <ReadOnlyField gridArea="longitude" label="Longitude" value={longitude} />
          <ReadOnlyField
            gridArea="sampleCount"
            label="# Samples"
            value={isFetching ? <MuiSkeleton sx={{ width: '3em' }} /> : sampleCount + addSampleCount}
          />
          <ReadOnlyField
            gridArea="waterbodyName"
            label="Waterbody"
            value={isFetching ? <MuiSkeleton sx={{ width: '10em' }} /> : waterbodyName}
          />
          <ReadOnlyField
            gridArea="waterbodyType"
            label="Waterbody Type"
            value={isFetching ? <MuiSkeleton sx={{ width: '10em' }} /> : waterbodyTypeName}
          />
          <ReadOnlyField
            gridArea="waterbodyCode"
            label="Waterbody Code"
            value={isFetching ? <MuiSkeleton sx={{ width: '10em' }} /> : waterbodyCode}
          />
          <ReadOnlyField
            gridArea="isReference"
            label="Reference"
            value={isFetching ? <MuiSkeleton sx={{ width: '10em' }} /> : isReference}
          />
          <ReadOnlyField
            gridArea="description"
            label="Description"
            value={isFetching ? <MuiSkeleton sx={{ width: '20em' }} /> : description}
          />
          <ReadOnlyField
            gridArea="createdDate"
            label="Created"
            value={isFetching ? <MuiSkeleton sx={{ width: '7em' }} /> : formatDate(createdDate)}
          />
          <ReadOnlyField
            gridArea="updatedDate"
            label="Updated"
            value={isFetching ? <MuiSkeleton sx={{ width: '7em' }} /> : formatDate(updatedDate)}
          />
        </div>
        {children}
      </div>
    )
  },
  () => null
)
