import gql from 'graphql-tag'
import { Box as GraphBox } from '@namcbugdb/common'
import { sortBy } from 'lodash'

import { Api } from '@namcbugdb/react-common'
import { Box } from '@namcbugdb/react-common'
import { transformBox } from './fetchBoxes'
import { BOX_STATE_ID } from '../selectors'

export const fetchActiveBoxesQuery = gql`
  query ($offset: Int, $boxStateId: Int) {
    boxes(limit: 1000, offset: $offset, boxStateId: $boxStateId) {
      records {
        boxId
        customerName
        customerId
        createdDate
        boxReceivedDate
        submittedBy
        boxState
        sampleCount
        midges
        measurements
        sortComplete
        idComplete
        alias
      }
      nextOffset
    }
  }
`

export const fetchActiveBoxes = (api: Api) => async (): Promise<Box[]> => {
  const data = await api.queryAllPages<{ boxes: { records: GraphBox[] } }>(
    fetchActiveBoxesQuery,
    { boxStateId: BOX_STATE_ID.active },
    { dataProperty: 'boxes' }
  )

  const boxes = (data.boxes.records ?? []).map(transformBox)

  return sortBy(boxes, ({ receivedDate }) => receivedDate).reverse()
}
