import gql from 'graphql-tag'
import { PaginatedRecords, Sample as GraphSample } from '@namcbugdb/common'

import { Api } from '@namcbugdb/react-common'
import { MinimalSample } from '@namcbugdb/react-common'
import { transformMinimalSample } from './fetchMinimalSamplesByIds'

export const fetchMinimalSamplesByUsStateIdQuery = gql`
  query ($usStateId: Int, $offset: Int) {
    samples(limit: 1000, offset: $offset, usStateId: $usStateId) {
      records {
        sampleId
        sampleDate
        sampleType
        sampleMethod
        area
      }
      nextOffset
    }
  }
`

export const fetchMinimalSamplesByUsStateId =
  (api: Api) =>
  async (usStateId: number): Promise<MinimalSample[]> => {
    const data = await api.queryAllPages<{
      samples: PaginatedRecords<GraphSample>
    }>(fetchMinimalSamplesByUsStateIdQuery, { usStateId }, { dataProperty: 'samples' })

    return data.samples.records.map(transformMinimalSample)
  }
