import gql from 'graphql-tag'
import { Attribute as GraphAttribute, PaginatedRecords } from '@namcbugdb/common'

import { Api } from '@namcbugdb/react-common'
import { Attribute } from '@namcbugdb/react-common'

export const fetchAttributesQuery = gql`
  query ($offset: Int) {
    attributes(limit: 1000, offset: $offset) {
      records {
        attributeId
        attributeName
      }
      nextOffset
    }
  }
`

export const transformAttribute = ({ attributeId: id, attributeName: name, ...rest }: GraphAttribute): Attribute => ({
  id,
  name,
  ...rest,
})

export const fetchAttributes = (api: Api) => async (): Promise<Attribute[]> => {
  const data = await api.queryAllPages<{ attributes: PaginatedRecords<GraphAttribute> }>(
    fetchAttributesQuery,
    {},
    { dataProperty: 'attributes' }
  )

  return data.attributes.records.map(transformAttribute)
}
