import gql from 'graphql-tag'

import { Api, ApiError } from '@namcbugdb/react-common'
import { InvalidateFn } from '../selectors'

export interface PredictorInput {
  abbreviation: string
  calculation: string
  calculationScript: string
  cellRepresents: string
  cellSize: string
  description: string
  geometryFilePath: string
  inputType: string
  isGee: string
  isTemporal: boolean
  isText: string
  maxValue: number | null
  minValue: number | null
  name: string // # -> predictorName
  notes: string
  smallShedException: string
  source: string
  transformation: string
  typeId: number // # -> predictorTypeId
  units: string
}

export const createPredictorMutation = gql`
  mutation ($predictor: PredictorInput!) {
    createPredictor(predictor: $predictor)
  }
`

export const createPredictor =
  (api: Api, invalidate: InvalidateFn) =>
  async (predictor: PredictorInput): Promise<number> => {
    const { name: predictorName, typeId: predictorTypeId, ...restPredictor } = predictor
    const data = await api.mutation<{
      createPredictor: number | null // NOTE: new predictor ID
    }>(createPredictorMutation, { predictor: { predictorName, predictorTypeId, ...restPredictor } })

    const id = data.createPredictor
    if (!id) throw new ApiError('predictor did not create')

    invalidate('predictors')
    return id
  }
