import React from 'react'
import { useRecoilValue } from 'recoil'

import * as selectors from '../../data/selectors'
import { withSuspense } from '@namcbugdb/react-common'
import { ModelDetail as Component } from './ModelDetail'
import { Fallback } from './Fallback'

export type ModelDetailProps =
  | {
      modelId: string | number
      isNew?: never
    }
  | {
      modelId?: never
      isNew: true // unconsumed; used to positively differentiate modes
    }

export const ModelDetail = withSuspense(({ modelId }: ModelDetailProps): JSX.Element => {
  const model = useRecoilValue(modelId ? selectors.selectModelById(Number(modelId)) : selectors.selectNull)

  if (modelId && !model) return <h2>Model {modelId} not found.</h2>

  return <Component model={model} />
}, Fallback)
