import gql from 'graphql-tag'
import { Metric as GraphMetric } from '@namcbugdb/common'

import { Api, deNull } from '@namcbugdb/react-common'
import { Metric } from '@namcbugdb/react-common'

export const fetchMetricsQuery = gql`
  query ($offset: Int!) {
    metrics(offset: $offset, limit: 1000) {
      records {
        metricId
        metricName
        abbreviation
        metricGroupId
        metricGroupName
        codeFunction
        isActive
        reportIds
        displayText
        description
        calculationId
        taxonomyIds
        attributeId
        attributeValues
        rarefactionTypeId
      }
      nextOffset
    }
  }
`

export const fetchMetrics = (api: Api) => async (): Promise<Metric[]> => {
  const data = await api.queryAllPages<{ metrics: { records: GraphMetric[] } }>(
    fetchMetricsQuery,
    {},
    { dataProperty: 'metrics' }
  )

  return data.metrics.records.map(
    ({ metricId: id, metricName: name, metricGroupName: groupName, metricGroupId: groupId, ...rest }) =>
      deNull(
        {
          id,
          name,
          groupName,
          groupId,
          ...rest,
        },
        {
          abbreviation: '',
          description: '',
          displayText: '',
          reportIds: [],
          taxonomyIds: [],
        }
      )
  )
}
